<template>
  <div class="course">
    <h2></h2>
    <div class="tab1">
      <div class="left">
        <span class="em"></span>
        <span
          class="all"
          @click="goall"
          :class="{'act':isshow}"
        ></span>
      </div>
      <ul class="right">
        <li
          v-for="(item,index) in menuList"
          :key="index"
          @click="change(index,item.id)"
          :class="activeClass == index ? 'active':''"
        >{{item.name}}</li>
      </ul>
    </div>
    <div class="tab2">
      <div class="left">
        <span class="em"></span>
        <span
          class="all"
          @click="toall"
          :class="{'tiv':isshow1}"
        ></span>
      </div>
      <ul class="right">
        <li
          v-for="(item,index) in meList"
          :key="index"
          @click="changemol(index,item.id)"
          :class="activeIndex == index ? 'active':''"
        >{{item.name}}</li>
      </ul>
    </div>
    <p class="tab3">
      <span class="em"></span>
      <ul class="tag">
        <li
          @click="changeone()"
          :class="{active:show1}"
        ></li>
        <li
          @click="changetwo()"
          :class="{active:show2}"
        ></li>
        <li
          @click="changetre()"
          :class="{active:show3}"
        ></li>
      </ul>
      <input
        type="text"
        placeholder=""
        v-model=" keyword"
      />
      <button @click="search"></button>
    </p>
    <!-- <a-empty
      v-if="courseList.length==0"
      description=""
    /> -->
    <div class="notbox"  v-if="courseList.length==0">
      <img
        src="../assets/blank/notlist.png"
        alt=""
      >
      <p class="inp"></p>
    </div>
    <ul
      class="list"
      v-else
    >
      <li
        v-for="item in courseList"
        :key="item.id"
        @click="todetail(item.id)"
      >
        <img
          :src="item.cover"
          alt=""
        >
        <div class="detail">
          <p>{{item.title}}</p>
          <p>{{item.teacher}}
            <span v-if="item.isFree!=0"></span>
            <span
              v-else
              class="you"
            ></span>
          </p>
        </div>
      </li>
    </ul>
    <div class="page">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
    <!--  -->
    <div
      class="gohead"
      v-show="flaghead"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>

<script>
import { getcourseListApi, getcoursecommonApi } from "@/request/api";
import pagination from "@/components/common/pagination";
export default {
  name: "course",
  components: { pagination },
  data() {
    return {
      show3: "",
      show1: true,
      isshow: true,
      isshow1: true,
      activeClass: -1,
      activeIndex: -1,
      flaghead: false,
      show2: "",
      menuList: "",
      meList: [],
      nuList: ["", "", ""],
      courseList: "",
      keyword: "",
      Info: {
        page: 1,
        pageSize: 16,
        majorId: "",
        isFree: "",
        keyword: "",
      },
      pagination: { total: "", pageSize: 16 },
    };
  },
  created() {
    this.init();
    this.getMajorList();
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(getcourseListApi, this.Info, (res) => {
        this.courseList = res.data.rows;
        this.pagination.total = res.data.count;
      });
    },
    getMajorList() {
      this.getToken(getcoursecommonApi, this.Info, (res) => {
        this.menuList = res.data;
        // console.log(this.menuList)
        this.meList = [];
        res.data.forEach((item) => {
          item.children.forEach((v) => {
            this.meList.push(v);
          });
        });
      });
    },
    //
    goall() {
      this.isshow = true;
      this.activeClass = -1;
      this.Info.majorId = "";
      this.init();
      this.getMajorList();
    },
    //
     change(index, id) {
      this.activeClass = index;
      this.isshow = false;
      this.meList = this.menuList.filter((item) => item.id == id)[0].children;
      this.Info.majorId = id;
      this.init();
    },
    //
    toall() {
      this.isshow1 = true;
      this.activeIndex = -1;
      if(this.activeClass==-1){
        this.Info.majorId=''
      }else{
        this.Info.majorId=this.menuList[this.activeClass].id
      }
      this.init()
      // if(this.isshow){
      //   this.Info.majorId = "";
      //   this.init();
      //   this.getMajorList()
      // }else{
      //   // this.Info.majorId="this."
      //   this.init()
      // }
    },
    //
    changemol(index, id) {
      this.activeIndex = index;
      this.isshow1 = false;
      this.Info.majorId = id;
      this.init();
    },
    changeone() {
      this.show1 = true;
      this.show2 = false;
      this.show3 = false;

      this.Info.isFree = "";
      this.init();
    },
    changetwo() {
      this.show2 = true;
      this.show1 = false;
      this.show3 = false;

      this.Info.isFree = 1;
      this.init();
    },
    changetre() {
      this.show3 = true;
      this.show2 = false;
      this.show1 = false;

      this.Info.isFree = 0;
      this.init();
    },
    todetail(id) {
      this.$router.push({ path: "/courseInfo", query: { id } });
    },
    search() {
      this.Info.keyword = this.keyword;
      this.init();
    },
    changePage(currentPage) {
      this.Info.page = currentPage;
      this.init();
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flaghead = true;
      } else {
        this.flaghead = false;
      }
    },
  },
};
</script>

