<template>
  <div class="education industryDetail">
    <div class="header">
      <h2 class="title"><span>/ Industry Education Cooperation</span></h2>
      <div class="breadCrumb">
        <span @click="returnHome()"> ></span>
        <span @click="returnIndustry()"> ></span>
        <span></span>
      </div>
    </div>
    <detailCommon
      :info="info"
      :footerInfo="footerInfo"
      :returnText="''"
      @todetail="todetail"
      @returnback="returnIndustry()"
      :itemName="''"
    ></detailCommon>
    <!--  -->
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import detailCommon from "@/components/common/detailCommon";
import { getInstryNewsApi, getNewsListApi } from "@/request/api";
export default {
  name: "industryDetail",
  components: { detailCommon },
  created() {
    this.industryId = this.$route.query.id;
    this.init();
  },
  data() {
    return {
      industryId: null,
      info: {},
      footerInfo: {
        last: "",
        next: "",
      },
      flag: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(getInstryNewsApi, { newsId: this.industryId }, (res) => {
        this.info = { ...res.data, name: res.data.title };
      });
      this.getToken(getNewsListApi, {}, (res) => {
        let list = res.data.rows;
        let itemIndex;
        list.forEach((item, index) => {
          if (item.id == this.industryId) {
            itemIndex = index;
          }
        });
        if (itemIndex == 0) {
          this.footerInfo.last = "";
          this.footerInfo.next = list[itemIndex + 1];
          this.footerInfo.next.name = this.footerInfo.next.title;
        } else if (itemIndex == list.length - 1) {
          this.footerInfo.last = list[itemIndex - 1];
          this.footerInfo.last.name = this.footerInfo.last.title;
          this.footerInfo.next = "";
        } else {
          this.footerInfo.last = list[itemIndex - 1];
          this.footerInfo.next = list[itemIndex + 1];
          this.footerInfo.last.name = this.footerInfo.last.title;
          this.footerInfo.next.name = this.footerInfo.next.title;
        }
      });
    },
    returnHome() {
      this.$router.push({ path: "/" });
    },
    todetail(id) {
      this.industryId = id;
      this.init(id);
    },
    returnIndustry() {
      this.$router.push({ path: "/industry" });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>
<style lang="stylus" scoped>
.header {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  padding-right: 69px;
  h2 {
      bacground:pink;
      padding-top:30px;
      width: 600px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      padding-bottom: 50px;
      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }
  .breadCrumb {
      position: absolute;
      right: 69px;
      top: 40px;
      span{
        color:#333
      }
      span {
        cursor: pointer;
      }
      span:last-child{
        color:#999;
      }
  }
}

.gohead {
  width: 66px;
  height: 58px;
  background-color: #fff;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 240px;
  bottom: 50px;
  cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead1.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead1.png') -18px -15px no-repeat;
  }
}
</style>