<template>
  <div class="studyview">
    <div class="title">
      <p>
        
        <span class="ban">{{title}}</span>
        <span class="course">{{courseTitle}}</span>
      </p>
      <p class="back" @click="tomanClass">
        <img src="../../assets/mine/back.png" alt />
        
      </p>
    </div>
    <div class="sea">
      <span></span>
      <div class="filter">
        <input type="text" placeholder="" v-model="key" />
        <button class="ban" @click="searchbtn"></button>
        <download
        class="repbtn"
        :api="'personalCenter/exportStudyList'"
        :name="''"
        :fileName="'.xlsx'"
        :isPrimary="true"
        methods='post'
        :formData="{'classId':this.classId}"
      ></download>
        <!-- <button class="repbtn" @click="repbtn"></button> -->
      </div>
    </div>
    <div class="box">
      <a-table
        :columns="columns"
        :data-source="studentlist"
        :rowKey="record=>record.id"
        :locale="{'emptyText': ''}"
        bordered
      >
        <div slot="tar" slot-scope="index,record">
          <span>{{record.tar}}</span>
          <p>
            <span>{{record.finished}}</span>/
            <span>{{record.totalTask}}</span>
          </p>
        </div>
        <div slot="status" slot-scope="index,record">
          <span v-if="record.status==0"></span>
          <span v-else style="color:green"></span>
        </div>
      </a-table>
    </div>
    <!-- <div class="pagemodel">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>-->
  </div>
</template>

<script>
const columns = [
  {
    title: "",
    dataIndex: "index",
    width: 50,
  },
  {
    title: "",
    dataIndex: "account",
    width: 80,
  },
  {
    title: "",
    dataIndex: "name",
    width: 70,
  },
  {
    title: "",
    dataIndex: "sex",
    width: 60,
  },
  {
    title: "",
    dataIndex: "idnum",
  },
  {
    title: "",
    dataIndex: "phone",
    width: 60,
  },
  {
    title: "",
    dataIndex: "school",
    width: 80,
  },
  {
    title: "",
    dataIndex: "mojor",
    width: 80,
  },
  {
    title: "",
    scopedSlots: { customRender: "tar" },
    width: 80,
  },
  {
    title: "",
    dataIndex: "thery",
    width: 50,
  },
  {
    title: "",
    dataIndex: "practice",
    width: 50,
  },
  {
    title: "",
    scopedSlots: { customRender: "status" },
    width: 50,
  },
];
import { getstudytscoreApi,exportStudyListApi} from "@/request/api";
// import eform from "@/components/common/form"
// import pagination from "@/components/common/pagination";
import download from "@/components/common/download"
export default {
  name: "studyview",
  components:{download},
  data() {
    return {
      columns,
      pagination: { total: 0, pageSize: 10 },
      key: "",
      title: "",
      courseTitle: "",
      classId: "",
      searchInfo: {
        classId: "",
        keyword: "",
        page: 1,
        pageSize: 10,
      },
      studentlist: [],
    };
  },
  created() {
    this.classId = this.$route.query.id;
    this.init();
  },
  methods: {
    init() {
      this.searchInfo.classId = this.classId;
      this.getToken(getstudytscoreApi, this.searchInfo, (res) => {
        this.title = res.data.title;
        this.courseTitle = res.data.courseTitle;
        this.studentlist = res.data.Student.map((item, index) => ({
          index: (this.searchInfo.page - 1) * 10 + index + 1,
          account: item.account,
          name: item.realname,
          sex: item.gender,
          idnum: item.idCardNum,
          phone: item.telephone,
          school: item.instituteName,
          mojor: item.major,
          tar: item.finishedPercentage,
          finished: item.finished,
          totalTask: item.totalTask,
          thery: item.theoryScore,
          practice: item.operationScore,
          status: item.passed || 0,
        }));
      });
    },
    tomanClass() {
      this.$router.push({ path: "/manClass" });
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    searchbtn() {
      this.searchInfo.keyword = this.key;
      this.init();
    },
    repbtn(){
      this.getToken(exportStudyListApi,{},res=>{
        console.log(res)
      })
    }
  },
};
</script>
<style >
.studyview .ant-table table {
  text-align: center;
}
.studyview thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  text-align: center;
  font-weight: 600;
  font-size: 16px;
}
.studyview .ant-table-bordered .ant-table-thead > tr > th,
.studyview .ant-table-bordered .ant-table-tbody > tr > td {
  border-right: 1px solid #e8e8e8;
  text-align: center;
}
.studyview .ant-table-thead > tr > th {
    color:#333;
    font-weight: 600;
    font-size: 14px;
    /* background-color: #eaeef7; */
    height:30px;  
}
.studyview .ant-table-bordered .ant-table-tbody{
  background:#fff;
}
.studyview .ant-table-pagination.ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
}
.studyview .ant-table-thead > tr > th, .ant-table-tbody > tr > td {
    padding: 10px 10px ;
}
.studyview .ant-table-pagination.ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
  /* display: none; */
}
.studyview .ant-table-thead > tr > th,
.ant-table-tbody > tr > td {
  padding: 8px 8px;
}
</style>
<style lang="stylus" scoped>
.studyview {
  margin-bottom: 100px;

  .title {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-bottom: 1px solid #eff2f2;
    display: flex;
    align-items: center;
    justify-content: space-between;

    p {
      height: 45px;
      line-height: 45px;
      margin-left: 30px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
      margin-bottom: 0px;

      .ban {
        color: #c56b2f;
        font-size: 14px;
        margin-left: 20px;
      }
      

      .course {
        color: #c56b2f;
        font-size: 14px;
        margin-left: 10px;
      }
    }

    .back {
      color: #4281ff;
      width: 50px;
      height: 30px;
      font-size: 14px;
      margin-right: 20px;
      line-height: 30px;
      cursor: pointer;

      img {
        width: 15px;
        height: 15px;
        margin-top: -3px;
      }
    }
  }

  .sea {
    height: 45px;
    margin-left: 30px;
    margin-top: 20px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    position: relative;

    span {
      color: #333;
      font-size: 16px;
      flex: 1;
    }

    .filter {
      margin-right: 30px;

      input {
        outline: none;
        width: 220px;
        height: 35px;
        border: solid 1px #d4d4d4;
        text-indent: 8px;
      }

      .ban {
        outline: none;
        width: 60px;
        height: 35px;
        background-color: #4281ff;
        color: #ffffff;
        font-size: 14px;
        cursor: pointer;
        border: 0;
      }
      .repbtn {
        outline: none;
        width: 75px;
        height: 35px;
        background-color: #ff904f;
        color: #ffffff;
        font-size: 14px;
        cursor: pointer;
        border: 0;
        margin-left:20px;
        border-radius:3px;
      }
    }
  }

   .box {
    margin-top:30px;
    margin-left:30px;
    margin-right: 30px;
  }
}

.pagemodel {
  margin-top: 30px;
  padding-top: 30px;
}
</style>
