import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/components/layout'

//
import teacherLecture from '@/components/teacherLecture'
import teacherLectureDetail from '@/components/teacherLectureDetail'

//
import training from '@/components/training'

//
import register from '@/components/register'

//
import traincenter from '@/components/traincenter'

//
import formation from '@/components/formation'
import company from '@/components/company'
import companyDetail from '@/components/companyDetail'

//
import home from '@/components/home'
import mine from '@/components/mine'
import forgetModal from "@/components/forgetModal"

//
import education from "@/components/education"
import introduction from "@/components/introduction"
import alliance from "@/components/alliance"
import allianceDetail from "@/components/allianceDetail"
import talent from "@/components/talent"
import industry from "@/components/industry"
import industryDetail from "@/components/industryDetail"
import quality from "@/components/quality"

//1+X
import Service from "@/components/Service"
import ServiceDetail from "@/components/ServiceDetail"
import examDetail from "@/components/examDetail"

//
import teachingCourse from "@/components/teachingCourse"
import course from "@/components/course"
import courseDetail from "@/components/courseDetail"
import courseInfo from "@/components/courseInfo"
import catalog from "@/components/catalog"
import catalogDetail from "@/components/catalogDetail"
import test from "@/components/test"
import theoryexam from "@/components/theoryexam"
import prexam from "@/components/prexam"
import answer from "@/components/answer"
import answerDetail from "@/components/answerDetail"
import answerNew from "@/components/answerNew"
import cultivate from "@/components/cultivate"
import cultivateDetail from "@/components/cultivateDetail"

//
import basicInfo from "@/components/mine/basicInfo"
import mysubject from "@/components/mine/mysubject"
import myClass from "@/components/mine/myClass"
import manClass from "@/components/mine/manClass"
import manstudent from "@/components/mine/manstudent"
import studyview from "@/components/mine/studyview"
import myviedo from "@/components/mine/myviedo"
import trainexam from "@/components/mine/trainexam"
import myscore from "@/components/mine/myscore"
import myPurge from "@/components/mine/myPurge"
import myask from "@/components/mine/myask"
import myreply from "@/components/mine/myreply"
import message from "@/components/mine/message"
import myexam from "@/components/mine/myexam"
import invlinexam from "@/components/mine/invlinexam"
import mytrain from "@/components/mine/mytrain"
import invlintrain from "@/components/mine/invlintrain"
import mystudent from "@/components/mine/mystudent"

//
import basicInfos from "@/components/mine/basicInfos"
import Site from "@/components/mine/Site"
import require from "@/components/mine/require"
import examplan from "@/components/mine/examplan"
import mark from "@/components/mine/mark"
import examine from "@/components/mine/examine"
import newexamplan from "@/components/mine/newexamplan"
import report from "@/components/mine/report"
import audit from "@/components/mine/audit"
import newrequire from "@/components/mine/newrequire"
import Delivery from "@/components/mine/Delivery"

//
import  NotFound from  "@/components/NotFound.vue";

import Antd from 'ant-design-vue/dist/antd.css';
import store from "@/store/index"
Vue.use(Router)
Vue.use(Antd);

export default new Router({
  mode: 'history',
  base: '/huashuWeb',
  routes: [
    {
      path: '/',
      component: layout,
      children: [{
        path: '/',
        component: home      //
      }, {
        path: '/teacherLecture',         //
        component: teacherLecture
      }, {
        path: '/teacherLectureDetail',   //
        component: teacherLectureDetail
      }, {
        path: '/training',               //
        component: training
      }, {
        path: '/register',               //
        component: register
      }, {
        path: '/traincenter',              //
        component: traincenter,
      }, {
        // path: "/formation",              //
        path: '/',
        component: formation,
        children: [
          {
            path: "/company",
            component: company
          },
          {
            path: "/companyDetail",
            component: companyDetail
          },
        ]
      }, {
        // path: '/education',
        path: '/',
        component: education,      //
        children: [
          {
            path: "/introduction",
            component: introduction
          }, {
            path: "/alliance",
            component: alliance
          }, {
            path: "/allianceDetail",
            component: allianceDetail
          }, {
            path: "/talent",
            component: talent
          }, {
            path: "/industry",
            component: industry
          }, {
            path: "/industryDetail",
            component: industryDetail
          }, {
            path: "/quality",
            component: quality
          },
        ]
      }, {
        // path: '/teachingCourse',
        path: '/',
        component: teachingCourse,      //
        children: [
          {
            path: "/course",
            component: course
          }, {
            path: "/cultivate",
            component: cultivate
          }
        ]
      }, {
        // path: '/courseDetail',
        path: "/",
        component: courseDetail,      //
        children: [
          {
            path: "/courseInfo",
            component: courseInfo
          }, {
            path: "/catalog",
            component: catalog
          }, {
            path: "/answer",
            component: answer,
          }, {
            path: '/answerDetail',
            component: answerDetail
          }, {
            path: '/answerNew',
            component: answerNew
          }
        ]
      }, {
        path: "/cultivateDetail",
        component: cultivateDetail,
      }, {
        path: "/catalogDetail",
        component: catalogDetail,
      }, {
        path: "/test",
        component: test
      }, {
        path: "/theoryexam",
        component: theoryexam
      }, {
        path: "/prexam",
        component: prexam
      }, {
        // path: '/mine',
        path: '/',
        component: mine,
        meta:{requireAuth:true},
        //
        beforeEnter(to,from,next){
          if(localStorage.getItem("token")){
            next()
          }else{
            next({
              path: "/",
              query: {redirect: to.meta.redirect} 
            });
          }
        },
        children: [
          {
            path: '/basicInfo',
            component: basicInfo,
            meta:{requireAuth:true},
          },
          {
            path: '/basicInfos',
            component: basicInfos,
            meta:{requireAuth:true},
          },
          {
            path: '/mysubject',
            component: mysubject,
            meta:{requireAuth:true},
          },
          {
            path: '/myClass',
            component: myClass,
            meta:{requireAuth:true},
          }, {
            path: '/manClass',
            component: manClass,
            meta:{requireAuth:true},
          }
          , {
            path: '/manstudent',
            component: manstudent,
            meta:{requireAuth:true},
          },
          {
            path: '/studyview',
            component: studyview,
            meta:{requireAuth:true},
          },
          {
            path: '/myviedo',
            component: myviedo,
            meta:{requireAuth:true},
          },
          {
            path: '/trainexam',
            component: trainexam,
            meta:{requireAuth:true},
          },
          {
            path: '/myscore',
            component: myscore,
            meta:{requireAuth:true},
          },
          {
            path: '/myPurge',
            component: myPurge,
            meta:{requireAuth:true},
          },
          {
            path: '/myask',
            component: myask,
            meta:{requireAuth:true},
          },{
            path: '/myreply',
            component: myreply,
            meta:{requireAuth:true},
          },
          {
            path: '/message',
            component: message,
            meta:{requireAuth:true},
          },
          {
            path: '/mytrain',
            component: mytrain,
            meta:{requireAuth:true},
          },{
            path: '/invlintrain',
            component: invlintrain,
            meta:{requireAuth:true},
          },
          {
            path: '/myexam',
            component: myexam,
            meta:{requireAuth:true},
          },{
            path: '/invlinexam',
            component: invlinexam,
            meta:{requireAuth:true},
          },
          {
            path: '/mystudent',
            component: mystudent,
            meta:{requireAuth:true},
          },{
            path: '/Site',
            component: Site,
            meta:{requireAuth:true},
          },{
            path: '/require',
            component: require,
            meta:{requireAuth:true},
          },{
            path: '/examine',
            component: examine,
            meta:{requireAuth:true},
          },{
            path: '/examplan',
            component: examplan,
            meta:{requireAuth:true},
          },{
            path: '/mark',
            component: mark,
            meta:{requireAuth:true},
          },{
            path: '/newexamplan',
            component: newexamplan,
            meta:{requireAuth:true},
          },{
            path: '/report',
            component: report,
            meta:{requireAuth:true},
          },{
            path:'/audit',
            component:audit,
            meta:{requireAuth:true},
          },{
            path:'/newrequire',
            component:newrequire,
            meta:{requireAuth:true},
          },{
            path:'/Delivery',
            component:Delivery,
            meta:{requireAuth:true},
          }
        ]
      }, {
        path: '/forgetModal',
        component: forgetModal
      }, {
        path: '/Service',         //1+X
        component: Service
      },{
        path: '/ServiceDetail',         //1+X
        component: ServiceDetail
      },
      {
        path: '/examDetail',         //1+X
        component: examDetail
      }, 
      {
        path:"*",
        component:NotFound
      }

      ]


    }
  ],
  scrollBehavior() {
    return { x: 0, y: 0 }
  },
  // 
  //  router.beforeEach 
  beforeEach(to, from, next) {
    if(store.state.isLogin==1){
      next("/")
    }else{
      next()
    }
  },

})
