<template>
  <div id="education">
    <template v-if="bannerList.length>0">
      <div class="banner" v-for="item in bannerList" :key="item.id">
        <a :href="item.link">
            <img :src="item.img" alt="">
        </a>
      </div>
    </template>
    <div class="banner" v-else>
      <!-- <img
        src="../assets/banner/cjrh_banner.png"
        alt=""
      > -->
      <p>banner</p>
    </div>
    
    <div class="head">
      <div class="header">
        <ul class="nav">
          <li
            v-for="(item,index) in menuList"
            :key="index"
            @click="changPage(item)"
            :class="{'active':current==item}"
          >{{item}}<em></em></li>
        </ul>
      </div>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import {getbannerApi} from "@/request/api"
export default {
  name: "education",
  data() {
    return {
      menuList: ["", "", "", "", ""],
      current: "",
      currentIndex: {},
      id: null,
      info: {},
      bannerList:[]
    };
  },
  watch:{
    $route(to) {
      let arr = to.path.split("/");
      switch (arr[arr.length - 1]) {
      case "introduction":
        this.current = "";
        break;
      case "alliance":
        this.current = "";
        break;
      case "allianceDetail":
        this.current = "";
        break;
      case "talent":
        this.current = "";
        break;
      case "industry":
        this.current = "";
        break;
      case "quality":
        this.current = "";
        break;
    }
    }
  },
  created(){
     this.init()
  },
  mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "introduction":
        this.current = "";
        break;
      case "alliance":
        this.current = "";
        break;
      case "allianceDetail":
        this.current = "";
        break;
      case "talent":
        this.current = "";
        break;
      case "industry":
        this.current = "";
        break;
      case "quality":
        this.current = "";
        break;
    }
  },
  methods: {
    init(){
      this.getToken(getbannerApi,{module:8},res=>{
                this.bannerList=res.data
            })
    },
    changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/introduction";
          break;
        case "":
          path = "/alliance";
          break;
        case "":
          path = "/talent";
          break;
        case "":
          path = "/industry";
          break;
        case "":
          path = "/quality";
          break;
      }
      this.$router.push({ path: path });
    },
  },
};
</script>

<style lang="stylus" scoped>
#education {
  background: #eee;

  .banner {
    height: 261px;
    margin:0 auto;
    p{
        color:#333;
        font-size:20px;
        width:100%;
        height:100%;
        display:flex;
        align-items center;
        justify-content center;
        background:#d5d5d5;
        text-align center;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }

  .head {
    height: 66px;
    background: #fff;
    line-height: 66px;
    box-shadow: 0px 0px 10px 0px rgba(93, 93, 93, 0.1);

    .header {
      width: 1205px;
      margin: auto;
      height: 73px;
      border-bottom: 1px solid #000;
    }

    .nav {
      hegint: 73px;
      width: 900px;
      list-style: none;
      display: flex;

      li {
        display: inline;
        float: left;
        width: 200px;
        height: 66px;
        text-align: center;
        font-family: MicrosoftYaHei;
        font-size: 18px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #333333;
        position: relative;
        cursor: pointer;
      }

      .active {
        background-color: #4182fa;
        color: #fff;

        em {
          position: absolute;
          left: 50%;
          bottom: -10px;
          font-size: 30px;
          width: 0px;
          height: 0px;
          border: 5px solid transparent;
          border-top: 5px solid #4182fa;
          z-index: 999;
        }
      }
    }
  }

  .content {
    width: 1205px;
    margin: 0 auto;
    background: #fff;
    border-top: 1px solid #eee;
    // h1
    // width: 500px;
    // height: 24px;
    // font-family: MicrosoftYaHei;
    // font-size: 26px;
    // font-weight: normal;
    // font-stretch: normal;
    // letter-spacing: 0px;
    // color: #333333;
    // padding-left:69px;
    // padding-top:33px;
    // margin-bottom:50px;
    // dispaly:none;
    // span
    // width: 132px;
    // height: 16px;
    // font-family: MicrosoftYaHei;
    // font-size: 16px;
    // font-weight: normal;
    // font-stretch: normal;
    // line-height: 40px;
    // letter-spacing: 0px;
    // color: #999999;
    // padding-left:8px;
  }
}
</style>