<template>
    <div class="newrequire">
        <div class="title">
            <p></p>
            <p class="back" @click="torequire">
                <img src="../../assets/mine/back.png" alt="">
                
            </p>
        </div>
        <div class="box">
            <eform
            :formData="formData"
            class="examform"
            ref="form"
            >
                <div slot="jobname">
                    <a-input
                        v-decorator="['jobname',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobsort">
                    <a-input
                        v-decorator="['jobsort',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobnum">
                    <a-input
                        v-decorator="['jobnum',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobask">
                    <a-select
                        class="selbtn"
                        allow-clear
                        placeholder=""
                        :notFoundContent="''"
                        @change="Selectjob"
                        v-decorator="['jobask',{ rules: [{ required: true, message: '' }] }]"
                        >
                        <a-select-option v-for="(item,index) in jobList" :value="item" :key="index">
                            {{item}}
                        </a-select-option>
                    </a-select>
                </div>
                <div slot="jobmoney">
                    <a-input
                        v-decorator="['jobmoney',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobaddress">
                    <Taddress 
                     v-decorator="['jobaddress',{ rules: [{ required: true, message: '' }] }]"
                    @changeAddress="changeAddress"
                    class='jobbtn'
                    ></Taddress>
                </div>
                <div slot="jobinfo">
                    <a-textarea
                        class="infobox" 
                        v-decorator="['jobinfo',{ rules: [{ required: true, message: '' }] }]"
                        placeholder=""
                    />
                </div>
            </eform>
            <p class="allbtn">
                <!-- <a-button class="delbtn"></a-button> -->
                <a-button class="surebtn" @click="surebtn"></a-button>
            </p>
        </div>
    </div>
</template>

<script>
import {addjobListApi} from "@/request/api"
import eform from "@/components/common/form";
import Taddress from "@/components/common/Taddress";
export default {
  name: 'newrequire',
  components:{eform,Taddress},
  data(){
      const formData = [
      {
        label: "",
        scopedSlots: { customRender: "jobname" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobsort" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobask" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobmoney" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobaddress" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobinfo" },
      },
    ];
     return{
         formData,
         newaddress:'',
         province:'',
         city:'',
         district:'',
         jobList:[ "","","","","","","","",""]
     }
  },
  methods:{
    //
    torequire(){
        this.$router.push({path:"/require"})
    },
    //
    changeAddress(data){
       this.newaddress=data.join('')
       this.$refs.form.form.setFieldsValue({["jobaddress"]:data.join('')})
       this.province=data[0]
       this.city=data[1]
       this.district=data[2]
    },
    //
    Selectjob(data){
        console.log(data)
    },
    //
    surebtn(){
        this.$refs.form.form.validateFields().then((res) => {
            this.getToken(addjobListApi,{
                title:res.jobname,
                type:res.jobsort,
                education:res.jobask,
                needNum:res.jobnum,
                address:res.jobaddress,
                content:res.jobinfo,
                salary:res.jobmoney,
                province:this.province,
                city:this.city,
                district:this.district

            },val=>{
                
                if(val.code==200){
                    this.$message.success("")
                    this.$router.push({path:"/require"})
                }
            })
        })
    }
  }

}
</script>
<style>
.examform .ant-form-item-label label {
    color: #333;
    font-size:16px;
}
.examform .ant-input {
    font-size: 15px;
    border-radius: 0px;
    height:40px;
}
.examform .ant-calendar-picker{
    width:100%;
}
.examform .infobox{
    min-height:220px;
    width:100%;
    outline: none;
}
.examform .jobbtn{
    width:100%;
}
</style>
<style lang="stylus" scoped>
.newrequire{
    margin-bottom: 50px;
    .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      display:flex;
      align-items center;
      justify-content space-between;
        p {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            margin-bottom:0px;
            .ban{
            color: #c56b2f;
            font-size:14px;
            margin-left:20px;
            }
        }
        .back {
            color: #4281ff;
            width: 50px;
            height: 30px;
            font-size:14px;
            margin-right:20px;
            line-height: 30px;
            cursor: pointer;
            img{
                width:15px;
                height:15px;
                margin-top:-3px;
            }
        }
    }
    .box{
        margin-top:40px;
        .allbtn{
            text-align center ;
            margin-top:40px;
            .surebtn{
                width: 101px;
                height: 38px;
                background-color: #4182fa;
                border-radius: 3px;
                border:0;
                color: #4182fa;
                font-size:16px;
                color:#fff;
            }
        }
    }
}
</style>
