<template>
  <div class="commonDetail">
    <div class="h1" >
      <h2 :title="info.title">{{info.title}}</h2>
    </div>
    <div class="content">
      <p class="shwotime">
        {{info.startTime}}~{{info.endTime}}
      </p>
      <p class="showtime">
        {{info.activityStart}}~{{info.activityEnd}}
      </p>
      <!-- <p>{{info.title}}</p> -->
      <template v-if="info.info">
        <div
          class="info ql-editor"
          v-html="info.info"
        >
      </div>
      <ul class="fujianbox" v-if="info.attachFile">
        <li v-for="(item,index) in info.attachFile" :key="index">
          <a class="fujanbox"  :href="item.path"  target="_blank">
            <!-- <a-icon type="file-pdf" style="font-size:50px;" /> -->
            <div class="box">
              <div class="boxModel">
                <img src="../../assets/serve/icon.png" alt="">
                <span></span>
              </div>
              <span class="filebox" :title="item.filename">{{item.filename}}</span>
            </div>
          </a>
        </li>
      </ul>
      </template>
      <div class="notbox"  v-else>
        <img
          src="../../assets/blank/notlist.png"
          alt=""
        >
        <p class="inp"></p>
      </div>
    </div>
    <div
      class="footer"
      v-if="isfooter"
    >
      <div class="left">
        <p
          v-if="footerInfo.last"
          @click="$emit('todetail',footerInfo.last.id)"
        >{{itemName}}{{footerInfo.last.name}}</p>
        <p
          v-if="footerInfo.next"
          @click="$emit('todetail',footerInfo.next.id)"
        >{{itemName}}{{footerInfo.next.name}}</p>
      </div>
      <div class="right">
        <div
          class="backbtn"
          @click="$emit('returnback',footerInfo.type)"
        >{{returnText}} 
        </div>
        <span></span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "commonDetail",
  props: {
    info: Object,
    footerInfo: Object,
    returnText: String,
    itemName: String,
    isfooter: { type: Boolean, default: true },
  },
};
</script>
<style lang="stylus" scoped>
.commonDetail {
  margin-top: 45px;

  .h1 {
    text-align: center;
    font-weight: normal;
    border-bottom: 1px solid #eee;
    padding-bottom: 45px;
    font-weight: 600;
    color: #000;
    width: 1065px;
    margin: 0 auto;
    margin-bottom: 15px;
    
    h2{
      margin:0 auto;
      font-size: 26px;
      overflow hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
      width:800px;
    }
  }

  .content{
    border-bottom: 1px solid #cecece;
    min-height:300px;
    .fujianbox{
      margin-top:40px;
      margin-bottom:40px
      li{
        margin-bottom:20px;
        height:40px;
        width:1065px;
        .fujanbox{
          color:#666;
          .box{
            display:flex;
            .boxModel{
              width: 75px;
              color:#4182fa
              height: 26px;
              background-color: #ffffff;
              border-radius: 3px;
              border: solid 1px #4182fa;
              display:flex;
              justify-content center;
              align-items center;
              img{
                margin-right:5px;
              }
            }
            .filebox{
              color:#666;
              font-size:14px;
              margin-left:8px;
              flex:1;
              overflow hidden;
              text-overflow:ellipsis;
              white-space: nowrap;  
              cursor pointer
            }
            .filebox:hover{
              color:#4182fa
            }
          }
        }
      }
    }
    .notbox{
      margin:0 auto;
      margin-bottom:30px;
      margin-top:40px;
      text-align center;
      img {
        display:block
        margin:0 auto;
      }
    .inp {
        font-size: 18px;
        color: #666;
        margin: 0 auto;
        text-align: center;
        margin-top: 10px;
      }
    }
  }

  .footer {
    min-height: 100px;
    display: flex;
    justify-content: space-between;
    height: 125px;
    align-items: center;

    .left {
      color: #666;

      p {
        cursor: pointer;

        &:hover {
          color: #4182fa;
        }
      }
    }

    .right {
      width: 162px;
      height: 46px;
      display: flex;
      align-items: center;
      justify-content center;
      background-color: #4182fa;
      border-radius: 3px;
      .backbtn {
        font-size: 16px;
        color:#fff;
        cursor pointer
      }
      span{
            background: url('../../assets/back.png') no-repeat;
            width: 15px;
            height: 13px;
            display:flex;
        }
    }
  }
}
</style>