<template>
  <div class="basicInfos">
    <div class="title">
      <h2></h2>
    </div>
    <div class="man">
        <div class="tit">
            <h2 v-if="role==''"></h2>
            <h2 v-else></h2>
        </div>
        <div class="box">
            <div class="left">
                <p v-if="role==''"><span></span>{{infoList.name}}</p>
                <p v-else><span></span>{{infoList.name}}</p>
                <p v-if="role==''"><span></span>{{infoList.account}}</p>
                <p v-else><span></span>{{infoList.account}}</p>
                <div class="telp">
                  <span></span>
                  <span v-if="infoList.telephone">{{infoList.telephone}}</span>
                  <template v-else>
                    <span></span>
                    <span @click="bindphone"></span>
                  </template>
                </div>
                <div class="ress">
                  <span></span>
                  <span v-if="infoList.address">{{infoList.address}}</span>
                  <span v-else></span>
                  <span @click="changeadd"></span>
                </div>
                <div
                    class="addinp"
                    v-if="addressshow"
                >
                    <eAddress @changeAddress="changeAddress"></eAddress>
                    <button
                    class="adsbtn"
                    @click="addsure"
                    ></button>
                </div>
            </div>
            <div class="right">
                <p v-if="role==''">logo</p>
                <p v-else>logo</p>
                <div class="pho">
                    <eUpload
                    :imageUrl="imageUrl"
                    class="loadpho"
                    @getUrl="getUrl"
                    ></eUpload>
                    <p>logo</p>
                </div>
            </div>
        </div>
        <div class="Profile">
            <div class="pro">
                <div class="infoleft"  style="width:80px" v-if="role==''"></div>
                <div class="infoleft" style="width:80px" v-else></div>
                <div class="infobox">
                    <template v-if="infoList.info">
                      <div :class="{'con ql-editor':true,'active':isopen}" v-html="infoList.info" style="white-space:wrap;"></div>
                      <p class="open" @click="changeopen"><span :class="{'active':isopen}" ></span></p>
                      <p class="rec"><span class="changebtn" @click="changeinfo"></span></p>
                    </template>
                    <template v-else>
                       <div class="nonum"><span></span><span @click="changeinfo"></span></div>
                    </template>
                </div>
            </div>
        </div>
        <p v-if="role==''" class="Pilot" >
          <span></span>
          <!-- 1   2    3    4  -->
          <span v-if="infoList.isTrialSite==1" style="color:#49af4f"></span>
          <span v-else-if="infoList.isTrialSite==2" style="color:#ee9537"></span>
          <span v-else-if="infoList.isTrialSite==3"  style="color:#ff0000"></span>
          <span v-else></span>
          <span v-if="infoList.isTrialSite==2||infoList.isTrialSite==3||infoList.isTrialSite==4" @click="Pilotbtn(infoList.isTrialSite)"></span>
          <span v-else style="color:#333"></span>
        </p>
        <p class="unit">
          <span></span>
          <span v-if="infoList.isAlliance==1" style="color:#49af4f"></span>
          <span v-else-if="infoList.isAlliance==2" style="color:#ee9537"></span>
          <span v-else-if="infoList.isAlliance==3" style="color:#ff0000"></span>
          <span v-else></span>
          <span v-if="infoList.isTrialSite==null||infoList.isAlliance==2||infoList.isAlliance==3||infoList.isAlliance==4" @click="unitbtn(infoList.isAlliance)"></span>
          <span v-else style="color:#333"></span>
        </p>
    </div>
    <div class="safe">
        <div class="tit">
            <h2></h2>
        </div>
        <div class="box">
            <p><span></span><span>********</span><span @click="changepwd"></span></p>
            <p>
            <span></span>
            <span v-if="infoList.email">{{infoList.email}}</span>
            <span v-else></span>
            <span
                v-if="infoList.email"
                class="eamilbtn"
                @click="recEmail(infoList.email)"
            >
            </span>
            <span
                v-else
                class="eamilbtn"
                @click="changeEmail"
            >
            </span>
            </p>
        </div>
    </div>
    <a-modal
      title=""
      :visible="changeinfoshow"
      @ok="infoOk"
      @cancel="infocancel()"
      okText=""
      cancelText=""
      class="infomodal"
    >
      <div class="infobox">
        <eQuill
            class="quillbox"
            @getQuillContent="getQuillContent"
            v-decorator="['content',{rules: [{ required: true, message: '' }]}]"
            :defaultData="quillInfo"    
        ></eQuill>
      </div>
    </a-modal>
    <a-modal
        :title="type==0?'':''"
        :visible="Pilotshow"
        @ok="PilotOk"
        @cancel="Pilotcancel()"
        :okText="infoList.logo&&infoList.info?'':''"
        cancelText=""
        class="Pilotmodal"
    >
        <div class="box">
            <div class="upper">
                <p class="unitname"><span></span><span>{{infoList.name}}</span></p>
                <p class="unitattr"><span></span>
                  <span>{{infoList.role}}</span>
                </p>
                <div class="unitlogo">
                    <span class="logotitle">logo</span>
                    <img v-if="infoList.logo" :src="imageUrl" alt="">
                    <template v-else>
                        <!-- <img v-if="infoList.lofo" :src="imageUrl" alt=""> -->
                        <span class="nologo"></span>
                    </template>
                </div>
            </div>
            <div class="under">
               <div class="conbox">
                    <span class="protitle"></span>
                    <div v-if="infoList.info" class="applycon ql-editor" placeholder="" v-html="infoList.info"></div>
                    <span v-else class="noinfo"></span>
               </div>
            </div> 
        </div>
    </a-modal>
    <a-modal
      title=""
      :visible="pwdshow"
      @ok="pwdOk"
      @cancel="pwdcancel()"
      okText=""
      cancelText=""
      class="pwdmodal"
    >
      <eform
        :formData="formPwd"
        class="form"
        ref="form"
      >
        <div slot="pwd">
          <a-input
            type="password"
            v-decorator="['pwd',{rules: [{ required: true, message: '' },{validator:validatorpwd}]}]"
            class="input"
          />
        </div>
        <div slot="newpwd">
          <a-input
            type="password"
            v-decorator="['newpwd',{rules: [{ required: true, message: '' },{validator:validatornewpwd}]}]"
            class="input"
          />
        </div>
        <div slot="renewpwd">
          <a-input
            type="password"
            v-decorator="['renewpwd',{rules: [{ required: true, message: '' },{validator:validatorrenewpwd}]}]"
            class="input"
          />
        </div>
      </eform>
    </a-modal>
    <a-modal
      title=""
      :visible="emailshow"
      @ok="emailOk"
      @cancel="emailcancel()"
      okText=""
      cancelText=""
      class="emailmodal"
      :destroyOnClose='true'
    >
      <eform
        :formData="formEmail"
        class="form"
        ref="form"
        :fields="fields"
      >
        <div slot="email">
          <a-input
            v-decorator="['email',{rules: [{ required: true, message: '' },{validator:validatoremail}]}]"
            class="input"
            placeholder=""
          />
        </div>
        <div slot="reemail">
          <a-input
            v-decorator="['reemail',{rules: [{ required: true, message: '' },{validator:validatorreemail}]}]"
            class="input"
            placeholder=""
          />
        </div>
      </eform>
    </a-modal>
    <a-modal
      title=""
      :visible="phoneshow"
      @ok="phoneOk"
      @cancel="phonecancel"
      okText=""
      cancelText=""
      class="phonemodal"
    >
       <div class="formbox">
        <eform
             :formData="formtel"
              class="form"
              ref="form"
          >
              <div slot="binphone">
                <a-input
                  v-decorator="['binphone',
                  {rules: [{ required: true, message: ' ' },{validator:validatorphone}]}]"
                  class="input"
                  placeholder=""
                />
              </div>
              <div slot=bincode  class="iteminp">
                  <a-input
                    class="cadeinp"
                    v-decorator="['bincode',
                      { rules: [{ required: true, message: ' ' },{validator:validatorcaptcha}] },]"
                    placeholder=""
                  />
                <a-button v-if="show" class="subbtn" @click="gocade">
                  <span></span>
                </a-button>
                <a-button v-if="!show" class="subbtn">
                  <span class="count">{{count}}s</span>
                </a-button>
              </div>
          </eform>
       </div>
    </a-modal>
  </div>
</template>

<script>
import {getinstituteCenterApi,getupdateInfo,bingphomeApi, getupdatepwd,checkPhoneApi,getcodeApi,trialApplyApi,allianceApplyApi} from "@/request/api"
import eUpload from "@/components/common/eUpload";
import eQuill from "@/components/common/eQuill"
import eform from "@/components/common/form";
import eAddress from "@/components/common/eAddress";
export default {
  name: '',
  components: { eUpload, eAddress,eform,eQuill},
  data(){
     const formtel=[
      {
          label: "",
          scopedSlots: { customRender: "binphone" },
      },{
          label: "",
          scopedSlots: { customRender: "bincode" },
      }
    ]
    let validatorphone = (rule, value, callback) => {
      var regphone = /^1[3456789]\d{9}$/;
      if (regphone.test(value)) {
          this.phone = value;
          callback()
      } else {
        callback(new Error(""));
      }
    };
    let validatorcaptcha = (rule, value,callback) => {
      if(value==""||value.trim()){
          this.captcha = value;
          callback()
      }else{
        callback(new Error(""))
      }
      
    };
       const formPwd = [
      {
        label: "",
        scopedSlots: { customRender: "pwd" },
        
      },
      {
        label: "",
        scopedSlots: { customRender: "newpwd" },
        type: "password",
      },
      {
        label: "",
        scopedSlots: { customRender: "renewpwd" },
        type: "password",
      },
    ];
    let validatorpwd = (rule, value, callback) => {
      let reg = /^[\w$]{8,16}$/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error("816"));
      }
    };
    let validatornewpwd = (rule, value, callback) => {
      let reg = /^[\w$]{8,16}$/;
      if (value == "" || reg.test(value)) {
        this.newpwd = value;
        callback();
      } else {
        callback(new Error("816"));
      }
    };
    let validatorrenewpwd = (rule, value, callback) => {
      this.renewpwd=value
      if (value == "" || value == this.newpwd) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    const formEmail = [
      {
        label: "",
        scopedSlots: { customRender: "email" },
      },
      {
        label: "",
        scopedSlots: { customRender: "reemail" },
      },
    ];
    let validatoremail = (rule, value, callback) => {
      let reg = /^[\w]\w{0,20}@(qq.com|126.com|163.com|QQ.com)$/;
      if (value==""||reg.test(value)) {
        this.email = value;
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorreemail = (rule, value, callback) => {
      if (value==""||value == this.email) {
        callback();           
      } else {
        callback(new Error(""));
      }
    };
      return{
        form: this.$form.createForm(this, { name: "horizontal_login" }),
        addressshow:false,
        imageUrl:require("../../assets/mine/logo.png"),
        quillInfo:"",
        Pilotshow:false,
        pwdshow: false,
        emailshow:false,
        formPwd,
        validatorpwd,
        validatornewpwd,
        validatorrenewpwd,
        formEmail,
        validatoremail,
        validatorreemail,
        isopen:false,
        type:'',
        changeinfoshow:false,
        telephone:"",
        phoneshow:false,
        validatorphone,
        count: "",
        timer: null,
        show:true,
        validatorcaptcha,
        formtel,
        infoList:[],
        infodata:'',
        phone:'',
        captcha:'',
        fields:'',
        quilldata:''
      }
  },
  computed: {
    role() {
      return this.$store.state.role;
    },
    icon() {
      return this.$store.state.icon;
    },
  },
  created(){
     this.init()
  },
  watch:{
    icon:function(){
      this.init()
    },
  },
  methods:{
    init(){
        this.getToken(getinstituteCenterApi,{},res=>{
          this.infoList=res.data
           this.idCardNum = res.data.idCardNum;
           if(res.data.logo){
             this.imageUrl = res.data.logo;
           }else{
             this.imageUrl = require("../../assets/mine/logo.png");
           }

        })
    },
     //
    getUrl(data) {
     this.imageUrl = data;
      this.getToken(getupdateInfo, { "logo": this.imageUrl }, () => {
        // console.log(this.imageUrl)
        // localStorage.setItem("icon",this.imageUrl );
        // this.$store.commit("changeIcon", {'icon':this.imageUrl})
        // this.init();
      });
    },
     //
    bindphone(){
      this.phoneshow=true
    },
    //
    phoneOk(){
       if(this.phone){
        if(this.captcha){
           this.getToken(bingphomeApi,{telephone:this.phone,captcha: this.captcha,},res=>{
            if(res.code==200){
              this.init()
              this.$message.success("")
              this.phoneshow=false
              this.$refs.form.form.resetFields();
            }
          })
        }else{
          this.$message.error("")
        }
          
      }else{
        this.$message.error("")
      }
    },
    //
    phonecancel(){
      this.$refs.form.form.resetFields();
      this.phoneshow=false
    },
    //
    gocade() {
      this.getToken(checkPhoneApi,{telephone:this.phone},res=>{
            if(res.isExist){
                  this.$message.error("")
              }else{
                  this.getToken(getcodeApi,{telephone:this.phone},res=>{
                  if (res.code == 200) {
                        this.$message.info("");
                        const TIME_COUNT = 60;
                        if (!this.timer) {
                          this.count = TIME_COUNT;
                          this.show = false;
                          this.timer = setInterval(() => {
                            if (this.count > 0 && this.count <= TIME_COUNT) {
                              this.count--;
                            } else {
                              this.show = true;
                              clearInterval(this.timer);
                              this.timer = null;
                            }
                          }, 1000);
                        }
                      }
                })
              }
          })
       
    },
      //
    changeadd() {
      this.addressshow = true;
    },
    //
    changeAddress(data){
         this.province = data[0];
          this.city = data[1];
          this.district = data.address;
          if (data.address) {
            this.teladdress = data.join("") + data.address;
          } else {
            this.teladdress = data.join("");
          }
    },
     //
    addsure(){
      if (this.teladdress) {
        this.getToken(
          getupdateInfo,
          {
            address: this.teladdress,
            province: this.province,
            city: this.city,
            district: this.district,
          },
          (res) => {
            if (res.code == 200) {
              this.$message.success("!");
              this.init();
            }
          }
        );
      } else {
        this.$message.error("");
      }
      this.addressshow = false;
    },
    //
    changeopen(){
      this.isopen=!this.isopen
    },
    //
    getQuillContent(data){
      this.quilldata=data
    },
    //
    changeinfo(){
      this.changeinfoshow=true
      this.quillInfo=this.infoList.info
    },
    //
    infoOk(){
          this.infoList.info=this.quilldata
          this.getToken(getupdateInfo,{"info":this.infoList.info},res=>{
              if (res.code == 200) {
                    this.$message.success("!");
                    this.init();
                    this.changeinfoshow=false
              }
          })
    },
    //
    infocancel(){
      this.changeinfoshow=false
    },
    //
    
    // <!-- 1   2    3    4  -->
    Pilotbtn(status){
      console.log(status)
      if(status==2){
         this.$message.info("")
      }else{
        this.type=0
        this.Pilotshow=true
      }
     
    },
    //
    unitbtn(status){
       if(status==2){
         this.$message.info("")
      }else{
        this.type=1
        this.Pilotshow=true
      }
    },
    //
    PilotOk(){
      //
      if(this.type==0){
        if(this.infoList.logo&&this.infoList.info){
           this.getToken(trialApplyApi,{},()=>{
            //  console.log(res)
            this.init()
           })
        }else{
          this.Pilotshow=false
        }
      }else{
        // 
        if(this.infoList.logo&&this.infoList.info){
          this.getToken(allianceApplyApi,{},()=>{
            // console.log(res)
            this.init()
          })
        }else{
          this.Pilotshow=false
        }
      }
        this.Pilotshow=false
    },
    //
    Pilotcancel(){
        this.Pilotshow=false
    },
    //
    changepwd(){
      this.pwdshow = true;
    },
    //
    pwdOk(){
        this.$refs.form.form.validateFields().then((res) => {
        if(res.newpwd===res.renewpwd){
                this.getToken(
                getupdatepwd,
                { pwd: res.pwd, newPwd: this.newpwd },
                (value) => {
                  console.log("value:",value)
                  localStorage.setItem("token", value.data.token);
                  this.init()
                  if (value.code == 200) {
                    this.pwdshow = false;
                    this.$message.success("!");
                    this.$refs.form.form.resetFields(["pwd"]);
                    this.$refs.form.form.resetFields(["newpwd"]);
                    this.$refs.form.form.resetFields(["renewpwd"]);
                    // this.init();
                  }
                }
              );
        }else{
          this.$message.error("")
        }
      })
    },
    //
    pwdcancel(){
        this.pwdshow = false;
    },
    //
    changeEmail() {
      this.emailshow = true;
    },
    //
    recEmail(data){
      this.emailshow = true;
      this.fields={email:data,reemail:data}
    },
    //
    emailcancel() {
      this.emailshow = false;
      this.$refs.form.form.resetFields(["email"]);
      this.$refs.form.form.resetFields(["reemail"]);
    },
    //
    emailOk() {
      this.$refs.form.form.validateFields().then((res) => {
        this.getToken(getupdateInfo, { email: res.email }, (res) => {
          if (res.code == 200) {
            this.init();
            this.$message.success("!");
            this.emailshow = false;
            this.$refs.form.form.resetFields(["email"]);
            this.$refs.form.form.resetFields(["reemail"]);
          }
        });
      });
    },
  }
}
</script>
<style >
.infomodal{
  width:700px !important;
}
.infomodal .quillbox{
  max-height:500px;
  overflow: auto;
}
.Pilotmodal{
    width:600px !important;
}
.basicInfos .ant-upload.ant-upload-select-picture-card {
  width: 115px;
  height: 100px;
  margin: 0;
  text-align: center;
  vertical-align: top;
  border: 0;
  cursor: pointer;
}
.Pilotmodal .ant-upload.ant-upload-select-picture-card {
  width:70px;
  height: 70px;
  margin: 0;
  text-align: center;
  vertical-align: top;
  border: 0;
  cursor: pointer;
}
.basicInfos .ant-upload.ant-upload-select-picture-card > .ant-upload,
.Pilotmodal .ant-upload.ant-upload-select-picture-card > .ant-upload  {
  display: table-cell;
  width: 100%;
  height: 100%;
  padding: 0;
  text-align: center;
  vertical-align: middle;
   border-radius: 0% !important;
}
.basicInfos .eUpload img,
.Pilotmodal .eUpload img {
  width: 100%;
  height: 100%;
  border-radius: 0% !important;
}
.basicInfos .loadpho {
  width: 100%;
  height: 100%;
  border-radius: 0% !important;
}
.basicInfos .loadpho img {
  width: 100%;
  height: 100%;
  border-radius: 0% !important;
}

.phonemodal .ant-modal-body{
  margin-top: 20px;
  margin-bottom:20px;
  font-size: 14px;
  word-wrap: break-word;
  margin-left:30px;
  margin-right:30px;
}
.phonemodal .formbox{
  width:420px;
  margin: 0 auto;
}
.phonemodal .formbox .ant-btn-primary {
  height: 45px;
  width: 420px;
  background-color: #4182fa;
  border-radius: 3px;
  font-size: 18px;
  border: 0;
}
.phonemodal .formbox .ant-input-affix-wrapper {
  line-height: 45px;
  height: 45px;
}
.phonemodal .formbox .iteminp{
  display:flex;
}
.phonemodal .formbox .iteminp .cadeinp {
  width: 290px;
  margin-right: 10px;
  padding-left: -10px;
}
.phonemodal .formbox .iteminp .subbtn {
  width: 118px;
  height: 45px;
  line-height: 45px;
  background: #ffffff;
  border-radius: 3px;
  border: solid 1px #4182fa;
  color: #4182fa;
  outline: none;
}
.phonemodal .formbox .iteminp .subbtn .count {
  color: red;
}
.Pilotmodal{
  width:700px !important;
}
.Pilotmodal .ant-modal-body {
  margin-top: 20px;
  margin-bottom:20px;
  font-size: 14px;
  word-wrap: break-word;
  margin-left:30px;
  margin-right:30px;
}

.Pilotmodal .box .upper  .unitname{
    margin-bottom:25px;
    width:100%;
}
.Pilotmodal .box .upper  .unitname span{
    width:80px;
    margin-right:30px;
    font-size:14px;
    color:#333;
}
.Pilotmodal .box .upper  .unitattr{
    margin-bottom:20px;
    background-color: #f8fafe;
}
.Pilotmodal .box .upper  .unitattr span{
    width:80px;
    margin-right:30px;
    font-size:14px;
    color:#333;
}
.Pilotmodal .box .upper  .unitlogo{
    margin-bottom:20px;
    display:flex;
}
.Pilotmodal .box .upper  .unitlogo .logotitle{
    width:80px;
    margin-right:30px;
    font-size:14px;
    color:#333;
}
.Pilotmodal .box .upper  .unitlogo .nologo{
    font-size:14px;
    color: #ea6125;
}
.Pilotmodal .box .upper  .unitlogo img{
    width:115px;
    height:100px;
}
.Pilotmodal .box .under{
    width:100%;
}
.Pilotmodal .box .under .conbox{
   display:flex;
   background-color: #f8fafe;
   margin-bottom:20px;
}
.Pilotmodal .box .under .conbox .protitle{
    color: #333;
    font-size: 14px;
    width:90px;
    display:block;
    margin-bottom:0;
}
.Pilotmodal .box .under .conbox .applycon{
    max-height:300px;
    width:550px;
    overflow:auto;
}
