<template>
  <div class="myexam">
    <div class="title">
      <h2></h2>
    </div>
    <div class="sea">
      <ul class="nav">
        <li
          v-for="(item,index) in menuList"
          :key="index"
          @click="changPage(item)"
          :class="{'active':current==item}"
        >{{item}}
        <p></p>
        </li>
      </ul>
      <div class="mod">
        <input
        type="text"
        placeholder=""
        v-model="key"
      >
      <button class="ban" @click="search"></button>
      </div>
    </div>
    
    <div class="box">
      <a-table
        :columns="columns"
        :data-source="examList"
        :rowKey="record=>record.id"
        :pagination="pagination"
        @change="changePage"
        :locale="{'emptyText': ''}"
        bordered
      >
        <div
          slot="status"
          slot-scope="index,record"
        >
          <span
            style="color: #ee9537;"
            v-if="record.status==0"
          ></span>
          <span
            style="color: #49af4f;"
            v-else-if="record.status==1"
          ></span>
          <span
            style="color: #ff0000;"
            v-else
          ></span>
        </div>
        <div
          slot="action"
          class="tableAction"
          slot-scope="index,record"
        >
          <span @click="todetail(record.id)"></span>
        </div>
      </a-table>
    </div>
  </div>
</template>

<script>
const columns = [
  {
    title: "",
    dataIndex: "index",
    width: 70,
  },
  {
    title: "",
    dataIndex: "title",
    width: 300,
  },
  {
    title: "",
    dataIndex: "startTime",
  },
  {
    title: "",
    dataIndex: "endTime",
  },
  {
    title: "",
    scopedSlots: { customRender: "status" },
    width:100
  },
  {
    title: "",
    scopedSlots: { customRender: "action" },
    width:90
  },
];

import {getonePlusXExamApi} from "@/request/api"
export default {
  name: "myexam",
  data() {
    return {
      columns,
      current:"",
      key:'',
      menuList:["",""],
      searchInfo:{
        type:1,
        page:1,
        pageSize:10,
        keyword:''
      },
      examList:[],
      pagination:{total:0,pageSize:10}
    };
  },
  computed: {
    role() {
      return this.$store.state.role;
    },
  },
  created() {
    if(this.role==""){
      this.menuList.splice(1,1)
    }
    this.init()
  },
  mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "myexam":
        this.current = "";
        break;
      case "invlinexam":
        this.current = "";
        break;
    }
  },
  methods: {
    init(){
       this.getToken(getonePlusXExamApi,this.searchInfo,res=>{
          this.examList = res.data.rows.map((item, index) => ({
              index: (this.searchInfo.page - 1) * 10 + index + 1,
              title: item.onePlusXExam.title,
              startTime: item.onePlusXExam.startTime,
              endTime: item.onePlusXExam.endTime,
              status: item.finalCheck,
              id: item.onePlusXExam.id,
          }));
       })
    },
    //
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    //
     changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/myexam";
          break;
        case "":
          path = "/invlinexam";
          break;
      }
      this.$router.push({ path: path });
    },
    //
      todetail(id){
          this.$router.push({path:'/examDetail',query:{id}})
      },
    //
    search(){
      this.searchInfo.keyword=this.key
      this.init()
    }
  },
};
</script>
<style lang="stylus">
.myexam .ant-table table {
  text-align: center;
}
.myexam thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  text-align: center;
  font-weight: 600;
  font-size: 16px;
}
.myexam .ant-table-bordered .ant-table-thead > tr > th,
.myexam .ant-table-bordered .ant-table-tbody > tr > td {
  border-right: 1px solid #e8e8e8;
  text-align: center;
}
.myexam .ant-table-thead > tr > th {
    color:#333;
    font-weight: 600;
    font-size: 14px;
    /* background-color: #eaeef7; */
    height:30px;  
}
.myexam .ant-table-bordered .ant-table-tbody{
  background:#fff;
}
.myexam .ant-table-pagination.ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
}
.myexam .ant-table-thead > tr > th, .ant-table-tbody > tr > td {
    padding: 10px 10px ;
}
</style>
<style lang="stylus" scoped>
.myexam {
  margin-bottom: 40px;
  .title {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-bottom:1px solid #eff2f2;
    h2 {
      height:45px;
      line-height: 45px;
      margin-left: 30px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
    }
  }

  .sea {
    height: 45px;
    margin-left: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    position: relative;
    justify-content space-between
    .nav {
      display: flex;
      height: 45px;
      line-height: 45px;
      margin-left: 12px;
      margin-top: 20px;

    li {
      width: 100px;
      margin-right: 30px;
      height: 35px;
      line-height: 35px;
      font-size: 16px;
      color: #333333;
      text-align: center;
      cursor pointer;
       p{
        width: 21px;
        height: 2px;
        background-color: #4281ff;
        border-radius: 1px;
        text-align center;
        margin:0 auto;
        display:none
      }
    }
    li:last-child {
      margin-right: 0;
    }
    .active {
      color: #4281ff;
      font-weight: 600;
      p{
        display:block;
      }
    }
  }
    .mod{
      margin-right:30px;
      input {
      outline: none;
      width: 220px;
      height: 35px;
      border: solid 1px #d4d4d4;
      text-indent: 8px;
    }
    .ban {
      outline: none;
      width: 60px;
      height: 35px;
      background-color: #4281ff;
      color: #ffffff;
      font-size:14px;
      cursor: pointer;
      border:0
    }
    }
  }
  
  .box {
    margin-top:30px;
    margin-left: 30px;
    margin-right: 30px;
  }

  .pagemodel {
    margin-top: 30px;
  }
}
</style>
