<template>
    <div class="newexamplan">
        <div class="title">
            <p></p>
            <p class="back" @click="tonexamplan">
                <img src="../../assets/mine/back.png" alt="">
                
            </p>
        </div>
        <div class="box">
            <eform
            :formData="formData"
            class="examform"
            ref="form"
            >
                <div slot="examname">
                    <a-input
                        v-decorator="['examname',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="baotime"
                    v-decorator="['baotime',{ rules: [{ required: true, message: '' }] }]"
                >
                    <a-range-picker
                        :locale="locale"
                        :show-time="{ format: 'HH:mm' }"
                        format="YYYY-MM-DD HH:mm"
                        :placeholder="['', '']"
                        class="timebox"
                        @change="onbaoChange"
                    />
                </div>
                <div slot="examtime"
                    v-decorator="['examtime',{ rules: [{ required: true, message: '' }] }]"
                >
                    <a-range-picker
                        :locale="locale"
                        :show-time="{ format: 'HH:mm' }"
                        format="YYYY-MM-DD HH:mm"
                        :placeholder="['', '']"
                        class="timebox"
                        @change="onexamChange"
                    />
                </div>
                <div slot="examsort"
                    
                >
                    <examsort
                        @changeExamType="changeExamType"
                        v-decorator="['examsort',{ rules: [{ required: true, message: '' }] }]"
                    ></examsort>
                </div>
                <div slot="examequ">
                    <a-input
                        v-decorator="['examequ',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="equmodel">
                    <a-input
                        v-decorator="['equmodel',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="examnum">
                    <a-input
                        v-decorator="['examnum',{ rules: [{ required: true, message: '' },{validator:validatornum}] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="examinfo"
                    
                >
                    <a-textarea
                        v-decorator="['examinfo',{ rules: [{ required: true, message: '' }] }]"
                        class="infobox" 
                        placeholder=""
                    />
                </div>
            </eform>
            <p class="allbtn">
                <!-- <a-button class="delbtn" ></a-button> -->
                <a-button class="surebtn" @click="addexamplan"></a-button>
            </p>
        </div>
    </div>
</template>

<script>
import examsort from "@/components/common/examsort"
import locale from 'ant-design-vue/es/date-picker/locale/zh_CN';
import {addExamApi,professionListApi,skillListApi} from "@/request/api"
import eform from "@/components/common/form";
export default {
  name: 'newexamplan',
  components:{eform,examsort},
  data(){
      const formData = [
      {
        label: "",
        scopedSlots: { customRender: "examname" },
      },
      {
        label: "",
        scopedSlots: { customRender: "baotime" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examtime" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examsort" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examequ" },
      },
      {
        label: "",
        scopedSlots: { customRender: "equmodel" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examinfo" },
      },
    ];
    let validatornum=(rule,value,callback)=>{
        var regnum=/^[0-9]*$/
        if(value==""||regnum.test(value)){
            callback()
        }else{
            callback(new Error(""))
        }
    }
   
        return{
            formData,
            prolist:[],
            skilllist:[],
            professionId:'',
            skillId:'',
            examtype:'',
            startTime:'',
            endTime:'',
            activityEnd:'',
            activityStart:'',
            locale,
            validatornum,
        }
    },
    created(){
        this.init()
    },
  methods:{
    init(){
        this.getToken(professionListApi,{},res=>{
            this.prolist=res.data
        })
        this.getToken(skillListApi,{},res=>{
            this.skilllist=res.data
        })
    },
    //
    tonexamplan(){
        this.$router.push({path:"/examplan"})
    },
    //
    onbaoChange(value, dateString) {
    //   console.log('Selected Time: ', value);
    //   console.log('Formatted Selected Time: ', dateString);
      this.startTime=dateString[0]
      this.endTime=dateString[1]
      this.$refs.form.form.setFieldsValue({["baotime"]:[this.startTime,this.endTime]})
    },
    //
    onexamChange(value, dateString) {
    //   console.log('Selected Time: ', value);
      this.activityStart=dateString[0]
      this.activityEnd=dateString[1]
      this.$refs.form.form.setFieldsValue({["examtime"]:[this.activityStart,this.activityEnd]})
    },
    //
    proChange(value) {
      console.log("",value)
      this.professionId=value
      this.$refs.form.form.setFieldsValue({["examsort"]:[this.professionId,this.skillId,this.examtype]})
    },
    //
    skillChange(value){
      console.log("",value)
      this.skillId=value
      this.$refs.form.form.setFieldsValue({["examsort"]:[this.professionId,this.skillId,this.examtype]})
    },
    //
    sortChange(value){
       console.log("",value)
       this.examtype=value
       this.$refs.form.form.setFieldsValue({["examsort"]:[this.professionId,this.skillId,this.examtype]})
    },
    changeExamType(data){
        if(data.length==3){
            this.$refs.form.form.setFieldsValue({["examsort"]:data})
        }
    },
    //
    addexamplan(){
        this.$refs.form.form.validateFields().then((res) => {
            this.getToken(addExamApi,{
                'title':res.examname,
                'startTime':res.baotime[0],
                'endTime':res.baotime[1],
                'activityStart':res.examtime[0],
                'activityEnd':res.examtime[1],
                'professionId':res.examsort[0],
                'skillId':res.examsort[1],
                'examType':res.examsort[2],
                'device':res.examequ,
                'deviceType':res.equmodel,
                'examineeAmount':res.examnum,
                'info':res.examinfo
            },value=>{
               if(value.code==200){
                   this.$message.success("")
                   this.$router.push({path:'/examplan'})
                   this.$refs.form.form.resetFields();
               }
            })
        })
    }
    
  }
}
</script>
<style>
.examform .ant-form-item-label label {
    color: #333;
    font-size:16px;
}
.examform .ant-input {
    font-size: 15px;
    border-radius: 0px;
    height:40px;
}
.examform .ant-calendar-picker{
    width:100%;
}
.examform .infobox{
    min-height:220px;
    width:100%;
    outline: none;
}
.examform .timebox{
    width:100% !important;
}
.examform .level{
    margin-right:10px;
}
.examform .skill{
    margin-right:10px;
}
.examform .ant-select-selection{
    height:40px;
    font-size: 15px;
    line-height: 40px;
    border-radius: 0px;
}

</style>
<style lang="stylus" scoped>
.newexamplan{
    margin-bottom: 100px;
    .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      display:flex;
      align-items center;
      justify-content space-between;
        p {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            margin-bottom:0px;
            .ban{
            color: #c56b2f;
            font-size:14px;
            margin-left:20px;
            }
        }
        .back {
            color: #4281ff;
            width: 50px;
            height: 30px;
            font-size:14px;
            margin-right:20px;
            line-height: 30px;
            cursor: pointer;
            img{
                width:15px;
                height:15px;
                margin-top:-3px;
            }
        }
    }
    .box{
        margin-top:40px;
        .allbtn{
            margin-top:50px;
            text-align center ;
            .delbtn{
                width: 101px;
                height: 38px;
                background-color: #ffffff;
                border-radius: 3px;
                border: solid 1px #4182fa;
                color: #4182fa;
                font-size:16px;
                margin-right:16px;
            }
            .surebtn{
                width: 101px;
                height: 38px;
                background-color: #4182fa;
                border-radius: 3px;
                border:0;
                color: #4182fa;
                font-size:16px;
                color:#fff;
            }
        }
    }
}
</style>
