<template>
  <div class="Service">
    <template v-if="bannerList.length>0">
        <div class="banner" v-for="item in bannerList" :key="item.id">
            <a :href="item.link">
                <img :src="item.img" alt="">
            </a>
        </div>
    </template>
    <div class="banner" v-else>
      <!-- <img src="../assets/banner/1+X_banner.png" alt=""> -->
      <p>banner</p>
    </div>
    
    <ul class='nav'>
        <li v-for="(item,index) in navlist" :key="index" @click="changenav(index,item.title)" :class="{'active':index==isactive}">
            <img :src="item.imgurl" alt="">
            <span>{{item.title}}</span>
        </li>
    </ul>
    <div class="box">
        <h2>{{navtitle}}</h2>
        <ul class='conlist'>
            <div class="notbox"  v-if="list.length==0">
                <img
                    src="../assets/blank/notlist.png"
                    alt=""
                >
                <p class="inp"></p>
            </div>
            <template v-else>
                <li v-for="item in list" :key="item.id">
                    <div class="left">
                        <img class="tar" src="../assets/serve/tag.png" alt="">
                        <img v-if="item.type==1" class="bg" src="../assets/serve/kaohebg.png" alt="">
                        <img v-else-if="item.type==2" class="bg" src="../assets/serve/shizibg.png" alt="">
                        <img v-else-if="item.type==3" class="bg" src="../assets/serve/kaowubg.png" alt="">
                        <img v-else class="bg" src="../assets/serve/kaoshibg.png" alt="">
                        <span class='tarspan' v-if="item.type==1"></span>
                        <span class='tarspan' v-else-if="item.type==2"></span>
                        <span class='tarspan'  v-else-if="item.type==3"></span>
                        <span class='tarspan' v-else></span>
                        <!-- <div class="bgspan">
                            
                        </div> -->
                    </div>
                    <div class="right">
                        <p class="contitle">{{item.title}}</p>
                        <p class="contime">{{(item.startTime||'').slice(0,16)}}~{{(item.endTime||'').slice(0,16)}}</p>
                        <span v-if="item.type==1||item.type==2||item.type==3" @click="todetail(item.id,item.type)"></span>
                        <span v-else @click="toexamdetail(item.id)"></span>
                    </div>
                </li>
            </template>
        </ul>
    </div>
    <div class="page">
        <pagination
          :pagination="pagination"
          @changePage="changePage"
        ></pagination>
    </div>
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>

<script>
import pagination from "@/components/common/pagination";
import {getServicelistApi,getbannerApi} from "@/request/api"
export default {
  name: 'Service',
  components: { pagination},
  data(){
      return{
          navlist:[
              {'title':"",
              'imgurl':require('../assets/serve/checkexam.png')
              },
              {'title':"",
              'imgurl':require('../assets/serve/teachertrain.png')
              },
              {'title':"",
              'imgurl':require('../assets/serve/examtrain.png')
              },
              {'title':"",
              'imgurl':require('../assets/serve/studentexam.png')
              }
            ],
            list:[],
            isactive:0,
            flag:false,
            navtitle:"",
            searchInfo: {
                page: 1,
                pageSize: 8,
                type:1,
            },
             pagination: { total: "", pageSize: 8 },
             bannerList:[]
      }
  },

   mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  created(){
       this.searchInfo.type=this.$route.query.type||1
       this.isactive=this.$route.query.serviceIndex||0
       if(this.searchInfo.type==1){
           this.navtitle=""
       }else if(this.searchInfo.type==2){
           this.navtitle=""
       }else if(this.searchInfo.type==3){
           this.navtitle=""
       }else{
           this.navtitle=""
       }
       this.init()
  },
   methods:{
       init(){
          this.getToken(getServicelistApi,this.searchInfo,(res)=>{
              this.list=res.data.rows
              this.pagination.total=res.data.count
          })
           this.getToken(getbannerApi,{module:7},res=>{
                this.bannerList=res.data
            })
       },
       changenav(index,title){
           this.isactive=index
           this.navtitle=title
           if(title==""){
              this.searchInfo.type=1
              this.init()
           }else if(title==""){
               this.searchInfo.type=2
               this.init()
           }else if(title==""){
               this.searchInfo.type=3
               this.init()
           }else{
               this.searchInfo.type=4
               this.init()
           }
       },
       watchScroll() {
        var scrollTop =
            window.pageYOffset ||
            document.documentElement.scrollTop ||
            document.body.scrollTop;
            if (scrollTop > 100) {
                this.flag = true;
            } else {
                this.flag = false;
            }
        },
        changePage(currentPage) {
            this.searchInfo.page = currentPage;
            this.init();
        },
        todetail(id,type){
            this.$router.push({path:'/ServiceDetail',query:{id,type}})
        },
        toexamdetail(id){
            this.$router.push({path:'/examDetail',query:{id}})
        }
   }
}
</script>

<style lang="stylus" scoped>
.Service{
    .banner {
        height:261px;
        margin:0 auto;
        p{
            color:#333;
            font-size:20px;
            width:100%;
            height:100%;
            display:flex;
            align-items center;
            justify-content center;
            background:#d5d5d5;
            text-align center;
        }
        img {
            width: 100%;
            height:100%;
        }
    }
    .nav{
        display:flex;
        width:1205px;
        justify-content space-between
        margin:50px  auto;
        margin-bottom:30px;
        li{
            width: 273px;
            height: 99px;
            display:flex;
            align-items center;
            background-color: #ffffff;
            color:#000;
            box-shadow: 0px 0px 7px 0px rgba(110, 169, 240, 0.2);
            border: solid 1px #ebebeb;
            cursor pointer
            justify-content center;
            img{
                width:61px;
                height:61px;
                margin-right:20px;
            }
            span{
                font-size:20px;
            }
        }
        .active{
            color: #4182fa;
            box-shadow: 0px 0px 7px 0px rgba(110, 169, 240, 0.2);
            border: solid 1px #4182fa;
        }
        li:hover{
             box-shadow: 0px 0px 7px 0px rgba(110, 169, 240, 0.4);
        }
    }
    .box{
        margin:0 auto;
        width:1205px;
        min-height:300px;
        border-top: solid 1px #eeeeee;
        padding-top:20px;
        h2{
            color: #333333;
            font-size:20px;
            font-weight:bolder;
            margin-bottom:25px;
        }
        .conlist{
            display:flex;
            flex-wrap:wrap;
            margin-bottom:0;
            .notbox{
                margin:0 auto;
                margin-bottom:30px;
                margin-top:40px;
                text-align center;
                img {
                display:block
                margin:0 auto;
                }
            .inp {
                font-size: 18px;
                color: #666;
                margin: 0 auto;
                text-align: center;
                margin-top: 10px;
                }
            }
            li{
                width: 575px;
                height: 152px;
                background-color: #fafafa;
                border: solid 1px #ebebeb;
                margin-right:55px;
                margin-bottom:25px;
                display:flex;
                .left{
                    width:200px;
                    height:100%;
                    position relative
                    .tar{
                        position:absolute;
                        left:0;
                        top:10px;
                        width:83px;
                        height:23px;
                    }
                    .tarspan{
                        font-size 13px;
                        color:#fff;
                        position:absolute;
                        top:12px;
                        left:22px;
                        z-index 55
                    }
                    .bgspan{
                        font-size:16px;
                        color:#fff;
                        position:absolute;
                        left:50%;
                        transform :translateX(-50%)
                        top:55px;
                        line-height: 28px;
                        width:150px;
                        height:50px;
                        text-align center
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2
                        overflow: hidden;

                    }
                    .bg{
                        width:100%;
                        height:100%;
                    }
                }
                .right{
                    flex:1
                    overflow hidden
                    padding-left:18px;
                    position relative
                    .contitle{
                        font-size: 16px;
                        letter-spacing: 0px;
                        color: #333333;
                        margin-top:24px;
                        margin-bottom:21px;
                        width:320px;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        white-space: nowrap;
                    }
                    .contime{
                        color: #999999;
                        font-size 14px;
                    }
                    span{
                        display:block;
                        width: 82px;
                        height: 28px;
                        border-radius: 3px;
                        border: solid 1px #4182fa;
                        color: #4182fa;
                        cursor pointer
                        text-align center;
                        line-height:28px;
                        position:absolute;
                        right:20px;
                        bottom:15px;
                    }
                    span:hover{
                        background:#4182fa;
                        color:#fff;
                    }
                }
            }
            li:nth-child(2n){
                margin-right:0px;
            }
            li:hover{
                box-shadow: 0px 0px 7px 0px rgba(110, 169, 240, 0.2);
                .right{
                    .contitle{
                      color: #4182fa;
                    }
                }
            }
        }
    }
    .page {
    //margin: 30px 0;
    padding-top: 30px;
    padding-bottom: 30px;
    }
    .gohead {
        width: 66px;
        height: 58px;
        background-color: #f9f9fa;
        border-radius: 3px;
        border: solid 1px #eeeeee;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        right: 240px;
        bottom: 50px;
        cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead3.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}
}
</style>
