<template>
  <div class="courseDetail">
    <div class="content">
      <div class="breadCrumb">
        <span @click="returnHome()"> ></span>
        <span @click="returncourse()"> ></span>
        <span>{{list.title}}</span>
      </div>
      <courseItem
        :list="list"
        @logup="logup"
        @signup="signup"
        @tocollect="tocollect"
        @tostudy="tostudy"
        :taskId="taskId"
      ></courseItem>
      <div class="detail">
        <div
          class="left"
          :style="{width:widthClass+'px'}"
        >
          <ul class="title">
            <li
              v-for="(item,index) in menuList"
              :key="index"
              @click="changePage(item)"
              :class="{'active':current==item}"
            >{{item}}</li>
          </ul>
          <div class="inner">
            <router-view></router-view>
          </div>
        </div>
        <div
          class="right"
        >
          <div class="top">
            <img
              src="../assets/teacherLogo.png"
              alt=""
            >
            : {{list.teacher}}
          </div>
          <div class="bottom">
            <template v-if="list.teacherInfo">
              {{list.teacherInfo}}
            </template>
            <!-- <template v-else>
              <img
                src="../assets/course/teacherinfo.png"
                alt=""
              >
              <p class="inp"></p>
            </template> -->
            <div class="notbox"  v-else>
              <img
                src="../assets/blank/notteacher.png"
                alt=""
              >
              <p class="inp"></p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <popBox
      :isBox="isBox"
      @closemodal="closemodal"
      @sureModal="sureModal"
    ></popBox>
    <loginModal
      :isShow="isShow"
      @closemodal="isShow=false"
      @logined="logined"
      @goregister="goregister"
      @forget="forget"
    ></loginModal>
    <!--  -->
    <div
      class="gohead"
      v-show="showhead"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>

<script>
import {
  getcourseDetailApi,
  joincourseApi,
  getcollectApi,
  getcontinueApi,
  getupdateStatusApi,
} from "@/request/api";
import courseItem from "@/components/common/courseItem";
import loginModal from "@/components/common/loginModal";
import popBox from "@/components/common/popBox";
export default {
  name: "courseDetail",
  components: { courseItem, loginModal, popBox },
  data() {
    return {
      menuList: ["", "", ""],
      current: null,
      Id: null,
      taskId: null,
      list: {},
      courseId: null,
      isShow: false,
      isBox: false,
      widthClass: 933,
      isexit: true,
      joined: "",
      title: "",
      showhead: false,
      chapterId: "",
      info: {
        courseId: "",
        collected: "",
      },
    };
  },
  created() {
    this.Id = this.$route.query.id;
    this.init();
  },
  watch:{
    isLogin:function(){
      this.init()
    }
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "courseInfo":
        this.current = "";
        break;
      case "catalog":
        this.current = "";
        break;
      case "answer":
        this.current = "";
        break;
    }
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    role(){
      return this.$store.state.role
    }
  },
  
  methods: {
    init() {
      this.getToken(getcourseDetailApi, { courseId: this.Id }, (res) => {
        this.list = res.data;
        this.joined = res.data.joined;
      });
    },
    returnHome() {
      this.$router.push({ path: "/" });
    },
    returncourse() {
      this.$router.push({ path: "/course" });
    },
    changePage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/courseInfo";
          break;
        case "":
          path = "/catalog";
          break;
        case "":
          path = "/answer";
          break;
      }
      this.$router.push({
        path: path,
        query: { id: this.Id,"joined":this.joined},
      });
      // if (data == "") {
      //   this.widthClass = 1205;
      //   this.isexit = false;
      // } else {
      //   this.widthClass = 933;
      //   this.isexit = true;
      // }
    },
    logined() {
      this.isShow = false;
      this.init();
    },
    logup(isLogin) {
      if (isLogin == 0) {
        this.isShow = false;
        this.init();
      } else {
        this.isShow = true;
        this.init();
      }
    },
    signup(id) {
        if(this.role==""||this.role==""){
        this.$message.info("")
      }else{
          joincourseApi({ courseId: id }).then((res) => {
        if (res.code == 426) {
          this.isBox = true;
        } else {
          this.$message.success("")
          this.$store.commit('changeCourseStatus',{isJoined:true})
          this.isBox = false; 
          this.init();
        }
      });
      }
      
    },
    closemodal() {
      this.isBox = false;
    },
    sureModal() {
      this.isBox = false;
    },
    tocollect(isLogin) {
      if (isLogin == 0) {
        this.list.collected = !this.list.collected;
        this.info.collected = this.list.collected;
        this.info.courseId = this.Id;
        this.getToken(getcollectApi, this.info, () => {
          this.init();
        });
      } else {
        this.$message.error("");
        this.init();
      }
    },
    //
    tostudy(id) {
        this.getToken(getcontinueApi, { courseId: id }, (res) => {
        //
          if(res.data.type==1){
            //

            this.getToken(getupdateStatusApi,{ courseId: this.Id, taskId: res.data.id, status: 1 },() => {
                this.$store.commit('getIds',{'chapterId':this.chapterId,'taskId':res.data.id})
                this.$router.push({
                  path: "/catalogDetail",
                  query: { id, menuId: res.data.id, type: res.data.type }
                });
            })
            
          }else{
              this.$router.push({path: "/catalogDetail",
              query: { id, menuId: res.data.id, type: res.data.type }});
          }
      });
    },
    goregister() {
      this.$router.push({ path: "/register" }); //  
      this.isShow = false;
    },
    forget() {
      this.$router.push({ path: "/forgetModal" });  //
      this.isShow = false;

    },
    //
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.showhead = true;
      } else {
        this.showhead = false;
      }
    },
  },
};
</script>
<style lang="stylus" scoped>
.ql-editor {
  padding: 0px 0px;
}
</style>
<style lang="stylus" scoped>
.courseDetail {
  background: #eee;

  .content {
    width: 1205px;
    margin: 0 auto;
    min-height: 500px;
    overflow: hidden;

    .breadCrumb {
      min-width: 230px;
      margin-top: 22px;
      margin-bottom: 24px;
      height: 20px;
      font-family: MicrosoftYaHei;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 1px;
      color: #333333;
      span:hover {
        color: #4182fa;
        cursor: pointer;
      }

      span:last-child {
        font-family: MicrosoftYaHei;
        font-size: 14px;
        font-weight: normal;
        letter-spacing: 1px;
        color: #999999;
        cursor: default;
      }
    }

    .detail {
      margin-top: 20px;
      overflow: hidden;

      .left {
        width: 930px;
        float: left;
        background: #fff;
        min-height: 300px;
        margin-bottom: 18px;

        .title {
          height: 53px;
          display: flex;
          padding-left: 29px;

          li {
            margin-right: 60px;
            height: 50px;
            line-height: 53px;
            font-size: 18px;
            font-weight: normal;
            font-stretch: normal;
            line-height: 70px;
            letter-spacing: 0px;
            color: #333333;
            text-align: center;
            cursor: pointer;
          }

          li:last-child {
            margin-right: 0px;
          }

          .active {
            color: #4182fa;
            border-top: 2px solid #4182fa;
          }
        }

        .inner {
          padding-left: 29px;
          margin-top: 25px;
          padding-right: 30px;
          margin-bottom: 30px;
        }
      }

      .right {
        padding: 0 30px;
        width: 251px;
        float: right;
        background: #fff;
        min-height: 300px;

        .top {
          border-bottom: 1px solid #ddd;
          height: 85px;
          display: flex;
          align-items: center;
          color: #666;

          img {
            margin-right: 25px;
          }
        }

        .bottom {
          margin-top: 30px;
          color: #666;
          line-height: 24px;
          padding-bottom: 20px;

          .notbox{
            margin:0 auto;
            margin-bottom:30px;
            margin-top:40px;
            text-align center;
            img {
              display:block
              margin:0 auto;
            }
          .inp {
              font-size: 18px;
              color: #666;
              margin: 0 auto;
              text-align: center;
              margin-top: 10px;
            }
          }
        }
      }
    }
  }
}

.gohead {
  width: 66px;
  height: 58px;
  background-color: #f9f9fa;
  border-radius: 3px;
  border: solid 1px #eeeeee;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 240px;
  bottom: 50px;
  cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead3.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}
</style>
