<template>
  <div class="isvideo">
    <div class="title">
      <span>{{title}}</span>
      <button @click="$emit('gocourse',catId)"></button>
    </div>
    <div class="content">
      <div class="prism-player" id="myPlayer"></div>
    </div>
  </div>
</template>

<script>
import "../../../lib/aliyun-upload-sdk-1.5.0.min.js"
import "../../../lib/aliyun-oss-sdk-5.3.1.min.js"
import {getupdateStatusApi,getisvideoApi} from "@/request/api";
export default {
  name: "isvideo",
  props: {
    resourceUrl:String,
    title:String,
    catId:[String, Number],
    menuId:[String, Number],
    joined:Boolean,
    chapterId:[String,Number]
  },
  data() {
    return {
      isPlay: false,
      PlayAuth:'',
      videoUrl:'',
      videoId:'',
      coverUrl:'',
      player:''
    };
  },
  //
  watch:{
     resourceUrl:function(){
       this.init()
     }
  },
  created(){
     this.init()
  },
  methods: {
    init(){
      //
       this.getToken(getisvideoApi,{'videoId':this.resourceUrl},res=>{

        //
          this.PlayAuth=res.data.PlayAuth
        
        //
          this.coverUrl=res.data.VideoMeta.CoverURL
          var that=this
          //
            if(this.player){
              this.player.dispose()
            }
            this.player = new Aliplayer({
                "id": "myPlayer",      //domid
                "vid":this.resourceUrl,     //id
                "playauth": this.PlayAuth, // 
                "width": "100%",        //
                "height": "700px",     //
                "autoplay": false,  // 
                "isLive": false,    // 
                "cover": this.coverUrl,     //,autoplayfalse
                "rePlay": false,    // 
                "playsinline": true,  //H5
                "preload": false,    //
                "language": "zh-cn",     
                "controlBarVisibility": "always",  // click hover always 
                "useH5Prism": true,    //H5
                'encryptType':1,
               },function(){
                    // console.log("playAuth:",that.playAuth);
                    //  player.replayByVidAndPlayAuth(that.resourceUrl,that.PlayAuth)
                    console.log("")
               }
            );
              // 
              function endedHandle()
              {
                  if(that.joined){
                      getupdateStatusApi({'status':1,'courseId':that.catId,'taskId':that.menuId}).then(()=>{
                          that.$store.commit('getIds',{'chapterId':that.chapterId,'taskId':that.menuId})
                      })
                  }
              }
            this.player.on("ended", endedHandle); 
        })     
    }
  },
};
</script>
<style lang="stylus" scoped>
.isvideo {
  .title {
    height: 65px;
    border-bottom: 1px solid #eee;
    margin-left: 38px;
    margin-right: 39px;
    display: flex;
    position: relative;
    span {
      color: #333333;
      font-size: 18px;
      padding-top: 30px;
    }
    button {
      border: 0;
      outline: none;
      width: 99px;
      height: 30px;
      background-color: #ffffff;
      border: solid 1px #cecece;
      color: #4182fa;
      font-size: 12px;
      position: absolute;
      right: 17px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .content {
    margin: 0 auto;
    margin-top: 24px;
    margin-left: 33px;
    margin-right: 33px;
    margin-bottom: 50px;
    overflow: hidden;
    min-height: 800px;
    video {
      margin-top:20px;
      width: 100%;
      // height: 700px;
    }
  }
}
</style>