<template>
  <a-carousel arrows autoplay>
    <div
      slot="prevArrow" slot-scope="props" class="custom-slick-arrow" style="left: 10px;zIndex: 1"
    >
      <a-icon style="font-size:40px;opacity:0.5" type="left" />
    </div>
    <div slot="nextArrow" slot-scope="props" class="custom-slick-arrow" style="right: 40px">
      <a-icon style="font-size:40px;opacity:0.5" type="right" />
    </div>
    
    
    <template v-if="bannerList.length>0">
      <div class="bannerbox" v-for="item in bannerList" :key="item.id">
        <a :href="item.link" target="_blank" >
          <img :src="item.img" alt="">
      </a>
      </div>
    </template >
    <div v-else class="bannerbox" >
        <!-- <img
        src="../../assets/home/homebanner.png"
        alt=""
      > -->
      <p>banner</p>
    </div>
  </a-carousel>
</template>
<script>
import {getbannerApi} from "@/request/api"
export default {
    props:'',
    data(){
       return{
           bannerList:[]
       }
    },
    created(){
        this.init()
    },
    methods:{
        init(){
            this.getToken(getbannerApi,{module:1},res=>{
                this.bannerList=res.data
            })
        }
    }
};
</script>
<style scoped>
/* For demo */
.ant-carousel >>> .slick-slide {
  text-align: center;
  height: 350px;
  line-height: 160px;
  /* background: #364d79; */
  overflow: hidden;
}

.ant-carousel .custom-slick-arrow {
  width: 40px;
  height:40px;
  font-size: 40px;
  color: #fff;
  background-color: rgba(31, 45, 61, 0.11);
  opacity: 0.3;
}
.ant-carousel  .custom-slick-arrow:before {
  display: none;
}
.ant-carousel .custom-slick-arrow:hover {
  opacity: 0.9;
}
.ant-carousel .bannerbox p{
  color:#333;
  font-size:20px;
  width:100%;
  height:350px;
  display:flex;
  align-items:center;
  justify-content:center;
  background:#d5d5d5;
  text-align:center;
}
.ant-carousel .bannerbox a img {
  width:100%;
  height:350px;
}
</style>