<template>
  <div class="answerDetail">
    <div class="title">
      <div class="breadCrumb">
        <span @click="returnanswer(courseId)"> ></span>
        <span></span>
      </div>
    </div>
    <div class="box">
      <div class="left" v-if='list.creator'>
        <img
          class="touimg"
          v-if="list.creator.icon"
          :src="list.creator.icon"
          alt=""
        >
        <img
          class="touimg"
          v-else
          src="../assets/course/icon.png"
          alt=""
        >
      </div>
      <div class="right"  v-if='list.creator'>
        <p class="title"><span>{{list.creator.name}}</span><span>{{list.pubTime}}</span></p>
        <p class="info">{{list.title}}</p>
        <p class="rep">{{list.content}}</p>
        <p class="log">
          <ul class="logul">
            <li
              v-for="(v,index) in list.tag"
              :key="index"
            >{{v}}</li>
          </ul>
          <ul class="vwul">
            <li>
              <span></span>
              <img
                src="../assets/course/icon1.png"
                alt=""
              >
              <span>{{list.scannedNum}}</span>
            </li>
          </ul>
        </p>
      </div>
      <div
        class="btn"
        @click="scrollToPosition"
      ></div>
    </div>
    <div class="reply">
      <div class="total">
        <span>{{total}}</span>
      </div>
      <div
        class="list"
        v-for="item in list.replies"
        :key="item.id"
      >
        <div class="left">
          <img
            class="touimg"
            v-if="item.creator.icon"
            :src="item.creator.icon"
            alt=""
          >
          <img
            class="touimg"
            v-else
            src="../assets/course/icon.png"
            alt=""
          >
        </div>
        <div class="right">
          <p class="title"><span>{{item.creator.name}}</span><span>{{item.pubTime}}</span></p>
          <p class="info">{{item.content}}</p>
        </div>
      </div>
    </div>
    <div class="equill">
      <textarea
        v-model="keyword"
        placeholder=""
      ></textarea>
      <p>{{tishi}}</p>
    </div>
    <div
      class='btn'
      @click="tosubmit"
    >
      
    </div>
    <div class="page">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import { getanswerDetailApi, getreplyApi } from "@/request/api";
import pagination from "@/components/common/pagination";
export default {
  name: "answerDetail",
  components: { pagination },
  data() {
    return {
      quillInfo: "",
      askId: "",
      list: [],
      total: "",
      con: "",
      content: "",
      pagination: { total: "", pageSize: 6 },
      keyword: "",
      tishi: "",
      searchInfo: {
        page: 1,
        pageSize: 6,
      },
      courseId:''
    };
  },
  created() {
    this.askId = this.$route.query.newid;
    this.courseId=this.$route.query.id
    this.init();
  },
  methods: {
    init() {
      this.getToken(getanswerDetailApi, { askId: this.askId }, (res) => {
        this.list = res.data;
        // console.log(this.list.creator.icon)
        this.total = res.data.replies.length;
        this.pagination.total = res.data.replies.length;
      });
    },
    returnanswer(id) {
      this.$router.push({ path: "/answer", query: {id} });
    },
    scrollToPosition() {
      document.getElementsByClassName("equill")[0].scrollIntoView();
    },
    tosubmit() {
      this.con = this.keyword;
      this.con = this.con.replace(/(^\s*)|(\s*$)/g, ""); //;
      if (this.con == "" || this.con == undefined || this.con == null) {
        this.$message.error("");
      } else {
        this.getToken(
          getreplyApi,
          { askId: this.askId, content: this.con },
          () => {
            this.init();
            this.$message.success("");
            this.keyword = "";
          }
        );
      }
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
  },
};
</script>

<style lang="stylus" scoped>
