      letter-spacing: 1px;
      color: #ffffff;
      position: absolute;
      right: 26px;
      bottom: 23px;
      text-align: center;
      padding: 0px;
      line-height: 38px;
    }
  }

  .reply {
    .total {
      width: 1148px;
      margin: 0 auto;
      border-bottom: 1px solid #eee;
      height: 35px;
