<template>
  <div class="require">
    <div class="title">
      <h2></h2>
    </div>
    <div class="sea">
        <span></span>
        <div class="mod">
            <a-select
              class="selbtn"
              allow-clear
              placeholder=""
              :notFoundContent="''"
              @change="selectstatus"
              v-decorator="['modelname',{rules:[{required:true,message:''}]}]"
            >
              <a-select-option value="0">
                
              </a-select-option>
              <a-select-option value="1">
                
              </a-select-option>
              <a-select-option value="2">
                
              </a-select-option>
            </a-select>
        </div>
        <div class="oper">
            <input
            type="text"
            placeholder=""
            v-model="key"
          >
          <button
            class="ban"
            @click="searchbtn"
          ></button>
          <button class="addbtn" @click="addbtn"></button>
        </div>
    </div>
    <a-empty
      v-if="requireList.length==0"
      description=""
    />
    <template v-else>
      <div class="box" v-for="(item,i) in requireList" :key="item.id">
        <div class="left">
          <span class="indexspan">{{index(i)}}</span>
          <img class="sortimg" src="../../assets/mine/news.png" alt="">
        </div>
        <div class="right">
            <p class="examtitle">
              <span class="head" >{{item.title}}</span>
              <span class="examstu">
                <span v-if="item.status==0" style="color: #ee9537;"></span>
                <span v-else-if="item.status==1" style="color:green"></span>
                <span v-else style="color: #ff0000;"></span>
              </span>
            </p>
            <p class="examsort">
                <span>{{item.type}}</span>
                <span>{{item.needNum}}</span>
            </p>
            <p class="examtime">
              <span>{{item.education}}</span>
              <span>{{item.postCount}}</span>
            </p>
            <p class="examaddress">
              <span>{{item.salary}}</span>
              <span>{{(item.createTime||"").slice(0,10)}}</span>
            </p>
            <p class="examrelnum">
              <span class="phone">{{item.address}}</span>
              <span class="allspan">
                <span @click="recjob(item.id)"></span>
                <span @click="deljob(item.id,item.title)"></span>
                <span @click="toDelivery(item.id,item.postCount)"></span>
              </span>
            </p>
        </div>
    </div>
    </template>
    <a-modal
      title=""
      :visible="requireshow"
      @ok="requireOk"
      @cancel="requirecancel()"
      okText=""
      cancelText=""
      class="requiremodal"
      :destroyOnClose='true'
    >
      <div class="box">
            <eform
            :formData="formData"
            :fields="fields"
            class="requireform"
            ref="form"
            >
                <div slot="jobname">
                    <a-input
                        v-decorator="['jobname']"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobsort">
                    <a-input
                        v-decorator="['jobsort']"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobnum">
                    <a-input
                        v-decorator="['jobnum']"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobask">
                    <a-input
                        v-decorator="['jobask']"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobmoney">
                    <a-input
                        v-decorator="['jobmoney']"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobaddress">
                    <!-- <Taddress 
                    v-decorator="['jobaddress']"
                    @changeAddress="changeAddress"
                    class='jobbtn'
                    ></Taddress> -->
                    <a-input
                        v-decorator="['jobaddress']"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="jobinfo">
                    <a-textarea
                        class="infobox" 
                        v-decorator="['jobinfo']"
                        placeholder=""
                    />
                </div>
            </eform>
        </div>
    </a-modal>
    <div class="page">
      <pagination
         :pagination="pagination"
         @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import {jobListApi,deljobListApi,recjobListApi} from "@/request/api"
import eform from "@/components/common/form"
// import Taddress from "@/components/common/Taddress";
import pagination from "@/components/common/pagination"
export default {
  name: 'require',
  components:{eform,pagination},
  data(){
    const formData = [
      {
        label: "",
        scopedSlots: { customRender: "jobname" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobsort" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobask" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobmoney" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobaddress" },
      },
      {
        label: "",
        scopedSlots: { customRender: "jobinfo" },
      },
    ];
     return{
        formData,
        key:'',
        requireshow:false,
        fields:null,
        searchInfo:{
          page:1,
          pageSize:5,
          status:'',
          keyword:''
        },
        pagination:{total:'',pageSize:5},
        requireList:[],
        recList:[],
        province: "",
        city: "",
        district: "",
        recaddress:[],
        jobId:''
    }
  },
  created(){
     this.init()
  },
  methods:{
    init(){
       this.getToken(jobListApi,this.searchInfo,res=>{
           this.requireList=res.data.rows
           this.pagination.total=res.data.count
       })
    },
    //
    index(index){
        return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
    addbtn(){
       this.$router.push({path:'/newrequire'})
    },
    //
    searchbtn(){
       this.searchInfo.keyword=this.key
       this.init()
    },
    //
    selectstatus(val){
      this.searchInfo.status=val
      this.init()
    },
    //
    recjob(id){
      this.requireshow=true
      this.jobId=id
      this.recList=this.requireList.find((item)=>item.id===id)
      console.log(this.recList)
      this.fields={
         jobname:this.recList.title,
         jobsort:this.recList.type,
         jobnum:this.recList.needNum,
         jobask:this.recList.education,
         jobmoney:this.recList.salary,
         jobaddress:this.recList.address,
         jobinfo:this.recList.content,
      }
    },
    //
    requireOk(){
      this.$refs.form.form.validateFields().then((res) => {

          this.getToken(recjobListApi,{
            jobId:this.jobId,
            title:res.jobname,
            type:res.jobsort,
            needNum:res.jobnum,
            education:res.jobask,
            salary:res.jobmoney,
            address:res.jobaddress,
            content:res.jobinfo
          },res=>{
            console.log(res)
            if(res.code==200){
              this.requireshow=false
              this.$message.success("")
              this.init()
            }
          })
      })
    },
    //
    requirecancel(){
      this.requireshow=false
    },
    //
    deljob(id,title){
       this.$confirm({
        title: `"${title}"?`,
        okText: '',
        cancelText: '',
        onOk:()=> {
          this.getToken(deljobListApi,{jobId:id},res=>{
            if(res.code==200){
              this.$message.success("")
              this.init()
            }
          })
        },
        onCancel() {
          // console.log('Cancel');
        }
      });
    },
    //
    toDelivery(id,postCount){
      //
      if(postCount==0){
        this.$message.info("")
      }else{
        this.$router.push({path:'/Delivery',query:{id}})
      }
    },
    //
    changeAddress(data){
      console.log(data)
      this.province = data[0];
      this.city = data[1];
      this.district = data[2];
      this.recaddress=data
    },
    //
    changePage(currentPage){
      this.searchInfo.page=currentPage
      this.init()
    }
  }
}
</script>
<style >
.requiremodal{
  width:600px !important;
}
.requiremodal .ant-modal-body {
  margin-top: 20px;
  padding: 0px;
  font-size: 14px;
  word-wrap: break-word;
}
.requiremodal .ant-modal-body .box .requireform{
   width:100%;
   padding:0;
   margin-left:20px;
   margin-right:20px;
}
.requireform .infobox{
    min-height:200px;
    width:100%;
    outline: none;
}
.requireform .jobbtn{
    width:100%;
}
</style>
<style lang="stylus" scoped>
.require{
    margin-bottom: 30px;
    .title {
        height: 45px;
        line-height: 45px;
        overflow: hidden;
        border-bottom:1px solid #eff2f2;
        h2 {
        height:45px;
        line-height: 45px;
        margin-left: 30px;
        font-size: 18px;
        font-weight: bold;
        color: #333333;
        }
    }
    .sea {
      height: 45px;
      margin-left: 30px;
      margin-right:30px;
      margin-top: 20px;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      justify-content space-between
      span {
        color: #333;
        font-size: 16px;
        margin-right: 50px;
      }
      .mod {
        width: 204px;
        margin-right: 24px;
        margin-left: 180px;
        .selbtn {
          width: 100%;
          height: 100%;
        }
      }
      .oper{
        input {
        outline: none;
        width: 220px;
        height: 35px;
        border: solid 1px #d4d4d4;
        text-indent: 8px;
      }

        .ban {
          outline: none;
          width: 60px;
          height: 35px;
          background-color: #4281ff;
          color: #ffffff;
          font-size:14px;
          cursor: pointer;
          border:0
        }
        .addbtn {
          outline: none;
          width: 75px;
          height: 35px;
          background-color: #ff904f;
          border-radius: 3px;
          color: #ffffff;
          font-size:14px;
          cursor: pointer;
          border:0
          margin-left:20px;
        }
      }
    }
    .box{
      margin-top:20px;
      margin-left:30px;
      margin-right:30px;
      border: solid 1px #e6e6e6;
      margin-bottom:15px;
      display:flex;
      .left{
        display:flex;
        min-width:80px;
        padding-right:15px;
        padding-left:10px;
        .indexspan{
          color:#666;
          font-size:14px;
          display:block;
          overflow hidden;
          margin-top:30px;
          margin-right:15px;
          
        }
        .sortimg{
          width:40px;
          height:40px;
          display:block;
          margin-top:20px;
        }
      }
      .right{
        overflow hidden
        flex:1;
        .examtitle{
          margin-top:18px;
          position relative
          margin-bottom:13px;
          display:flex;
          .head{
            font-size:16px;
            font-weight: bold;
            display:block
            color: #333333;
            width:580px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .apllystu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:150px;
          }
          .examstu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:23px;
          }
        }
        .examsort{
           font-size:14px;
           height:20px;
           color:#666;
           display:flex;
           margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examtime{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examaddress{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examrelnum{
          font-size:14px;
          color:#666;
          position relative;
          justify-content space-between
          display flex;
          .phone{
            color:#666;
          }
          .allspan{
            width:240px;
            display flex;
            span{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #f6f8ff;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              margin-right:9px;
              cursor pointer
            }
            span:last-child{
              margin-right:0px;
            }
          }
        }
      }
    }
    .page {
      margin-top: 30px;
    }
}
</style>
