<template>
    <div class="Delivery">
        <div class="title">
            <p></p>
            <p class="back" @click="torequire">
                <img src="../../assets/mine/back.png" alt="">
                
            </p>
        </div>
        <div class="sea">
            <p class="jobapp">
                <span></span>
                <span>{{jobtitle}}</span>
            </p>
            <div class="mod">
                <input type="text" v-model="key" placeholder="">
                <button
                    class="ban"
                    @click="searchbtn"
                ></button>
            </div>
        </div>
        <a-empty
          v-if="deliveryList.length==0"
          description=""
        />
        <template v-if="deliveryList">
          <div class="box" v-for="(item,i) in deliveryList" :key="item.id">
            <div class="left">
              <span class="indexspan">{{index(i)}}</span>
              <img class="sortimg" src="../../assets/mine/news.png" alt="">
            </div>
            <div class="right">
                <p class="examtitle">
                    <span class="head" >{{item.title}}</span>
                </p>
                <p class="examsort">
                    <span>{{item.user.realname}}</span>
                    <span>{{item.user.telephone}}</span>
                </p>
                <p class="examtime">
                    <span>{{item.user.gender}}</span>
                    <span>{{item.user.email}}</span>
                </p>
                <p class="examrelnum">
                    <span>{{item.user.age}}</span>
                    <span>{{(item.createTime||"").slice(0,10)}}</span>
                    <span class="allspan">
                        <span @click="deljob(item.id,item.title)"></span>
                        <span> 
                          <a  :href="item.url" target="_Blank"></a>
                        </span>
                    </span>
                </p>
            </div>
        </div>
        </template>
      <div class="page">
        <pagination
          :pagination="pagination"
          @changePage="changePage"
        ></pagination>
      </div>
    </div>
</template>

<script>
// import axios from "axios";
import {cvListApi,delcvListApi} from "@/request/api"
import pagination from "@/components/common/pagination"
export default {
  name: 'Delivery',
  components:{pagination},
  data(){
      return{
         key:'',
         searchInfo:{
            page:1,
            pageSize:5,
            keyword:'',
            jobId:''
          },
        pagination:{total:'',pageSize:5},
        deliveryList:[],
        downList:[],
        jobtitle:''
      }
  },
  created(){
    this.searchInfo.jobId=this.$route.query.id
    this.init()
  },
  methods:{
    init(){
      this.getToken(cvListApi,this.searchInfo,res=>{
        this.deliveryList=res.data.rows
        this.jobtitle=res.data.rows[0].Job.title
        this.pagination.total=res.data.count
      })
    },
    //
    torequire(){
        this.$router.push({path:'/require'})
    },
    //
    searchbtn(){
      this.searchInfo.keyword=this.key
      this.init()
    },
     //
    index(index){
        return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
    deljob(id,title){
        this.$confirm({
        title: `${title}?`,
        okText: '',
        cancelText: '',
        onOk:()=> {
          this.getToken(delcvListApi,{cvId:id},res=>{
              if(res.code==200){
                this.$message.success("")
                this.init()
              }
          })
        },
        onCancel() {
          // console.log('Cancel');
        },
      });
    },
    //
    // downFile(id){
    //     this.downList=this.deliveryList.find((item)=>item.id===id)

    //       // let a = document.createElement('a')
    //       //   a.href = this.downList.url 
    //       //   a.click()
    // },
    changePage(currentPage){
      this.searchInfo.page=currentPage
      this.init()
    }
  }
}
</script>

<style lang="stylus" scoped>
.Delivery{
     margin-bottom: 30px;
    .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      display:flex;
      align-items center;
      justify-content space-between;
        p {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            margin-bottom:0px;
            .ban{
            color: #c56b2f;
            font-size:14px;
            margin-left:20px;
            }
        }
        .back {
            color: #4281ff;
            width: 50px;
            height: 30px;
            font-size:14px;
            margin-right:20px;
            line-height: 30px;
            cursor: pointer;
            img{
                width:15px;
                height:15px;
                margin-top:-3px;
            }
        }
    }
    .sea {
        height: 45px;
        margin-left: 30px;
        margin-right:30px;
        margin-top: 20px;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content space-between
        .jobapp {
            font-size: 16px;
            margin-bottom:0px;
            span:nth-child(1){
                color: #333333;
                margin-right:20px;
            }
            span:nth-child(2){
                color: #c56b2f;
            }
        }
        .mod {
            input {
                outline: none;
                width: 220px;
                height: 35px;
                border: solid 1px #d4d4d4;
                text-indent: 8px;
            }
            .ban {
                outline: none;
                width: 60px;
                height: 35px;
                background-color: #4281ff;
                color: #ffffff;
                font-size:14px;
                cursor: pointer;
                border:0
            }
        }
    }
    .box{
      margin-top:20px;
      margin-left:30px;
      margin-right:30px;
      border: solid 1px #e6e6e6;
      margin-bottom:15px;
      display:flex;
      .left{
        display:flex;
        min-width:80px;
        padding-right:15px;
        padding-left:10px;
        .indexspan{
          color:#666;
          font-size:14px;
          display:block;
          overflow hidden;
          margin-top:30px;
          margin-right:15px;
        }
        .sortimg{
          width:40px;
          height:40px;
          display:block;
          margin-top:20px;
        }
      }
      .right{
        flex:1;
        .examtitle{
          margin-top:18px;
          position relative
          margin-bottom:13px;
          display:flex;
          .head{
            font-size:16px;
            font-weight: bold;
            display:block
            color: #333333;
            width:580px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .apllystu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:150px;
          }
          .examstu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:23px;
          }
        }
        .examsort{
           font-size:14px;
           height:20px;
           color:#666;
           display:flex;
           margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examtime{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examrelnum{
          font-size:14px;
          color:#666;
          position relative;
          justify-content space-between
          display flex;
          span:nth-child(1){
             width:280px;
           }
          span:nth-child(2){
              flex:1;
              display:block;
              overflow hidden;
              text-overflow:ellipsis;
            white-space: nowrap;
          }
          .allspan{
            width:140px;
            margin-right:22px;
            display flex;
            span{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #f6f8ff;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              margin-right:9px;
              cursor pointer
            }
            span:last-child{
              margin-right:0px;
            }
          }
        }
      }
    }
    .page {
      margin-top: 30px;
    }
}
</style>
