<template>
  <div class="myClass">
    <div class="title">
      <h2></h2>
    </div>
    <div class="sea">
      <span></span>
      <div class="mod">
        <a-select
          class="selbtn"
          allow-clear
          placeholder=""
          :notFoundContent="''"
          @change="Select"
          v-decorator="['modelname',{rules:[{required:true,message:''}]}]"
        >
          <a-select-option
            v-for="(item,index) in classlist"
            :key="index"
            :value="item.id"
          >
            {{item.title}}
          </a-select-option>
        </a-select>
      </div>
      <div class="oper">
        <input
        type="text"
        placeholder=""
        v-model="key"
      >
      <button
        class="ban"
        @click="searchbtn"
      ></button>
      </div>
    </div>
    <ul class="list">
      <a-empty
        class="empty"
        v-if="list.length==0"
        description=""
      />
      <li
        v-else
        v-for="(item,index) in list"
        :key="index"
      >
        <p class="tit">{{item.title}}</p>
        <p class="cou">
          <img src="../../assets/mine/sub.png" alt="">
          {{item.course.title}}
        </p>
        <p class="stu">
           <img src="../../assets/mine/num.png" alt="">
          <span>{{item.studentNum}}</span>
        </p>
        <p
            class="view"
            @click="todetail(item.id)"
          >
        </p>
      </li>
      
      
    </ul>
    <div class="pagemodel">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import { getmyclassApi, getmyCourseListApi } from "@/request/api";
import pagination from "@/components/common/pagination";
export default {
  name: "myClass",
  components: { pagination },
  data() {
    return {
      classlist: "",
      searchInfo: {
        page: 1,
        pageSize: 9,
        keyword: null,
        courseId: null,
      },
      pagination: { total: "", pageSize: 9 },
      list: [],
      key: "",
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      this.getToken(getmyclassApi, this.searchInfo, (res) => {
        this.pagination.total = res.data.count;
        this.list = res.data.rows;
      });
      this.getToken(getmyCourseListApi, {}, (res) => {
        this.classlist = res.data;
        //  console.log(this.classlist)
      });
    },
    //
    Select(data) {
      // console.log(data)
      this.searchInfo.courseId = data;
      this.init();
    },
    //
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    //
    todetail(id) {
      this.$router.push({ path: "/mystudent", query: { id } });
    },
    searchbtn() {
      this.searchInfo.keyword = this.key;
      this.init();
    },
  },
};
</script>
<style>
.empty {
  margin: 0 auto;
  margin-top: 30px;
}
.ant-select-selection{
    height: 35px;
    border-radius: 0px;
    border: solid 1px #d4d4d4;
}
</style>
<style lang="stylus" scoped>
