<template>
  <div id="traincenter">
    <template v-if="bannerList.length>0">
      <div class="banner" v-for="item in bannerList" :key="item.id">
        <a :href="item.link">
            <img :src="item.img" alt="">
        </a>
      </div>
    </template>
    <div class="banner" v-else>
      <!-- <img
        src="../assets/banner/sxzx_banner.png"
        alt=""
      > -->
      <p>banner</p>
    </div>
    <div class="head">
      <div class="header">
        <ul class="nav">
          <li
            v-for="(item,index) in menuList"
            :key="index"
            @click="getDetail(item.title,item.id)"
            :class="{'active':current==item.title}"
          >{{item.title}}<em></em></li>
        </ul>
      </div>
    </div>
    <div class="content">
      <trainitem  :info="info"></trainitem>
    </div>
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import trainitem from "@/components/common/trainitem";
import { getTrainingListApi, getTrainingDetailApi,getbannerApi} from "@/request/api";
export default {
  name: "traincenter",
  components: { trainitem },
  data() {
    return {
      current: "",
      currentIndex: {},
      menuList: "",
      list: "",
      flag: false,
      info: {},
      traincenterId: null,
      bannerList:[]
    };
  },
  created() {
    this.traincenterId = this.$route.query.id;
    this.init();
  },
  mounted() {
    // let arr=window.location.pathname.split('/')
    // switch(arr[arr.length-1]){
    //     case 'cnc' : this.current=''
    //     break;
    //     case 'robot' : this.current=''
    //     break;
    //     case 'manu' : this.current=''
    //     break;
    //     case 'inte' : this.current=''
    //     break;
    // }
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(getTrainingListApi, this.searchInfo, (res) => {
        this.menuList = res.data.rows;
        if (this.traincenterId) {
          let item = res.data.rows.filter(
            (item) => item.id == this.traincenterId
          )[0];
          this.getDetail(item.title, item.id);
        } else {
          this.getDetail(res.data.rows[0].title, res.data.rows[0].id);
        }
      });
      this.getToken(getbannerApi,{module:5},res=>{
                this.bannerList=res.data
      })
    },
    getDetail(title, id) {
      this.getToken(getTrainingDetailApi, { trainingId: id }, (res) => {
        this.current = title;
        this.info = { name: title, content: res.data.content };
      });
    },
    changPage(data) {
      this.current = data.title;
      // let path=''

      // switch(data.title){
      //     case '' : path='/cnc'
      //     break;
      //     case '' : path='/robot'
      //     break;
      //     case '' : path='/manu'
      //     break;
      //      case '' : path='/inte'
      //     break;
      // }
      // this.$router.push({path:"/cnc",query:{id:data.id,"title":path.split('/')[path.split('/').length-1]}})
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      //   400 
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>
<style lang="stylus" scoped>#traincenter {
  .banner {
    height:261px;
    margin:0 auto;
    p{
        color:#333;
        font-size:20px;
        width:100%;
        height:100%;
        display:flex;
        align-items center;
        justify-content center;
        background:#d5d5d5;
        text-align center;
    }
    img {
      width: 100%;
      height:100%;
    }
  }

  background: #eee;

  .head {
    height: 66px;
    background: #fff;
    line-height: 66px;
    box-shadow: 0px 0px 10px 0px rgba(93, 93, 93, 0.1);

    .header {
      width: 1205px;
      margin: 0 auto;
      height: 73px;
      border-bottom: 1px solid #eeeeee;
    }

    .nav {
      hegint: 73px;
      width: 900px;
      list-style: none;
      display: flex;

      li {
        display: inline;
        float: left;
        width: 225px;
        height: 66px;
        text-align: center;
        font-family:'Microsoft YaHei';
        font-size: 17px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #333333;
        position: relative;
        cursor: pointer;
      }

      .active {
        background-color: #4182fa;
        color: #fff;

        //font-weight: bold;
        em {
          position: absolute;
          left: 50%;
          bottom: -10px;
          font-size: 30px;
          width: 0px;
          height: 0px;
          border: 5px solid transparent;
          border-top: 5px solid #4182fa;
          z-index: 999;
        }
      }
    }
  }

  .tit {
    margin: 0 auto;
    overflow: hidden;

    h1 {
      width: 1065px;
      height: 30px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 36px;
      letter-spacing: 0px;
      color: #000000;
      width: 1065px;
      height: 75px;
      margin: 0 auto;
      text-align: center;
      border-bottom: 1px solid #eee;
      margin-top: 60px;
    }
  }

  .content {
    width: 1205px;
    margin: 0 auto;
    background: #fff;
    position: relative;
    border-top: 1px solid #eee;
    padding-bottom: 80px;
    min-height:400px;
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>