<template>
  <div id="eQuill">
    <quill-editor
      class="ql-editor"
      v-model="content"
      ref="myQuillEditor"
      :options="editorOption"
      @click="clearContent"
    >
    </quill-editor>
  </div>
</template>
<script>
import Video from "@/units/quillVideo"; //video
Quill.register(Video, true); //video
import { quillEditor } from "vue-quill-editor"; // 
import { getupload } from "@/request/api"; // 
import "quill/dist/quill.snow.css"; //   
import * as Quill from "quill"; // quill
import ImageResize from "quill-image-resize-module"; //
import { ImageDrop } from "quill-image-drop-module"; //
Quill.register("modules/imageResize", ImageResize);
Quill.default.register("modules/imageDrop", ImageDrop);
//quill
let fonts = [
  "SimSun",
  "SimHei",
  "Microsoft-YaHei",
  "KaiTi",
  "FangSong",
  "Arial",
  "Times-New-Roman",
  "sans-serif",
];
let Font = Quill.import("formats/font");
Font.whitelist = fonts; //
Quill.register(Font, true);

const toolbarOptions = [
  ["bold", "italic", "underline", "strike"], // toggled buttons
  ["blockquote", "code-block"],
  [{ header: 1 }, { header: 2 }], // custom button values
  [{ list: "ordered" }, { list: "bullet" }],
  // [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
  [{ indent: "-1" }, { indent: "+1" }], // outdent/indent
  [{ direction: "rtl" }], // text direction

  [
    {
      size: [
        "12px",
        "14px",
        "16px",
        "18px",
        "20px",
        "22px",
        "24px",
        "26px",
        "32px",
        "48px",
      ],
    },
  ], // custom dropdown
  [{ header: [1, 2, 3, 4, 5, 6, false] }],

  [{ color: [] }],
  [{ background: [] }], // dropdown with defaults from theme
  [{ font: fonts }],
  [{ align: [] }],
  ["link", "image", "video"],
  ["clean"], // remove formatting button
];
const titleConfig = {
  "ql-bold": "",
  "ql-color": "",
  "ql-font": "",
  "ql-code": "",
  "ql-italic": "",
  "ql-link": "",
  "ql-background": "",
  "ql-size": "",
  "ql-strike": "",
  "ql-script": "/",
  "ql-underline": "",
  "ql-blockquote": "",
  "ql-header": "",
  "ql-indent": "",
  "ql-list": "",
  "ql-align": "",
  "ql-direction": "",
  "ql-code-block": "",
  "ql-formula": "",
  "ql-image": "",
  "ql-video": "",
  "ql-clean": "",
  "ql-upload": "",
};
export default {
  name: "eQuill",
  props: { defaultData: String },
  mounted() {
    this.content = this.defaultData;
    this.editor = this.$refs.myQuillEditor.quill;
    let toolbar = this.editor.getModule("toolbar");
    this.addQuillTitle();
    let upload = (accept, className, type) => {
      let fileInput = toolbar.container.querySelector(
        "input.ql-image[type=file]"
      );
      if (fileInput == null) {
        fileInput = document.createElement("input");
        fileInput.setAttribute("type", "file");
        fileInput.setAttribute("accept", accept);
        fileInput.classList.add(className);
        fileInput.addEventListener("change", () => {
          if (fileInput.files != null && fileInput.files[0] != null) {
            let formData = new FormData();
            formData.append("file", fileInput.files[0]);
            this.getToken(getupload, formData, (res) => {
              if (res.data.imgUrl.length > 0) {
                this.uploadImg = res.data.imgUrl[0];
              } else if (res.data.videoUrl.length > 0) {
                this.uploadImg = res.data.videoUrl[0];
              }
              let length = this.$refs.myQuillEditor.quill.selection.savedRange
                .index;
              this.editor.insertEmbed(length, type, this.uploadImg);
              this.editor.setSelection(length + 1);
            });
          }
        });
      }
      fileInput.click();
    };
    toolbar.addHandler("image", () => {
      upload(
        "image/png, image/gif, image/jpeg, image/bmp, image/x-icon",
        "ql-image",
        "image"
      );
    });
    toolbar.addHandler("video", () => {
      upload("audio/mp4, video/mp4", "ql-video", "video");
    });
  },
  watch: {
    content: function (val) {
      this.$emit("getQuillContent", val);
    },
  },
  components: { quillEditor },
  data() {
    return {
      content: "",
      imageUrl: "",
      uploadImg: "",
      apiUrl: window.g.apiUrl,
      editorOption: {
        placeholder: "",
        modules: {
          imageDrop: true,
          toolbar: {
            container: toolbarOptions, // 
          },
          imageResize: {
            displayStyles: {
              backgroundColor: "black",
              border: "none",
              color: "white",
            },
            modules: ["Resize", "DisplaySize", "Toolbar"],
          },
        },
      },
    };
  },
  methods: {
    addQuillTitle() {
      const oToolBar = document.querySelector(".ql-toolbar"),
        aButton = oToolBar.querySelectorAll("button"),
        aSelect = oToolBar.querySelectorAll("select");
      aButton.forEach(function (item) {
        if (item.className === "ql-script") {
          item.value === "sub" ? (item.title = "") : (item.title = "");
        } else if (item.className === "ql-indent") {
          item.value === "+1"
            ? (item.title = "")
            : (item.title = "");
        } else {
          item.title = titleConfig[item.classList[0]];
        }
      });
      aSelect.forEach(function (item) {
        item.parentNode.title = titleConfig[item.classList[0]];
      });
    },
    clearContent(){
      // console.log(123)
    }
  },
};
</script>
<style lang="stylus">
#eQuill {
  width: 100%;
  min-height: 248px;

  .ql-toolbar.ql-snow + .ql-container.ql-snow {
    border-top: 0px;
    min-height: 200px;

    .ql-video {
      width: 60%;

      .ql-toolbar.ql-snow {
        background: #f1f1f1;
      }
    }
  }
}
</style>