<template>
  <div class="theoryexam">
    <div class="exampaper">
        <div class="paper" ref="paper">
          <div class="content">
            <div class="header">
              <div class="score" v-if="isChecked">{{scoreData}}</div>
              <h2>{{testlist&&testlist.taskTitle}}</h2>
              <button class="back" @click="tocourse"></button>
              <div class="paperTotal">
                <b> {{testlist&&testlist.judgeNum+testlist.multiNum+testlist.singleNum}}{{testlist&&testlist.examTime }}{{testlist&&testlist.totalScore}},{{testlist&&testlist.passScore}}</b>
              </div>
            </div>
            <div :class="isChecked ? 'body is_checked' : 'body'">
              <div class="left">
                <div class="single" v-if="single.length!==0">
                  <div class="title">{{single.length}}{{testlist.singleNum*testlist.perSingle}}</div>
                  <a
                    :name="'single'+index"
                    :id="'single'+index"
                    class="item"
                    v-for="(item,index) in single"
                    :key="index"
                  >
                    <img
                      v-if="isChecked"
                      :src="item.answer === item.isAnswer ? rightUrl : wrongUrl"
                      alt=""
                    >
                    <div class="stem">{{index+1}}. {{item.title}} {{testlist.perSingle}}</div>
                    <div class="answer">
                      <a-radio-group
                        @change="(e)=>singleChange(e,item)"
                        v-model="item.isAnswer"
                        :disabled="true"
                        v-if='isChecked'
                      >
                        <a-radio
                          :style="radioStyle"
                          :value="options[index]"
                          v-for="(value,index) in item.option.split('&&')"
                          :key="index"
                        >
                          {{options[index]}}{{value}}
                        </a-radio>
                      </a-radio-group>
                      <a-radio-group
                        @change="(e)=>singleChange(e,item)"
                        v-else
                        v-model="item.isAnswer"
                      >
                        <a-radio
                          :style="radioStyle"
                          :value="options[index]"
                          v-for="(value,index) in item.option.split('&&')"
                          :key="index"
                        >
                          {{options[index]}}{{value}}
                        </a-radio>
                      </a-radio-group>
                    </div>
                    <div
                      v-if="isChecked"
                      class="question_analysis"
                    >
                      <p><span class="right_answer">{{item.answer}}</span> <i></i>
                        <span :class="item.answer === item.isAnswer? 'right_answer' : 'my_answer'">{{item.isAnswer||''}}</span>
                      </p>
                      <p>{{item.analyse || ''}}</p>
                    </div>
                  </a>
                </div>
                <div
                  class="checked"
                  v-if="checked.length!==0"
                >
                  <div class="title">{{testlist.singleNum===0?'':''}}{{testlist.multiNum}}{{testlist.multiNum*testlist.perMulti}}</div>
                  <a
                    class="item"
                    :name="'checked'+index"
                    :id="'checked'+index"
                    v-for="(item,index) in checked"
                    :key="index"
                  >
                    <img
                      v-if="isChecked"
                      :src="item.answer === item.isAnswer ? rightUrl : wrongUrl"
                      alt=""
                    >
                    <div class="stem">{{single.length+index+1}}. {{item.title}} {{testlist.perMulti}}</div>
                    <div class="answer">
                      <a-checkbox-group
                        @change="e=>checkChange(e,item)"
                        v-if="isChecked"
                        :disabled="true"
                        :value="item.isAnswer&&item.isAnswer.split('')"
                      >
                        <a-row>
                          <a-col
                            :span="24"
                            :value="index"
                            v-for="(value,index) in item.option.split('&&')"
                            :key="index"
                          >
                            <a-checkbox :value="options[index]">
                              {{options[index]}}{{value}}</a-checkbox>
                          </a-col>
                        </a-row>
                      </a-checkbox-group>
                      <a-checkbox-group
                        @change="e=>checkChange(e,item)"
                        v-else
                        :value="item.isAnswer&&item.isAnswer.split('')"
                      >
                        <a-row>
                          <a-col
                            :span="24"
                            :value="index"
                            v-for="(value,index) in item.option.split('&&')"
                            :key="index"
                          >
                            <a-checkbox :value="options[index]">
                              {{options[index]}}{{value}}</a-checkbox>
                          </a-col>
                        </a-row>
                      </a-checkbox-group>
                    </div>
                    <div
                      v-if="isChecked"
                      class="question_analysis"
                    >
                      <p><span class="right_answer">{{item.answer}}</span> <i></i>
                        <span :class="item.answer === item.isAnswer? 'right_answer' : 'my_answer'">{{item.isAnswer||''}}</span>
                      </p>
                      <p>{{item.analyse || ''}}</p>
                    </div>
                  </a>
                </div>
                <div
                  class="judge"
                  v-if="judge.length!==0"
                >
                  <div class="title">{{judgeIndex}}{{judge.length}}{{testlist.judgeNum*testlist.perJudge}}</div>
                  <a
                    class="item"
                    :name="'judge'+index"
                    :id="'judge'+index"
                    v-for="(item,index) in judge"
                    :key="index"
                  >
                    <img
                      v-if="isChecked"
                      :src="item.answer === item.isAnswer ? rightUrl : wrongUrl"
                      alt=""
                    >
                    <div class="stem">{{single.length+checked.length+index+1}}. {{item.title}} {{testlist.perJudge}}</div>
                    <div class="answer">
                      <a-radio-group
                        @change="e=>judgeChange(e,item)"
                        v-if="isChecked"
                        :disabled="true"
                        v-model="item.isAnswer"
                      >
                        <a-radio
                          :style="radioStyle"
                          value=""
                        ></a-radio>
                        <a-radio
                          :style="radioStyle"
                          value=""
                        ></a-radio>
                      </a-radio-group>
                      <a-radio-group
                        @change="e=>judgeChange(e,item)"
                        v-else
                        v-model="item.isAnswer"
                      >
                        <a-radio
                          :style="radioStyle"
                          value=""
                        ></a-radio>
                        <a-radio
                          :style="radioStyle"
                          value=""
                        ></a-radio>
                      </a-radio-group>
                    </div>
                    <div
                      v-if="isChecked"
                      class="question_analysis"
                    >
                      <p><span class="right_answer">{{item.answer}}</span> <i></i>
                        <span :class="item.answer === item.isAnswer? 'right_answer' : 'my_answer'">{{item.isAnswer||''}}</span>
                      </p>
                      <p>{{item.analyse || ''}}</p>
                    </div>
                  </a>
                </div>
              </div>
              <div
                :class="fixed?'right fixed':'right'"
                ref="rightBox"
              >
                <div class="header">
                  <a-icon type="clock-circle" /><span></span>{{endTime}}
                </div>
                <div class="right_content">
                  <div>
                    <a-collapse
                      defaultActiveKey="1"
                      :activeKey="defaultActiveKey"
                    >
                      <a-collapse-panel
                        v-if="single.length!==0"
                        :header="''"
                        key="1"
                      >
                        <p v-if="isChecked">
                          <a
                            class="index"
                         
                              @click="goAnchor('#single'+index)"
                            v-for="(value,index) in single"
                            :class="value.isAnswer === value.answer ? 'right_item index' : 'wrong_item index'"
                            :key="index"
                          >
                            {{index+1}}
                          </a>
                        </p>
                        <p v-else>
                          <a
                            class="index"
                            @click="goAnchor('#single'+index)"
                            v-for="(value,index) in single"
                            :class="value.isAnswer? 'isAnswer index': 'index'"
                            :key="index"
                          >
                            {{index+1}}
                          </a>
                        </p>
                      </a-collapse-panel>
                      <a-collapse-panel
                        v-if="checked.length!==0"
                        :header="single.length===0?''+'':''+''"
                        key="2"
                      >
                        <p v-if="isChecked">
                          <a
                            class="index"
                            v-for="(value,index) in checked"
                            :key="index"
                             @click="goAnchor('#checked'+index)"
                            :class="value.isAnswer === value.answer ? 'right_item index' : 'wrong_item index'"
                          >
                            {{single.length+index+1}}
                          </a>
                        </p>
                        <p v-else>
                          <a
                            class="index"
                            v-for="(value,index) in checked"
                            :key="index"
                             @click="goAnchor('#checked'+index)"
                            :class="value.isAnswer? 'isAnswer index': 'index'"
                          >
                            {{single.length+index+1}}
                          </a>
                        </p>
                      </a-collapse-panel>
                      <a-collapse-panel
                        v-if="judge.length!==0"
                        :header="judgeIndex+''"
                        key="3"
                      >
                        <p v-if="isChecked">
                          <a
                            class="index"
                            v-for="(value,index) in judge"
                            @click="goAnchor('#judge'+index)"
                            :key="index"
                            :class="value.isAnswer === value.answer ? 'right_item index': 'wrong_item index'"
                          >
                            {{single.length+checked.length+index+1}}
                          </a>
                        </p>
                        <p v-else>
                          <a
                            class="index"
                            v-for="(value,index) in judge"
                            @click="goAnchor('#judge'+index)"
                            :key="index"
                            :class="value.isAnswer? 'isAnswer index': 'index'"
                          >
                            {{single.length+checked.length+index+1}}
                          </a>
                        </p>
                      </a-collapse-panel>
                    </a-collapse>
                  </div>
                </div> 
                <div class="footer">

                  <div v-if="isChecked">
                    <cite class="right_target"></cite>
                    <cite class="wrong_target"></cite>
                  </div>
                  <div v-else>
                    <cite class="had"></cite>
                    <cite class="not_had"></cite>
                  </div>
                  <button
                    v-if="!isChecked"
                    @click="submitPaper()"
                  ></button>
                </div>
              </div>
            </div>
          </div>
          <div>
            <a-back-top />
          </div>
        </div>
      </div>
  </div>
</template>

<script>
let min = 0;
let sec = 0;
import entry from "../units/entryDetry.js";
import { formatDate } from "@/units/format.js";
import {
  getcourseDetailApi,
  getcatalogApi,
  getcatDetail,
  getcommitPaperApi,
  getupdateStatusApi,
  getscheduledAnswerApi,
} from "@/request/api";
export default {
  name: "theoryexam",
  data() {
    return {
      testId: null,
      list: [],
      munelist: [],
      currentId: null,
      paperId: null,
      menuId: null,
      testlist: null,
      key: null,
      iv: null,
      type: null,
      userAnswer: null,
      timer: null,
      radioStyle: {
        display: "block",
        height: "30px",
        lineHeight: "30px",
        fontSize: "16px",
      },
      examId: null,
      single: [],
      checked: [],
      judge: [],
      fixed: false,
      options: ["A", "B", "C", "D", "E", "F"],
      isChecked: false,
      wrongUrl: require("../assets/wrong.png"),
      rightUrl: require("../assets/right.png"),
      time: "",
      examinee_answer: [],
      defaultActiveKey: "1",
      during_time: 0,
      subinfo: {
        userAnswer: "",
        courseId: "",
        taskId: "",
        timeUse: "",
        paperId: "",
        type: "",
        startTime: "",
      },
      isFinished: false,
      chapterId:''
    };
  },
  watch: {
    testlist: function () {
      this.single = this.testlist.paperInfo
        .filter((item) => item.type == 0)
        .map((item) => ({ ...item, score: this.testlist.perSingle }));
      this.checked = this.testlist.paperInfo
        .filter((item) => item.type == 1)
        .map((item) => ({ ...item, score: this.testlist.perMulti }));
      this.judge = this.testlist.paperInfo
        .filter((item) => item.type == 2)
        .map((item) => ({ ...item, score: this.testlist.perJudge }));
      // this.time=this.testlist.examTime*60*1000
    },
    userAnswer: function () {
      this.checkInit();
    },
  },
  mounted() {
    document.addEventListener("scroll", this.Scroll);
    window.onbeforeunload = function (e) {
      e = e || window.event;
      // IE8Firefox 4
      if (e) {
        e.returnValue = "";
      }
      // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
      return "";
    };
    document.oncontextmenu = function() {event.returnValue = false;}
  },
  created() {
    this.paperId = this.$route.query.paperId;
    this.subinfo.courseId = this.$route.query.id;
    this.testId = this.$route.query.id;
    this.subinfo.taskId = this.$route.query.menuId;
    this.menuId = this.$route.query.menuId;
    this.isFinished = this.$route.query.isFinished;
    this.chapterId=this.$route.query.chapterId
    // console.log(this.isFinished)
    // cosnole.log
    this.init();
  },
  beforeRouteLeave(to, from, next) {
    if (localStorage.getItem("clear") || this.isChecked) {
      next();
    } else {

        this.$confirm({
          title: '',
          okText: '',
          cancelText: '',
          onOk:()=> {
             
             let single = this.single.map((item) => ({id: item.id, answer: item.isAnswer && item.isAnswer,}));
              let checked = this.checked.map((item) => ({id: item.id,answer: item.isAnswer && item.isAnswer,}));
              let judge = this.judge.map((item) => ({id: item.id,answer: item.isAnswer && item.isAnswer,}));
                //Encrypt(JSON.stringify ...)
              this.subinfo.userAnswer = entry.Encrypt(JSON.stringify([...single, ...checked, ...judge]),"w2SIy2Hx%2i!tTCd-ikwHEv_kQmB58xI","R7#54X&gLB!6Q&s=");
              this.subinfo.timeUse =(this.testlist.examTime * 60 * 1000 - this.time) / 1000 / 60;
              this.subinfo.paperId = this.paperId;
              this.subinfo.type = this.type;
              this.getToken(getcommitPaperApi,{...this.subinfo,startTime: formatDate("yyyy-MM-dd hh:mm:ss", new Date()),},() => {
                  this.getToken(
                      getupdateStatusApi,
                      { 
                        taskId: this.subinfo.taskId,
                        courseId: this.subinfo.courseId,
                        status: 1,
                        },
                      () => {
                        this.$store.commit('getIds',{'chapterId':this.chapterId,'taskId':this.subinfo.taskId})
                      }
                    );
            }
          );
              let isAllAnswer = true;
              let allItem = this.single.concat(this.checked).concat(this.judge);
              allItem.forEach((item) => {
                if (!item.isAnswer) {
                  isAllAnswer = false;
                  this.backTop();
                }
              });
              clearTimeout(this.timer);
              if (isAllAnswer) {
                    this.isChecked = true;
                    clearTimeout(this.timer);
                    this.backTop();
              } else {
                    this.isChecked = true;
                    clearTimeout(this.timer);
                    this.backTop();
              }
              // next();
          },
          onCancel() {
             next(false);
          },
        });

    }
  },
  computed: {
    // 
    endTime() {
      min = parseInt((this.time / 1000 / 60) % 60);
      sec = parseInt((this.time / 1000) % 60);
      min = min > 9 ? min : "0" + min;
      sec = sec > 9 ? sec : "0" + sec;
      return `${
        "0" + parseInt((this.time / 1000 / 60 / 60) % 24)
      }:${min}:${sec}`;
    },
    // 
    scoreData() {
      let score = 0;
      let itemList = this.single.concat(this.checked).concat(this.judge);
      itemList.forEach((item) => {
        if (item.answer === item.isAnswer) {
          score += item.score;
        }
      });
      return score;
    },
    // 
    singleTotal() {
      let result = 0;
      this.single.forEach((item) => {
        result += item.score;
      });
      // this.testlist.paperInfo.filter(item=>
      //          item.type==0)

      return result;
    },
    // 
    checkTotal() {
      let result = 0;
      this.checked.forEach((item) => {
        result += item.score;
      });
      return result;
    },
    // 
    judgeTotal() {
      let result = 0;
      this.judge.forEach((item) => {
        result += item.score;
      });
      return result;
    },
    // 
    judgeIndex() {
      let index = "";
      if (this.single.length === 0 && this.checked.length === 0) {
        index = "";
      } else if (this.single.length !== 0 && this.checked.length === 0) {
        index = "";
      } else if (this.single.length === 0 && this.checked.length !== 0) {
        index = "";
      } else {
        index = "";
      }
      return index;
    },
  },
  methods: {
    init() {
      this.getToken(getcourseDetailApi, { courseId: this.testId }, (res) => {
        this.list = res.data;
        this.munelist = res.data.chapters;
      });
      this.getToken(getcatalogApi, { taskId: this.menuId }, (res) => {
        this.info = res.data;
        this.resourceUrl = res.data.resourceUrl;
        this.currentId = this.menuId;
        this.type = res.data.type;
        this.getToken(
          getcatDetail,
          {
            paperId: this.paperId,
            taskId: this.menuId,
            courseId: this.testId,
            type: res.data.type,
            isFinished: res.data.isFinished,
          },
          (res) => {
            if (res.data.userAnswer) {
              this.userAnswer =
                res.data.userAnswer.length == 0 ? null : res.data.userAnswer;
            } else {
              this.time = res.data.examTime * 60 * 1000;
              // this.isChecked=true
            }
            let paperInfo = JSON.parse(
              JSON.parse(
                entry.Decrypt(
                  res.data.theoryQuestion,
                  res.data.key,
                  res.data.iv
                )
              )
            );
            this.testlist = { ...res.data, paperInfo: paperInfo };
             this.single = this.testlist.paperInfo
              .filter((item) => item.type == 0)
              .map((item) => ({ ...item, score: this.testlist.perSingle }));
            this.checked = this.testlist.paperInfo
              .filter((item) => item.type == 1)
              .map((item) => ({ ...item, score: this.testlist.perMulti }));
            this.judge = this.testlist.paperInfo
              .filter((item) => item.type == 2)
              .map((item) => ({ ...item, score: this.testlist.perJudge }));
            if (this.userAnswer) {
              this.checkInit();
              this.isChecked = this.isFinished;
              this.time =
                  (this.testlist.examTime - this.testlist.timeUse) * 1000 * 60;
              if (this.isChecked === false) {
                this.countdown();
              }
            } else {
              // this.time = this.testlist.timeUse * 60 * 1000;
              this.countdown();
              this.normalSubmit();
            }
            //JSON.parse(JSON.parse(entry.Decrypt...)
            // this.key=entry.Decrypt(res.miyao.key,res.data.key)
            // this.iv=entry.Decrypt(res.miyao.iv,res.data.iv)
            // let paperInfo=res.data.theoryQuestion
          }
        );
      });
    },
    tocourse() {
      this.$router.push({ path: "/catalogDetail", query: {id:this.testId, 'menuId': this.menuId,"paperId":this.paperId} });
    },
     goAnchor(selector, offset = -80){
        // 
        let top = 0;
        // 
        const scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
        if (typeof selector === 'number') {
          top = selector - scrollTop;
        } else {
          // 
          const anchor = document.querySelector(selector) || { offsetTop: 0 };
          top = anchor.offsetTop - scrollTop;
          //  =  - 
        }
        //  smooth 
        window.scrollBy({ top: top + offset, behavior: 'smooth' });
      },
    checkInit() {
      let userAnswer = JSON.parse(this.userAnswer);
      if (this.single.length > 0) {
        this.single.forEach((item) => {
          item.isAnswer = userAnswer.filter(
            (value) => value.id == item.id
          )[0].answer;
        });
      }
      if (this.checked.length > 0) {
        this.checked.forEach((item) => {
          item.isAnswer = userAnswer.filter(
            (value) => value.id == item.id
          )[0].answer;
        });
      }
      if (this.judge.length > 0) {
        this.judge.forEach((item) => {
          item.isAnswer = userAnswer.filter(
            (value) => value.id == item.id
          )[0].answer;
        });
      }
    },
    singleChange(e, item) {
      item.isAnswer = e.target.value;
      this.defaultActiveKey = "1";
      this.normalSubmit();
    },
    checkChange(e, data) {
      this.defaultActiveKey = "2";
      data.isAnswer = e.join("");
      data.isAnswer = data.isAnswer&&data.isAnswer.split("").sort().join("");
      this.normalSubmit();
    },
    judgeChange(e, data) {
      this.defaultActiveKey = "3";
      data.isAnswer = e.target.value;
      this.normalSubmit();
    },
    // 
    Scroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.fixed = scrollTop > 246 ? true : false;
    },
    // 
    backTop() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    // 
    submitPaper() {
      //
      let single = this.single.map((item) => ({
        id: item.id,
        answer: item.isAnswer && item.isAnswer,
      }));
      let checked = this.checked.map((item) => ({
        id: item.id,
        answer: item.isAnswer && item.isAnswer,
      }));
      let judge = this.judge.map((item) => ({
        id: item.id,
        answer: item.isAnswer && item.isAnswer,
      }));
      //Encrypt(JSON.stringify ...)
      this.subinfo.userAnswer = entry.Encrypt(
        JSON.stringify([...single, ...checked, ...judge]),
        "w2SIy2Hx%2i!tTCd-ikwHEv_kQmB58xI",
        "R7#54X&gLB!6Q&s="
      );
      this.subinfo.timeUse =
        (this.testlist.examTime * 60 * 1000 - this.time) / 1000 / 60;
      this.subinfo.paperId = this.paperId;
      this.subinfo.type = this.type;
      this.getToken(
        getcommitPaperApi,
        {
          ...this.subinfo,
          startTime: formatDate("yyyy-MM-dd hh:mm:ss", new Date()),
        },
        (res) => {
          if (res.code == 200) {
            this.getToken(
              getupdateStatusApi,
              {
                taskId: this.subinfo.taskId,
                courseId: this.subinfo.courseId,
                status: 1,
              },
              () => {
                this.$store.commit('getIds',{'chapterId':this.chapterId,'taskId':this.subinfo.taskId})
              }
            );
          }
        }
      );
      let isAllAnswer = true;
      let allItem = this.single.concat(this.checked).concat(this.judge);
      allItem.forEach((item) => {
        if (!item.isAnswer) {
          isAllAnswer = false;
          this.backTop();
        }
      });
      if (isAllAnswer) {
        this.$confirm({
          title: "",
          okText: "",
          cancelText: "",
          onOk: () => {
            this.isChecked = true;
            clearTimeout(this.timer);
            this.backTop();
          },
        });
      } else {
        this.$confirm({
          title: "?",
          okText: "",
          cancelText: "",
          onOk: () => {
            this.isChecked = true;
            clearTimeout(this.timer);
            this.backTop();
          },
          onCancel() {},
        });
      }
    },
    // 
    onceTest() {
      this.$router.go(0);
      this.isChecked = false;
      // this.testInit()
    },
    // 
    normalSubmit() {
      let single = this.single.map((item) => ({
        id: item.id,
        answer: item.isAnswer && item.isAnswer,
      }));
      let checked = this.checked.map((item) => ({
        id: item.id,
        answer: item.isAnswer && item.isAnswer,
      }));
      let judge = this.judge.map((item) => ({
        id: item.id,
        answer: item.isAnswer && item.isAnswer,
      }));
      this.subinfo.userAnswer = entry.Encrypt(
        JSON.stringify([...single, ...checked, ...judge]),
        "w2SIy2Hx%2i!tTCd-ikwHEv_kQmB58xI",
        "R7#54X&gLB!6Q&s="
      );
      this.subinfo.timeUse =
        (this.testlist.examTime * 60 * 1000 - this.time) / 1000 / 60;
      this.subinfo.paperId = this.paperId;
      this.subinfo.type = this.type;
      this.getToken(
        getscheduledAnswerApi,
        {
          ...this.subinfo,
          startTime: formatDate("yyyy-MM-dd hh:mm:ss", new Date()),
        },
        () => {}
      );
      this.during_time = 0;
    },
    countdown() {
      this.timer = setTimeout(() => {
        this.during_time++;
        if (this.during_time >= 5) {
          this.normalSubmit();
        }

        if (this.time <= 0) {
          
           
          let single = this.single.map((item) => ({id: item.id, answer: item.isAnswer && item.isAnswer,}));
          let checked = this.checked.map((item) => ({id: item.id,answer: item.isAnswer && item.isAnswer,}));
          let judge = this.judge.map((item) => ({id: item.id,answer: item.isAnswer && item.isAnswer,}));
          //Encrypt(JSON.stringify ...)
          this.subinfo.userAnswer = entry.Encrypt(JSON.stringify([...single, ...checked, ...judge]),"w2SIy2Hx%2i!tTCd-ikwHEv_kQmB58xI","R7#54X&gLB!6Q&s=");
          this.subinfo.timeUse =(this.testlist.examTime * 60 * 1000 - this.time) / 1000 / 60;
          this.subinfo.paperId = this.paperId;
          this.subinfo.type = this.type;
          this.getToken(getcommitPaperApi,{...this.subinfo,startTime: formatDate("yyyy-MM-dd hh:mm:ss", new Date()),},() => {
              this.getToken(
                  getupdateStatusApi,
                  { 
                    taskId: this.subinfo.taskId,
                    courseId: this.subinfo.courseId,
                    status: 1,
                    },
                  () => {
                    this.$store.commit('getIds',{'chapterId':this.chapterId,'taskId':this.subinfo.taskId})
                  }
                );
        }
      );
          let isAllAnswer = true;
          let allItem = this.single.concat(this.checked).concat(this.judge);
          allItem.forEach((item) => {
            if (!item.isAnswer) {
              isAllAnswer = false;
              this.backTop();
            }
          });
          clearTimeout(this.time);
          if (isAllAnswer) {
                this.isChecked = true;
                clearTimeout(this.timer);
                this.backTop();
          } else {
                this.isChecked = true;
                clearTimeout(this.timer);
                this.backTop();
          }


        } else {
          this.time -= 1000;
          this.countdown();
          // console.log(123);
        }
      }, 1000);
    },
  },
  destroyed() {
    window.onbeforeunload = null;
    window.clearTimeout(this.timer);
  },
};
</script>

