<template>
  <div class="ispdf">
    <div class="title">
      <span>{{title}}</span>
      <button @click="$emit('gocourse',catId)"></button>
    </div>
    <div class="content">
       <div class="bigbox">
          <div
          class="pdfbox"
          ref="all"
        >
          <div class="head">
            <div class="mod">
              <div
                class="left"
                :class="{select:idx==2}"
                @touchstart="idx=2"
                @touchend="idx=-1"
                @click="changePdfPage(0)"
              ><img
                  src="../../assets/course/left.png"
                  alt=""
                >
              </div>
              <span>{{currentPage}} /{{pageCount}}</span>
              <div
                class="right"
                :class="{select:idx==3}"
                @touchstart="idx=3"
                @touchend="idx=-1"
                @click="changePdfPage(1)"
              ><img
                  src="../../assets/course/right.png"
                  alt=""
                ></div>
            </div>
            <ul>
              <li
                :class="{select:idx==1}"
                @touchstart="idx=1"
                @touchend="idx=-1"
                @click="scaleX"
              ><img
                  src="../../assets/course/small.png"
                  alt=""
                ></li>
              <li
                :class="{select:idx==0}"
                @touchstart="idx=0"
                @touchend="idx=-1"
                @click="scaleD"
              ><img
                  src="../../assets/course/big.png"
                  alt=""
                ></li>
              <li
                class="btn-fullscreen"
                @click="screenfull"
              ><img
                  src="../../assets/course/src.png"
                  alt=""
                ></li>
            </ul>
          </div>
          <pdf
            class="con"
            :src="resourceUrl"
            :page="currentPage"
            @progress="loadedRatio = $event"
            @num-pages="pageCount=$event"
            @page-loaded="currentPage=$event"
            @loaded="loadPdfHandler"
            ref="wrapper"
          >
          </pdf>
        </div>

       </div>

    </div>


  </div>
</template>
<script>

import pdf from "vue-pdf";
import screenfull from "screenfull";
export default {
  props: {
    resourceUrl: String,
    title: String,
    catId: [String,Number],
    // chapterId:[String,Number]
  },
  components: {
    pdf,
  },
  data() {
    return {
      currentPage: 1,
      pageCount: 0,
      scale: 100, // 
      idx: -1,
      clauseTitle: "",
      loadedRatio: 0,
      fullscreen: false,
    };
  },
  methods: {
    changePdfPage(val) {
      if (val === 0 && this.currentPage > 1) {
        this.currentPage--;
      }
      if (val === 1 && this.currentPage < this.pageCount) {
        this.currentPage++;
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    // pdf
    loadPdfHandler() {
      this.currentPage = 1; // 
    },
    // 
    scaleD() {
      this.scale += 5;
      // this.$refs.wrapper.$el.style.transform = "scale(" + this.scale + ")";
      this.$refs.all.style.width = parseInt(this.scale) + "%";
    },
    // 
    scaleX() {
      if (this.scale <= 20) {
        return;
      }
      this.scale += -5;
      this.$refs.all.style.width = parseInt(this.scale) + "%";
      // this.$refs.wrapper.$el.style.transform = "scale(" + this.scale + ")";
    },
    //  ()
    screenfull() {
      if (!screenfull.isEnabled) {
        // console.log('')
        return false;
      }
      screenfull.toggle(this.$refs.all);
      this.fullscreen = true;
    },
    // Esc 
    checkFull() {
      let isFull =
        document.fullscreenEnabled ||
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      if (isFull === undefined) {
        isFull = false;
      }
      return isFull;
    },
  },
  mounted() {
    window.onresize = () => {
      // ESC
      if (!this.checkFull()) {
        // 
        this.fullscreen = false;
      }
    };
  },
};
</script>
<style lang="stylus" scoped>
.ispdf {
  .title {
    height: 65px;
    border-bottom: 1px solid #eee;
    margin-left: 38px;
    margin-right: 39px;
    display: flex;
    position: relative;
    span {
      color: #333333;
      font-size: 18px;
      padding-top: 30px;
    }
    button {
      border: 0;
      outline: none;
      width: 99px;
      height: 30px;
      background-color: #ffffff;
      border: solid 1px #cecece;
      color: #4182fa;
      font-size: 12px;
      position: absolute;
      right: 17px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .content {
    margin: 0 auto;
    margin-top:30px;
    margin-bottom: 50px;
    margin-left:33px;
    margin-right:33px;
    min-height:500px;
    .bigbox{
      max-width:1400px;
      max-height:700px;
      overflow-y:auto
      overflow-x:auto
      margin: 0 auto;
      background-color: #f6f6f6;
      .pdfbox {   
        padding: 0 0 rem(80);
        .head {
          height: 28px;
          line-height: 28px;
          display: flex;
          background: #ddd;
          min-width:1400px;
          position: relative;

        .mod {
          display: flex;
          margin: 0 auto;
          line-height: 28px;

          .left {
            height: 28px;
            line-height: 28px;
            overflow: hidden;

            img {
              width: 34px;
              height: 20px;
              margin-top: -2px;
            }
          }

          .right {
            height: 28px;
            line-height: 28px;
            overflow: hidden;

            img {
              width: 34px;
              height: 20px;
              margin-top: -2px;
            }
          }

          span {
            padding-left: 5px;
            padding-right: 5px;
          }
        }

        ul {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 20px;
          display: flex;
          li {
            width: 30px;
            border-right: 1px solid #b3b3b3;
            text-align: center;

            img {
              width: 17px;
              height: 16px;
            }
          }

          li:last-child {
            border-right: 0;
          }
         }
        }
        .con {
          margin: 0 auto;
          background:#f6f6f6;
          width:900px;

        canvas {
          height: 100% !important;
          width: 100%;
        }

        .annotationLayer {
          height: 100% !important;
          width: 100%;
        }
        }
      }
      
    }
  }
}
</style>
