<template>
  <div id="home">
    <div class="banner">
      <eCarousel></eCarousel>
    </div>
    <div class="wrap ">
      <div class="innerWrap">
        <div class="title">
          <span></span>
          <span
            class="more"
            @click="tocourse"
          >></span>
        </div>
        <div class="notbox"  v-if="courselist.length==0">
            <img
              src="../assets/blank/notlist.png"
              alt=""
            >
            <p class="inp"></p>
        </div>
        <ul class="list" v-else>
            <li
            v-for="(item,index) in courselist"
            :key="index"
            @click="tocourseDetail(item.id)"
          >
            <img
              :src="item.cover"
              alt=""
            >
            <div class="detail">
              <p>{{item.title}}</p>
              <p>{{item.teacher}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="wrap lecture">
      <div class="innerWrap">
        <div class="title">
          <span></span>
          <span
            class="more"
            @click="golecture"
          >></span>
        </div>
        <div class="lectureList">
          <div class="notbox"  v-if="currentLecture.length==0">
              <img
                src="../assets/blank/notlist.png"
                alt=""
              >
              <p class="inp"></p>
            </div>
          <carousel
            v-else
            :dots="false"
            @beforeChange="beforeChange"
          >
            <template slot="content" >
              <div
                class="box"
                v-for="item in currentLecture"
                :key="item.id"
              >
                <div class="left">
                  <img
                    class="cover"
                    :src="item.cover"
                    alt=""
                  >
                  <p class="lectitle" :title="item.title">{{item.title}}</p>
                  <div
                    class="btn"
                    @click="toLecture(item.id)"
                  >{{item.status!=2?"":""}}</div>
                  <div class="modal">
                    <div class="infoLeft">
                      <img
                        src="../assets/home/mainLecture.png"
                        alt=""
                      >
                      {{item.teacher}}
                      <img
                        src="../assets/home/people.png"
                        alt=""
                      >
                      {{item.appointNum}}
                    </div>
                    <div class="infoRight">
                      {{item.startTime}}
                    </div>
                  </div>
                  <div class="leftModal"></div>
                </div>
                <div class="right">
                  <ul :style="{'margin-top':'-'+marginTop+'px'}">
                    <li
                      v-for="(item,itemIndex) in lecturelist"
                      :key="item.id"
                      @click="clickLecture(item.id,itemIndex)"
                      :title="item.title"
                      :class="{'isActive':itemIndex==lectureIndex}"
                    >
                      <span style="margin-right:30px;">
                        {{item.startTime.slice(5,7)}}{{item.startTime.slice(8,10)}}</span>
                      {{item.title}}
                    </li>
                  </ul>
                  <div class="border"></div>
                </div>
              </div>
            </template>
          </carousel>
        </div>
      </div>
    </div>
    <div class="wrap service">
      <div class="innerWrap">
        <div class="title">
          <span>1+X </span>
          <span class="more" @click="toserviceKaohe">></span>
        </div>
        <ul class="list1">
          <li @click="toserviceKaohe">
            <div class="top">
              <div class="tot">
                <img
                  src="../assets/home/icon-1.png"
                  alt=""
                >
              </div>
              <h2></h2>
            </div>
            <div class="btm">
              <b></b>
              <p>.....</p>
              <img
                src="../assets/home/1+x.png"
                alt=""
              >
            </div>
          </li>
          <li @click="toserviceShizi">
            <div class="top">
              <div class="tot">
                <img
                  src="../assets/home/icon-3.png"
                  alt=""
                >
              </div>
              <h2></h2>
            </div>
            <div class="btm">
              <b></b>
              <p>...</p>
              <img
                src="../assets/home/1+x.png"
                alt=""
              >
            </div>
          </li>
          <li @click="toserviceKaowu">
            <div class="top">
              <div class="tot">
                <img
                  src="../assets/home/icon-2.png"
                  alt=""
                >
              </div>
              <h2></h2>
            </div>
            <div class="btm">
              <b></b>
              <p>ABB,FANUC,OTC....</p>
              <img
                src="../assets/home/1+x.png"
                alt=""
              >
            </div>
          </li>
          <li @click="toserviceKaoshi">
            <div class="top">
              <div class="tot">
                <img
                  src="../assets/home/icon-4.png"
                  alt=""
                >
              </div>
              <h2></h2>
            </div>
            <div class="btm">
              <b></b>
              <p></p>
              <img
                src="../assets/home/1+x.png"
                alt=""
              >
            </div>
          </li>

        </ul>
        <div class="foot">
          <img
            @click="gotraining"
            src="../assets/home/1+xbanner.png"
            alt=""
          >
        </div>
      </div>
    </div>
    <div class="wrap train">
      <div class="innerWrap">
        <div class="title">
          <span></span>
          <span
            class="more"
            @click="gotrain"
          >></span>
        </div>
        <div class="list2">
          <div class="bleft">
            <div
              class="left"
              v-show="flag1"
            >
              <img
                src="../assets/home/train_1.png"
                alt=""
              >
            </div>
            <div
              class="left"
              v-show="flag2"
            >
              <img
                src="../assets/home/train_2.png"
                alt=""
              >
            </div>
            <div
              class="left"
              v-show="flag3"
            >
              <img
                src="../assets/home/train_3.png"
                alt=""
              >
            </div>
            <div
              class="left"
              v-show="flag4"
            >
              <img
                src="../assets/home/train_4.png"
                alt=""
              >
            </div>
            <a-empty
              v-show="flag1"
              v-if="trainingfirstlist.length==0"
              description=""
            />
            <div
              v-else
              class="mod"
              v-show="flag1"
              v-for="item in trainingfirstlist"
              :key="item.id"
            >
              <h2>{{item.title}}</h2>
              <h3>:</h3>
              <p class="p1">{{item.info.length>155?item.info.substring(0,155).concat(str):item.info}}</p>
              <h4>:</h4>
              <p></p>
              <span @click="gotrain1(item.id)"><em></em></span>
            </div>
            <a-empty
              v-show="flag2"
              v-if="trainingtwolist.length==0"
              description=""
            />
            <div
              v-else
              class="mod"
              v-show="flag2"
              v-for="item in trainingtwolist"
              :key="item.id"
            >
              <h2>{{item.title}}</h2>
              <h3>:</h3>
              <p class="p1">{{item.info.length>155?item.info.substring(0,155).concat(str):item.info}}</p>
              <h4>:</h4>
              <p></p>
              <span @click="gotrain2(item.id)"><em></em></span>
            </div>
            <a-empty
              v-show="flag3"
              v-if="trainingthreelist.length==0"
              description=""
            />
            <div
              v-else
              class="mod"
              v-show="flag3"
              v-for="item in trainingthreelist"
              :key="item.id"
            >
              <h2>{{item.title}}</h2>
              <h3>:</h3>
              <p class="p1">{{item.info.length>155?item.info.substring(0,155).concat(str):item.info}}</p>
              <h4>:</h4>
              <p></p>
              <span @click="gotrain3(item.id)"><em></em></span>
            </div>
            <a-empty
              v-show="flag4"
              v-if="trainingfourlist.length==0"
              description=""
            />
            <div
              v-else
              class="mod"
              v-show="flag4"
              v-for="item in trainingfourlist"
              :key="item.id"
            >
              <h2>{{item.title}}</h2>
              <h3>:</h3>
              <p class="p1">{{item.info.length>155?item.info.substring(0,155).concat(str):item.info}}</p>
              <h4>:</h4>
              <p></p>
              <span @click="gotrain4(item.id)"><em></em></span>
            </div>
          </div>
          <div class="right">
            <ul class="md">
              <li
                @click="gotrainshukong"
                :class="{isActive:classshow1}"
              >
                <span class="sp1"></span>
                <p></p>
              </li>
              <li
                @click="gotraingongye"
                :class="{isActive:classshow2}"
              >
                <span class="sp2"></span>
                <p></p>
              </li>
              <li
                @click="gotrainzhineng"
                :class="{isActive:classshow3}"
              >
                <span class="sp3"></span>
                <p></p>
              </li>
              <li
                @click="gotrainzhizao"
                :class="{isActive:classshow4}"
              >
                <span class="sp4"></span>
                <p></p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="wrap formation ">
      <div class="innerWrap">
        <div class="title">
          <span></span>
          <span
            class="more"
            @click="goformation"
          >></span>
        </div>
        <div class="notbox"  v-if="comlist.length==0">
          <img
            src="../assets/blank/notlist.png"
            alt=""
          >
          <p class="inp"></p>
        </div>
        <div class="list3" v-else >
          <div class="bleft">
            <div class="left">
              <img
                src="../assets/home/formation.png"
                alt=""
              >
            </div>
            <div class="mod">
              <ul class="nav">
                <li
                  @click="godt"
                  :class="{active:flagshow}"
                ></li>
                <li
                  @click="gohy"
                  :class="{active:flag}"
                ></li>
              </ul>
              <ul
                class="news"
                v-show="flagshow"
              >
                <li
                  v-for="item in comlist"
                  :key="item.id"
                  @click="gomore(item.id,item.type)"
                >
                  <p
                    class="h2"
                    :title="item.title"
                  >{{item.title}}</p>
                  <p class="pone">{{item.info.length>55?item.info.substring(0,55).concat(str):item.info}}</p>
                  <img
                    @click="gomore(item.id,item.type)"
                    src="../assets/home/1+x.png"
                    alt=""
                  >
                </li>
              </ul>
              <ul
                class="news"
                v-show="flag"
              >
                <li
                  v-for="item in poclist"
                  :key="item.id"
                  @click="gomoree(item.id,item.type)"
                >
                  <p
                    class="h2"
                    :title="item.title"
                  >{{item.title}}</p>
                  <p class="pone ">{{item.info.length>55?item.info.substring(0,55).concat(str):item.info}}</p>
                  <img
                    @click="gomoree(item.id,item.type)"
                    src="../assets/home/1+x.png"
                    alt=""
                  >
                </li>
              </ul>
            </div>
          </div>
          <div class="right">
            <h2><span></span></h2>
            <ul class="not">
              <li
                v-for="item in nolist"
                :title="item.title"
                :key=item.id
                @click="tofordetail(item.id,item.type)"
                :class="{active:showyan}"
              ><span></span>{{item.title.length>20?item.title.substring(0,20).concat(str):item.title}} </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="wrap education">
      <div class="innerWrap">
        <div class="ti">
          <span class="to"></span>
          <span
            class="more"
            @click="goalliance"
          >></span>
        </div>
        <!-- <a-empty
          v-if="alliancelist.length==0"
          description=""
        /> -->
        <div class="notbox"  v-if="alliancelist.length==0">
          <img
            src="../assets/blank/notlist.png"
            alt=""
          >
          <p class="inp"></p>
        </div>
        <ul
          class="booklist"
          v-else
        >
          <li
            v-for="item in alliancelist"
            :key="item.id"
            @click="toalldetail(item.id)"
          >
            <img
              style="max-width:210px;max-height:80px;"
              :src="item.institute.logo"
              alt=""
            >
          </li>
        </ul>
      </div>
    </div>

    <!--  -->
    <div
      class="gohead"
      v-show="flaghead"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import carousel from "@/components/common/carousel";
import eCarousel from "@/components/common/eCarousel";
import { gethomeApi} from "@/request/api";
export default {
  name: "home",
  components: { carousel,eCarousel },
  data() {
    return {
      courseList: {
        img: require("../assets/during/1.png"),
        title: "",
        name: "",
      },
      flag: false,
      flag1: true,
      flag2: false,
      flag3: false,
      flag4: false,
      flagshow: true,
      flaghead: false,
      alliancelist: [],
      lecturelist: [],
      lelist: [],
      comlist: [],
      poclist: [],
      nolist: [],
      traininglist: [],
      trainingfirstlist: "",
      trainingtwolist: "",
      trainingthreelist: "",
      trainingfourlist: "",
      lectureIndex: 0,
      marginTop: 0,
      classshow1: true,
      classshow2: false,
      classshow3: false,
      classshow4: false,
      showyan: false,
      str: "......",
      currentLecture: [],
      courselist: [],
    };
  },
  created() {
    this.init();
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(gethomeApi, this.searchInfo, (res) => {
        this.alliancelist = res.data.alliance;
        this.lecturelist = res.data.lecture;
        this.currentLecture = res.data.lecture.filter(
          (item) => item.id == res.data.lecture[0].id
        );
        this.lectureIndex = 0;
        this.comlist = res.data.news.company;
        this.poclist = res.data.news.policy;
        this.nolist = res.data.news.notice;
        this.traininglist = res.data.training;
        this.trainingfirstlist = this.traininglist.filter(
          (item) => item.title == ""
        );
        this.trainingtwolist = this.traininglist.filter(
          (item) => item.title == ""
        );
        this.trainingthreelist = this.traininglist.filter(
          (item) => item.title == ""
        );
        this.trainingfourlist = this.traininglist.filter(
          (item) => item.title == ""
        );
        this.courselist = res.data.course;
        // console.log(res.data)
        // console.log(this.comlist)
      });
    },
    toserviceKaohe() {
      this.$router.push({ path: "/Service",query:{type:1,serviceIndex:0}});
    },
    toserviceShizi() {
      this.$router.push({ path: "/Service",query:{type:2,serviceIndex:1}});
    },
    toserviceKaowu() {
      this.$router.push({ path: "/Service",query:{type:3,serviceIndex:2}});
    },
    toserviceKaoshi() {
      this.$router.push({ path: "/Service",query:{type:4,serviceIndex:3}});
    },
    tocourse() {
      this.$router.push({ path: "/course" });
    },
    tocourseDetail(id) {
      this.$router.push({ path: "/courseInfo", query: { id } });
    },
    toalldetail(id) {
      this.$router.push({ path: "/allianceDetail", query: { id } });
    },
    godt() {
      this.flagshow = true;
      this.flag = false;
    },
    gohy() {
      this.flagshow = false;
      this.flag = true;
    },
    goalliance() {
      this.$router.push({ path: "/alliance" }); //
    },
    goformation() {
      this.$router.push({ path: "/company" }); //
    },
    gomore(id, type) {
      this.$router.push({ path: "/companyDetail", query: { id, type } }); //
    },
    gomoree(id, type) {
      this.$router.push({ path: "/companyDetail", query: { id, type } }); //
    },
    gotrain() {
      this.$router.push({ path: "/traincenter" }); //
    },
    gotrain1(id) {
      this.$router.push({ path: "/traincenter", query: { id: id } }); //
    },
    gotrain2(id) {
      this.$router.push({ path: "/traincenter", query: { id: id } }); //
    },
    gotrain3(id) {
      this.$router.push({ path: "/traincenter", query: { id: id } }); //
    },
    gotrain4(id) {
      this.$router.push({ path: "/traincenter", query: { id: id } }); //
    },
    gotraining() {
      this.$router.push({ path: "/training" }); //
    },
    toLecture(id) {
      this.$router.push({ path: "/teacherLectureDetail", query: { id: id } });
    },
    beforeChange(to, from) {
      this.lectureIndex = from;
      if (from >= 7) {
        this.marginTop = from * 47;
      } else if (from == 0) {
        this.marginTop = 0;
      }
    },
    tofordetail(id, type) {
      this.$router.push({ path: "/companyDetail", query: { id, type } });
      this.showyan = true;
    },
    golecture() {
      this.$router.push({ path: "/teacherLecture" }); //
    },
    gotrainshukong() {
      this.flag1 = true;
      this.flag2 = false;
      this.flag3 = false;
      this.flag4 = false;
      this.classshow1 = true;
      this.classshow2 = false;
      this.classshow3 = false;
      this.classshow4 = false;
    },
    gotraingongye() {
      this.flag1 = false;
      this.flag2 = true;
      this.flag3 = false;
      this.flag4 = false;
      this.classshow1 = false;
      this.classshow2 = true;
      this.classshow3 = false;
      this.classshow4 = false;
    },
    gotrainzhineng() {
      this.flag1 = false;
      this.flag2 = false;
      this.flag3 = true;
      this.flag4 = false;
      this.classshow1 = false;
      this.classshow2 = false;
      this.classshow3 = true;
      this.classshow4 = false;
    },
    gotrainzhizao() {
      this.flag1 = false;
      this.flag2 = false;
      this.flag3 = false;
      this.flag4 = true;
      this.classshow1 = false;
      this.classshow2 = false;
      this.classshow3 = false;
      this.classshow4 = true;
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flaghead = true;
      } else {
        this.flaghead = false;
      }
    },
    clickLecture(id, index) {
      this.currentLecture = this.lecturelist.filter((item) => item.id == id);
      this.lectureIndex = index;
    },
  },
};
</script>
<style lang="stylus">
.lectureList {
  .ant-carousel {
    .slick-dots {
      position: relative;
      bottom: -50px;

      li {
        button {
          background: #d6d6d6;
        }
      }

      .slick-active {
        button {
          background: #94baff;
        }
      }
    }
    .notbox{
      margin:0 auto;
      margin-bottom:30px;
      margin-top:40px;
      width:1205px;
      text-align center;
      img {
        display:block
        margin:0 auto;
      }
    .inp {
        font-size: 18px;
        color: #666;
        margin: 0 auto;
        text-align: center;
        margin-top: 10px;
      }
    }
  }
}
</style>
<style lang="stylus" scoped>
#home{
  .banner {
    img {
      width:100%;
      height:100%;
    }
  }

.wrap {
  padding-top: 34px;

  .innerWrap {
    width: 1205px;
    margin: 0 auto;

    .title {
      line-height: 68px;
      border-bottom: 1px solid #eee;
      text-align: center;
      font-size: 28px;
      color: #333;
      font-weight: bold;
      position: relative;

      span {
        display: inline-block;
        border-bottom: 1px solid #4182fa;
      }
      .more {
        position: absolute;
        color: #4182fa;
        font-size: 14px;
        right: 0;
        bottom: 5px;
        line-height: initial;
        cursor: pointer;
        border: none;
      }
    }
    .notbox{
      margin:0 auto;
      margin-bottom:30px;
      margin-top:40px;
      text-align center;
      img {
        display:block
        margin:0 auto;
      }
    .inp {
        font-size: 18px;
        color: #666;
        margin: 0 auto;
        text-align: center;
        margin-top: 10px;
      }
    }
    .list {
      display: flex;
      flex-wrap: wrap;
      >li {
        margin-bottom: 25px;
        margin-right: 23px;
        height:244px;
        background-color: #ffffff;
        cursor: pointer;
        transition:0.3s;

        &:nth-child(4n) {
          margin-right: 0;
        }

        &:hover {
          box-shadow: 0px 4px 9px 0px rgba(73, 83, 104, 0.2);
          transition: 0.3s;
          margin-top: -4px;
        }

        img {
          width: 284px;
          height: 161px;
        }

        .detail {
          height: 83px;
          width: 284px;
          border: 1px solid #ebebeb;
          border-top: 0;
          padding: 17px 0 14px 10px;

          p {
            margin: 0;

            &:nth-child(1) {
              color: #333333;
              font-size: 16px;
              margin-bottom: 10px;
              font-weight: 500;
              font-family: MicrosoftYaHei;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
              width:100%;
            }

            &:nth-child(2) {
              color: #666;
              font-size: 14px;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
              width:100%;
            }
          }
        }
      }
    }
  }
}

.lecture {
  margin-top: -15px;

  .lectureList {
    // padding-top: 39px;
    .notbox{
      margin:0 auto;
      margin-bottom:30px;
      margin-top:40px;
      width:1205px;
      text-align center;
      img {
        display:block
        margin:0 auto;
      }
    .inp {
        font-size: 18px;
        color: #666;
        margin: 0 auto;
        text-align: center;
        margin-top: 10px;
      }
    }
    .box {
      display: flex !important;
      width: 1205px;

      .left {
        position: relative;
        width: 613px;

        img.cover {
          width: 613px;
          height: 347px;
          position: relative;
          z-index: 3;
        }

        .lectitle {
          color: #fff;
          font-size: 24px;
          text-align: center;
          position: absolute;
          width:500px;
