<template>
  <div class="leatureDetail">
    <div class="content">
      <div class="bread">
        <span @click="changePage('/')"></span> > <span @click="changePage('/teacherLecture')"></span> > <span style="color:#999">{{info.title}}</span>
      </div>
      <div class="list">
        <teacherItem
          :list="[info]"
          @signup="signup"
          :isTag="false"
        ></teacherItem>
      </div>
      <div class="detail">
        <div class="left">
          <div class="title">
            <span></span>
          </div>
          <div
            class="inner ql-editor"
            v-html="info.info"
          >

          </div>
        </div>
        <div class="right">
          <div class="top">
            <img
              src="../assets/teacherLogo.png"
              alt=""
            >
            {{info.teacher}}
          </div>
          <div class="bottom">
            {{info.teacherInfo}}
          </div>
        </div>
      </div>
    </div>
    <loginModal
      :isShow="isShow"
      @closemodal="isShow=false"
      @logined="logined"
      @goregister="goregister"
      @forget="forget"
    ></loginModal>
    <modal-box
      :type="'success'"
      :isShow='isJoinShow'
      @closemodal="closemodal"
      @submit="submit"
    >
      <div
        slot="modalContent"
        class="modalContent"
      >
        <ul>
          <li> {{info.title}}</li>
          <li> {{info.startTime}}{{info.endTime}}</li>
          <li><span> {{info.url}}</span></li>
        </ul>
      </div>
    </modal-box>
    <!--  -->
    <div
      class="gohead"
      v-show="flaghead"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import { getTeacherLectureDetailApi, joinLectureApi } from "@/request/api";
import teacherItem from "@/components/common/teacherItem";
import modalBox from "@/components/common/modalBox";
import loginModal from "@/components/common/loginModal";

export default {
  name: "teacherLectureDetail",
  components: { teacherItem, modalBox, loginModal },
  created() {
    this.lectureId = this.$route.query.id;
    this.init();
  },
  data() {
    return {
      lectureId: null,
      info: {},
      isShow: false,
      isJoinShow: false,
      isfooter: false,
      flaghead: false,
    };
  },
  computed:{
      role(){
      return this.$store.state.role
    }
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(
        getTeacherLectureDetailApi,
        { lectureId: this.lectureId },
        (res) => {
          this.info = { ...res.data, joined: res.joined };
        }
      );
    },
    changePage(path) {
      this.$router.push({ path: path });
    },
    closemodal() {
      this.isJoinShow = false;
      this.init();
    },
    submit() {
      this.isJoinShow = false;
      this.init();
    },
    signup(isLogin, id, data) {
      if (isLogin == 0) {
         if(this.role==''||this.role==""){
            this.$message.info("")
         }else{
            this.joinInfo = data;
            this.getToken(joinLectureApi, { lectureId: id }, () => {
                this.isJoinShow = true;
            });
         }
        
      } else {
        this.isShow = true;
      }
    },
    logined() {
      this.isShow = false;
      this.init();
    },
    goregister() {
      this.$router.push({ path: "/register" }); //
      this.isShow = false;
    },
    forget() {
      this.$router.push({ path: "/forgetModal" });
      this.isShow = false;
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flaghead = true;
      } else {
        this.flaghead = false;
      }
    },
  },
};
</script>
<style lang="stylus" scoped>
.leatureDetail {
  background: #eee;
  padding-bottom: 20px;

  .content {
    width: 1200px;
    margin: 0 auto;

    .bread {
      line-height: 68px;
      height: 68px;

      span {
        color:#333;
        font-family: MicrosoftYaHei;
        &:hover {
          color: #4182fa;
          cursor: pointer;
        }

        &:nth-child(4) {
          color: #999;
        }
      }
    }

    .detail {
      margin-top: 18px;
      overflow: hidden;
      min-height:300px;
      .left {
        width: 930px;
        float: left;
        background: #fff;
        padding: 0 36px 100px 36px;

        .title {
          margin-bottom: 50px;

          span {
            display: inline-block;
            font-size: 18px;
            color: #0096ff;
            padding-top: 20px;
            border-top: 2px solid #0096ff;
          }
        }

        .inner {
          line-height: 24px;
        }
      }

      .right {
        padding: 0 20px;
        width: 251px;
        float: right;
        background: #fff;

        .top {
          border-bottom: 1px solid #ddd;
          height: 85px;
          display: flex;
          align-items: center;
          color: #666;

          img {
            margin-right: 25px;
          }
        }

        .bottom {
          margin-top: 30px;
          color: #666;
          text-align: justify;
          line-height: 24px;
          padding-bottom: 20px;
        }
      }
    }
  }

  .modalContent {
    height: 121px;
    background: #f0f0f0;
    margin: 20px 0;
    padding: 20px 20px;

    li {
      font-size: 16px;
      color: #666;
      line-height: 27px;

      span {
        color: #4182fa;
        cursor: pointer;
      }
    }
  }
}

.gohead {
  width: 66px;
  height: 58px;
  background-color: #f9f9fa;
  border-radius: 3px;
  border: solid 1px #eeeeee;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 240px;
  bottom: 50px;
  cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead3.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}
</style>