<template>
  <div class="industry education">
    <h2 class="title">
      
      <span>/ Industry Education Cooperation</span>
    </h2>
    <div class="wrap">
      <newsItem v-if="newList.length>0" :newList="newList" @todetail="todetail"></newsItem>
      <div class="notbox" v-else>
        <img src="../assets/blank/notlist.png" alt />
        <p class="inp"></p>
      </div>
      <div class="page">
        <pagination :pagination="pagination" @changePage="changePage"></pagination>
      </div>
    </div>
    <!-- <a-empty
      v-else
      description=""
    />-->

    <!--  -->
    <div class="gohead" v-show="flag">
      <a href="#layout"></a>
    </div>
  </div>
</template>

<script>
import newsItem from "@/components/common/newsItem";
import pagination from "@/components/common/pagination";
import { getNewsListApi } from "@/request/api";
export default {
  name: "industry",
  components: { newsItem, pagination },
  created() {
    this.init();
  },
  data() {
    return {
      newList: [],
      searchInfo: {
        page: 1,
        pageSize: 6,
      },
      pagination: { pageSize: 6, total: 0 },
      flag: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(getNewsListApi, this.searchInfo, (res) => {
        this.newList = res.data.rows;
        this.pagination.total = res.data.count;
      });
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    todetail(id) {
      this.$router.push({ path: "/industryDetail", query: { id } });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>

<style lang="stylus" scoped>
.industry {
  overflow: hidden;
  min-height: 500px;

  h2 {
      bacground:pink;
      padding-top:30px;
      width: 600px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      padding-bottom: 50px;
      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }

  .wrap {
    padding: 0 69px;

    .page {
      margin: 40px 0;
    }
  }

  .notbox {
    margin: 0 auto;
    margin-bottom: 30px;
    margin-top: 40px;
    text-align: center;

    img {
      display: block;
      margin: 0 auto;
    }

    .inp {
      font-size: 18px;
      color: #666;
      margin: 0 auto;
      text-align: center;
      margin-top: 10px;
    }
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>
