<template>
  <div
    id="company"
    class="education"
  >
    <div class="header">
      <h2
      class="title"
      v-if="this.type=='company'||!this.type"
    >
      
      <span>/ Company News</span>
    </h2>
    <h2
      class="title"
      v-else-if="this.type=='policy'"
    >
      
      <span>/ Industry policy</span>
    </h2>
    <h2
      class="title"
      v-else
    >
      
      <span>/ Notice</span>
    </h2>
    </div>
    <div class="wrap">
      <newsItem
        :newList="newList"
        @todetail="todetail"
      >
      </newsItem>
      <div class="page">
        <pagination
          :pagination="pagination"
          @changePage="changePage"
        ></pagination>
      </div>
    </div>
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import newsItem from "@/components/common/newsItem";
import { getFormationListApi } from "@/request/api";
import pagination from "@/components/common/pagination";
export default {
  name: "company",
  components: { pagination, newsItem },
  watch: {
    $route(to) {
      this.type = to.query.type;
      this.init();
    },
  },
  data() {
    return {
      newList: [],
      searchInfo: {
        page: 1,
        pageSize: 6,
        type: "company",
      },
      type: null,
      pagination: { total: "", pageSize: 6 },
      currentIndex: {},
      flag: false,
    };
  },
  created() {
    this.type = this.$route.query.type;
    this.init();
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.searchInfo.type = this.type || "company";
      this.getToken(getFormationListApi, this.searchInfo, (res) => {
        this.newList = res.data.rows;
        // console.log(this.newList)
        // console.log(this.newList.trainingClass)
        this.pagination.total = res.data.count;
      });
    },
    todetail(id) {
      this.$router.push({
        path: "/companyDetail",
        query: { id, type: this.searchInfo.type },
      });
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>

<style lang="stylus" scoped>
#company {
  margin: 0 auto;
  min-height: 500px;
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 25px;
    margin-top: 10px;
    h2 {
      width: 400px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      margin-bottom: 40px;

      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }
  }
  

  .page {
    //margin: 30px 0;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .wrap {
    padding: 0 69px;
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>
