<template>
    <div class="audit">
        <div class="title">
            <p></p>
            <p class="back" @click="tonexamplan">
                <img src="../../assets/mine/back.png" alt="">
                
            </p>
        </div>
        <ul class="filter">
            <li
            v-for="(item,index) in menuList"
            :key="index"
            @click="changPage(item)"
            :class="{'active':current==item}"
            >{{item}}
            <p></p>
            </li>
        </ul>
        <a-empty
          v-if="auditList.length==0"
          description=""
        />
        <template v-else>
          <div class="box" v-for="(item,i) in auditList" :key="item.id">
            <div class="left">
              <span class="indexspan">{{index(i)}}</span>
              <img class="sortimg" src="../../assets/mine/news.png" alt="">
            </div>
            <div class="right">
                <p class="examtitle">
                  <span class="head" >{{item.creator.realname}}</span>
                  <span class="examstu">
                    <span v-if='item.firstCheck==0' style="color: #ee9537;"></span>
                    <span v-else-if='item.firstCheck==1' style="color: #49af4f;"></span>
                    <span v-else style='color: #ff0000;'></span>
                    </span>
                </p>
                <p class="examsort">
                    <span>{{item.creator.gender}}</span>
                    <span>{{item.creator.instituteName}}</span>
                </p>
                <p class="examtime">
                  <span>{{item.creator.idCardNum}}</span>
                  <span>{{item.creator.major}}</span>
                </p>
                <p class="examrelnum">
                  <span class="phone">{{item.creator.telephone}}</span>
                  <span class="allspan">
                    <span @click="passbtn(item.id)"></span>
                    <span @click="nopassbtn(item.id)"></span>
                    <span @click="delbtn(item.id,item.creator.realname)"></span>
                  </span>
                </p>
            </div>
          </div>
        </template>
        <div class="page">
          <pagination
            :pagination="pagination"
            @changePage="changePage"
          ></pagination>
        </div>
    </div>
</template>

<script>
import {examineeListApi,deleteExamineeApplyApi,updateExamineeApplyStatusApi} from "@/request/api"
import pagination from "@/components/common/pagination"
export default {
  name: 'audit',
  components:{pagination},
  data(){
    return{
      menuList: ["", ""],
      current: "",
      auditList:[],
      searchInfo:{
        page:1,
        pageSize:5,
        status:0,
        examId:''
       },
       pagination:{total:'',pageSize:5}
    }
  },
   mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "report":
        this.current = "";
        break;
      case "audit":
        this.current = "";
        break;
    }
  },
  created(){
      this.searchInfo.examId=this.$route.query.id
      this.init()
  },
  methods:{
    init(){
       this.getToken(examineeListApi,this.searchInfo,res=>{
          this.auditList=res.data.rows
          this.pagination.total=res.data.count
       })
    },
    //
    tonexamplan(){
      this.$router.push({path:"/examplan"})
    },
    //
    changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/report";
          break;
        case "":
          path = "/audit";
          break;
      }
      this.$router.push({ path: path,query:{id:this.searchInfo.examId}});
    },
    //
    passbtn(id){
      this.getToken(updateExamineeApplyStatusApi,{applyId:id,firstCheck:1},res=>{
        if(res.code==200){
          this.$message.success("")
          this.init()
        }
      })
    },
    //
    nopassbtn(id){
      this.getToken(updateExamineeApplyStatusApi,{applyId:id,firstCheck:2},res=>{
          if(res.code==200){
            this.$message.success("")
            this.init()
          }
      })
    },
    //
    delbtn(id,title){
      this.$confirm({
        title: `${title}?`,
        okText: '',
        cancelText: '',
        onOk:()=> {
          this.getToken(deleteExamineeApplyApi,{applyId:id},res=>{
            if(res.code==200){
              this.init()
              this.$message.success("")
            }
          })
        },
        onCancel() {
          // console.log('Cancel');
        }
      });
    },
    //
    index(index){
      return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
     changePage(currentPage){
      this.searchInfo.page=currentPage
      this.init()
    }
    
  }
}
</script>

<style lang="stylus" scoped>
.audit{
    margin-bottom: 30px;
    .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      display:flex;
      align-items center;
      justify-content space-between;
        p {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            margin-bottom:0px;
            .ban{
            color: #c56b2f;
            font-size:14px;
            margin-left:20px;
            }
        }
        .back {
            color: #4281ff;
            width: 50px;
            height: 30px;
            font-size:14px;
            margin-right:20px;
            line-height: 30px;
            cursor: pointer;
            img{
                width:15px;
                height:15px;
                margin-top:-3px;
            }
        }
    }
    .filter {
      display: flex;
      height: 45px;
      line-height: 45px;
      margin-left: 20px;
      margin-top: 20px;

      li {
        width: 100px;
        margin-right: 20px;
        height: 35px;
        line-height: 35px;
        font-size: 16px;
        color: #333333;
        text-align: center;
        cursor pointer
        p{
          width: 21px;
          height: 2px;
          background-color: #4281ff;
          border-radius: 1px;
          text-align center;
          margin:0 auto;
          display:none
        }
      }
      li:last-child {
        margin-right: 0;
      }
      .active {
        color: #4281ff;
        font-weight: 600;
        p{
          display:block;
        }
      }
    }
    .box{
      margin-top:20px;
      margin-left:30px;
      margin-right:30px;
      border: solid 1px #e6e6e6;
      margin-bottom:15px;
      display:flex;
      .left{
         display:flex;
        min-width:80px;
        padding-right:15px;
        padding-left:10px;
        .indexspan{
          color:#666;
          font-size:14px;
          display:block;
          overflow hidden;
          margin-top:30px;
          margin-right:15px;
        }
        .sortimg{
          width:40px;
          height:40px;
          display:block;
          margin-top:20px;
        }
      }
      .right{
        overflow hidden
        flex:1;
        .examtitle{
          margin-top:18px;
          position relative
          margin-bottom:13px;
          display:flex;
          .head{
            font-size:16px;
            font-weight: bold;
            display:block
            color: #333333;
            width:580px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .apllystu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:150px;
          }
          .examstu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:23px;
          }
        }
        .examsort{
           font-size:14px;
           height:20px;
           color:#666;
           display:flex;
           margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examtime{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examrelnum{
          font-size:14px;
          color:#666;
          position relative;
          justify-content space-between
          display flex;
          .phone{
            color:#666;
          }
          .allspan{
            width:240px;
            display flex;
            span{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #f6f8ff;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              margin-right:9px;
              cursor pointer
            }
            span:last-child{
              margin-right:0px;
            }
          }
        }
      }
    }
    .page {
      margin-top: 30px;
    }
}
</style>
