<template>
  <div
    id="alliance"
    class="education"
  >
    <h2 class="title"><span>/ Alliance Member</span></h2>
    <div
      class="mod"
      v-if="schoolList.length>0||allianceList.length>0"
    >
      <p></p>
      <ul class="allList" v-if="schoolList.length>0">
        <li v-for="(item,index) in schoolList" :key="index" @click="toDetail(item.id)"> 
           <img
           style="max-width:230px;max-height:100px;"
              :src="item.logo"
            >
        </li>
      </ul>
      <div class="notbox"  v-else>
        <img
          src="../assets/blank/notlist.png"
          alt=""
        >
        <p class="inp"></p>
      </div>
      <p style="margin-top:10px;"></p>
      <ul class="boxList" v-if="allianceList.length>0">
        <li v-for="(item,index) in allianceList"  @click="toDetail(item.id)" :key="index">
           <img
              style="max-width:230px;max-height:100px;"
              :src="item.logo"
            >
        </li>
      </ul>
      <!-- <div v-if="allianceList.length>0">
        <img
          v-for="(item,index) in allianceList"
          :key="index"
          :src="item.logo"
          @click="toDetail(item.id)"
        >
      </div> -->
      <div class="notbox"  v-else>
        <img
          src="../assets/blank/notlist.png"
          alt=""
        >
        <p class="inp"></p>
      </div>
    </div>
    <!-- <a-empty
      v-else
      description=""
    /> -->
    <div class="notbox"  v-else>
      <img
        src="../assets/blank/notlist.png"
        alt=""
      >
      <p class="inp"></p>
    </div>
    <!--  -->
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import { getallianceListApi } from "@/request/api";
export default {
  name: "alliance",
  data() {
    return {
      schoolList: [],
      allianceList: [],
      flag: false,
    };
  },
  created() {
    this.init();
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(getallianceListApi, {}, (res) => {
        this.schoolList = res.data.rows
          .map((item) => ({ ...item, ...item.institute, id: item.id }))
          .filter((item) => item.role == "");
          console.log(this.schoolList)
        this.allianceList = res.data.rows
          .map((item) => ({ ...item, ...item.institute, id: item.id }))
          .filter((item) => item.role == "");
      });
    },
    toDetail(id) {
      this.$router.push({ path: "/allianceDetail", query: { id } });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>

<style lang="stylus" scoped>
#alliance {
  h2 {
      padding-top:30px;
      width: 400px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      padding-bottom: 40px;
      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }
  .mod {
    width: 1205px;
    background-color: #ffffff;
    padding: 0 69px 40px;
    min-height: 500px;
    p {
      font-size: 18px;
      color: #333;
    }
    .allList{
      display:flex;
      width:100%;
      flex-wrap:wrap;
      li{
        cursor: pointer;
        width:250px;
        height:110px;
        margin-right: 22px;
        margin-bottom: 22px;
        border:1px solid #ddd;
        display:flex;
        justify-content center;
        align-items center;
        // img{
        //   width:100%;
        //   height:100%;
        // }
      }
      li:nth-child(4n) {
        margin-right: 0;
      }
      li:hover {
        box-shadow: 0 0 10px #ddd;
      }
    }
    .boxList{
      display:flex;
      flex-wrap:wrap;
      // background:yellow;
      li{
        cursor: pointer;
        width:250px;
        height:110px;
        display:flex;
        justify-content center;
        align-items center;
        margin-right: 22px;
        margin-bottom: 22px;
        border:1px solid #ddd;
        // img{
        //   width:100%;
        //   height:100%;
        // }
      }
      li:nth-child(4n) {
        margin-right: 0;
      }
      li:hover {
        box-shadow: 0 0 10px #ddd;
      }
    }
    
  }
  .notbox{
    margin:0 auto;
    margin-bottom:30px;
    margin-top:40px;
    text-align center;
    img {
      display:block
      margin:0 auto;
    }
  .inp {
      font-size: 18px;
      color: #666;
      margin: 0 auto;
      text-align: center;
      margin-top: 10px;
    }
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>
