<template>
  <div class="basicInfo">
    <div class="title">
      <h2></h2>
    </div>
    <div class="man">
      <div class="tit">
        <h2></h2>
      </div>
      <div class="box">
        <div class="left">
          <p><span></span>{{list.name}}</p>
          <p><span></span>{{list.account}}</p>
          <div class="telp">
            <span></span>
            <span v-if="list.telephone">{{list.telephone}}</span>
            <template v-else>
              <span></span>
              <span @click="bindphone"></span>
            </template>
          </div>
          <div class="ress">
            <span></span>
            <span v-if="list.address">{{list.address}} </span>
            <span v-else></span>
            <span @click="changeadd"></span>
          </div>
          <div
            class="addinp"
            v-if="addressshow"
          >
            <eAddress @changeAddress="changeAddress"></eAddress>
            <button
              class="adsbtn"
              @click="addsure"
            ></button>
          </div>
          <p>
            <span v-if="role==''"></span>
            <span v-else></span>
            {{list.instituteName}}</p>
          <p class="maj">
            <span></span>
            <template>
              <span v-if="list.major">{{list.major}}</span>
              <span v-else></span>
            </template>
            <span class="majspan" @click="changemaj"></span>
            <input
              v-if="majshow"
              class="majshow"
              type="text"
              v-model="major"
              placeholder=""
            ><button
              v-if="majshow"
              class="majbtn"
              @click="majbtn"
            ></button></p>
          <div
            class="job"
            v-if="role==''"
          >
            <span></span>
            <span class='jobspan' v-if="list.proTitle">{{list.proTitle}}</span>
            <span class='jobspan' v-else></span>
            <span
              class="changejob"
              @click="changejob"
            ></span>
            <div
              class="mod"
              v-show="jobshow"
            >
              <a-select
                class="selbtn"
                allow-clear
                placeholder=""
                :notFoundContent="''"
                @change="changejobmodel"
                v-decorator="['modelname',{rules:[{required:true,message:''}]}]"
              >
                <a-select-option
                  v-for="(item,index) in navlist"
                     :key="index"
                  :value="item.jobtitle"
                >
                  {{item.jobtitle}}
                </a-select-option>
              </a-select>
              <button
                class="jobbtn"
                @click="jobbtn"
              ></button>
            </div>
          </div>
          <div
            class="job"
            v-else-if="role==''"
          >
            <span></span>
            <span class='jobspan' v-if="list.proTitle">{{list.proTitle}}</span>
            <span class='jobspan' v-else></span>
            <span
              class="changejob"
              @click="changejob"
            ></span>
            <div
              class="mod"
              v-show="jobshow"
            >
              <a-select
                class="selbtn"
                allow-clear
                placeholder=""
                :notFoundContent="''"
                @change="indexSelect"
                v-decorator="['modelname',{rules:[{required:true,message:''}]}]"
              >
                <a-select-option
                  v-for="(item,index) in biglist"
                  :key="index"
                  :value="item.jobtitle"
                >
                  {{item.jobtitle}}
                </a-select-option>
              </a-select>
              <button
                class="jobbtn"
                @click="jobbtn"
              ></button>
            </div>
          </div>
          <p v-if="role==''||role==''"><span></span>{{list.authentation}}</p>
          <p class="purview" v-if="role==''||role==''">
            <span></span>
            <span v-if="list.title">{{list.title}}</span>
            <span v-else></span>
          </p>
          <div class="pic">
            <div class="pictitle"></div>
            <eCropper
              class="loadpic"
              v-if="resUrl"
              :recUrl="resUrl"
              @imgfile="imgfile"
            ></eCropper>
          </div>
        </div>
        <div class="right">
          <div class="pho">
            <eUpload
              :imageUrl="imageUrl"
              class="loadpho"
              @getUrl="getUrl"
            ></eUpload>
            <p></p>
          </div>
        </div>
      </div>
    </div>
    <div class="real">
      <div class="tit">
        <h2></h2>
        <button  v-if="this.idCardNum"
          class="realbtn"
          type="primary"
        >
        </button>
        <button v-else
          class="realbtn"
          type="primary"
          @click="showModal()"
        >
        </button>
      </div>
      <div class="realbox">
        <p><span></span>{{list.realname}} </p>
        <p><span></span>{{list.gender}}</p>
        <p><span></span>{{list.idCardNum}}</p>
        <p><span></span>{{list.instituteName}}</p>
      </div>
    </div>
    <div class="expert" v-if="role==''||role==''">
      <div class="tit">
        <h2></h2>
      </div>
      <div class="box">
        <button v-if="isexpert==null||isexpert==0||isexpert==2" class="expertbtn" @click="exbtn"></button>
        <button v-if="isexpert==1" class="expertbtn"></button>
        <p>
          <span></span>
          <span v-if="isexpert==null" ></span>
          <span v-else-if="isexpert==0" style="	color: #ee9537;"></span>
          <span v-else-if="isexpert==1" style="color: #49af4f;"></span>
          <span v-else style='color: #ff0000;'></span>
        </p>
      </div>
    </div>
    <div class="safe">
      <div class="tit">
        <h2></h2>
      </div>
      <div class="box">
        <p><span></span><span>********</span><span @click="changepwd"></span></p>
        <p>
          <span></span>
          <span v-if="list.email">{{list.email}}</span>
          <span v-else></span>
          <span
            v-if="list.email"
            class="eamilbtn"
            @click="recEmail(list.email)"
          >
          </span>
          <span
            v-else
            class="eamilbtn"
            @click="changeEmail"
          >
          </span>
          </p>
      </div>
    </div>
    <a-modal
      title=""
      :visible="phoneshow"
      @ok="phoneOk"
      @cancel="phonecancel"
      okText=""
      cancelText=""
      class="phonemodal"
    >
        <div class="formbox">
          <eform
             :formData="formtel"
              class="form"
              ref="form"
          >
              <div slot="binphone">
                <a-input
                  v-decorator="['binphone',
                  {rules: [{ required: true, message: ' ' },{validator:validatorphone}]}]"
                  class="input"
                  placeholder=""
                />
              </div>
              <div slot=bincode  class="iteminp">
                  <a-input
                    class="cadeinp"
                    v-decorator="['bincode',
                      { rules: [{ required: true, message: ' ' },{validator:validatorcaptcha}] },]"
                    placeholder=""
                  />
                <a-button v-if="show" class="subbtn" @click="gocade">
                  <span></span>
                </a-button>
                <a-button v-if="!show" class="subbtn">
                  <span class="count">{{count}}s</span>
                </a-button>
              </div>
          </eform>
       </div>
    </a-modal>
    <a-modal
      title=""
      :visible="confirmModal"
      @ok="handleOk"
      @cancel="cancel"
      okText=""
      cancelText=""
      class="realmodal"
    >
      <eform
        :formData="formData"
        class="formreal"
        ref="form"
      >
        <div slot="name">
          <a-input
            v-decorator="['name',{rules: [{ required: true, message: ' ' },{validator:validatorName}]}]"
            class="input"
          />
        </div>
        <div slot="gender">
          <a-radio-group v-decorator="['gender',{rules: [{ required: true, message: '' }]}]">
            <a-radio :value="1">
              
            </a-radio>
            <a-radio :value="2">
              
            </a-radio>
          </a-radio-group>
        </div>
        <div slot="idnum">
          <a-input
            v-decorator="['idnum',{rules: [{ required: true, message: '' },{validator:validatorIdnum}]}]"
            class="input"
          />
        </div>
        <div slot="address">
          <a-input
            v-decorator="['address',{rules: [{ required: true, message: '' }]}]"
            class="input"
          />
        </div>
      </eform>
      <div
        class="imp"
        style="color:red;margin-left:50px"
      >
        
        <p style="margin-bottom:10px;width:350px">1.</p>
        <p>2.</p>
      </div>
    </a-modal>
    <a-modal
      title=""
      :visible="expertshow"
      @ok="expertshowOk"
      @cancel="expertcancel"
      okText=""
      cancelText=""
      class="expertmodal"
    > 
       <div class="box">
          <p></p>
          <table>
            <tr>
              <td></td>
              <td>{{list.realname}}</td>
              <td></td>
              <td>{{list.gender}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{list.account}}</td>
              <td></td>
              <td>{{list.idCardNum}}</td>
            </tr>
            <tr>
              <td></td>
              <td>{{list.telephone}}</td>
              <td></td>
              <td>{{list.instituteName}}</td>
            </tr>
            <tr>
              <td></td>
              <td colspan="3">{{list.address}}</td>
            </tr>
          </table>
          <!-- <div class="con">
            <p><span>*</span>:</p>
            <textarea type="text" placeholder=""/>
          </div>
            <p class="material"><span>*</span> <button class="dwloadbtn"></button></p> -->
          <eform action=""
              :formData="formfile"
              class="under"
              ref="form"
          >
              <div slot="content" class="conbox" >
                  <a-textarea 
                    class="applycon" 
                    placeholder="" 
                    v-decorator="['content',{rules: [{ required: true, message: '' },{validator:validatorContent}]}]"
                  >
                  </a-textarea>
              </div>
              <div slot="file" class="pro" >
                  <a-upload 
                      v-decorator="['file',{rules: [{ required: true, message: '' },{validator:validateFile}]}]"
                      :file-list="fileList"
                      :remove="handleRemove"
                      :before-upload="beforeUpload"
                      :multiple="false"
                      accept=".pdf,.doc,.docx,.ppt,.xls,.xlsx"
                  >
                      <button class="loadbtn"></button>
                  </a-upload>
                  
              </div>
          </eform>
       </div>
    </a-modal>
    <a-modal
      title=""
      :visible="pwdshow"
      @ok="pwdOk"
      @cancel="pwdcancel()"
      okText=""
      cancelText=""
      class="pwdmodal"
    >
      <eform
        :formData="formPwd"
        class="form"
        ref="form"
      >
        <div slot="pwd">
          <a-input
            type="password"
            v-decorator="['pwd',{rules: [{ required: true, message: '' },{validator:validatorpwd}]}]"
            class="input"
          />
        </div>
        <div slot="newpwd">
          <a-input
            type="password"
            v-decorator="['newpwd',{rules: [{ required: true, message: '' },{validator:validatornewpwd}]}]"
            class="input"
          />
        </div>
        <div slot="renewpwd">
          <a-input
            type="password"
            v-decorator="['renewpwd',{rules: [{ required: true, message: '' },{validator:validatorrenewpwd}]}]"
            class="input"
          />
        </div>
      </eform>
    </a-modal>
    <a-modal
      title=""
      :visible="emailshow"
      @ok="emailOk"
      @cancel="emailcancel()"
      okText=""
      cancelText=""
      class="emailmodal"
      :destroyOnClose="true"
    >
      <eform
        :formData="formEmail"
        :fields="fields"
        class="form"
        ref="form"
      >
        <div slot="email">
          <a-input
            v-decorator="['email',{rules: [{ required: true, message: '' },{validator:validatoremail}]}]"
            class="input"
          />
        </div>
        <div slot="reemail">
          <a-input
            v-decorator="['reemail',{rules: [{ required: true, message: '' },{validator:validatorreemail}]}]"
            class="input"
          />
        </div>
      </eform>
    </a-modal>
  </div>
</template>

<script>
// import { VueCropper }  from 'vue-cropper' 
import { getmyInfo, getupdateInfo,bingphomeApi, checkExpertAppliedApi,
getupdatepwd,checkPhoneApi,getcodeApi,expertApplyApi} from "@/request/api";
import eUpload from "@/components/common/eUpload";
import eform from "@/components/common/form";
import eAddress from "@/components/common/eAddress";
import eCropper from "@/components/common/eCropper"
export default {
  name: "basicInfo",
  components: { eUpload, eAddress, eform,eCropper},
  data() {
    const formfile=[
      {
            label: "",
            scopedSlots: { customRender: "content" },
        },
        {
            label: "",
            scopedSlots: { customRender: "file" },
        },
    ]
    let validatorContent = (rule, value, callback) => {
        if (value) {
            let reg = /^[\s\S]*/;
            if (value==""||reg.test(value)) {
                this.content=value
               callback();
            } else {
            callback(new Error(""));
            }
        } else {
            callback(new Error(" "));
        }
    };
    let validateFile = (rule, value, callback) => {
      if (value) {
        const isJpgOrPng =
          value.file.type === "application/pdf" ||
          value.file.type === "application/msword" ||
          value.file.type ===
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
          value.file.type === "application/vnd.ms-powerpoint" ||
          value.file.type === "application/vnd.ms-excel" ||
          value.file.type ===
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
        // console.log(value.file.type)
        if(!isJpgOrPng){
          callback(new Error("pdf/doc/docx/ppt/xls/xlsx!"));
        } else {
          // console.log('123')
          callback();
        }
      } else {
        callback(new Error(" "));
      }
    };
    
    const formtel=[
      {
          label: "",
          scopedSlots: { customRender: "binphone" },
      },{
          label: "",
          scopedSlots: { customRender: "bincode" },
      }
    ]
    let validatorphone = (rule, value, callback) => {
      var regphone = /^1[3456789]\d{9}$/;
      if (regphone.test(value)) {
          this.phone = value;
          callback()
      } else {
        callback(new Error(""));
      }
    };
    let validatorcaptcha = (rule, value,callback) => {
      if(value==""||value.trim()){
          this.captcha = value;
          callback()
      }else{
        callback(new Error(""))
      }
      
    };
    const formData = [
      {
        label: "",
        scopedSlots: { customRender: "name" },
      },
      {
        label: "",
        scopedSlots: { customRender: "gender" },
      },
      {
        label: "",
        scopedSlots: { customRender: "idnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "address" },
      },
    ];
    let validatorName = (rule, value, callback) => {
      let reg = /^[\u4e00-\u9fa5]/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorIdnum = (rule, value, callback) => {
      let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    const formPwd = [
      {
        label: "",
        scopedSlots: { customRender: "pwd" },
        
      },
      {
        label: "",
        scopedSlots: { customRender: "newpwd" },
        type: "password",
      },
      {
        label: "",
        scopedSlots: { customRender: "renewpwd" },
        type: "password",
      },
    ];
    let validatorpwd = (rule, value, callback) => {
      let reg = /^[\w$]{8,16}$/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error("816"));
      }
    };
    let validatornewpwd = (rule, value, callback) => {
      let reg = /^[\w$]{8,16}$/;
      if (value == "" || reg.test(value)) {
        this.newpwd = value;
        callback();
      } else {
        callback(new Error("816"));
      }
    };
    let validatorrenewpwd = (rule, value, callback) => {
      this.renewpwd=value
      if (value == "" || value == this.newpwd) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    const formEmail = [
      {
        label: "",
        scopedSlots: { customRender: "email" },
      },
      {
        label: "",
        scopedSlots: { customRender: "reemail" },
      },
    ];
    let validatoremail = (rule, value, callback) => {
      let reg = /^[\w]\w{0,20}@(qq.com|126.com|163.com|QQ.com)$/;
      if (value==""||reg.test(value)) {
        this.email = value;
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorreemail = (rule, value, callback) => {
      if (value==""||value == this.email) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      list: [],
      form: this.$form.createForm(this, { name: "horizontal_login" }),
      fields:null,
      validatorContent,
      validateFile,
      fileList:[],
      formfile,
      loading: false,
      imageUrl: "",
      resUrl:'',
      addressshow: false,
      teladdress: "",
      majshow: false,
      major: "",
      idCardNum: "",
      confirmModal: false,
      formData,
      formtel,
      validatorName,
      validatorIdnum,
      validatorphone,
      sex: "",
      pwdshow: false,
      expertshow:false,
      formPwd,
      validatorpwd,
      validatornewpwd,
      validatorrenewpwd,
      newpwd: "",
      renewpwd:'',
      emailshow: false,
      formEmail,
      validatoremail,
      validatorreemail,
      email: "",
      navlist: [
        { jobtitle: "" },
        { jobtitle: "" },
        { jobtitle: "" },
        { jobtitle: "" },
      ],
      biglist: [
        { jobtitle: "" },
        { jobtitle: "" },
        { jobtitle: "" },
      ],
      jobshow: false,
      jobmenu: "",
      province: "",
      city: "",
      district: "",
      phoneshow:false,
      count: "",
      timer: null,
      show:true,
      validatorcaptcha,
      phone:'',
      captcha:'',
      isexpert:'',
      expertisExist:''
    };
  },
  created() {
    this.init();
  },
  computed: {
    role() {
      return this.$store.state.role;
    },
    icon() {
      return this.$store.state.icon;
    },
  },
  watch:{
    icon:function(){
      this.init()
    }
  },
  methods: {
    //
    init() {
      this.getToken(getmyInfo, {}, (res) => {
        this.list = res.data;
        this.idCardNum = res.data.idCardNum;
        if(res.data.icon){
           this.imageUrl = res.data.icon;
        }else{
           this.imageUrl = require("../../assets/course/icon.png");
        }
         if(res.data.photo){
           this.resUrl = res.data.photo;
          //  console.log(this.resUrl)
        }else{
           this.resUrl = require("../../assets/mine/touxiang.jpg");
        }
       
      });
      this.getToken(checkExpertAppliedApi,{},res=>{
            this.isexpert=res.data.status
            // console.log(this.isexpert)
            this.expertisExist=res.data.isExist
      })
    },
    //
    getUrl(data) {
      this.imageUrl = data;
      this.getToken(getupdateInfo, { "icon": this.imageUrl }, () => {
        // console.log(this.imageUrl)
        localStorage.setItem("icon",this.imageUrl );
        this.$store.commit("changeIcon", {'icon':this.imageUrl})
        this.init();
      });
    },
    //
    imgfile(){
      // console.log("",img)
      // this.resUrl=data
    },
    //
    bindphone(){
      this.phoneshow=true
    },
    //
    gocade(){
        this.getToken(checkPhoneApi,{telephone:this.phone},res=>{
            if(res.isExist){
                  this.$message.error("")
              }else{
                  this.getToken(getcodeApi,{telephone:this.phone},res=>{
                  if (res.code == 200) {
                        this.$message.info("");
                        const TIME_COUNT = 60;
                        if (!this.timer) {
                          this.count = TIME_COUNT;
                          this.show = false;
                          this.timer = setInterval(() => {
                            if (this.count > 0 && this.count <= TIME_COUNT) {
                              this.count--;
                            } else {
                              this.show = true;
                              clearInterval(this.timer);
                              this.timer = null;
                            }
                          }, 1000);
                        }
                      }
                })
              }
          })
    },
    //
    phoneOk(){
      if(this.phone){
        if(this.captcha){
           this.getToken(bingphomeApi,{telephone:this.phone,captcha: this.captcha,},res=>{
            if(res.code==200){
              this.init()
              this.$message.success("")
              this.phoneshow=false
              this.$refs.form.form.resetFields();
            }
          })
        }else{
          this.$message.error("")
        }
          
      }else{
        this.$message.error("")
      }
    },
    //
    phonecancel(){
      this.$refs.form.form.resetFields();
      this.phoneshow=false
    },
    //
    changeadd() {
      this.addressshow = true;
    },
     //
    changeAddress(data) {
      this.province = data[0];
      this.city = data[1];
      this.district = data.address;
      if (data.address) {
        this.teladdress = data.join("") + data.address;
      } else {
        this.teladdress = data.join("");
      }
    },
     //
    addsure() {
      if (this.teladdress) {
        this.getToken(
          getupdateInfo,
          {
            address: this.teladdress,
            province: this.province,
            city: this.city,
            district: this.district,
          },
          (res) => {
            if (res.code == 200) {
              this.$message.success("!");
              this.init();
            }
          }
        );
      } else {
        this.$message.error("");
      }
      this.addressshow = false;
    },
     //
    changemaj() {
      this.majshow = true;
    },
     //
    majbtn() {
      if (this.major) {
        this.getToken(getupdateInfo, { major: this.major }, (res) => {
          if (res.code == 200) {
            this.$message.success("!");
            this.init();
          }
        });
      } else {
        this.$message.error("");
      }
      this.major = "";
      this.majshow = false;
    },
    //
    showModal() {
      if (this.idCardNum) {
        this.$message.info("");
      } else {
        this.confirmModal = true;
      }
    },
    //
    handleOk() {
      this.$refs.form.form.validateFields().then((res) => {
        if (res.gender == 1) {
          this.sex = "";
        } else if (res.gender == 2) {
          this.sex = "";
        }
        this.getToken(
          getupdateInfo,
          {
            realname: res.name,
            gender: this.sex,
            idCardNum: res.idnum,
            instituteName: res.address,
          },
          (res) => {
            if (res.code == 200) {
              this.confirmModal = false;
              this.$message.success("!");
              this.init();
            }
          }
        );
      });
    },
    // 
    exbtn(){
      if(this.idCardNum){
        if(this.expertisExist){
          this.$message.info("")
        }else{
          this.expertshow=true
        }
      }else{
        this.$message.error("")
      }
    },
    //
    expertshowOk(){
      this.$refs.form.form.validateFields().then((res) => {
                    // console.log(res)
          let formData = new FormData();
              formData.append("content", res.content);
              formData.append("type", 1);
              formData.append("file", this.fileList[0]);
              this.getToken(expertApplyApi, formData, (data) => {
              this.$message.success(data.message);
              this.init()
              this.expertshow = false;
          });
      });
    },
    //
    expertcancel(){
      this.$refs.form.form.resetFields(["contnet"]);
      this.$refs.form.form.resetFields(["file"]);
      this.fielList=[]
      this.expertshow = false;
    },
    handleRemove(file) {
      const index = this.fileList.indexOf(file);
      const newFileList = this.fileList.slice();
      newFileList.splice(index, 1);
      this.fileList = newFileList;
    },
    beforeUpload(file) {
        const isJpgOrPng =
        file.type === "application/pdf" ||
        file.type === "application/msword" ||
        file.type ===
        "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        file.type === "application/vnd.ms-powerpoint" ||
        file.type === "application/vnd.ms-excel" ||
        file.type ===
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
        if (!isJpgOrPng) {
            this.fileList = [];
            return false;
        }else {
            this.fileList = [file];
        }
        return false;
    },
    //
    cancel() {
      this.$refs.form.form.resetFields(["name"]);
      this.$refs.form.form.resetFields(["gender"]);
      this.$refs.form.form.resetFields(["idnum"]);
      this.$refs.form.form.resetFields(["address"]);
      this.confirmModal = false;
    },
    //
    changepwd() {
      this.pwdshow = true;
    },
    //
    pwdOk() {
      this.$refs.form.form.validateFields().then((res) => {
        if(res.newpwd===res.renewpwd){
                this.getToken(
                getupdatepwd,
                { pwd: res.pwd, newPwd: this.newpwd },
                (value) => {
                  this.init();
                  localStorage.setItem("token", value.data.token);
                  if (value.code == 200) {
                    this.pwdshow = false;
                    this.$message.success("!");
                    this.$refs.form.form.resetFields(["pwd"]);
                    this.$refs.form.form.resetFields(["newpwd"]);
                    this.$refs.form.form.resetFields(["renewpwd"]);
                  }
                }
              );
        }else{
          this.$message.error("")
        }
      })
    },
    //
    pwdcancel() {
      this.$refs.form.form.resetFields(["pwd"]);
      this.$refs.form.form.resetFields(["newpwd"]);
      this.$refs.form.form.resetFields(["renewpwd"]);
      this.pwdshow = false;
    },
    //
    changeEmail() {
      this.emailshow = true;
    },
    //
    recEmail(data){
      this.emailshow = true;
      this.fields={email:data,reemail:data}
    },
    //
    emailcancel() {
      this.emailshow = false;
      this.$refs.form.form.resetFields(["email"]);
      this.$refs.form.form.resetFields(["reemail"]);
    },
    //
    emailOk() {
      this.$refs.form.form.validateFields().then((res) => {
        this.getToken(getupdateInfo, { email: res.email }, (res) => {
          if (res.code == 200) {
            this.init();
            this.$message.success("!");
            this.emailshow = false;
            this.$refs.form.form.resetFields(["email"]);
            this.$refs.form.form.resetFields(["reemail"]);
          }
        });
      });
    },
    //
    changejobmodel(data){
      this.jobmenu=data
      console.log(this.jobmenu)
    },
      //
    indexSelect(data) {
      this.jobmenu = data;
      console.log(this.jobmenu)
    },
    //
    jobbtn() {
      if (this.jobmenu) {
        this.getToken(getupdateInfo, { proTitle: this.jobmenu }, (res) => {
          if (res.code == 200) {
            this.init();
            this.$message.success("!");
            this.jobshow = false;
          }
        });
      } else {
        this.$message.error("");
      }
      this.addressshow = false;
    },
    //
    changejob() {
      this.jobshow = true;
    },
  },
};
</script>
