<template>
  <div class="teacherLecture">
    <template v-if="bannerList.length>0">
      <div class="banner" v-for="item in bannerList" :key="item.id">
          <a :href="item.link">
            <img :src="item.img" alt="">
          </a>
      </div>
    </template>
    <div class="banner" v-else>
      <!-- <img
        src="../assets/banner/msjt_banner.png"
        alt=""
      > -->
      <p>banner</p>
    </div>
    <div class="content">
      <div class="bar">
        <div class="left">
          <span></span>
          <span @click="sortTime()"> <i
              class='row'
              v-if="show"
            ></i><i
              class='row'
              v-else
            ></i></span>
        </div>
        <div class="right">
          <input
            placeholder=""
            v-model="keyword"
            @keyup.13="search()"
          >
          <button @click="search()"></button>
        </div>
      </div>
      <div class="list">
        <!-- <a-empty
          v-if="list.length==0"
          description=""
        /> -->
        <div class="notbox"  v-if="list.length==0">
          <img
            src="../assets/blank/notlist.png"
            alt=""
          >
          <p class="inp"></p>
        </div>
        <lecture-item
          v-else
          :list="list"
          @todetail="todetail"
          @signup="signup"
        ></lecture-item>
      </div>
      <div class="page">
        <pagination
          :isChange="isChange"
          v-if="pagination.total"
          :pagination="pagination"
          @changePage="changePage"
        ></pagination>
      </div>
    </div>
    <loginModal
      :isShow="isShow"
      @closemodal="isShow=false"
      @logined="logined"
    ></loginModal>
    <modal-box
      :type="'success'"
      :isShow='isJoinShow'
      @closemodal="closemodal"
      @submit="submit"
    >
      <div
        slot="modalContent"
        class="modalContent"
      >
        <ul>
          <li> {{joinInfo.title}}</li>
          <li> {{joinInfo.startTime}}{{joinInfo.endTime}}</li>
          <li><span> {{joinInfo.url}}</span></li>
        </ul>
      </div>
    </modal-box>
    <!--  -->
    <div
      class="gohead"
      v-show="flaghead"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import {getbannerApi, lectureSearchApi,joinLectureApi,} from "@/request/api";
import lectureItem from "@/components/common/lectureItem";
import pagination from "@/components/common/pagination";
import loginModal from "@/components/common/loginModal";
import modalBox from "@/components/common/modalBox";
export default {
  name: "teacherLecture",
  components: { lectureItem, pagination, loginModal, modalBox },
  created() {
    this.init();
  },
  data() {
    return {
      list: [],
      searchInfo: {
        page: 1,
        pageSize: 4,
        sort: "ASC",
        keyword: "",
      },
      pagination: { total: "", pageSize: 4 },
      keyword: "",
      isShow: false,
      isJoinShow: false,
      joinInfo: {},
      show: true,
      isChange: 0,
      flaghead: false,
      bannerList:[]
    };
  },
  computed:{
      role(){
      return this.$store.state.role
    }
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.searchInfo.sort = "";
      this.getToken(lectureSearchApi, this.searchInfo, (res) => {
        this.list = res.data.rows;
        this.pagination.total = res.data.count;
      });
      this.getToken(getbannerApi,{module:3},res=>{
          this.bannerList=res.data
      })
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    todetail(id) {
      this.$router.push({ path: "/teacherLectureDetail", query: { id: id } });
    },
    sortTime() {
      this.searchInfo.sort = this.searchInfo.sort == "ASC" ? "DESC" : "ASC";
      this.getToken(lectureSearchApi, this.searchInfo, (res) => {
        this.list = res.data.rows;
        this.pagination.total = res.data.count;
        this.show = !this.show;
      });
    },
    search() {
      this.searchInfo.keyword = this.keyword;
      this.searchInfo.page = 1;
      this.isChange++;
      this.getToken(lectureSearchApi, this.searchInfo, (res) => {
        this.list = res.data.rows;
        this.pagination.total = res.data.count;
      });
    },
    logined() {
      this.isShow = false;
      this.init();
    },
    signup(isLogin, id, data) {
      if (isLogin == 0) {
        if(this.role==""||this.role==""){
          this.$message.info("")
        }else{
          
          this.joinInfo = data;
          this.getToken(joinLectureApi, { lectureId: id }, () => {
            this.isJoinShow = true;
          });
        }
      } else {
        this.isShow = true;
      }
    },
    closemodal() {
      this.isJoinShow = false;
      this.init();
    },
    submit() {
      this.isJoinShow = false;
      this.init();
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flaghead = true;
      } else {
        this.flaghead = false;
      }
    },
  },
};
</script>
<style lang="stylus">
.modalContent {
    height: 121px;
    background: #f0f0f0;
    margin: 20px 0;
    padding: 20px 20px;
    li {
      font-size: 16px;
      color: #666;
      line-height: 27px;
      span {
        color: #4182fa;
        cursor: pointer;
      }
    }
  }
</style>
<style lang="stylus" scoped>
.teacherLecture {
  .banner {
    height:261px;
    margin:0 auto;
    p{
        color:#333;
        font-size:20px;
        width:100%;
        height:100%;
        display:flex;
        align-items center;
        justify-content center;
        background:#d5d5d5;
        text-align center;
    }
    img {
      width: 100%;
      height:100%;
    }
  }
  .content {
    width: 1205px;
    margin: 0 auto;
    ming-height:500px;
    // background:pink;
    .title {
      font-size: 28px;
      color: #333;
      line-height: 84px;
    }

    .bar {
      display: flex;
      margin-top: 35px;
      justify-content: space-between;
      background: #fbfbfb;
      height: 50px;
      align-items: center;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
      padding: 0 20px;
      margin-bottom: 27px;
      .left {
        span {
          font-size: 14px;
          color: #666;
          &:nth-child(2) {
            color: #4182fa;
            cursor: pointer;
            margin-left: 10px;
          }
          .row {
            color: #4182fa;
            font-size: 16px;
          }
        }
      }
      input {
        width: 205px;
        height: 32px;
        border: 1px solid #ddd;
        margin-right: 5px;
        padding-left: 10px;
        outline: none;
      }
      button {
        color: #4182fa;
        background: #fff;
        border: 1px solid #4182fa;
        width: 57px;
        height: 32px;
      }
      input::-webkit-input-placeholder {
        color: #999;
        font-size: 12px;
      }
    }
    .list{
      min-height:280px;
    }
    .notbox{
      margin:0 auto;
      margin-bottom:30px;
      margin-top:40px;
      width:1205px;
      text-align center;
      img {
        display:block
        margin:0 auto;
      }
    .inp {
        font-size: 18px;
        color: #666;
        margin: 0 auto;
        text-align: center;
        margin-top: 10px;
      }
    }
    .page {
      margin-top: 50px;
      margin-bottom: 50px;
    }
  }
  
}

.gohead {
  width: 66px;
  height: 58px;
  background-color: #f9f9fa;
  border-radius: 3px;
  border: solid 1px #eeeeee;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 240px;
  bottom: 50px;
  cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead3.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}
</style>