<template>
  <div class="examplan">
    <div class="title">
      <h2></h2>
    </div>
    <div class="sea">
      <span></span>
      <div class="mod">
          <input
        type="text"
        placeholder=""
        v-model="key"
      >
      <button
        class="ban"
        @click="searchbtn"
      ></button>
      <button
        class="addbtn"
        @click="addbtn"
      ></button>
      </div>
    </div>
    <a-empty
      v-if="examList.length==0"
      description=""
    />
    <div class="box" v-for="(item,i) in examList" :key="item.id">
        <div class="left">
          <span class="indexspan">{{index(i)}}</span>
          <img class="sortimg" src="../../assets/mine/news.png" alt="">
        </div>
        <div class="right">
            <p class="examtitle">
              <span class="head" >{{item.title}}</span>
              <span class="apllystu">
                <template v-if="item.isCommitted==0">
                    <span></span>
                </template>
                <template v-else>
                  <span v-if="item.status==0" style='color: #ee9537;'></span>
                  <span v-else-if="item.status==1" style="color: #49af4f;"></span>
                  <span v-else style="color: #ff0000;"></span>
                </template>
              </span>
              <span class="examstu">
                <span v-if="item.auditStatus==0">------</span>
                <span v-else-if="item.auditStatus==1" style='color: #ee9537;'></span>
                <span v-else style="color: #49af4f;"></span>
              </span>
            </p>
            <p class="examsort">
                <span></span>
                <span>
                  {{item.profession&&item.profession.name}} | {{item.skill&&item.skill.name}} | 
                  <template v-if="item.examType==0"></template>
                  <template v-else-if="item.examType==1"></template>
                  <template v-else>+</template>
                </span>
            </p>
            <p class="baotime">
              <span></span>
              <span>{{(item.startTime||"").slice(0,16)}}~{{(item.endTime||"").slice(0,16)}}</span>
            </p>
            <p class="examtime">
              <span></span>
              <span>{{(item.activityStart||'').slice(0,16)}}~{{(item.activityEnd||'').slice(0,16)}}</span>
            </p>
            <p class="examnum">
              <span></span>
              <span>{{item.examineeAmount}}</span>
            </p>
            <p class="examrelnum">
              <span></span>
              <span>{{item.examineeCount}}</span>
              <span class="allbtn">
                <span @click="recexamplan(item.id)"></span>
                <span @click="delexamplan(item.id)"></span>
                <span @click="examdetail(item.id)"></span>
                <span @click="subexamplan(item.id)" v-if="item.isCommitted==0"></span>
                <span  v-else style="color: #ee9537;"></span>
                <span @click="toreport(item.id,item.status)"></span>
              </span>
            </p>
        </div>
    </div>
    <a-modal
      title=""
      :visible="examplanshow"
      @ok="examOk"
      @cancel="examcancel"
      okText=""
      cancelText=""
      class="exammodal"
      :destroyOnClose='true'
    >
    <div class="box">
            <eform
            :formData="formData"
            class="examform"
            ref="form"
            :fields="fields"
            >
                <div slot="examname">
                    <a-input
                        v-decorator="['examname',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="baotime">
                    <a-range-picker
                        v-decorator="['joinTime',{ rules: [{ required: true, message: '' }] }]"
                        :locale="locale"
                        :show-time="{ format: 'HH:mm' }"
                        format="YYYY-MM-DD HH:mm"
                        :defaultValue="baoTime"
                        :placeholder="['', '']"
                        class="timebox"
                        @change="onbaoChange"
                    />
                </div>
                <div slot="examtime">
                    <a-range-picker
                      v-decorator="['examTime',{ rules: [{ required: true, message: '' }] }]"
                        :locale="locale"
                        :show-time="{ format: 'HH:mm' }"
                        format="YYYY-MM-DD HH:mm"
                        :defaultValue="examTime"
                        :placeholder="['', '']"
                        class="timebox"
                        @change="onexamChange"
                    />
                </div>
                <div slot="examsort"
                >
                  <examsort
                        @changeExamType="changeExamType"
                        :defaultExamType="defaultExamType"
                        v-decorator="['examsort',{ rules: [{ required: true, message: '' }] }]"
                    ></examsort>
                    <!-- <a-select 
                        class="level"
                        style="width: 180px" 
                        placeholder=""
                        :notFoundContent="''"
                        :defaultValue="provalue"
                        @change="proChange"
                    >
                         <a-select-option v-for="item in prolist" :key="item.id" :value="item.id">
                             {{item.name}}
                        </a-select-option>
                    </a-select>

                    <a-select 
                        class="skill"
                        style="width: 150px" 
                        placeholder=""
                        :notFoundContent="''"
                        :defaultValue="skillvalue"
                        @change="skillChange"
                    >
                        <a-select-option v-for="item in skilllist" :key="item.id" :value="item.id">
                            {{item.name}}
                        </a-select-option>
                    </a-select>
                    <a-select 
                        class="sort"
                        style="width: 150px" 
                        placeholder=""
                        :notFoundContent="''"
                        :defaultValue="sortvalue"
                        @change="sortChange"
                    >
                        <a-select-option value=0>
                            
                        </a-select-option>
                        <a-select-option value=1>
                            
                        </a-select-option>
                        <a-select-option value=2>
                            +
                        </a-select-option>
                    </a-select> -->
                </div>
                <div slot="examequ">
                    <a-input
                        v-decorator="['examequ',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="equmodel">
                    <a-input
                        v-decorator="['equmodel',{ rules: [{ required: true, message: '' }] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="examnum">
                    <a-input
                        v-decorator="['examnum',{ rules: [{ required: true, message: '' },{validator:validatornum}] }]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="examinfo">
                    <a-textarea
                        class="infobox" 
                        v-decorator="['examinfo',{ rules: [{ required: true, message: '' }] }]"
                        placeholder=""
                    />
                </div>
            </eform>
        </div>
    </a-modal>
    <a-modal
      title=""
      :visible="examdetailshow"
      @ok="detailOk"
      @cancel="examdetailshow=false"
      :footer="null"
      okText=""
      cancelText=""
      class="examdetailmodal"
    >
      <div class="detailbox" v-for="v in detailList" :key="v.id">
        <p><span></span><span>{{v.title}}</span></p>
        <p><span></span><span>{{v.startTime}}{{v.endTime}}</span></p>
        <p><span></span><span>{{v.activityStart}}{{v.activityEnd}}</span></p>
        <p><span></span>
          <span >{{v.profession.name}} | {{v.skill.name}} | 
            <template v-if="v.examType==0"></template>
            <template v-else-if="v.examType==1"></template>
            <template v-else>+</template>
          </span>
        </p>
        <p><span></span><span>{{v.examineeAmount}}</span></p>
        <div class="con">
          <span></span>
          <div class='conbox'>{{v.info}}</div>
        </div>
      </div>
    </a-modal>
    <div class="page">
      <pagination
         :pagination="pagination"
         @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>
<script>
import examsort from "@/components/common/examsort"
import moment from 'moment';
import 'moment/locale/zh-cn';
import locale from 'ant-design-vue/es/date-picker/locale/zh_CN';
import {examplanlistApi,deleteExamApi,updateExamApi,getsubmitApi,professionListApi,skillListApi} from "@/request/api"
import pagination from "@/components/common/pagination"
import eform from "@/components/common/form";
export default {
  name: 'examplan',
  components:{eform,pagination,examsort},
  data(){
    const formData = [
      {
        label: "",
        scopedSlots: { customRender: "examname" },
      },
      {
        label: "",
        scopedSlots: { customRender: "baotime" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examtime" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examsort" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examequ" },
      },
      {
        label: "",
        scopedSlots: { customRender: "equmodel" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "examinfo" },
      },
    ];
    let validatornum=(rule,value,callback)=>{
        var regnum=/^[0-9]*$/
        if(value==""||regnum.test(value)){
            callback()
        }else{
            callback(new Error(""))
        }
    }
     return{
       moment,
      formData,
      key:'',
      examplanshow:false,
      examdetailshow:false,
      searchInfo:{
        page:1,
        pageSize:5,
        keyword:''
      },
      pagination:{total:'',pageSize:5},
      examList:[],
      fields:null,
      detailList:[],
      recList:[],
      delList:[],
      locale,
      professionId:'',
      skillId:'',
      examtype:'',
      startTime:'',
      endTime:'',
      activityEnd:'',
      activityStart:'',
      validatornum,
      prolist:[],
      skilllist:[],
      recId:'',
      baoTime:[],
      examTime:[],
      provalue:'',
      skillvalue:'',
      sortvalue:'',
      defaultExamType:[]
     }
  },
  created(){
      this.init()
  },
  methods:{
    init(){
        this.getToken(examplanlistApi,this.searchInfo,res=>{
             this.examList=res.data.rows
             this.pagination.total=res.data.count
            //  console.log(res)
        })
        this.getToken(professionListApi,{},res=>{
            this.prolist=res.data
        })
        this.getToken(skillListApi,{},res=>{
            this.skilllist=res.data
        })
    },
    //
    index(index){
        return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
    searchbtn(){
       this.searchInfo.keyword=this.key
       this.init()
    },
    changeExamType(data){
      this.$refs.form.form.setFieldsValue({["examsort"]:data})
    },
    //
    addbtn(){
        this.$router.push({path:'/newexamplan'})
    },
    //
    toreport(id,status){
      if(status==1){
        this.$router.push({path:'/report',query:{id}})
      }else{
        this.$message.info("")
      }
    },
    //
    recexamplan(id){
      this.recId=id
      this.recList=this.examList.find(item=>item.id===id)
      if(this.recList.isCommitted==1){
        this.$message.info("")
      }else{
        this.examplanshow=true
        //
        this.provalue=this.recList.profession.name
        this.skillvalue=this.recList.skill.name
        this.sortvalue=this.recList.examType==0?"":(this.recList.examType==1?"":"+")
        //
        this.baoTime=[moment(this.recList.startTime,"YYYY-MM-DD HH:mm"),moment(this.recList.endTime,"YYYY-MM-DD HH:mm")]
        this.examTime=[moment(this.recList.activityStart,"YYYY-MM-DD HH:mm"),moment(this.recList.activityEnd,"YYYY-MM-DD HH:mm")]
        this.defaultExamType=[this.recList.profession.id,this.recList.skill.id,this.recList.examType]
        this.fields={
          examname:this.recList.title,
          examequ:this.recList.device,
          equmodel:this.recList.deviceType,
          examnum:this.recList.examineeAmount,
          examinfo:this.recList.info,
          examsort:this.defaultExamType,
          joinTime:this.baoTime,
          examTime:this.examTime
        }
        this.$refs.form.form.setFieldsValue({["examsort"]:this.defaultExamType})
        this.$refs.form.form.setFieldsValue({["joinTime"]:this.baoTime})
        this.$refs.form.form.setFieldsValue({["examTime"]:this.examTime})
      }
       
    },
    //
    examOk(){
      this.$refs.form.form.validateFields().then((res) => {
          console.log(res)
           this.getToken(updateExamApi,{
              'examId':this.recId,
              'title':res.examname,
              'startTime':moment(res.joinTime[0]).format('YYYY-MM-DD HH:mm'),
              'endTime':moment(res.joinTime[1]).format('YYYY-MM-DD HH:mm'),
              'activityStart':moment(res.examTime[0]).format('YYYY-MM-DD HH:mm'),
              'activityEnd':moment(res.examTime[1]).format('YYYY-MM-DD HH:mm'),
              'professionId':res.examsort[0],  
              'skillId':res.examsort[1],
              'examType':res.examsort[2],
              'device':res.examequ,
              'deviceType':res.equmodel,
              'examineeAmount':res.examnum,
              'info':res.examinfo
          },val=>{
              if(val.code==200){
                 this.$message.success("")
                 this.init()
                 this.examplanshow=false
              }
            })
      })
      
    },
    //
    examcancel(){
      this.examplanshow=false
    },
    //
    delexamplan(id){
      this.delList=this.examList.find(item=>item.id===id)
      if(this.delList.isCommitted==1){
        this.$message.info("")
      }else if(this.delList.isCommitted==0){
          this.$confirm({
            title: `"${this.delList.title}"`,
            okText: '',
            cancelText: '',
            onOk:()=>{
                this.getToken(deleteExamApi,{examId:id},res=>{
                  if(res.code==200){
                      this.$message.success("")
                      this.init()
                  }
                 })
            },
            onCancel() {
              // console.log('Cancel');
            }
        })
      }
    },
    //
    onbaoChange(value, dateString) {
    //   console.log('Selected Time: ', value);
    //   console.log('Formatted Selected Time: ', dateString);
      this.startTime=dateString[0]
      this.endTime=dateString[1]
      // console.log(dateString)
      this.$refs.form.form.setFieldsValue({["baotime"]:[this.startTime,this.endTime]})
    },
    //
    onexamChange(value, dateString) {
    //   console.log('Selected Time: ', value);
      this.activityStart=dateString[0]
      this.activityEnd=dateString[1]
      this.$refs.form.form.setFieldsValue({["examtime"]:[this.activityStart,this.activityEnd]})
    },
    // //
    // proChange(value) {
    //   this.professionId=value
    //   this.$refs.form.form.setFieldsValue({["examsort"]:[this.professionId,this.skillId,this.examtype]})
    // },
    // //
    // skillChange(value){
    //   this.skillId=value
    //   this.$refs.form.form.setFieldsValue({["examsort"]:[this.professionId,this.skillId,this.examtype]})
    // },
    // //
    // sortChange(value){
    //    this.examtype=value
    //    this.$refs.form.form.setFieldsValue({["examsort"]:[this.professionId,this.skillId,this.examtype]})
    // },
    //
    subexamplan(id){
        this.$confirm({
          title: '?',
          okText: '',
          cancelText: '',
        onOk:()=>{
          this.getToken(getsubmitApi,{examId:id},res=>{
            if(res.code==200){
              this.init()
            }
          })
        },
        onCancel() {
          // console.log('Cancel');
        }
      })
    },
    //
    examdetail(id){
      this.examdetailshow=true
      this.detailList=this.examList.filter((item)=>{
            if(item.id==id){
              return item
            }
      })
      // this.detailList=this.examList.find(item=>item.id===id)
      // console.log(this.detailList)
    },
    //
    detailOk(){
      this.examdetailshow=false
    },
    changePage(currentPage){
      this.searchInfo.page=currentPage
      this.init()
    },
    

  }
}
</script>
<style>
.exammodal{
  width:800px !important;
}
.exammodal .ant-modal-body {
  margin-top: 20px;
  padding: 0px;
  font-size: 14px;
  word-wrap: break-word;
}
.exammodal .ant-modal-body .box .examform{
   width:100%;
   padding:0;
   margin-left:20px;
   margin-right:20px;
}
.examdetailmodal{
  width:700px !important;
}
.examdetailmodal .ant-modal-body{
    margin-top: 20px;
    margin-left:30px;
    margin-right:30px;
    padding: 0px;
    font-size: 14px;
    word-wrap: break-word;
}
.examdetailmodal .ant-modal-body .detailbox {
   color:#333;
   font-size:16px;
}
.examdetailmodal .ant-modal-body .detailbox p{
    display:flex;
}
.examdetailmodal .ant-modal-body .detailbox p span:nth-child(1){
    width:80px;
    margin-right:10px;
}
.examdetailmodal .ant-modal-body .detailbox p span:nth-child(2){
    flex:1;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;      
}
.examdetailmodal .ant-modal-body .detailbox .con{
    display:flex;
    padding-bottom:20px;
}
.examdetailmodal .ant-modal-body .detailbox .con span{
  width:80px;
  margin-right:10px;
}
.examdetailmodal .ant-modal-body .detailbox .con .conbox{
  border:1px solid #ddd;
  padding:5px;
  flex:1;
  min-height:200px;
}
.examform .timebox{
    width:100% !important;
}
</style>
<style lang="stylus" scoped>
.examplan{
    margin-bottom: 30px;
    .title {
        height: 45px;
        line-height: 45px;
        overflow: hidden;
        border-bottom:1px solid #eff2f2;
        h2 {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
        }
    }
    .sea{
      height: 45px;
      margin-left: 30px;
      margin-right: 30px;
      margin-top: 20px;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      justify-content space-between
      span {
        color: #333;
        font-size: 16px;
      }
      .mod {
        input {
          outline: none;
          width: 220px;
          height: 35px;
          border: solid 1px #d4d4d4;
          text-indent: 8px;
        }

        .ban {
          outline: none;
          width: 60px;
          height: 35px;
          background-color: #4281ff;
          color: #ffffff;
          font-size:14px;
          cursor: pointer;
          border:0
        }

        .addbtn {
          width: 75px;
          height: 35px;
          background-color: #ff904f;
          border-radius: 3px;
          outline: none;
          border:0;
          color:#fff;
          font-size:14px;
          cursor: pointer;
          margin-left:37px;
        }
      }
    }
    .box{
      margin-top:20px;
      margin-left:30px;
      margin-right:30px;
      border: solid 1px #e6e6e6;
      margin-bottom:15px;
      display:flex;
      .left{
        display:flex;
        min-width:80px;
        padding-right:15px;
        padding-left:10px;
        .indexspan{
          color:#666;
          font-size:14px;
          display:block;
          overflow hidden;
          margin-top:30px;
          margin-right:15px;
          
        }
        .sortimg{
          width:40px;
          height:40px;
          display:block;
          margin-top:20px;
        }
      }
      .right{
        overflow hidden
        flex:1;
        .examtitle{
          margin-top:18px;
          position relative
          margin-bottom:13px;
          display:flex;
          .head{
            font-size:16px;
            font-weight: bold;
            display:block
            color: #333333;
            width:580px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .apllystu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:150px;
          }
          .examstu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:23px;
          }
        }
        .examsort{
           font-size:14px;
           height:20px;
           color:#666;
           display:flex;
           margin-bottom:10px;
           span:nth-child(1){
             width:70px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examtime{
          font-size:14px;
          color:#666;
          margin-bottom:10px;
        }
        .baotime{
          font-size:14px;
          color:#666;
          margin-bottom:10px;
        }
        .examnum{
          font-size:14px;
          color:#666;
          margin-bottom:10px;
        }
        .examrelnum{
          font-size:14px;
          color:#666;
          margin-bottom:10px;
          position relative;
          display flex;
          .allbtn{
            width:360px;
            position:absolute;
            right:20px;
            top:50%;
            transform:translateY(-50%)
            display flex;
            span{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #f6f8ff;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              margin-right:9px;
              cursor pointer
            }
            span:last-child{
              margin-right:0px;
            }
          }
        }
      }
    }
    .page {
      margin-top: 30px;
    }
}
</style>
