<template>
  <div class="register">
    <div class="box">
      <div class="left">
        <img src="../assets/register/bg.png" alt />
      </div>
      <div class="right">
        <div class="nav">
          <h2></h2>
          <ul>
            <li @click="torole" :class="{'active':isbg}">
              
              <img src="../assets/register/jt.png" />
            </li>
            <li @click="tobrole" :class="{'active':isbig}">
              
              <img src="../assets/register/jt.png" />
            </li>
          </ul>
        </div>
      </div>
      <div class="regfrom" >
        <ul class="role" v-show="toshow">
          <li
            v-for="(item,index) in rolelist"
            :key="index"
            @click="changerole(index,item)"
            :class="{'active':current==index}"
          >{{item}}</li>
        </ul>
        <ul class="role" v-show="isshow">
          <li
            v-for="(name,index) in brolelist"
            :key="index"
            @click="changebrole(index,name)"
            :class="{'active':currentindex==index}"
          >{{name}}</li>
        </ul>
        
          <div :class="{'formbox':true,'addclass':isopen}">
            <a-form :form="form" ref="form" >
              <a-form-item>
                <a-input v-show="toshow"
                  v-decorator="[
                                  'user',
                                  { rules: [{ required: true, message: ' ' },{validator:validatoruser}] },
                                  ]"
                  placeholder=""
                >
                  <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
                </a-input>
                <a-input v-show="isshow"
                  v-decorator="[
                                  'user',
                                  { rules: [{ required: true, message: ' ' },{validator:validatoruser}] },
                                  ]"
                  placeholder=""
                >
                  <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
                </a-input>
              </a-form-item>
              <a-form-item>
                <a-input
                  v-decorator="[
                                  'phone',
                                  { rules: [{ required: true, message: '' },{validator:validatorphone}] },
                                  ]"
                  placeholder=""
                >
                  <a-icon slot="prefix" type="phone" style="color:rgba(0,0,0,.25)" />
                </a-input>
              </a-form-item>
              <a-form-item class="iteminp">
                <a-input
                  class="cadeinp"
                  v-decorator="[
                              'captcha',
                              { rules: [{ required: true, message: '' },{validator:validatorcaptcha}] },
                              ]"
                  placeholder=""
                >
                  <a-icon slot="prefix" type="text" style="color:rgba(0,0,0,.25)" />
                </a-input>
                <a-button v-if="show" class="subbtn" @click="gocade">
                  <span></span>
                </a-button>
                <a-button v-if="!show" class="subbtn">
                  <span class="count">{{count}}s</span>
                </a-button>
              </a-form-item>
              <a-form-item>
                <a-input
                  v-decorator="[
                              'pwd',
                              { rules: [{ required: true, message: '' },{validator:validatorPwd}] },
                              ]"
                  type="password"
                  placeholder="8-16"
                >
                  <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
                </a-input>
              </a-form-item>
              <a-form-item>
                <a-input
                  v-decorator="[
                              'repwd',
                              { rules: [{ required: true, message: '' },{validator:validatorrePwd}] },
                              ]"
                  type="password"
                  placeholder=""
                >
                  <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
                </a-input>
              </a-form-item>
              <a-form-item class="reginp">
                <a-button class="regbtn" type="primary" @click="toreg"></a-button>
              </a-form-item>
              <!-- <p class="tpxieyi" ><a href="www.baidu.com" target="_brank"></a></p> -->
            </a-form>
          </div>
      </div>
    </div>
    <div class="Box" v-if="isBox">
      <div class="boxModel">
        <div class="header"></div>
        <div class="info">
          <p class="mod">
            <img src="../assets/course/chenggong.png" alt />
            <span></span>
          </p>
          <div class="close" @click="closeModal">X</div>
        </div>
        <div class="content">
          <p></p>
          <div class="sub">
            <button class="minebtn" @click="tobasicInfo"></button>
            <button class="closebtn" @click="closeModal"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getcodeApi, getregister, getcheckPhoneApi,userLoginApi} from "@/request/api";
export default {
  name: "register",
  data() {
    let validatoruser = (rule, value, callback) => {
      if (value.trim()) {
        if (value == "" || value) {
          this.username = value;
          callback();
        } else {
          callback(new Error(""));
        }
      } else {
          callback(new Error(""));
      }
    };
    let validatorphone = (rule, value, callback) => {
      var regphone = /^1[3456789]\d{9}$/;
      if (value == "" || regphone.test(value)) {
        this.phone = value;
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorPwd = (rule, value, callback) => {
      var regpwd = /^[\w$]{8,16}$/; //
      if (value == "" || regpwd.test(value)) {
        this.pwd = value;
        callback();
      } else {
        callback(new Error("816"));
      }
    };
    let validatorcaptcha = (rule, value) => {
      this.captcha = value;
    };
    let validatorrePwd = (rule, value, callback) => {
      this.repwd = value;
      if (value == this.pwd) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      left:0,
      validatorphone,
      validatoruser,
      validatorPwd,
      validatorrePwd,
      validatorcaptcha,
      form: this.$form.createForm(this, { name: "horizontal_login" }),
      navlist: ["", ""],
      rolelist: ["", "", ""],
      brolelist: ["", ""],
      regIndex: "",
      current: "",
      currentindex:'',
      toshow: true,
      isshow: false,
      isbg: true,
      isbig: false,
      phone: "",
      captcha: "",
      pwd: "",
      username: "",
      show: true,
      count: "",
      timer: null,
      role:"",
      isBox: false,
      repwd: "",
      isopen:false,
    };
  },
  methods: {
    torole() {
      //
      this.isbg = true;
      this.isbig = false;
      //
      this.toshow = true;
      this.isshow = false;
      this.role=""
    },
    tobrole() {
      //
      this.isbg = false;
      this.isbig = true;
      //
      this.toshow = false;
      this.isshow = true;
      this.role=""
    },
    //
    changerole(index, title) {                
      this.current = index;
      
      this.isopen=!this.isopen
      setTimeout(()=>{this.isopen=false},300)
      // console.log(this.isopen)
      if (title == "") {
        this.role = "";
        this.show = true;
        this.$refs.form.form.resetFields(["user"]);
        this.$refs.form.form.resetFields(["phone"]);
        this.$refs.form.form.resetFields(["captcha"]);
        this.$refs.form.form.resetFields(["pwd"]);
        this.$refs.form.form.resetFields(["repwd"]);
        // console.log(this.role)
      } else if (title == "") {
        this.role = "";
        this.show = true;
        this.$refs.form.form.resetFields(["user"]);
        this.$refs.form.form.resetFields(["phone"]);
        this.$refs.form.form.resetFields(["captcha"]);
        this.$refs.form.form.resetFields(["pwd"]);
        this.$refs.form.form.resetFields(["repwd"]);
        // console.log(this.role)
      } else if (title == "") {
        this.role = "";
        this.show = true;
        this.$refs.form.form.resetFields(["user"]);
        this.$refs.form.form.resetFields(["phone"]);
        this.$refs.form.form.resetFields(["captcha"]);
        this.$refs.form.form.resetFields(["pwd"]);
        this.$refs.form.form.resetFields(["repwd"]);
        // console.log(this.role)
      }
    },
    //
    changebrole(index, title){
        this.isopen=!this.isopen
        setTimeout(()=>{this.isopen=false},300)
        this.currentindex=index
      if (title =="") {
        this.role ="";
        this.show = true;
      } else{ 
        this.role ="";
        this.show = true;
      }
    },
    //
    gocade() {
      if (this.phone) {
        this.getToken(getcheckPhoneApi, { telephone: this.phone }, (res) => {
          if (res.isExist == false) {
            this.getToken(getcodeApi, { telephone: this.phone }, (res) => {
              if (res.code == 200) {
                this.$message.info("");
                const TIME_COUNT = 60;
                if (!this.timer) {
                  this.count = TIME_COUNT;
                  this.show = false;
                  this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                      this.count--;
                    } else {
                      this.show = true;
                      clearInterval(this.timer);
                      this.timer = null;
                    }
                  }, 1000);
                }
              }
            });
          } else {
            this.$message.error("");
          }
        });
      } else {
        this.$message.error("");
      }
    },
    //
    toreg() {
      if (this.username) {
        if (this.phone) {
          if (this.pwd) {
            if (this.pwd === this.repwd) {
              this.getToken(
                getregister,
                {
                  telephone: this.phone,
                  username: this.username,
                  captcha: this.captcha,
                  password: this.pwd,
                  role: this.role,
                },
                (res) => {
                  if (res.code == 200) {
                    this.getToken(userLoginApi,{"account":this.phone,"password":this.pwd},value=>{
                       if(value.code==200){
                            localStorage.setItem("isLogin", 0);
                            localStorage.setItem("role", value.data.role);
                            localStorage.setItem("username", value.data.name);
                            localStorage.setItem("icon", value.data.icon);
                            localStorage.setItem("token", value.data.token);
                            localStorage.setItem("loginTime",new Date().getTime())
                            localStorage.setItem("authentation", value.data.authentation);
                            this.$store.commit("toggleIsLogin", {
                              isLogin: 0,
                              role: value.data.role,
                              username: value.data.name,
                              icon: value.data.icon,
                              authentation: value.data.authentation,
                          });
                            this.$message.success("");
                       }
                    })
                    this.isBox = true;
                  }
                }
              );
            } else {
              this.$message.error("");
            }
          } else {
            this.$message.error("");
          }
        } else {
          this.$message.error("");
        }
      } else this.$message.error("");
    },
    //
    tobasicInfo() {
      if(this.role==""||this.role==""){
          this.$router.push({ path: "/basicInfos" });
      }else{
          this.$router.push({ path: "/basicInfo" });
      }
    },
    //
    closeModal() {
      this.isBox = false;
      this.$router.push({ path: "/" });
    },
  },
};
</script>
<style lang="stylus" scoped>
@keyframes mymove{
  0%{-webkit-transform:rotateY(0deg);}
  100%{-webkit-transform:rotateY(360deg);}
}
.addclass{
   animation:mymove 0.3s;
}
.register {
  background: #fff;
  border: 1px solid #ccc;
  .box {
    margin: 0 auto;
    width: 1205px;
    display: flex;
  }
  .left {
    width: 326px;
    height: 716px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .right {
    margin-left: 50px;
    width: 241px;
    margin-right: 87px;
    .nav {
      overflow: hidden;
      h2 {
        font-size: 32px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #333;
        margin-bottom: 42px;
        margin-top: 39px;
      }
      ul {
        width: 241px;
        height: 181px;
        background-color: #ffffff;
        box-shadow: 0px 0px 3px 0px rgba(105, 105, 105, 0.1);
        border: solid 1px #eff2f2;
        padding-top: 6px;
        >li {
          font-size: 16px;
          font-weight: normal;
          font-stretch: normal;
          line-height: 36px;
          letter-spacing: 0px;
          color: #333333;
          height: 40px;
          line-height: 40px;
          padding-left: 22px;
          position: relative;
          cursor: pointer;
          img {
            width: 6px;
            height: 11px;
            position: absolute;
            right: 13px;
            top: 50%;
            transform: translateY(-50%);
          }
        }
        .active {
          background-color: #edf3fe;
          border-left: 3px solid #4182fa;
        }
      }
    }
  }
  .regfrom {
    min-height: 500px;
    width: 420px;
    // overflow: hidden;

    .role {
      width: 420px;
      display: flex;
      margin-top: 79px;
      margin-bottom: 35px;
      display: flex; 
      li {
        width: 140px;
        text-align: center;
        color: #666666;
        font-size: 16px;
        line-height: 37px;
        border-bottom: 1px solid #dddddd;
        font-weight: bold;
        flex: 1;
        cursor pointer
      }
      .active {
        letter-spacing: 0px;
        color: #4182fa;
        border-bottom: 1px solid #4182fa;
      }
    }
    .formbox {
      width: 420px;
      margin: 0 auto;
      margin-top: 60px;
      transform: rotateY(0deg);
      .ant-btn-primary {
        height: 45px;
        width: 420px;
        background-color: #4182fa;
        border-radius: 3px;
        font-size: 18px;
        border: 0;
      }
      .ant-input-affix-wrapper {
        line-height: 45px;
        height: 45px;
      }
      .iteminp {
        display: flex;
        .cadeinp {
          width: 290px;
          margin-right: 10px;
          padding-left: -10px;
        }
        .subbtn {
          width: 118px;
          height: 45px;
          line-height: 45px;
          background: #ffffff;
          border-radius: 3px;
          border: solid 1px #4182fa;
          color: #4182fa;
          outline: none;
          .count {
            color: red;
          }
        }
      }
      .reginp {
        display: flex;
        .regbtn {
          width: 420px;
          height: 44px;
          border-radius: 3px;
        }
      }
      .tpxieyi{
         margin-top:20px;
         color:#a7a7a7;
         font-size:16px
         a{
           color:#a7a7a7;
           text-decoration:underline;
         }
         a:hover{
           color:#4182fa;
           text-decoration:underline;
         }
      }
    }
  }

  .Box {
    position: relative;
    z-index: 999;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background: rgba(0, 0, 0, 0.5);

    .boxModel {
      width: 489px;
      height: 246px;
      background-color: #ffffff;
      border-radius: 3px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      .header {
        width: 100%;
        height: 5px;
        background-color: #0096ff;
      }

      .info {
        .mod {
          margin: 0 auto;
          width: 420px;
          border-bottom: 1px solid #dfdfdf;
          height: 70px;
          line-height: 70px;
          display: flex;
          align-items: center;
          position: relative;

          img {
            width: 32px;
            height: 32px;
          }

          span {
            color: #666666;
            font-size: 18px;
            padding-left: 11px;
          }
        }

        .close {
          width: 20px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          color: #999;
          font-size: 22px;
          position: absolute;
          right: 15px;
          top: 15px;
          cursor: pointer;
        }
      }

      .content {
        p {
          width: 420px;
          margin: 0 auto;
          padding-top: 25px;
          font-size: 16px;
          margin-bottom: 40px;
        }

        .sub {
          background: pink;
          display: flex;
          position: relative;

          .minebtn {
            background-color: #4182fa;
            border-radius: 3px;
            color: #fff;
            border: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            outline: none;
            font-size: 16px;
            position: absolute;
            right: 144px;
            width: 138px;
            height: 36px;
          }
          .closebtn {
            border: 1px solid #4182fa;
            background: #fff;
            color: #4182fa;
            border-radius: 3px;
            outline: none;
            position: absolute;
            right: 36px;
            height: 36px;
            width: 93px;
          }
        }
      }
    }
  }
}
</style>
