<template>
  <div class="allianceDetail education">
    <div class="header">
      <h2 class="title"><span>/ Alliance Member</span></h2>
      <div class="breadCrumb">
        <span @click="toHome()"> ></span>
        <span @click="toAlliance()"> ></span>
        <span>{{info.name&&info.name}}</span>
      </div>
      <div class='tag'>{{info.role==''?'':''}}</div>
    </div>
    <detailCommon
      :info="info"
      :footerInfo="footerInfo"
      :returnText="''"
      @todetail="todetail"
      @returnback="returnback"
      :itemName="''"
    ></detailCommon>
    <!-- <div class="allianceContent">
            <div class="h1">{{info.name}}</div>
            <div class="info">
                {{info.info}}
            </div>
            <div class="footer">
                <div class="left">
                    <p v-if="footerInfo.last" @click="todetail(footerInfo.last.id)">{{footerInfo.last.institute.name}}</p>
                    <p v-if="footerInfo.next" @click="todetail(footerInfo.next.id)">{{footerInfo.next.institute.name}}</p>
                </div>
                <div class="right">
                    <div class="btn" @click="returnback()"> <img src="../assets/back.png" alt=""> </div>
                </div>
            </div>
        </div> -->
    <!--  -->
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import { getAllianceDetailApi, getallianceListApi } from "@/request/api";
import detailCommon from "@/components/common/detailCommon";
export default {
  name: "alliance",
  components: { detailCommon },
  created() {
    this.allianceId = this.$route.query.id;
    this.init(this.allianceId);
  },
  data() {
    return {
      allianceId: "",
      info: {},
      footerInfo: {
        last: "",
        next: "",
      },
      flag: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init(id) {
      this.getToken(getAllianceDetailApi, { allianceId: id }, (res) => {
        this.info = {
          ...res.data,
          ...res.data.institute,
          content: res.data.institute.info,
        };
      });
      this.getToken(getallianceListApi, {}, (res) => {
        let list = res.data.rows;
        let itemIndex;
        list.forEach((item, index) => {
          if (item.id == this.allianceId) {
            itemIndex = index;
          }
        });
        if (itemIndex == 0) {
          this.footerInfo.last = "";
          this.footerInfo.next = list[itemIndex + 1];
          this.footerInfo.next.name = this.footerInfo.next.institute.name;
        } else if (itemIndex == list.length - 1) {
          this.footerInfo.last = list[itemIndex - 1];
          this.footerInfo.next = "";
          this.footerInfo.last.name = this.footerInfo.last.institute.name;
        } else {
          this.footerInfo.last = list[itemIndex - 1];
          this.footerInfo.next = list[itemIndex + 1];
          this.footerInfo.next.name = this.footerInfo.next.institute.name;
          this.footerInfo.last.name = this.footerInfo.last.institute.name;
        }
      });
    },
    returnback() {
      this.$router.push({ path: "/alliance" });
    },
    todetail(id) {
      this.allianceId = id;
      this.init(id);
    },
    toHome() {
      this.$router.push({ path: "/" });
    },
    toAlliance() {
      this.$router.push({ path: "/alliance" });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>
<style lang="stylus" scoped>
.allianceDetail {
  .header {
    position: relative;
    h2 {
      bacground:pink;
      padding-top:30px;
      width: 600px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      padding-bottom: 50px;
      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }
    .breadCrumb {
      position: absolute;
      right: 69px;
      top: 40px;
      span{
        color:#333
      }
      span {
        cursor: pointer;
      }
      span:last-child{
        color:#999;
      }
    }

    .tag {
      position: absolute;
      bottom: -10px;
      display: inline-block;
      background: #eff7ff;
      right: 69px;
      bottom: -96px;
      padding: 0 5px;
      color: #3d8cdb;
    }
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>