  margin-bottom: 100px;
  .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      h2 {
        height:45px;
        line-height: 45px;
        margin-left: 30px;
        font-size: 18px;
        font-weight: bold;
        color: #333333;
      }
  }

  .sea {
    height: 45px;
    margin-left: 30px;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    position: relative;
    justify-content space-between
    span {
      color: #333;
      font-size: 16px;
    }
    
    .find{
      display:flex;
      margin-right:30px;
      .mod {
        width: 204px;
        height:35px;
        line-height:35px;
        margin-right:24px;
        .selbtn {
          width: 100%;
          height: 100%;
        }
      }
      input {
        outline: none;
        width: 220px;
        height: 35px;
        border: solid 1px #d4d4d4;
        text-indent: 8px;
      }
      .ban {
        outline: none;
        width: 60px;
        height: 35px;
        background-color: #4281ff;
        color: #ffffff;
        font-size:14px;
        cursor: pointer;
        border:0
      }
    }
  }

  .nav {
    display: flex;
    margin-top: 20px;
    height: 45px;
