<template>
  <div class="ServiceDetail">
    <div class="banner">
      <img src="../assets/banner/1+X_banner.png" alt />
    </div>
    <div class="content">
      <div class="breadCrumb">
        <span @click="returnHome()"> ></span>
        <span @click="returnService()">1+X ></span>
        
      </div>
      <div class="time">{{(info.createTime||"").slice(0,10)}}</div>
      <div class="bao" @click="gobao(isLogin)">
        <img src="../assets/formationbaoming.png" alt />
        <span></span>
      </div>
      <serviceCommon
        :info="info"
        :footerInfo="footerInfo"
        :returnText="''"
        @todetail="todetail"
        @returnback="returnService()"
        :itemName="''"
      ></serviceCommon>
      <!--  -->
      <a-modal
        title=""
        class="realmodal"
        :visible="baoshow"
        @ok="handleOk"
        @cancel="baoshow=false"
        okText=""
        cancelText=""
      >
        <div class="box" v-if="baolist">
          <p>
            
            <span>{{baolist.name}}</span>
          </p>
          <p>
            
            <span>{{baolist.account}}</span>
          </p>
          <p>
            
            <span>{{baolist.realname}}</span>
          </p>
          <p>
            
            <span>{{baolist.idCardNum}}</span>
          </p>
          <p>
            
            <span>{{baolist.instituteName}}</span>
          </p>
          <p>
            
            <span :title="info.title">{{info.title}}</span>
          </p>
        </div>
      </a-modal>
      <a-modal
        title=""
        class="realmodal"
        :visible="noshow"
        @ok="tobasicInfo"
        @cancel="noshow=false"
        okText=""
        cancelText=""
      >
        <div class="box" v-if="baolist">
          <p>
            
            <span>{{baolist.name}}</span>
          </p>
          <p>
            
            <span>{{baolist.account}}</span>
          </p>
          <p>
            
            <span class="noreal"></span>
          </p>
          <p>
            
            <span class="noreal"></span>
          </p>
          <p>
            
            <span>{{baolist.address}}</span>
          </p>
          <p>
            
            <span :title="info.title">{{info.title}}</span>
          </p>
        </div>
      </a-modal>
      <a-modal
        title="!"
        class="successmodal"
        :visible="successshow"
        @ok="successshow=false"
        @cancel="successshow=false"
        okText=""
        cancelText=""
      >
        <p>
          
          
          <span class="new" @click="tomytriain">--</span>
          
        </p>
        <!-- <img class=".bg" src="../assets/course/chenggong.png" alt=""> -->
      </a-modal>
      <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
    </div>
  </div>
</template>

<script>
import Moment from 'moment'
import {
  getserviceDetailApi,
  getservicebaoDetailApi,
  getuserInfoApi,
} from "@/request/api";
import serviceCommon from "@/components/common/serviceCommon";
export default {
  name: "ServiceDetail",
  components: { serviceCommon },
  data() {
    return {
      serviceId: "",
      examId: "",
      info: {},
      footerInfo: {
        last: "",
        next: "",
      },
      baoshow: false,
      noshow: false,
      successshow: false,
      baolist: [],
      idnum: "",
      Time: "",
      flag:false,
      type:''
    };
  },
  created() {
    this.serviceId = this.$route.query.id;
    this.type=this.$route.query.type
    this.init();
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
   mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(
        getserviceDetailApi,
        { activityId: this.serviceId },
        (res) => {
          this.info = res.data;
          // console.log(this.info)
        }
      );
    },
    //
    gobao(isLogin) {
      // console.log(123)
      this.getToken(getuserInfoApi, {}, (res) => {
        this.baolist = res.data;
        console.log(res)
        this.idnum = res.data.idCardNum;
        //
        if (res.data.role == "" || res.data.role == "") {
          this.$message.info("");
        } else {
          if (isLogin == 0) {
       
            this.Time=Moment().format('YYYY-MM-DD HH:mm:ss')

            //
            console.log("",this.Time)
            console.log(this.info.startTime)

            if (this.Time > this.info.endTime) {
              this.$message.info("");
            } else if (this.Time < this.info.startTime) {
              this.$message.info("");
            } else {
              //
              if (this.idnum) {
                this.baoshow = true;
              } else {
                this.noshow = true;
              }
            }
          } else {
            // console.log("")
          }
        }
      });
    },
    //,
    handleOk() {
      this.getToken(
        getservicebaoDetailApi,
        { activityId: this.serviceId },
        (res) => {
          if (res.code == 436) {
            this.$message.info("-");
          } else {
            this.successshow = true;
            this.baoshow = false;
          }
        }
      );
    },
    //
    tobasicInfo() {
      this.$router.push({ path: "/basicInfo" });
    },
    //
    tomytriain() {
      this.$router.push({ path: "/mytrain" });
    },
    returnHome() {
      this.$router.push({ path: "/" });
    },
    returnService() {
      // console.log(this.type)
      if(this.type==1){
         this.$router.push({ path: "/Service",query:{type:1,serviceIndex:0}});
      }else if(this.type==2){
         this.$router.push({ path: "/Service" ,query:{type:2,serviceIndex:1}});
      }else{
         this.$router.push({ path: "/Service",query:{type:3,serviceIndex:2} });
      }
      
    },
    todetail(id) {
      this.serviceId = id;
      this.init();
    },
    watchScroll() {
        var scrollTop =
            window.pageYOffset ||
            document.documentElement.scrollTop ||
            document.body.scrollTop;
            if (scrollTop > 100) {
                this.flag = true;
            } else {
                this.flag = false;
            }
        },
  },
};
</script>
<style >
/* .realmodal  */
.realmodal .box {
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.realmodal .box p {
  width: auto;
  height: 38px;
  line-height: 38px;
  color: #666;
  font-size: 14px;
  margin-bottom: 0px;
  position: relative;
}
.realmodal .box p:nth-child(2n) {
  background-color: #f8fafe;
}
.realmodal .box p:last-child {
  margin-bottom: 20px;
}
.realmodal .box p span {
  position: absolute;
  left: 100px;
  overflow: hidden;
  width: 360px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.realmodal .box p .noreal {
  color: #ea6125;
}
.realmodal .ant-modal-header {
  background: url("../assets/course/tishi.png") no-repeat;
  background-position: 0px 10px;
  margin-left: 25px;
  margin-right: 25px;
  padding-left: 40px;
}
.realmodal .ant-modal-footer {
  margin-left: 25px;
  margin-right: 25px;
  text-align: right;
}
.successmodal p {
  margin-top: 32px;
  font-size: 16px;
  color: #666666;
  padding-left: 25px;
  padding-right: 25px;
}
.successmodal p .new {
  color: #4182fa;
  cursor: pointer;
}

.successmodal .ant-btn {
  display: none;
}
.successmodal .ant-modal-header {
  margin-left: 25px;
  margin-right: 25px;
  padding-left: 35px;
  color: rgba(0, 0, 0, 0.65);
  background: #fff;
  border-radius: 4px 4px 0 0;
  position: relative;
  background: url("../assets/course/chenggong.png") no-repeat;
  background-position: 0px 10px;
}
.successmodal .ant-modal-footer {
  margin-left: 25px;
  margin-right: 25px;
  text-align: right;
}
</style>
<style lang="stylus" scoped>
.ServiceDetail {
  background: #eee;

  .banner {
    img {
      width: 100%;
      height: 100%;
    }
  }

  .content {
    background: #fff;
    min-height: 300px;
    width: 1205px;
    padding-left: 70px;
    padding-right: 70px;
    padding-top: 40px;
    width: 1205px;
    margin: 0 auto;
    position: relative;

    .breadCrumb {
      min-width: 190px;
      height: 14px;
      font-size: 14px;
      color: #999999;
      margin-bottom: 50px;

      span {
        font-size: 14px;
        color: #333333;
      }

      span:hover {
        color: #4182fa;
        cursor: pointer;
      }
    }

    .time {
      fontFamily: 'MicrosoftYaHei';
      fontSize: 14px;
      lineHeight: 36px;
      color: '#666666';
      position: absolute;
      right: 532px;
      top: 160px;
    }

    .bao {
      width: 116px;
      height: 35px;
      background-color: #fd8d56;
      border-radius: 3px;
      position: absolute;
      right: 69px;
      top: 138px;
      line-height: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      span {
        width: 70px;
        font-family: MicrosoftYaHei;
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        line-height: 25px;
        letter-spacing: 1px;
        color: #ffffff;
        padding-left: 2px;
      }

      img {
        width: 23px;
        height: 13px;
        display: block;
        margin-top: 2px;
      }
    }
  }
}
    .gohead {
        width: 66px;
        height: 58px;
        background-color: #f9f9fa;
        border-radius: 3px;
        border: solid 1px #eeeeee;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        right: 240px;
        bottom: 50px;
        cursor: pointer;

          a {
            width: 33px;
            height: 32px;
            display: inline;
            text-align: center;
            background: url('../assets/gohead3.png') -18px -15px no-repeat;
          }
        }

.gohead:hover {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}

</style>
