<template>
  <div id="education companyDetail">
    <div class="header">
      <h2
        class="title"
        v-if="type=='company'"
      ><span>/ Company News</span></h2>
      <h2
        class="title"
        v-else-if="type=='policy'"
      ><span>/ Industry policy</span></h2>
      <h2
        class="title"
        v-else
      ><span>/ Notice</span></h2>
      <div class="breadCrumb">
        <span @click="returnHome()" class="sp1"> ></span>
        <span
        class="sp1"
          @click="returnIndustry(type)"
          v-if="type=='company'"
        > ></span>
        <span
        class="sp1"
          @click="returnIndustry(type)"
          v-else-if="type=='policy'"
        > ></span>
        <span
        class="sp1"
          @click="returnIndustry(type)"
          v-else
        > ></span>
        <span></span>
      </div>
    </div>
    <div class="time">{{(info.pubTime||"").slice(0,10)}}</div>
    <div
      class="bao"
      v-if="type=='notice'&&this.info.isTrainingClass==1"
      @click="gobao(isLogin)"
    >
      <img
        src="../assets/formationbaoming.png"
        alt=""
      >
      <span></span>
    </div>

    <detailCommon
      :info="info"
      :footerInfo="footerInfo"
      :returnText="''"
      @todetail="todetail"
      @returnback="returnIndustry(type)"
      :itemName="''"
    ></detailCommon>
    <!--  -->
    <a-modal
      title=""
      class="realmodal"
      :visible="baoshow"
      @ok="handleOk"
      @cancel="baoshow=false"
      okText=""
      cancelText=""
    >
      <div class="box">
        <p><span>{{baolist.name}}</span></p>
        <p><span>{{baolist.account}}</span></p>
        <p><span>{{baolist.realname}}</span></p>
        <p><span>{{baolist.idCardNum}}</span></p>
        <p><span>{{baolist.instituteName}}</span></p>
        <p><span :title="info.title">{{info.title}}</span></p>
      </div>
    </a-modal>
    <a-modal
      title=""
      class="realmodal"
      :visible="noshow"
      @ok="tobasicInfo"
      @cancel="noshow=false"
      okText=""
      cancelText=""
    >
      <div class="box">
        <p><span>{{baolist.name}}</span></p>
        <p><span>{{baolist.account}}</span></p>
        <p><span class="noreal"></span></p>
        <p><span class="noreal"></span></p>
        <p><span>{{baolist.address}}</span></p>
        <p><span :title="info.title">{{info.title}}</span></p>
      </div>
    </a-modal>
    <a-modal
      title="!"
      class="successmodal"
      :visible="successshow"
      @ok="successshow=false"
      @cancel="successshow=false"
    >
      <p><span
          class="new"
          @click="tomytriain"
        > --</span>
        </p>
      <!-- <img class=".bg" src="../assets/course/chenggong.png" alt=""> -->
    </a-modal>
    <!--  -->
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import detailCommon from "@/components/common/detailCommon";
import {
  getFormationListApi,
  getFormationDetailApi,
  getuserInfoApi,
  getapplyApi,
  getcheckAppliedApi,
} from "@/request/api";
export default {
  name: "companyDetail",
  components: { detailCommon },
  created() {
    this.companyId = this.$route.query.id;
    this.type = this.$route.query.type;
    this.init();
  },
  data() {
    return {
      companyId: null,
      info: {},
      footerInfo: {
        last: "",
        next: "",
      },
      type: "",
      flag: true,
      baoshow: false,
      noshow: false,
      successshow: false,
      baolist: [],
      idnum:''
    };
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(
        getFormationDetailApi,
        { newsId: this.companyId },
        (res) => {
          this.info = { ...res.data, name: res.data.title };
        }
      );
      this.getToken(getFormationListApi, {}, (res) => {
        let list = res.data.rows
          .filter((item) => item.type == this.type)
          .filter((item) => item.pubTime);
        let itemIndex;
        list.forEach((item, index) => {
          if (item.id == this.companyId) {
            itemIndex = index;
          }
        });
        if (itemIndex == 0) {
          this.footerInfo.last = "";
          this.footerInfo.next = list[itemIndex + 1];
          this.footerInfo.next.name = this.footerInfo.next.title;
        } else if (itemIndex == list.length - 1) {
          this.footerInfo.last = list[itemIndex - 1];
          this.footerInfo.last.name = this.footerInfo.last.title;
          this.footerInfo.next = "";
        } else {
          this.footerInfo.last = list[itemIndex - 1];
          this.footerInfo.next = list[itemIndex + 1];
          this.footerInfo.last.name = this.footerInfo.last.title;
          this.footerInfo.next.name = this.footerInfo.next.title;
        }
      });
    },
    returnHome() {
      this.$router.push({ path: "/" });
    },
    todetail(id) {
      this.companyId = id;
      this.init(id);
    },
    returnIndustry(type) {
      // alert(type);
      console.log(type)
      this.$router.push({ path: "/company", query: { type: type } });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
    //
    gobao(isLogin) {
      this.getToken(getuserInfoApi, {}, (res) => {
      this.baolist = res.data
      this.idnum=res.data.idCardNum
      if(res.data.role==""||res.data.role==""){
          this.$message.info("")
      }else{
          //
          if (isLogin == 0) {
            this.getToken(
                getcheckAppliedApi,
                { trainingClassId: this.info.trainingClassId },
                (res) => {
                  //
                  if (res.applied == false) {
                    //
                    if(this.info.trainingClassStatus==1){

                        this.getToken(getuserInfoApi, {}, (res) => {
                          this.baolist = res.data
                            if(this.idnum){
                              this.baoshow=true
                            }else{
                              this.noshow=true
                            }
                        });
                        
                    }else if(this.info.trainingClassStatus==0){
                        this.$message.info("");
                    }else{
                      this.$message.info("");
                    }
                  } else {
                    this.$message.info("-");
                  }
                }
              );
          } else {
            // this.$message.error("");
          }
      }
    });
      
    },
    //,
    handleOk() {
      this.getToken(
        getapplyApi,
        { trainingClassId: this.info.trainingClassId },
        (res) => {
          if (res.code == 200) {
            this.successshow = true;
            this.baoshow=false
          }
        }
      );
    },
    //
    tobasicInfo() {
      this.$router.push({ path: "/basicInfo" });
    },
    //
    tomytriain() {
      this.$router.push({ path: "/mytrain" });
    },
  },
};
</script>
<style >
.realmodal .box {
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.realmodal .box p {
  width: auto;
  height: 38px;
  line-height: 38px;
  color: #666;
  font-size: 14px;
  margin-bottom: 0px;
  position: relative;
}
.realmodal .box p:nth-child(2n) {
  background-color: #f8fafe;
}
.realmodal .box p:last-child {
  margin-bottom: 20px;
}
.realmodal .box p span {
  position: absolute;
  left: 100px;
  overflow: hidden;
  width: 360px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.realmodal .box p .noreal {
  color: #ea6125;
}
.realmodal .ant-modal-header {
  background: url("../assets/course/tishi.png") no-repeat;
  background-position: 0px 10px;
  margin-left: 25px;
  margin-right: 25px;
  padding-left: 40px;
}
.realmodal .ant-modal-footer {
  margin-left: 25px;
  margin-right: 25px;
  text-align: right;
}
.successmodal p {
  margin-top: 32px;
  font-size: 16px;
  color: #666666;
  padding-left: 25px;
  padding-right: 25px;
}
.successmodal p .new {
  color: #4182fa;
  cursor: pointer;
}

.successmodal .ant-btn{
  display: none;
}
.successmodal .ant-modal-header {
  margin-left: 25px;
  margin-right: 25px;
  padding-left: 35px;
  color: rgba(0, 0, 0, 0.65);
  background: #fff;
  border-radius: 4px 4px 0 0;
  position: relative;
  background: url("../assets/course/chenggong.png") no-repeat;
  background-position: 0px 10px;
}
.successmodal .ant-modal-footer {
  margin-left: 25px;
  margin-right: 25px;
  text-align: right;
  border:0;
}
</style>
<style lang="stylus" scoped>
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 25px;
  margin-top: 43px;

  h2 {
    width: 400px;
    height: 24px;
    font-family: MicrosoftYaHei;
    font-size: 26px;
    font-weight:200;
    color: #333333;
    padding-left: 69px;
    margin-bottom: 40px;

    span {
      font-size: 16px;
      font-weight: normal;
      letter-spacing: 0px;
      color: #999999;
    }
  }

  .breadCrumb {
    min-width: 190px;
    height: 14px;
    margin-bottom: 50px;
    font-family: MicrosoftYaHei;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 48px;
    letter-spacing: 1px;
    color: #333333;
    span:last-child {
      font-family: MicrosoftYaHei;
      font-size: 14px;
      font-weight: normal;
      letter-spacing: 1px;
      color: #999999;
    }

    .sp1:hover {
      color: #4182fa;
      cursor: pointer;
    }
  }
}

.time {
  fontFamily: 'MicrosoftYaHei';
  fontSize: 14px;
  lineHeight: 36px;
  color: '#666666';
  position: absolute;
  right: 532px;
  top: 206px;
}

.bao {
  width: 116px;
  height: 35px;
  background-color: #fd8d56;
  border-radius: 3px;
  position: absolute;
  right: 69px;
  top: 170px;
  line-height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  span {
    width: 70px;
    font-family: MicrosoftYaHei;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 25px;
    letter-spacing: 1px;
    color: #ffffff;
    padding-left: 2px;
  }

  img {
    width: 23px;
    height: 13px;
    display: block;
    margin-top: 2px;
  }
}

.gohead {
  width: 66px;
  height: 58px;
  background-color: #fff;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 240px;
  bottom: 50px;
  cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead1.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead1.png') -18px -15px no-repeat;
  }
}
</style>