<template>
  <div class="answer">
    <div class="find">
      <ul class="fit">
        <li
          v-for="(item,index) in List"
          :key="index"
          @click="gofind(index,item.title)"
          :class="{'active':index==current}"
        >{{item.title}}
        </li>
      </ul>
      <input
        type="text"
        placeholder=""
        v-model="key"
      >
      <button
        class="sea"
        @click="search()"
      ></button>
      <button
        class="btnInfo"
        @click="tonew(answerId)"
      ></button>
    </div>
    <div class="info">
      <h2>{{title}}</h2>
      <div class="list">
        <answerItem
          :answerList="answerList"
          @todetail="todetail"
          :answerId="answerId"
        ></answerItem>
      </div>
    </div>
    <div class="page">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import { getanswerApi } from "@/request/api";
import answerItem from "./common/answerItem";
import pagination from "./common/pagination";
export default {
  name: "answer",
  components: { answerItem, pagination },
  data() {
    return {
      List: [{ title: "" }, { title: "" }, { title: "" }],
      current: "",
      keyword: "",
      title: "",
      key: "",
      answerList: [],
      index: "",
      answerId:null,
      isjoined: "",
      info: {
        page: 1,
        pageSize: 6,
        keyword: "",
        orderNew: "",
        needReply: "",
        courseId: "",
      },
      pagination: { total: "", pageSize: 6 },
    };
  },
  created() {
    this.answerId = this.$route.query.id;
    this.isjoined = this.$route.query.joined;
    this.init();
  },
  mounted() {
    window.addEventListener("keydown", this.enterClick, false);
  },
  methods: {
    init() {
      this.info.courseId = this.answerId;
      this.getToken(getanswerApi, this.info, (res) => {
        this.answerList = res.data.rows;
        this.pagination.total = res.data.count;
      });
    },
    enterClick(e) {
      if (e.key === "Enter") {
        this.info.keyword = this.key;
        this.init();
      }
    },
    gofind(index, title) {
      this.current = index;
      this.title = title;

      if (title == "") {
        this.info.orderNew = "";
        this.info.needReply = "";
        this.init();
      } else if (title == "") {
        this.info.orderNew = 1;
        this.info.needReply = "";
        this.init();
      } else {
        this.info.needReply = 1;
        this.info.orderNew = "";
        this.init();
      }
    },
    search() {
      this.info.keyword = this.key;
      this.init();
    },
    changePage(currentPage) {
      this.Info.page = currentPage;
      this.init();
    },
    tonew(id) {
      if (this.isjoined) {
        this.$router.push({
          path: "/answerNew",
          query: { id, joined: this.isjoined },
        });
      } else {
        this.$message.info("");
      }
    },
    todetail(newid,id) {
      this.$router.push({ path: "/answerDetail", query: {newid,id} });
    },
  },
  destroyed() {
    window.removeEventListener("keydown", this.enterClick, false);
  },
};
</script>

<style lang="stylus" scoped>
.answer {
  .find {
    display: flex;
    align-items: center;
    position: relative;
    height: 50px;
    line-height: 55px;
    // margin-right: 29px;
    border-bottom: solid 1px #eeeeee;

    .fit {
      display: flex;
      color: #666;
      height: 30px;
      background: #fff;
      margin-bottom: 0px;
      li {
        width: 80px;
        height: 30px;
        border: solid 1px #dddddd;
        background-color: #ffffff;
        line-height: 30px;
        text-align: center;
        font-size: 14px;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #666666;
        margin:0px -1px -1px 0px ;
      }
      .active {
        border: solid 1px #4182fa;
        margin:0px;
        color: #4182fak;
       
      }
    }

    input {
      outline: none;
      width: 170px;
      height: 30px;
      background-color: #ffffff;
      border: solid 1px #dddddd;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 248px;
      text-indent: 12px;
    }

    .sea {
      outline: none;
      width: 57px;
      height: 30px;
      font-size: 14px;
      background: #fff;
      border: solid 1px #4182fa;
      color: #4182fa;
      line-height: 26px;
      text-align: center;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 185px;
    }

    .btnInfo {
      border: 0;
      outline: none;
      width: 101px;
      height: 38px;
      background-color: #4182fa;
      border-radius: 3px;
      letter-spacing: 1px;
      color: #ffffff;
      font-size: 14px;
      line-height: 38px;
      text-align: center;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0px;
    }
  }

  .info {
    overflow: hidden;

    h2 {
      min-width: 59px;
      overflow: hidden;
      height: 18px;
      font-size: 14px;
      font-weight: bold;
      font-stretch: normal;
      margin-top: 15px;
      margin-bottom: 10px;
      letter-spacing: 0px;
      color: #333333;
    }

    .list {
      margin: 0 auto;
      min-height: 200px;
      margin-bottom: 30px;
    }
  }

  .page {
    // margin: 30px 0;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
</style>
