<template>
  <div class="teacherItem">
    <div
      class="box"
      v-for="(item,index) in list"
      :key="index"
    >
      <div class="left">
        <img
          class="cover"
          v-if="item.cover"
          :src="item.cover"
          alt=""
        >
        <img
          class="cover"
          v-else
          src="../../assets/defaultCover.png"
          alt=""
        >
        <template v-if="item.status==2">
          <div
            class="lectureTag1"
            v-if="isTag"
          >
            <img
              src="../../assets/zhibo.png"
              alt=""
            >
            <div style="margin-left:10px"> </div>
          </div>
        </template>
        <template v-else-if="item.status==1">
          <div
            class="lectureTag"
            v-if="isTag"
          >
            <div style="margin-left:45px"> </div>
          </div>
        </template>
        <template v-else>
          <div
            class="lectureTag"
            v-if="isTag"
          >
            <img
              src="../../assets/zhibo.png"
              alt=""
            >
            {{item.startTime.slice(5,item.startTime.length-3)}} 
          </div>
        </template>
      </div>
      <div class='right'>
        <div
          class="lectureTitle"
          :title="item.title"
    
        >{{item.title}}</div>
        <div class="teacher">
          <img src="../../assets/teacher.png" />{{item.teacher}} <img src="../../assets/num.png" />{{item.appointNum}}
        </div>
        <div class="time">
          <p>{{item.startTime}}  {{item.endTime}}</p>
          <p
            v-if="item.joined"
            style="margin-top:12px"
          >{{item.status==2?'':''}}<span @click.prevent.stop.once="toRealLecture(item.url||item.playback)">{{item.status==2?item.playback:item.url}}</span></p>
        </div>
        <div
          class="teaBtn"
          v-if="item.joined"
        >
          
        </div>
        <div
          v-else
          class="teaBtn"
          @click="$emit('signup',isLogin,item.id,item)"
        >
          <span v-if="item.status==2"></span>
          <span v-else></span>
          <!-- <span v-else></span> -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "teacherItem",
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  watch: {
    isLogin: function () {},
  },
  props: {
    list: Array,
    isTag: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    toRealLecture(id) {
      window.open(id);
    },
  },
};
</script>
<style lang="stylus" scoped>
.teacherItem {
  .box {
    border: 1px solid #ebebeb;
    height: 292px;
    width: 100%;
    background: #fbfbfb;
    display: flex;
    margin-bottom: 18px;

    .left {
      width: 462px;
      position: relative;

      img.cover {
        height: 290px;
        width: 462px;
      }

      .lectureTag {
        position: absolute;
        top: 8px;
        left: -3px;
        width: 158px;
        height: 40px;
        background: url('../../assets/video1.png');
        display: flex;
        align-items: center;
        color: #fff;
        padding-bottom: 5px;
        padding-left: 15px;

        img {
          margin-right: 10px;
        }
      }

      .lectureTag1 {
        position: absolute;
        top: 8px;
        left: -3px;
        width: 113px;
        height: 40px;
        background: url('../../assets/video3.png');
        display: flex;
        align-items: center;
        color: #fff;
        padding-bottom: 5px;
        padding-left: 15px;
        // img
        // margin-right:px;
      }
    }

    .right {
      padding: 20px 20px 30px 36px;
      // 22px 36px 35px 36px;
      position: relative;

      .lectureTitle {
        font-size: 24px;
        color: #333;
        width: 561px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .teacher {
        font-size: 14px;
        color: #999;
        margin-top: 20px;
        display: flex;
        align-items: center;

        img {
          margin-right: 10px;

          &:nth-child(2) {
            margin-left: 50px;
          }
        }
      }

      .time {
        width: 561px;
        background: #f0f0f0;
        margin-top: 14px;
        padding: 12px 0 12px 12px;

        p {
          margin: 0;
          color: #666;
          font-size: 14px;

          span {
            color: #4182fa;
            cursor: pointer;
          }
        }
      }

      .teaBtn {
        position: absolute;
        bottom: 22px;
        width: 120px;
        height: 34px;
        line-height: 34px;
        text-align: center;
        background: #4182fa;
        color: #fff;
        font-size: 16px;
        cursor: pointer;
        border-radius: 3px;
      }
    }
  }
}
</style>