<template>
  <div class="report">
    <div class="title">
      <p></p>
      <p class="back" @click="tonexamplan">
          <img src="../../assets/mine/back.png" alt="">
          
      </p>
    </div>

    <div class="sea">
      <ul class="filter">
        <li
          v-for="(item,index) in menuList"
          :key="index"
          @click="changPage(item)"
          :class="{'active':current==item}"
        >{{item}}
          <p></p>
        </li>
      </ul>
      <div class="mod">
         <a-button class="bulkbtn" @click="bulkbtn"></a-button>
         <a-button class="addbtn" @click="addbtn"></a-button>
         <a-button class="improtbtn" @click="joinbtn"></a-button>
      </div>
    </div>
    <div class="nav">
      <span
        class="allbtn"
        @click="allbtn"
      ></span>
      <span
        class="allcancelbtn"
        @click="allcancelbtn"
      ></span>
      <!-- <span
        class="delbtn"
        @click="delbtn"
      ></span> -->
    </div>
    
     <a-empty
      v-if="examineeList.length==0"
      description=""
    />
    <template v-else>
      <div class="box" v-for="(item,i) in examineeList" :key="item.id">
        <div class="left">
          <span class="indexspan">{{index(i)}}</span>
          <img class="sortimg" src="../../assets/mine/news.png" alt="">
        </div>
        <div class="right">
            <p class="examtitle">
              <span class="head" >{{item.creator&&item.creator.realname}}</span>
              <span class="examstu">
                <span v-if="item.isCommitted==0">------</span>
                <template v-else>
                  <span v-if='item.finalCheck==0' style="color: #ee9537;"></span>
                  <span v-else-if='item.finalCheck==1' style="color: #49af4f;"></span>
                  <span v-else style='color: #ff0000;'></span>
                </template>
              </span>
            </p>
            <p class="examsort">
                <span>{{item.creator&&item.creator.gender}}</span>
                <span>{{item.creator&&item.creator.instituteName}}</span>
            </p>
            <p class="examtime">
              <span>{{item.creator&&item.creator.idCardNum}}</span>
              <span>{{item.creator&&item.creator.major}}</span>
            </p>
            <p class="examrelnum">
              <span>{{item.creator&&item.creator.telephone}}</span>
              <span v-if="item.isCommitted==1"></span>
              <span v-else></span>
              <span class="allbtn">
                <span @click="recexaminee(item.id)"></span>
                <span @click="delexaminee(item.id)"></span>
                <span v-if="item.isCommitted==0" @click="impexaminee(item.id)"></span>
                <span v-else style="color: #ee9537;"></span>
              </span>
            </p>
        </div>
        <div class="side">
          <span :class="{ 'urlactive':selectArr.indexOf(item.id)!=-1 }" @click="todel(item.id,item.isCommitted)"></span>
        </div>
    </div>
    </template>
    <a-modal
      title=''
      :visible="examineeshow"
      @ok="examineeOk"
      @cancel="examineecancel"
      okText=""
      cancelText=""
      class="examineemodal"
    >
        <div class="box">
            <eform
            :formData="formData"
            class="examineeform"
            ref="form"
            >
                <div slot="examineename">
                    <a-input
                      v-decorator="['examineename',{rules: [{ required: true, message: ' ' },{validator:validatorName}]}]"
                      class="input"
                      placeholder=""
                    />
                </div>
                <div slot="gender">
                    <a-radio-group v-decorator="['gender',{rules: [{ required: true, message: '' }]}]">
                      <a-radio :value="1">
                        
                      </a-radio>
                      <a-radio :value="2">
                        
                      </a-radio>
                  </a-radio-group>
                </div>
                <div slot="idnum">
                    <a-input
                         v-decorator="['idnum',{rules: [{ required: true, message: '' },{validator:validatorIdnum}]}]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="phone">
                    <a-input
                        v-decorator="['phone',{rules: [{ required: true, message: '' },{validator:validatorphone}]}]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="school">
                    <a-input
                        v-decorator="['school',{rules: [{ required: true, message: '' }]}]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="mojor">
                    <a-input
                        v-decorator="['mojor']"
                        class="input"
                        placeholder=""
                    />
                </div>
            </eform>
        </div>
    </a-modal>
    <a-modal
      title=''
      :visible="examshow"
      @ok="examOk"
      @cancel="examcancel"
      okText=""
      cancelText=""
      class="examineemodal"
      :destroyOnClose='true'
    >
        <div class="box">
            <eform
            :formData="formrec"
            :fields='fields'
            class="examineeform"
            ref="form"
            >
                <div slot="examname">
                    <a-input
                      v-decorator="['examname',{rules: [{ required: true, message: ' ' },{validator:validatorrecName}]}]"
                      class="input"
                      placeholder=""
                    />
                </div>
                <div slot="recgender">
                    <a-radio-group v-decorator="['recgender',{rules: [{ required: true, message: '' }]}]">
                      <a-radio :value="1">
                        
                      </a-radio>
                      <a-radio :value="2">
                        
                      </a-radio>
                  </a-radio-group>
                </div>
                <div slot="recidnum">
                    <a-input
                         v-decorator="['recidnum',{rules: [{ required: true, message: '' },{validator:validatorrecIdnum}]}]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="recphone">
                    <a-input
                        v-decorator="['recphone',{rules: [{ required: true, message: '' },{validator:validatorrecphone}]}]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="recschool">
                    <a-input
                        v-decorator="['recschool',{rules: [{ required: true, message: '' }]}]"
                        class="input"
                        placeholder=""
                    />
                </div>
                <div slot="recmojor">
                    <a-input
                        v-decorator="['recmojor']"
                        class="input"
                        placeholder=""
                    />
                </div>
            </eform>
        </div>
    </a-modal>
    <downloadAndUpload
      :uploadModal="uploadModal"
      @uploadcancel="uploadModal=false"
      :templateApiurl="'common/downloadExamineeTemplate'"
      :fileName="fileName"
      @upload="upload"
    >
    </downloadAndUpload>
    <div class="page">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import {examineeListApi,checkIdCardNumApi,checkPhoneApi,
registExamineeApi,addExamineeApi,commitExamineeApi,
delexamineeApi,updataExamineeApi,importExamineeApi,
bulkCreateExamineeApi} from "@/request/api"
import eform from "@/components/common/form"
import pagination from "@/components/common/pagination"
import downloadAndUpload from "@/components/common/downloadAndUpload";
export default {
  name: 'report',
  components:{eform,downloadAndUpload,pagination},
  data(){
    const formData = [
      {
        label: "",
        scopedSlots: { customRender: "examineename" },
      },
      {
        label: "",
        scopedSlots: { customRender: "gender" },
      },
      {
        label: "",
        scopedSlots: { customRender: "idnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "phone" },
      },
      {
        label: "",
        scopedSlots: { customRender: "school" },
      },
      {
        label: "",
        scopedSlots: { customRender: "mojor" },
      },
    ];
    let validatorName = (rule, value, callback) => {
      let reg = /^[\u4e00-\u9fa5]/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorIdnum = (rule, value, callback) => {
      let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (value == "" || reg.test(value)) {
         this.getToken(checkIdCardNumApi,{idCardNum:value},res=>{
            if(res.data.isExist){
               callback(new Error(""))
            }else{
                callback();
            }
         })
      } else {
        callback(new Error(""));
      }
    };
    let validatorphone = (rule, value, callback) => {
      let reg = /^1[3456789]\d{9}$/;
      if (value == "" || reg.test(value)) {
          this.getToken(checkPhoneApi,{telephone:value},res=>{
             if(res.isExist){
               callback(new Error(""))
             }else{
               callback()
             }
          })
      } else {
        callback(new Error(""));
      }
    };
    const formrec = [
      {
        label: "",
        scopedSlots: { customRender: "examname" },
      },
      {
        label: "",
        scopedSlots: { customRender: "recgender" },
      },
      {
        label: "",
        scopedSlots: { customRender: "recidnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "recphone" },
      },
      {
        label: "",
        scopedSlots: { customRender: "recschool" },
      },
      {
        label: "",
        scopedSlots: { customRender: "recmojor" },
      },
    ];
    let validatorrecName = (rule, value, callback) => {
      let reg = /^[\u4e00-\u9fa5]/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorrecIdnum = (rule, value, callback) => {
      let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (value == "" || reg.test(value)) {
         callback()
      } else {
        callback(new Error(""));
      }
    };
    let validatorrecphone = (rule, value, callback) => {
      let reg = /^1[3456789]\d{9}$/;
      if (value == "" || reg.test(value)) {
          callback()
      } else {
        callback(new Error(""));
      }
    };
    return{
      menuList: ["", ""],
      current: "",
      examineeshow:false,
      examshow:false,
      formData,
      formrec,
      uploadModal: false,
      fileName: ".xlsx",
      selectArr:[],
      validatorName,
      validatorIdnum,
      validatorphone,
      validatorrecName,
      validatorrecIdnum,
      validatorrecphone,
      sex:'',
      examineeList:[],
      searchInfo:{
         page:1,
         pageSize:5,
         status:1,
         examId:''
       },
       pagination:{total:'',pageSize:5},
       fields:null,
       recList:[],
       applyId:'',
       userArr:[],
       newmessage:'',
       isCommittedList:[]
    }
  },
  created(){
      this.searchInfo.examId=this.$route.query.id
      this.init()
  },
   mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "report":
        this.current = "";
        break;
      case "audit":
        this.current = "";
        break;
    }
  },
  methods:{
    init(){
       this.getToken(examineeListApi,this.searchInfo,res=>{
          this.examineeList=res.data.rows
          this.pagination.total=res.data.count
       })
    },
    //
    tonexamplan(){
      this.$router.push({path:"/examplan"})
    },
    //
    index(index){
        return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
    changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/report";
          break;
        case "":
          path = "/audit";
          break;
      }
      this.$router.push({ path: path,query:{id:this.searchInfo.examId}});
    },
     //
    addbtn(){
      this.examineeshow=true
    },
    //
    recexaminee(id){
      this.applyId=id
      this.recList=this.examineeList.find((item)=>item.id===id)
        if(this.recList.isCommitted==1){
            this.$message.info(",")
        }else{
          this.fields={
            examname:this.recList.creator&&this.recList.creator.realname,
            recgender:this.recList.creator&&this.recList.creator.gender==''?1:2,
            recidnum:this.recList.creator&&this.recList.creator.idCardNum,
            recphone:this.recList.creator&&this.recList.creator.telephone,
            recmojor:this.recList.creator&&this.recList.creator.major,
            recschool:this.recList.creator&&this.recList.creator.instituteName
          }
          this.examshow=true
        }
    },
    //
    examOk(){
      this.$refs.form.form.validateFields().then((res) => {
          if (res.recgender == 1) {
            this.sex = "";
          } else if (res.recgender == 2) {
            this.sex = "";
          }
          // console.log(res)
          this.getToken(updataExamineeApi,{
            realname: res.examname,
            gender: this.sex,
            major: res.recmojor,
            telephone: res.recphone,
            idCardNum: res.recidnum,
            instituteName: res.recschool,
            applyId:this.applyId
          },()=>{
                // console.log(val)
                this.examshow=false
                this.init()
              })
      })
    },
    //
    examineeOk(){
         this.$refs.form.form.validateFields().then((res) => {
          if (res.gender == 1) {
            this.sex = "";
          } else if (res.gender == 2) {
            this.sex = "";
          }
            this.getToken(registExamineeApi,{
              realname: res.examineename,
              gender: this.sex,
              major: res.mojor,
              telephone: res.phone,
              idCardNum: res.idnum,
              instituteName: res.school,
            },val=>{
               if(val.code==200){
                   this.getToken(addExamineeApi,{
                      examId:this.searchInfo.examId,
                      userId:val.data.userId
                   },value=>{
                     if(value.code==200){
                        this.$refs.form.form.resetFields()
                        this.examineeshow=false
                        this.$message.success("")
                        this.init()
                     }
                      
                   })
               }
            })
         })
    },
    //
    examineecancel(){
      this.examineeshow=false
      this.$refs.form.form.resetFields()
    },
    //
    examcancel(){
      this.examshow=false
    },
    //
    delexaminee(id){
        this.recList=this.examineeList.find((item)=>item.id===id)
        if(this.recList.isCommitted==1){
          this.$message.info(",")
       }else{
          this.$confirm({
              title: `"${this.recList.creator&&this.recList.creator.realname}"?'`,
              okText: '',
              cancelText: '',
              onOk:()=>{
                this.getToken(delexamineeApi,{ applyId:id},(res) => {
                    // console.log(res)
                      if (res.code == 200) {
                        this.init();
                        this.$message.success("!");
                      }
                    })
              },
              onCancel() {
                // console.log('Cancel');
              }
          })
       }
    },
    //
    impexaminee(id){
        this.$confirm({
        title: '?',
        okText: '',
        cancelText: '',
        onOk:()=>{
          this.getToken(commitExamineeApi,{applyIdArr:id},res=>{
            if(res.code==200){
              this.$message.success("")
              this.init()
            }
            
          })
        },
        onCancel() {
          // console.log('Cancel');
        }
      })
    },
    //
    joinbtn(){
      this.uploadModal = true;
    },
    //
    upload(data) {
      let formData = new FormData();
      formData.append("examId",this.searchInfo.examId);
      formData.append("file",data[0]);
      this.getToken(importExamineeApi, formData, (res) => {
        if (res.code == 200) {
          this.userArr=res.data
          this.newmessage=res.message
          this.uploadModal=false
          this.init()
          // console.log(this.userArr)
            this.$confirm({
              title: this.newmessage,
              okText: '',
              cancelText: '',
              onOk:()=>{
                this.getToken(bulkCreateExamineeApi,{examId:this.searchInfo.examId,userArr:this.userArr},res=>{
                  if(res.code==200){
                    this.$message.success("")
                    this.init()
                  }
                  
                })
              },
              onCancel() {
                  // this.init()
              }
            })
        }
      })
  
    },
    //
    todel(id,isCommitted){
      if(isCommitted==0){
          if(this.selectArr.indexOf(id)!=-1){
            this.selectArr.splice(this.selectArr.indexOf(id),1)
          }else{
            this.selectArr.push(id)
          }
      }else{
        this.$message.info("")
      }
    },
    //
    allbtn(){
      this.isCommittedList=this.examineeList.filter(item=>item.isCommitted)
      // console.log(this.isCommittedList)
      if(this.isCommittedList.map(item=>item.isCommitted)==0){
            this.examineeList.forEach(item=>{
            this.selectArr.push(item.id)
          })
      }else{
        // console.log("")
        this.$message.info("")
      }
      
    },
    //
    bulkbtn(){
      // console.log(this.selectArr.length)
      if(this.selectArr.length>0){
        this.$confirm({
              title: "",
              okText: '',
              cancelText: '',
              onOk:()=>{
                this.getToken(commitExamineeApi,{applyIdArr:this.selectArr},res=>{
                  if(res.code==200){
                    this.$message.success("")
                    this.init()
                    this.selectArr=[]
                  }
                })
              },
              onCancel() {
                  // this.init()
              }
            })
      }else{
        this.$message.error("")
      }
    },
    //
    allcancelbtn(){
       this.selectArr=[]
    },
   changePage(currentPage){
      this.searchInfo.page=currentPage
      this.init()
    }
    
  }
}
</script>
<style >
.examineemodal .ant-modal-body {
  margin-top: 20px;
  padding: 0px;
  font-size: 14px;
  word-wrap: break-word;
}
</style>
<style lang="stylus" scoped>
.report{
   margin-bottom:30px;
    .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      display:flex;
      align-items center;
      justify-content space-between;
        p {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            margin-bottom:0px;
            .ban{
            color: #c56b2f;
            font-size:14px;
            margin-left:20px;
            }
        }
        .back {
            color: #4281ff;
            width: 50px;
            height: 30px;
            font-size:14px;
            margin-right:20px;
            line-height: 30px;
            cursor: pointer;
            img{
                width:15px;
                height:15px;
                margin-top:-3px;
            }
        }
    }
    .sea {
      height: 45px;
      margin-left: 12px;
      display: flex;
      align-items: center;
      margin-top: 20px;
      margin-bottom: 20px;
      justify-content space-between
      .filter {
        display: flex;
        height: 45px;
        line-height: 45px;
        margin-left: 12px;
        margin-top: 20px;
        li {
          width: 100px;
          margin-right: 20px;
          height: 35px;
          line-height: 35px;
          font-size: 16px;
          color: #333333;
          text-align: center;
          cursor pointer
          p{
            width: 21px;
            height: 2px;
            background-color: #4281ff;
            border-radius: 1px;
            text-align center;
            margin:0 auto;
            display:none
          }
        }
        li:last-child {
          margin-right: 0;
        }
        .active {
          color: #4281ff;
          font-weight: 600;
          p{
            display:block;
          }
        }
      }
      .mod{
        margin-right:30px;
        .bulkbtn{
          height: 35px;
          background-color: #36a6d5;
          border-radius: 3px;
          margin-right:12px;
          color:#fff;
          border:0;
          font-size:14px;
        }
        .addbtn{
          height: 35px;
          background-color: #ff904f;
          border-radius: 3px;
          color:#fff;
          border:0;
          font-size:14px;
          margin-right:12px;
        }
        .improtbtn{
          height: 35px;
          background-color: #367dd5;
          border-radius: 3px;
          color:#fff;
          border:0;
          font-size:14px;
        }
        
      }
    }
    .nav {
      height: 30px;
      background-color: #fafafa;
      margin-left: 30px;
      margin-right:30px;
      align-items: center;
      margin-bottom: 20px;
      position relative
      .allbtn {
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        text-align: center;
        cursor: pointer;
        color: #4281ff;
        position:absolute
        right:100px;
      }
      .allcancelbtn {
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        text-align: center;
        cursor: pointer;
        color: #4281ff;
        position:absolute
        right:20px;
      }
    }
    .box{
      margin-top:20px;
      margin-left:30px;
      margin-right:30px;
      border: solid 1px #e6e6e6;
      margin-bottom:15px;
      display:flex;
      .left{
        display:flex;
        min-width:80px;
        padding-right:15px;
        padding-left:10px;
        .indexspan{
          color:#666;
          font-size:14px;
          display:block;
          overflow hidden;
          margin-top:30px;
          margin-right:15px;
        }
        .sortimg{
          width:40px;
          height:40px;
          display:block;
          margin-top:20px;
        }
      }
      .right{
        overflow hidden
        flex:1;
        .examtitle{
          margin-top:18px;
          position relative
          margin-bottom:13px;
          display:flex;
          .head{
            font-size:16px;
            font-weight: bold;
            display:block
            color: #333333;
            width:580px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .apllystu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:150px;
          }
          .examstu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:23px;
          }
        }
        .examsort{
           font-size:14px;
           height:20px;
           color:#666;
           display:flex;
           margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examtime{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
