<template>
  <div class="mark">
    <div class="title">
      <h2></h2>
    </div>
    <div class="sea">
      <span></span>
      <div class="mod">
        <a-select
          
          class="selbtn"
          allow-clear
          placeholder=""
          :notFoundContent="''"
          @change="Selectexam"
          v-decorator="['modelname',{rules:[{required:true,message:''}]}]"
        >
          <a-select-option v-for="item in professionList" :value="item.id" :key="item.id">
            {{item.name}}
          </a-select-option>
        </a-select>
      </div>
      <div class="oper">
         <input
        type="text"
        placeholder=""
        v-model="key"
      >
      <button
        class="ban"
        @click="searchbtn"
      ></button>
      <download
        class="impbtn"
        :api="'instituteCenter/exportOnePlusXRecord'"
        :name="''"
        :fileName="'.xlsx'"
        :isPrimary="true"
        methods='post'
      ></download>
      <!-- <button class="impbtn" @click="impall"></button> -->
      </div>
    </div>
     <a-empty
      v-if="markList.length==0"
      description=""
    />
    <template v-else>
      <div class="box" v-for="(item,i) in markList" :key="item.id">
        <div class="left">
          <span class="indexspan">{{index(i)}}</span>
          <img class="sortimg" src="../../assets/mine/mark.png" alt="">
        </div>
        <div class="right">
            <p class="examtitle">
              <span class="head" >{{item.owner.realname}}</span>
              <span class="examstu">
                <span v-if="item.passed==1" style="color: #49af4f;"></span>
                <span v-else style="color: #ff0000;"></span>
              </span>
            </p>
            <p class="examsort">
                <span>{{item.owner.gender}}</span>
                <span>{{item.onePlusXExam.title}}</span>
            </p>
            <p class="examtime">
              <span>{{item.owner.telephone}}</span>
              <span>{{item.onePlusXExam.profession.name}} |
                              {{item.onePlusXExam.skill.name}} |
                              <template v-if="item.onePlusXExam.examType==0"></template>
                              <template v-else-if="item.onePlusXExam.examType==1"></template>
                              <template v-else>+</template>
              </span>
            </p>
            <p class="examaddress">
              <span>{{item.owner.instituteName}}</span>
              <span>{{item.operationScore}}</span>
            </p>
            <p class="examrelnum">
              <span>{{item.owner.idCardNum}}</span>
              <span>{{item.theoryScore}}</span>
              <span class="allbtn">
                <template v-if="item.passed==1">
                  <span class="viewspan" @click="viewcer(item.certificate.imgUrl)"></span>
                  <span class="downspan" @click="godownload(item.certificate.imgUrl)"></span>
                </template>
                <template v-else>
                  <span class="noviewspan"></span>
                  <span class="nodownspan"></span>
                </template>
              </span>
            </p>
        </div>
      </div>
    </template>
    
    <!--  -->
    <a-modal
      title=""
      class="trainmodal"
      width="1000px"
      :footer="null"
      :visible="flag"
      @cancel="flag=false"
      okText=""
    >
      <div class="md">
        <div class="bimg">
          <canvas
            id="myCanvas"
            width="889"
            height="624"
          >
          </canvas>
        </div>
      </div>
      <div class="he">
        <button
          class="download"
          @click="godownload(imgUrl)"
        ><span></span></button>
      </div>
    </a-modal>
     <div class="page">
      <pagination
         :pagination="pagination"
         @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import {markApi,professionListApi} from "@/request/api"
import pagination from "@/components/common/pagination"
import download from "@/components/common/download"
export default {
  name: 'mark',
  components:{pagination,download},
  data(){
    return{
        flag:false,
        imgUrl: null,
        key:'',
        apiUrl: window.g.apiUrl,
        bimg: null,
        isShow: false,
        searchInfo:{
              page:1,
              pageSize:5,
              professionId:'',
              keyword:''
            },
        pagination:{total:'',pageSize:5},
        markList:[],
        passed:'',
        professionList:[]
    }
    
  },
  created(){
      this.init()
  },
  methods:{
    init(){
        this.getToken(markApi,this.searchInfo,res=>{
           this.markList=res.data.rows
           this.pagination.total=res.data.count
        })
        this.getToken(professionListApi,{},res=>{
          this.professionList=res.data
        })
    },
    //
    searchbtn(){
      this.searchInfo.keyword=this.key
      this.init()
    },
    //
    index(index){
        return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
    Selectexam(data){
       this.searchInfo.professionId=data
       this.init()
    },
    //
    viewcer(img){
      this.flag=true
      this.imgUrl=img

      var getimg = new Image();
      getimg.src = this.imgUrl;
      getimg.onload = () => {
        var convas = document.getElementById("myCanvas");
        var ctx = convas.getContext("2d");
        ctx.drawImage(getimg, 0, 0, 889, 624);
        ctx.rotate((-30 * Math.PI) / 180); // 
        for (let i = 0; i < 40; i++) {
          ctx.font = "26px microsoft yahei";
          ctx.fillStyle = "rgba(255,255,255,0.8)";
          ctx.fillText(
            "                                                                                                                                                                                                                                                                                        ",
            -1500 + i * 70,
            i * 70 - 450
          );
        }
        ctx.rotate((30 * Math.PI) / 180); //              
      };
    },
    //
    godownload(imgUrl){
      axios({
          url: this.apiUrl + "certificate/download",
          method: "post",
          data: { imgUrl: imgUrl },
          responseType: "blob",
        }).then((res) => {
          if (!res) {
            return;
          }
          let url = window.URL.createObjectURL(res.data);
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", ".jpg");
          document.body.appendChild(link);
          link.click();
        });
    },
    changePage(currentPage){
      this.searchInfo.page=currentPage
      this.init()
    }
  }
}
</script>
<style lang="stylus">
.trainmodal .he button{
  cursor pointer
}
</style>
<style lang="stylus" scoped>
.mark{
    margin-bottom: 30px;
    .title {
        height: 45px;
        line-height: 45px;
        overflow: hidden;
        border-bottom:1px solid #eff2f2;
        h2 {
            height:45px;
            line-height: 45px;
            margin-left: 30px;
            font-size: 18px;
            font-weight: bold;
            color: #333333;
        }
    }
    .sea {
      height: 45px;
      margin-left: 30px;
      margin-right:30px;
      margin-top: 20px;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      justify-content space-between
      span {
        color: #333;
        font-size: 16px;
        margin-right: 50px;
      }
      .mod {
        width: 204px;
        margin-right: 24px;
        margin-left: 180px;
        .selbtn {
          width: 100%;
          height: 100%;
        }
      }
      .oper{
          input {
          outline: none;
          width: 220px;
          height: 35px;
          border: solid 1px #d4d4d4;
          text-indent: 8px;
        }
        .ban {
          outline: none;
          width: 60px;
          height: 35px;
          background-color: #4281ff;
          color: #ffffff;
          font-size:14px;
          cursor: pointer;
          border:0
        }
        .impbtn {
          outline: none;
          width: 75px;
          height: 35px;
          background-color: #367dd5;
          border-radius: 3px;
          color: #ffffff;
          font-size:14px;
          cursor: pointer;
          border:0
          margin-left:20px;
        }
      }
    }
    .box{
      margin-top:20px;
      margin-left:30px;
      margin-right:30px;
      border: solid 1px #e6e6e6;
      margin-bottom:15px;
      display:flex;
      .left{
       display:flex;
        min-width:80px;
        padding-right:15px;
        padding-left:10px;
        .indexspan{
          color:#666;
          font-size:14px;
          display:block;
          overflow hidden;
          margin-top:30px;
          margin-right:15px;
        }
        .sortimg{
          width:40px;
          height:40px;
          display:block;
          margin-top:20px;
        }
      }
      .right{
        overflow hidden
        flex:1;
        .examtitle{
          margin-top:18px;
          position relative
          margin-bottom:13px;
          display:flex;
          .head{
            font-size:16px;
            font-weight: bold;
            display:block
            color: #333333;
            width:580px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
          }
          .apllystu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:150px;
          }
          .examstu{
            font-size:14px;
            color:#333;
            position:absolute;
            right:23px;
          }
        }
        .examsort{
           font-size:14px;
           height:20px;
           color:#666;
           display:flex;
           margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examtime{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examaddress{
          font-size:14px;
          color:#666;
          display flex;
          margin-bottom:10px;
           span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
        }
        .examrelnum{
          font-size:14px;
          color:#666;
          margin-bottom:10px;
          position relative;
          display flex;
          span:nth-child(1){
             width:280px;
           }
           span:nth-child(2){
               flex:1;
               display:block;
               overflow hidden;
               text-overflow:ellipsis;
              white-space: nowrap;
           }
          .allbtn{
            width:140px;
            position:absolute;
            right:21px;
            top:50%;
            transform:translateY(-50%)
            display flex;
            .viewspan{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #f6f8ff;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              margin-right:9px;
              cursor pointer
            }
            .noviewspan{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #eee;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              margin-right:9px;
            }
            .downspan{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #f6f8ff;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
              cursor pointer
            }
            .nodownspan{
              display:block;
              width:65px;
              text-align center;
              line-height:26px;
              height:26px;
              background-color: #eee;
              border-radius: 3px;
              color: #4281ff;
              font-size:14px;
            }
          }
        }
      }
    }
    .page {
      margin-top: 30px;
    }
}
</style>
