<template>
  <div class="message">
    <div class="title">
      <h2></h2>
    </div>
    <div class="sea">
      <ul class="filter">
        <li
          @click="toAll"
          :class="{'active':flagone}"
        >
        <p></p>
        </li>
        <li
          @click="toread"
          :class="{'active':flagtwo}"
        >
        <p></p>
        </li>
        <li
          @click="getread"
          :class="{'active':flagtre}"
        >
        <p></p>
        </li>
      </ul>
      <div class="mod">
        <input
        type="text"
        placeholder=""
        v-model="key"
      >
      <button
        class="ban"
        @click="searchbtn"
      ></button>
      </div>
    </div>
    <div class="nav">
      <span
        class="allbtn"
        @click="allbtn"
      ></span>
      <span
        class="allcancelbtn"
        @click="allcancelbtn"
      ></span>
      <span
        class="delbtn"
        @click="delbtn"
      ></span>
    </div>
    <a-empty
      v-if="list.length==0"
      description=""
    />
    <template v-else>
      <div class="box" v-for="(item,i) in list" :key="item.id">
        <div class="left">
          <span class="indexspan">{{index(i)}}</span>
          <img class="sortimg" src="../../assets/mine/news.png" alt="">
        </div>
        <div class="moddle">
           <p class="metit">
             <img v-if="item.status==0" src="../../assets/mine/nonews.png" alt="">
             <span class="newscon">{{item.content}}</span>
          </p>
           <p class='metime'>
             <span>{{item.from}}</span>
             <span>{{item.createTime}}</span>
             <span @click="todetail(item.id)">></span>
           </p>
        </div>
        <div class="right">
           <span :class="{ 'urlactive':selectArr.indexOf(item.id)!=-1 }" @click="todel(item.id)"></span>
        </div>
    </div>
    </template>
    <a-modal
        title=""
        :visible="messageshow"
        @ok="messageshow=false"
        @cancel="messagecancel()"
        class="askmodel"
        okText=""
        cancelText=""
      >
        <div class="askbox">
          <p class="tot"><span>{{detaillist.from}}</span><span>{{detaillist.createTime}}</span></p>
          <p class="tit">{{detaillist.content}}</p>
        </div>
      </a-modal>
    <div class="pagemodel">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import {
  getmessagelistApi,
  getmessageApi,
  getdelmessageApi,
  getmyInfo,
  getinstituteCenterApi
} from "@/request/api";
import pagination from "@/components/common/pagination";
export default {
  name: "message",
  components: { pagination },
  data() {
    return {
      flagone: true,
      flagtwo:false,
      flagtre:false,
      isActive:false,
      key: "",
      list: [],
      detaillist: [],
      messageshow: false,
      selectedRowKeys: [],
      searchInfo: {
        keyword: "",
        page: 1,
        pageSize: 8,
        status:"",
      },
      pagination: { total: 0, pageSize: 8 },
      selectArr:[]
    };
  },
  watch:{
     todetail:function(){
       this.init()

     }
  },
  created() {
    this.init();
    // this.message()
  },
  computed:{
    role() {
      return this.$store.state.role;
    },
  },
  methods: {
    init() {
      this.getToken(getmessagelistApi, this.searchInfo, (res) => {
        this.list=res.data.rows
        if(this.list.filter(item=>item.status==0).length==0){
          this.$store.commit('changeMessageStatus',{'status':false})
        }else{
          this.$store.commit('changeMessageStatus',{'status':true})
        }
        // console.log(this.list)
        this.pagination.total = res.data.count;
      });
    },
    // //
    message(){
      if(this.role==""||this.role==""||this.role==""){
          this.getToken(getmyInfo, {}, () => {
              //  console.log(res)
          })
      }else{
           this.getToken(getinstituteCenterApi,{},()=>{
          })
      }
    },
    //
    searchbtn() {
      this.searchInfo.keyword = this.key;
      this.init();
    },
    //
    index(index){
        return (this.searchInfo.page-1)*this.searchInfo.pageSize+index+1;
    },
    //
     toAll() {
      this.flagone = true;
      this.flagtwo = false;
      this.flagtre = false;
       this.searchInfo.status = "";
      this.init();
    },
    //
    toread() {
      this.flagone = false;
      this.flagtwo = true;
      this.flagtre = false;
      this.searchInfo.status = 1;
      this.init();
    },
    //
    getread() {
      this.flagone = false;
      this.flagtwo = false;
      this.flagtre = true;
      this.searchInfo.status = 0;
      this.init();
    },
    
    todetail(id) {
      this.getToken(getmessageApi, { messageId: id }, (res) => {
        this.detaillist = res.data;
      });
      this.messageshow = true;
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    //
    allbtn(){
      this.list.forEach(item=>{
        this.selectArr.push(item.id)
      })
    },
    //
    allcancelbtn(){
       this.selectArr=[]
    },
    todel(id){
      if(this.selectArr.indexOf(id)!=-1){
        this.selectArr.splice(this.selectArr.indexOf(id),1)
      }else{
        this.selectArr.push(id)
      }
    },
    //
    delbtn(){
      if(this.selectArr.length>0){
          this.$confirm({
          title: '',
          okText: '',
          cancelText: '',
          onOk:()=> {
          this.getToken(getdelmessageApi,{ messageId:this.selectArr},(res) => {
                if (res.code == 200) {
                  this.init();
                  this.$message.success("!");
                }
              })
          },
          onCancel() {
            //  console.log('OK');
          },
        });
      }else{
        this.$message.error("")
      }
    },
    messagecancel(){
      this.init()
      this.messageshow=false
    }
  }
};
</script>
<style >
.message .ant-table table {
  text-align: center;
}
.message thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  text-align: center;
  font-weight: 600;
  font-size: 16px;
}
.message .ant-table-bordered .ant-table-thead > tr > th,
.message .ant-table-bordered .ant-table-tbody > tr > td {
  border-right: 1px solid #e8e8e8;
  text-align: center;
}
.message .ant-table-pagination.ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
}
.askmodel .ant-btn {
  outline: 0;
  display: none;
}
.askmodel .askbox {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 30px;
  margin-top: 20px;
}
.askmodel .askbox .tot{
  display:flex;
  justify-content: space-between;
  color: #999999;
  font-size:14px;
  margin-bottom:22px;
}
.askmodel .askbox .tit{
  color: #333;
  font-size:14px;
  margin-bottom:0px;
}
.message .ant-table-pagination .ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
  display: none;
}
</style>
<style lang="stylus" scoped>
.message {
  .title {
      height: 45px;
      line-height: 45px;
      overflow: hidden;
      border-bottom:1px solid #eff2f2;
      h2 {
        height:45px;
        line-height: 45px;
        margin-left: 30px;
        font-size: 18px;
        font-weight: bold;
        color: #333333;
      }
  }
  .sea {
    height: 45px;
    margin-left: 10px;
    display: flex;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
    justify-content space-between
    .filter {
      display: flex;
      height: 45px;
      line-height: 45px;
      margin-left: 12px;
      margin-top: 20px;

    li {
      width: 100px;
      margin-right: 20px;
      height: 35px;
      line-height: 35px;
      font-size: 16px;
      color: #333333;
      text-align: center;
      cursor pointer;
       p{
        width: 21px;
        height: 2px;
        background-color: #4281ff;
        border-radius: 1px;
        text-align center;
        margin:0 auto;
        display:none
      }
    }
    li:last-child {
      margin-right: 0;
    }
    .active {
      color: #4281ff;
      font-weight: 600;
      p{
        display:block;
      }
    }
  }
    .mod{
      margin-right:30px;
       input {
      outline: none;
      width: 220px;
      height: 35px;
      border: solid 1px #d4d4d4;
      text-indent: 8px;
    }
      .ban {
        outline: none;
        width: 60px;
        height: 35px;
        background-color: #4281ff;
        color: #ffffff;
        font-size:14px;
        cursor: pointer;
        border:0
      }
    }
  }

  .nav {
    height: 30px;
    background-color: #fafafa;
    margin-left: 30px;
    margin-right:30px;
    align-items: center;
    margin-bottom: 20px;
    position relative
    .allbtn {
      height: 30px;
      line-height: 30px;
      font-size: 14px;
      text-align: center;
      cursor: pointer;
      color: #4281ff;
      position:absolute
      right:150px;
    }
    .allcancelbtn {
      height: 30px;
      line-height: 30px;
      font-size: 14px;
      text-align: center;
      cursor: pointer;
      color: #4281ff;
      position:absolute
      right:70px;
    }
    .delbtn {
      height: 30px;
      line-height: 30px;
      font-size: 14px;
      text-align: center;
      cursor: pointer;
      color: #4281ff;
      position:absolute
      right:20px;
    }
  }

  .box {
    margin-left:30px;
    margin-right:30px;
    // height: 80px;
    border: solid 1px #eff2f2;
    display:flex;
    margin-bottom:15px;
    .left{
      display:flex;
      align-items center;
      min-width:80px;
      position relative
      .indexspan{
        color:#666;
        font-size:14px;
        display:block;
        margin-left:8px;
      }
      .sortimg{
        width:40px;
        height:40px;
        margin-left:15px;
        margin-right:15px;
      }
    }
    .moddle{
       flex:1;
       .metit{
         margin-top:20px;
         margin-bottom:15px;
          img{
             width:6px;
             height:6px;
             margin-top:-3px;
          }
          .newscon{
            color: #333333;
            font-size:14px;
            width:700px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            margin-left:6px;
          }
          
       }
       .metime{
          margin-left:10px;
          span:nth-child(1){
            font-size: 12px;
            color: #999999;
            margin-right:20px;
          }
          span:nth-child(2){
            font-size: 12px;
            color: #999999;
            margin-right:20px;
          }
          span:nth-child(3){
            font-size: 12px;
            cursor pointer
            color: #4281ff;
            font-size: 14px;
          }
       }
    }
    .right{
      width:45px;
      display:flex;
      justify-content center;
      align-items center;
      span{
          width:20px;
          height:20px;
          display:block
          cursor pointer
          background:url("../../assets/mine/newnodel.png") no-repeat;
      }
      .urlactive{
        background:url("../../assets/mine/newdel.png") no-repeat;
      }
    }
  }
  .box:hover{
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1);
  }

  .pagemodel {
    margin-top: 30px;
    margin-bottom:30px;
  }
}
</style>
