<template>
  <div
    class="modalBox"
    v-if="isShow"
  >
    <div
      class="box"
      :style="'width:'+width+'px'"
    >
      <div class="header">
        <div v-if="type=='success'"><span></span></div>
        <div v-else><span class="other">!</span>{{boxTitle}}</div>
      </div>
      <div class="content">
        <slot name="modalContent"></slot>
      </div>
      <div
        class="footer"
        v-if="!isfooter"
      >
        <div
          class="modalBtn"
          @click="$emit('submit')"
        ></div>
      </div>
      <div
        class="footer"
        v-else
      >
        <div
          class="modalBtn reset"
          @click="$emit('reset')"
        ></div>
        <div
          class="modalBtn sure"
          @click="$emit('submit')"
        ></div>
      </div>
      <a-icon
        @click="$emit('closemodal')"
        class="close"
        type="close"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: "modalBox",
  props: {
    type: String,
    isShow: Boolean,
    boxTitle: String,
    width: Number,
    isfooter: { type: Boolean, default: false },
  },
};
</script>
<style lang="stylus" scoped>
.modalBox {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;

  .box {
    padding: 0 35px 20px;
    //height:315px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    z-index: 2;
    border-top: 4px solid #0096ff;
    border-radius: 5px;

    .header {
      height: 70px;
      border-bottom: 1px solid #dfdfdf;
      font-size: 18px;
      line-height: 70px;

      div {
        display: flex;
        align-items: center;

        span {
          width: 33px;
          height: 33px;
          background: #52c41a;
          text-align: center;
          line-height: 33px;
          display: inline-block;
          color: #fff;
          border-radius: 16.5px;
          font-size: 24px;
          margin-right: 10px;
        }

        .other {
          background: #0096ff;
        }
      }
    }

    .footer {
      .sure {
        width: 90px;
        height: 36px;
        border-radius: 3px;
        line-height: 36px;
        text-align: center;
        background: #4182fa;
        color: #fff;
        font-size: 16px;
        float: right;
        cursor: pointer;
      }

      .reset {
        width: 90px;
        height: 36px;
        border-radius: 3px;
        line-height: 36px;
        text-align: center;
        background: #4182fa;
        color: #fff;
        font-size: 16px;
        float: right;
        cursor: pointer;
        margin-left: 10px;
        background: #fff;
        color: #4182fa;
        border: 1px solid #4182fa;
        margin-top: 0;
      }
    }

    .close {
      position: absolute;
      top: 10px;
      right: 10px;
      font-size: 18px;
      color: #999;
      cursor: pointer;
    }
  }
}
</style>