<template>
  <div id="formation">
    <template v-if="bannerList.length>0">
      <div class="banner" v-for="item in bannerList" :key="item.id">
        <a :href="item.link">
            <img :src="item.img" alt="">
        </a>
      </div>
    </template>
    <div class="banner" v-else>
      <!-- <img
        src="../assets/banner/zxzx_banner.png"
        alt=""
      > -->
      <p>banner</p>
    </div>
    
    <div class="head">
      <div class="header">
        <ul class="nav">
          <li
            v-for="(item,index) in menuList"
            :key="index"
            @click="changPage(item)"
            :class="{'active':current==item}"
          >{{item}}<em></em></li>
        </ul>
      </div>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import {getbannerApi} from "@/request/api"
export default {
  name: "formation",
  data() {
    return {
      menuList: ["", "", ""],
      current: "",
      currentIndex: {},
      bannerList:[]
    };
  },
  watch:{
      $route() {
        switch (this.$route.query.type) {
        case "policy":
          this.current = "";
          break;
        case "notice":
          this.current = "";
          break;
        default:
          this.current = "";
      }
    }
  },
  created(){
     this.init()
  },
  mounted() {
    switch (this.$route.query.type) {
      case "policy":
        this.current = "";
        break;
      case "notice":
        this.current = "";
        break;
      default:
        this.current = "";
    }
  },
  methods: {
    init(){
      this.getToken(getbannerApi,{module:6},res=>{
                this.bannerList=res.data
            })
    },
    changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/company";
          break;
        case "":
          path = "/policy";
          break;
        case "":
          path = "/notice";
          break;
      }
      this.$router.push({
        path: "/company",
        query: { type: path.split("/")[path.split("/").length - 1] },
      });
    },
  },
};
</script>
<style lang="stylus" scoped>
#formation {
  background: #eeeeee;

  .banner {
    height:261px;
    margin:0 auto;
    p{
        color:#333;
        font-size:20px;
        width:100%;
        height:100%;
        display:flex;
        align-items center;
        justify-content center;
        background:#d5d5d5;
        text-align center;
    }
    img {
      width: 100%;
      height:100%;
    }
  }

  .head {
    height: 66px;
    background: #fff;
    line-height: 66px;
    box-shadow: 0px 0px 10px 0px rgba(93, 93, 93, 0.1);

    .header {
      width: 1205px;
      margin: auto;
      height: 73px;
      border-bottom: 1px solid #eeeeee;
    }

    .nav {
      hegint: 73px;
      width: 900px;
      list-style: none;
      display: flex;

      li {
        display: inline;
        float: left;
        width: 144px;
        height: 66px;
        text-align: center;
        font-family: MicrosoftYaHei;
        font-size: 18px;
        letter-spacing: 0px;
        color: #333333;
        position: relative;
        cursor: pointer;
      }

      .active {
        background-color: #4182fa;
        color: #fff;

        em {
          position: absolute;
          left: 50%;
          bottom: -10px;
          font-size: 30px;
          width: 0px;
          height: 0px;
          border: 5px solid transparent;
          border-top: 5px solid #4182fa;
          z-index: 999;
        }
      }
    }
  }

  .content {
    width: 1205px;
    margin: 0 auto;
    background: #fff;
    position: relative;
    border-top: 1px solid #eee;
  }
}
</style>