<template>
  <div class="catalogDetail">
    <div class="Left">
      <div class="title">
        <div class="box">
          <div class="left">
            <img
              :src="list.cover"
              alt=""
            >
          </div>
          <div class="right">
            <h2 :title="list.title">{{list.title}}</h2>
            <p>{{list.teacher}}</p>
          </div>
        </div>
      </div>
      <div class="menu">
        <div class="log">
          <Menu
            :munelist="munelist"
            @getdetail="getdetail"
          ></Menu>
        </div>
      </div>
    </div>
    <div class="Right">
      <isvideo
        v-if="info.type==0"
        :resourceUrl="resourceUrl"
        :title="title"
        :catId="catId"
        :menuId="menuId"
        @gocourse="gocourse"
        :joined="joined"
        :chapterId="chapterId"
      >
      </isvideo>
      <ispdf
        v-else-if="info.type==1"
        :resourceUrl="resourceUrl"
        :title="title"
        :catId="catId"
        @gocourse="gocourse"
        :chapterId="chapterId"
      >
      </ispdf>
      <istest
        v-else-if="info.type==2"
        :title="title"
        :testInfo="testInfo"
        :catId="catId"
        :menuId="menuId"
        @totest="totest"
        @seetest="seetest"
        :paperId="paperId"
        @gocourse="gocourse"
        :isFinished="isFinished"
        :joined="joined"
        :chapterId="chapterId"
      ></istest>
      <isexam
        v-else
        :title="title"
        :testInfo="testInfo"
        @totheoryexam="totheoryexam"
        @seetheoryexam="seetheoryexam"
        @toprexam="toprexam"
        :catId="catId"
        :menuId="menuId"
        :paperId="paperId"
        @gocourse="gocourse"
        :isFinished="isFinished"
        :examType="examType"
        :operationQuestionFinished="operationQuestionFinished"
        :chapterId="chapterId"
      ></isexam>
    </div>
  </div>
</template>

<script>
import Menu from "@/components/common/Menu";
import ispdf from "@/components/common/ispdf";
import isvideo from "@/components/common/isvideo";
import istest from "@/components/common/istest";
import isexam from "@/components/common/isexam";
import {
  getcourseDetailApi,
  getcatalogApi,
  getupdateStatusApi,
} from "@/request/api";

export default {
  name: "catalogDetail",
  components: { Menu, ispdf, isvideo, istest, isexam },
  data() {
    return {
      catId: null,
      menuId: "",
      list: [],
      munelist: [],
      info: "",
      type: "",
      title: "",
      testInfo: "",
      resourceUrl: "",
      paperId: "",
      isFinished: "",
      examType: "",
      operationQuestionFinished: "",
      operationQuestionAccess: "",
      joined: "",
      examAccess:"",
      chapterId:"",
    };
  },
   computed: {
     taskId() {
      return this.$store.state.taskId;
    },
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  watch:{
    isLogin:function(){
      this.init()
    },
    taskId:function(){
      this.init()
    },
  },
  created() {
    this.paperId = this.$route.query.paperId;
    this.catId = this.$route.query.id;
    this.menuId = this.$route.query.menuId;
    this.joined = this.$route.query.joined;
    this.init();
  },
  methods: {
    init() {
      this.getToken(getcourseDetailApi, {courseId: this.catId}, (res) => {
        this.list = res.data;
        this.examAccess=res.data.examAccess
        this.munelist = res.data.chapters;
        this.joined=res.data.joined
      });
      getcatalogApi({taskId: this.menuId}).then(res=>{
        if(res.code==200){
          this.info = res.data;
          this.resourceUrl = res.data.resourceUrl;
          this.isFinished = res.data.isFinished;
          this.operationQuestionFinished = res.data.operationQuestionFinished;
          this.operationQuestionAccess = res.data.operationQuestionAccess;
          this.joined=res.data.joined
          this.testInfo=res.data.testInfo
          this.examType=res.data.examType
          this.title=res.data.title
          // console.log(this.examType)
        }else{
          this.$message.error('')
          this.$router.go(-1)    //
        }
      })
    },
    gocourse(id) {
      this.$router.push({ path: "/catalog", query: { id } });
    },
    getdetail(catalogId,id, type, title, testInfo, paperId, isFinished, isLogin,isFree,chapterId) {
     if(this.joined){
        if(isFinished==false){
            //
            if(type==0||type==2){
                  getcatalogApi({taskId:id}).then(res=>{
                    // console.log(res.data.resourceUrl)
                        this.testInfo=testInfo
                        this.title=title
                        this.info = res.data;
                        this.menuId=id
                        this.chapterId=chapterId
                        this.paperId=paperId
                        this.resourceUrl = res.data.resourceUrl;
                        this.isFinished = res.data.isFinished;
                        this.operationQuestionFinished = res.data.operationQuestionFinished;
                        this.operationQuestionAccess = res.data.operationQuestionAccess;
                        this.joined=res.data.joined
                  });
            }else if(type==1){
               this.getToken(getupdateStatusApi,{"status":1,"courseId":this.catId,"taskId":id},()=>{
                  this.$store.commit('getIds',{'chapterId':chapterId,'taskId':id})
                  getcatalogApi({taskId:id}).then(res=>{
                           this.testInfo=testInfo
                            this.title=title
                            this.info = res.data;
                            this.menuId=id
                            this.chapterId=chapterId
                            this.paperId=paperId
                            this.resourceUrl = res.data.resourceUrl;
                            this.isFinished = res.data.isFinished;
                            this.operationQuestionFinished = res.data.operationQuestionFinished;
                            this.operationQuestionAccess = res.data.operationQuestionAccess;
                            this.joined=res.data.joined
                     });
               })
            }else{
               if(this.examAccess==false){
                  this.$message.error("")
               }else{
                 getcatalogApi({taskId:id}).then(res=>{
                          this.testInfo=testInfo
                         this.title=title
                          this.info = res.data;
                          this.menuId=id
                          this.chapterId=chapterId
                          this.paperId=paperId
                          this.resourceUrl = res.data.resourceUrl;
                          this.isFinished = res.data.isFinished;
                          this.operationQuestionFinished = res.data.operationQuestionFinished;
                          this.operationQuestionAccess = res.data.operationQuestionAccess;
                          this.joined=res.data.joined
                     });
               }
            }
        }else{
          getcatalogApi({taskId:id}).then(res=>{
                this.testInfo=testInfo
                this.title=title
                this.info = res.data;
                this.menuId=id
                this.chapterId=chapterId
                this.paperId=paperId
                this.resourceUrl = res.data.resourceUrl;
                this.isFinished = res.data.isFinished;
                this.operationQuestionFinished = res.data.operationQuestionFinished;
                this.operationQuestionAccess = res.data.operationQuestionAccess;
                this.joined=res.data.joined
          });
        }
      }else{
        // 
        if(isFree==1){
          getcatalogApi({taskId:id}).then(res=>{
                        this.testInfo=testInfo
                        this.title=title
                        this.info = res.data;
                        this.menuId=id
                        this.chapterId=chapterId
                        this.paperId=paperId
                        this.resourceUrl = res.data.resourceUrl;
                        this.isFinished = res.data.isFinished;
                        this.operationQuestionFinished = res.data.operationQuestionFinished;
                        this.operationQuestionAccess = res.data.operationQuestionAccess;
                     });
        }else{
          if(isLogin==0){
            this.$message.info("")
          }else{
            this.$message.info("")
          }
        }
      }
    },
    //
    totest() {
      this.$confirm({
          title: '',
          okText: '',
          cancelText: '',
          onOk:()=> {
             this.$router.push({
                path: "/test",
                query: {
                  paperId:this.paperId,
                  id:this.catId,
                  menuId:this.menuId,
                  title:this.title,
                  joined: this.joined,
                  chapterId:this.chapterId,
                  isFinished: this.isFinished,
                },
              });
          },
          onCancel() {
            //  console.log('OK');
          },
        });
    },
    //
      seetest(paperId, id, menuId, title) {
      this.$router.push({
        path: "/test",
        query: {
          paperId,
          id,
          menuId,
          title,
          joined: this.joined,
          chapterId:this.chapterId,
          isFinished: this.isFinished,
        },
      });
    },
    //
    examOk(){
      this.$router.push({
        path: "/theoryexam",
        query: { paperId:this.paperId, id:this.catId, menuId:this.menuId, isFinished: this.isFinished,chapterId:this.chapterId,},
      });
    },
    //
    totheoryexam() {
      this.$confirm({
          title: '',
          okText: '',
          cancelText: '',
          onOk:()=> {
              this.$router.push({
              path: "/theoryexam",
              query: { paperId:this.paperId, id:this.catId, menuId:this.menuId, isFinished: this.isFinished,chapterId:this.chapterId,},
            });
          },
          onCancel() {
            //  console.log('OK');
          },
        });
    },
    //
    seetheoryexam(paperId, id, menuId) {
      this.$router.push({
        path: "/theoryexam",
        query: { paperId, id, menuId, isFinished: this.isFinished,chapterId:this.chapterId, },
      });
    },
    //
    toprexam(paperId, id, menuId) {
      if (this.operationQuestionAccess) {
        this.$router.push({ path: "/prexam", query: { paperId, id, menuId,chapterId:this.chapterId} });
        
      } else {
        this.$message.info("");
      }
    },
  },
};
</script>

<style >
.catalogDetail .ant-menu-sub.ant-menu-inline {
  background: #eee;
}
.catalogDetail .ant-menu-inline .ant-menu-submenu-title {
  margin-bottom: 0px;
}

.catalogDetail .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: #4483fa !important;
  color: #fff;
  margin: 0px;
}
.catalogDetail .ant-menu-inline .ant-menu-item::after {
  border-right: 0px;
}
.catalogDetail .ant-menu-sub.ant-menu-inline > .ant-menu-item,
 .catalogDetail .ant-menu-sub.ant-menu-inline > .ant-menu-submenu > .ant-menu-submenu-title {
  height: 45px;
  line-height: 45px;
  list-style-position: inside;
  list-style-type: disc;
  padding-left: 25px !important;
}
.catalogDetail .ant-menu-inline {
    background: #eee;
}
.catalogDetail .ant-menu-inline > .ant-menu-submenu >
 .ant-menu-submenu-title {
  background: #fff;
  padding-left: 16px !important;
}
.catalogDetail .ant-menu-inline .ant-menu-submenu-title {
  margin-top: 0;
  font-size: 16px;
}
.catalogDetail .ant-menu-submenu > .ant-menu {
    background-color: #eee;
    border-radius: 4px;
}
.catalogDetail .ant-menu-inline .ant-menu-submenu {
    border-bottom: solid 1px #eff2f2;
    background: #fff;
    margin-bottom: 16px;
}
.ant-menu-inline {
    border-right: 1px solid #e8e8e8;
    background: #eee;
}
.catalogDetail .ant-menu-sub.ant-menu-inline > .ant-menu-item {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    background: #eee;
    font-size: 15px;
}
.sureModal .ant-modal-body {
  margin-top: 20px;
  padding: 0px;
  word-wrap: break-word;
}
.sureModal .ant-modal-body .box{
     margin:0 auto;
     margin-top:30px;
    font-size: 18px;
    margin-bottom:30px;
    text-align:center;
}
</style>
<style lang="stylus" scoped>
.catalogDetail {
  display: flex;
  border-top: 1px solid #ccc;

  .Left {
    width: 400px;
    background: #eee;

    .title {
      width: 400px;
      height: 103px;
      background: #d2d5da;
      margin-bottom: 8px;

      .box {
        width: 400px;
        display: flex;

        .left {
          width: 142px;
          height: 82px;
          padding-top: 10px;
          padding-left: 12px;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .right {
          padding-left: 10px;
          flex:1;
          h2 {
            font-size: 16px;
            color: #333333;
            padding-top: 12px;
            margin-bottom: 11px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            width:230px;
          }

          p {
            color: #666666;
            font-size: 14px;
            margin-bottom: 0;
          }
        }
      }
    }

    .menu {
      width: 400px;
      margin: 0 auto;
      background: #eee;
      min-height: 600px;
      margin-bottom: 50px;

      .log {
        width: 388px;
        padding-left: 6px;
        background: #eee;
      }
    }
  }

  .Right {
    background: #fff;
    flex: 1;
  }
}
</style>
