<template>
  <div class="quality education">
    <h2 class="title"><span>/ Quality Evaluation</span></h2>
    <div class='wrap'>
      <p></p>
      <div class="form">
        <a-form
          :form="form"
          ref="form"
        >
          <a-form-item label="">
            <a-input v-decorator="[
                            'title',
                            {
                            rules: [{ required: true, message: '' }],
                            },
                        ]" />
          </a-form-item>
          <a-form-item label="">
            <a-textarea
              v-decorator="[
                            'content',
                            {
                                rules: [{ required: true, message: '' }],
                                },
                            ]"
              placeholder=""
              :autosize="{ minRows: 9, maxRows: 12 }"
            />
          </a-form-item>

        </a-form>
        <div
          class="btn"
          @click="handleSubmit()"
        >
          
        </div>
      </div>
    </div>
    <!--  -->
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
import { submitQualityApi } from "@/request/api";
export default {
  name: "quality",
  data() {
    return {
      form: this.$form.createForm(this, { name: "advanced_search" }),
      flag: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    handleSubmit() {
      this.$refs.form.form.validateFields().then((res) => {
        this.getToken(submitQualityApi, res, (data) => {
          this.$message.success(data.message);
          this.$refs.form.form.resetFields();
        });
      });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>
<style lang="stylus">
.quality {
  .form {
    input[type='text'] {
      border-radius: 0;
      height: 37px;
    }

    .ant-form-item-label {
      label {
        font-size: 16px;
      }
    }

    form {
      textarea.ant-input {
        border-radius: 0;
      }
    }
  }
}
</style>
<style lang="stylus" scoped>
.quality {
  overflow: hidden;
  h2 {
      bacground:pink;
      padding-top:30px;
      width: 400px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      padding-bottom: 50px;
      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }
  .wrap {
    width: 800px;
    height: 574px;
    border: 1px solid #ddd;
    margin: 0 auto 75px;
    padding: 28px;

    p {
      border-bottom: 1px dashed #ddd;
      font-size: 16px;
      color: #333;
      padding-bottom: 22px;
    }

    .form {
      width: 587px;

      .btn {
        display: inline-block;
        width: 100px;
        height: 39px;
        padding: 5px 25px;
        font-size: 16px;
        line-height: 30px;
      }
    }
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>
