<template>
  <div
    class="loginModal"
    v-if="isShow"
  >
    <div class="loginBox">
      <div class="header">
        <span>&nbsp;&nbsp;</span>
        <a-icon
          @click="$emit('closemodal')"
          class="close"
          type="close"
        />
      </div>
      <div class="content">
        <eform
          :formData="formData"
          class="loginform"
          ref="form"
          :fields="fields"
          v-if="fields||isRemind"
        >
  
        </eform>
        <a-button
          type="primary"
          @click="handleSubmit"
        >
          
        </a-button>
        <div class="rem">
          <ul>
            <li><input
                type="checkbox"
                v-model="checked"
              ><span></span></li>
            <li @click="$emit('forget')"></li>
          </ul>
          <span
            class="reg"
            @click="$emit('goregister')"
          >&gt;</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { setCookie, getCookie } from "../../units/cookie.js";
import { userLoginApi } from "@/request/api";
import { mapState } from "vuex"; //state
// import {mapMutations} from "vuex"
import eform from "@/components/common/form";
export default {
  name: "loginModal",
  props: { isShow: Boolean, exitshow: Boolean },
  components: { eform },
  computed: mapState({
    isLogin: (state) => state.isLogin,
    role: (state) => state.role,
    username: (state) => state.username,
    icon: (state) => state.icon,
  }),
  watch: {
    isShow: function () {
      this.fields = null;
      this.checked = false;
      if (getCookie("username")) {
        this.checked = true;
        this.fields = {
          account: getCookie("username"),
          password: getCookie("userpwd"),
        };
      }
    },
  },
  data() {
    const formData = [
      {
        label: "",
        prop: "account",
        placeholder: "//",
      },
      {
        label: "",
        prop: "password",
        placeholder: "816",
        type: "password",
      },
    ];
    return {
      form: this.$form.createForm(this, { name: "horizontal_login" }),
      checked: false,
      password: "",
      formData,
      fields: null,
      isRemind: true,
    };
  },
  mounted() {
    window.addEventListener("keydown", this.enterClick, false);
  },
  destroyed() {
    window.removeEventListener("keydown", this.enterClick, false);
  },
  methods: {
    enterClick(e) {
      if (e.key === "Enter") {
        this.$refs.form.form.validateFields().then((res) => {
          console.log(res)
          if (res.account && res.password) {
            this.getToken(userLoginApi,{account:res.account.trim(),password:res.password.trim()}, (value) => {
              localStorage.setItem("isLogin", 0);
              localStorage.setItem("role", value.data.role);
              localStorage.setItem("username", value.data.name);
              localStorage.setItem("icon", value.data.icon);
              localStorage.setItem("token", value.data.token);
              localStorage.setItem("authentation", value.data.authentation);
              localStorage.setItem("loginTime",new Date().getTime())
              this.$store.commit("toggleIsLogin", {
                isLogin: 0,
                role: value.data.role,
                username: value.data.name,
                icon: value.data.icon,
                authentation: value.data.authentation,
                token:value.data.token
              });
              this.$message.success("");
              this.$emit("logined");
              if (value.code == 200) {
                // cookie
                if (this.checked == true) {
                  //3
                  setCookie("username", res.account, 7);
                  setCookie("userpwd", res.password, 7);
                } else {
                  //Cookie
                  setCookie("username", "", -1);
                  setCookie("userpwd", "", -1);
                }
              }
            });
          } else {
            this.$message.error("");
          }
        });
      }
    },
    handleSubmit() {
      this.$refs.form.form.validateFields().then((res) => {
        // console.log(res)
        if (res.account && res.password) {
          this.getToken(userLoginApi, {account:res.account.trim(),password:res.password.trim()}, (value) => {
            localStorage.setItem("isLogin", 0);
            localStorage.setItem("role", value.data.role);
            localStorage.setItem("username", value.data.name);
            localStorage.setItem("icon", value.data.icon);
            localStorage.setItem("token", value.data.token);
            localStorage.setItem("authentation", value.data.authentation);
            localStorage.setItem("loginTime",new Date().getTime())
            this.$store.commit("toggleIsLogin", {
              isLogin: 0,
              role: value.data.role,
              username: value.data.name,
              icon: value.data.icon,
              authentation: value.data.authentation,
            });
            this.$message.success("");
            let arr = window.location.pathname.split("/");
             if(arr[arr.length - 1]=='register'||arr[arr.length - 1]=='forgetModal'){
              this.$router.push({path:'/basicInfo'})
            }
            // if(res.data.role==""){
            //       if(arr[arr.length - 1]=='register'||arr[arr.length - 1]=='forgetModal'){
            //       this.$router.push({path:'/basicInfos'})
            //     }else{
            //           if(arr[arr.length - 1]=='register'||arr[arr.length - 1]=='forgetModal'){
            //           this.$router.push({path:'/basicInfo'})
            //         }
            //     }
            // }

            this.$emit("logined");
            if (value.code == 200) {
              // cookie
              if (this.checked == true) {
                //3
                setCookie("username", res.account, 7);
                setCookie("userpwd", res.password, 7);
              } else {
                //Cookie
                setCookie("username", "", -1);
                setCookie("userpwd", "", -1);
              }
            }
          });
        } else {
          this.$message.error("");
        }
      });
    },
  },
};
</script>


<style lang="stylus">
.loginModal .ant-form-item-control {
  position: relative;
  line-height: 45px;
  zoom: 1;
  width: 360px;
  height: 45px;
  background-color: #ffffff;
  border-radius: 3px;
}

.loginModal .ant-form-item-label {
  display: none !important;
  overflow: hidden;
  line-height: 39.9999px;
  white-space: nowrap;
  text-align: right;
  vertical-align: middle;
}

.loginModal .ant-form-item-children .ant-input {
  display: block;
  width: 360px;
  height: 40px;
  border:1px solid #ddd;
}
.loginModal  .ant-input:focus {
    border-color: #40a9ff;
    border-right-width: 1px !important;
    outline: 0;
    -webkit-box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.3);
    box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.3);
}
</style>
<style lang="stylus" scoped>
.loginModal {
  z-index: 999;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(0, 0, 0, 0.5);

  .loginBox {
    width: 440px;
    height: 395px;
    background-color: #ffffff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .header {
      height: 90px;
      // background:pink;
      width: 360px;
      border-bottom: 1px solid #ddd;
      margin: 0 auto;
      text-align: center;

      span {
        color: #414141;
        font-size: 28px;
        display: inline-block;
        margin-top: 30px;
        text-align: center;
      }

      .close {
        position: absolute;
        top: 10px;
        right: 10px;
        color: #999;
        font-size: 20px;
      }
    }

    .content {
      height: 332px;
      padding: 30px 40px;

      .ant-btn-primary {
        height: 40px;
        width: 358px;
        background-color: #4182fa;
        border-radius: 3px;
        font-size: 18px;
        border: 0;
      }
    }

    .ant-input-affix-wrapper {
      line-height: 45px;
      height: 45px;

      .ant-input-prefix {
        font-size: 16px;
        top: 39px;
        position: relative;
      }
    }

    .rem {
      display: flex;
      margin-top: 20px;
      justify-content: space-between;
      font-size: 12px;
      align-items: center;
      color: #999;
      height: 32px;

      ul {
        display: flex;
        align-items: center;
        height: 100%;
        margin-bottom: 0px;

        li {
          display: flex;
          align-items: center;
          text-align: center;
          width: 80px;
          justify-content: center;
          cursor: pointer;
          border-right: 1px solid #ccc;

          input {
            width: 16px;
            height: 16px;
            background-color: #ffffff;
            border: solid 1px #aaaaaa;
            margin-right: 8px;
          }

          span {
            color: #999;
          }
        }

        li:last-child {
          border: 0;
        }

        li:hover {
          color: #4182fa;

          span {
            color: #4182fa;
          }
        }
      }

      .reg {
        color: #4182fa;
        cursor: pointer;
        height: 20px;
      }

      .reg:hover {
        border-bottom: solid 1px #4182fa;
      }
    }
  }
}
</style>
