<template>
  <div class="mine">
    <div class="banner">
      <img src="../assets/mine/banner.png" alt="">
      <div class="roleshow">
        <p>
          <span :title="username">{{username}}</span>
          <span>{{role}}</span>
          <span v-if="role==''||role==''||role==''">{{list.authentation}}</span>
          <template v-else-if="role==''||role==''">
              <span v-if="infolist.isAlliance===1"></span>
          </template>
        </p>
      </div>
    </div>
    <div class="content">
      <a-layout id="components-layout-demo-custom-trigger">
        <a-layout-sider :trigger="null" >
          <a-menu
            :selectedKeys="selectedKeys"
            :open-keys="openKeys"
            @openChange="onOpenChange"
            @click="changePage"
            theme="dark"
            mode="inline"
          >
          <a-menu-item key="1" v-if="this.role ==''||this.role==''||this.role==''">
            <em></em><span></span>
          </a-menu-item>
          <a-menu-item key="1" v-if="this.role==''||this.role==''">
            <span class="jigou"></span><span></span>
          </a-menu-item>
          <a-menu-item key="2" v-if="this.role ==''||this.role==''||this.role==''">
            <span class="subject"></span><span></span>
          </a-menu-item>
          <a-menu-item key="3"  v-if="this.role ==''">
            <span class="banji"></span><span></span>
          </a-menu-item>
          <a-menu-item key="3"  v-else-if="this.role == ''&&list.authentation =='VIP'">
            <span class="banji"></span><span></span>
          </a-menu-item>
          <a-menu-item key="4" v-if="this.role ==''||this.role==''||this.role==''">
            <span class="zhibao"></span><span></span>
          </a-menu-item>
          <a-sub-menu key="sub1" v-if="this.role ==''||this.role==''||this.role==''">
            <div slot="title" class="mid"><em></em><span></span></div>
            <a-menu-item key="5-1">
              
            </a-menu-item>
            <a-menu-item key="5-2"> 
             
            </a-menu-item>
          </a-sub-menu>          
          <a-menu-item key="6" v-if="this.role ==''||this.role==''||this.role==''">
            <span class="score"></span><span></span>
          </a-menu-item>
          <a-menu-item key="7" v-if="this.role ==''||this.role==''||this.role==''">
            <span class="myar"></span><span></span>
          </a-menu-item>
          <a-menu-item key="9" v-if="this.role==''">
            <span class="mySite"></span><span></span>
          </a-menu-item>
          <a-sub-menu key="sub2" v-if="this.role==''&&infolist.isTrialSite==1">
            <div slot="title" class="examine"><em></em><span></span></div>
            <a-menu-item key="10-1">
              
            </a-menu-item>
            <a-menu-item key="10-2" > 
             
            </a-menu-item>
          </a-sub-menu>   
          <a-menu-item key="11" v-if="this.role==''&&infolist.isAlliance==1||this.role==''&&infolist.isAlliance==1">
            <span class="require"></span><span></span>
          </a-menu-item>
          <a-menu-item key="8" class="newsitem">
            <span class="mynews"></span><span></span><span v-if="msgStatus==1" class="redspan"></span>
          </a-menu-item>

            <!-- <template v-for="item in menulist">
              <a-sub-menu
                :key="item.key"
                v-if="item.children"
              >
                <span slot="title"><a-icon type="notification" /><span>{{item.name}}</span></span>
                <a-menu-item
                  :key="value.key"
                  v-for="value in item.children"
                >
                  {{value.name}}
                </a-menu-item>
              </a-sub-menu>
              <a-menu-item
                v-else
                :key="item.key"
              >
                <span><a-icon type="user" />{{item.name}}</span>
              </a-menu-item>
            </template> -->
          </a-menu>
        </a-layout-sider>
        <a-layout>
          <a-layout-content :style="{ background: '#fff', minHeight: '500px' }">
            <router-view></router-view>
          </a-layout-content>
        </a-layout>
      </a-layout>
    </div>
  </div>
</template>
<script>
import {getmyInfo,getinstituteCenterApi} from "@/request/api";
export default {
  watch: {
    $route(to) {
      if(to.fullPath=='/basicInfos'){
        this.selectedKeys=['1']
      }else if(to.fullPath=='/basicInfo'){
          this.selectedKeys=['1']
      }
    },
  },
  data() {
    return {
      selectedKeys: ["1"],
      rootSubmenuKeys: ["sub1"],
      openKeys: [],
      list:[],
      infolist:[],
      newMessage:''
      // menulist: [
      //   {
      //     name: "",
      //     key: "1",
      //   },
      //   {
      //     name: "",
      //     key: "2",
      //   },
      //   {
      //     name: "",
      //     key: "3",
      //   },
      //   {
      //     name: "",
      //     key: "4",
      //   },
      //   {
      //     name: "",
      //     key: "sub1",
      //     children: [
      //       { name: "", key: "5-1" },
      //       { name: "", key: "5-2" },
      //     ],
      //   },
      //   {
      //     name: "",
      //     key: "6",
      //   },
      //   {
      //     name: "",
      //     key: "7",
      //   },
      //   {
      //     name: "",
      //     key: "8",
      //   },
      // ],
    };
  },
  created(){
     this.init()
  },
  
  mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "mysubject":
        this.selectedKeys = ["2"];
        break;
      case "basicInfos":
        this.selectedKeys = ["1"];
        break;
      case "basicInfo":
        this.selectedKeys = ["1"];
        break;
      case "mytrain":
        this.openKeys = ["sub1"];
        this.selectedKeys = ["5-1"];
        break;
      case "invlintrain":
        this.openKeys = ["sub1"];
        this.selectedKeys = ["5-1"];
        break;
      case "myexam":
        this.openKeys = ["sub1"];
        this.selectedKeys = ["5-2"];
        break;
       case "invlinexam":
        this.openKeys = ["sub1"];
        this.selectedKeys = ["5-2"];
        break;
      case "myClass":
        this.selectedKeys = ["3"];
        break;
      case "mystudent":
      this.selectedKeys = ["3"];
      break;
      case "manClass":
        this.selectedKeys = ["3"];
        break;
      case "manstudent":
      this.selectedKeys = ["3"];
      break;
      case "studyview":
      this.selectedKeys = ["3"];
      break;
      case "myviedo":
        this.selectedKeys = ["4"];
        break;
      case "myscore":
        this.selectedKeys = ["6"];
        break;
      case "myask":
        this.selectedKeys = ["7"];
        break;
      case "myreply":
        this.selectedKeys = ["7"];
        break;
      case "message":
        this.selectedKeys = ["8"];
        break;
      case "Site":
        this.selectedKeys = ["9"];
        break;
      case "examplan":
        this.openKeys = ["sub2"];
        this.selectedKeys = ["10-1"];
        break;
      case "newexamplan":
        this.openKeys = ["sub2"];
        this.selectedKeys = ["10-1"];
        break;
      case "report":
        this.openKeys = ["sub2"];
        this.selectedKeys = ["10-1"];
        break;
      case "audit":
        this.openKeys = ["sub2"];
        this.selectedKeys = ["10-1"];
        break;
      case "mark":
        this.openKeys = ["sub2"];
        this.selectedKeys = ["10-2"];
        break;
      case "require":
        this.selectedKeys = ["11"];
        break;
      case "newrequire":
        this.selectedKeys = ["11"];
        break;
      case "Delivery":
        this.selectedKeys = ["11"];
        break;
    }
  },
  computed: {
     msgStatus(){
      return this.$store.state.msgStatus;
    },
    role() {
      return this.$store.state.role;
    },
    isLogin() {
      return this.$store.state.isLogin;
    },
    username() {
      return this.$store.state.username;
    },
    icon() {
      return this.$store.state.icon;
    },
    authentation() {
      return this.$store.state.authentation;
    },
   
  },
  // created() {
  //   if (this.role == "") {
  //     this.menulist.splice(2, 1);
  //   }
  //   if (this.role == "" && this.authentation == "VIP") {
  //     this.menulist[2].name = "";
  //   }
  //   if (this.role == "" && this.authentation == "") {
  //     this.menulist.splice(2, 1);
  //   }
  //   if (this.role == "" && this.authentation == "") {
  //     this.menulist.splice(2, 1);
  //   }
  // },
  methods: {
    init(){
      if(this.role==""||this.role==""||this.role==""){
          this.getToken(getmyInfo, {}, (res) => {
            this.list = res.data;
            this.newMessage=res.data.newMessage
            this.$store.commit('changeMessageStatus',{'status':this.newMessage==0?false:true})
          })
      }else{
           this.getToken(getinstituteCenterApi,{},(res)=>{
            this.infolist=res.data
            // console.log(this.infolist.isAlliance)
            this.newMessage=res.data.newMessage
            this.$store.commit('changeMessageStatus',{'status':this.newMessage==0?false:true})
          })
      }
     
    },       
    onOpenChange(openKeys) {
      const latestOpenKey = openKeys.find(
        (key) => this.openKeys.indexOf(key) === -1
      );
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },
    changePage({ key }) {
      this.selectedKeys = [key];
      if (key == 1&&(this.role==""||this.role==""||this.role=="")) {
        this.$router.push({ path: "/basicInfo" });
      } else if(key==1&&(this.role==""||this.role=="")){
        this.$router.push({ path: "/basicInfos" });
      }else if (key == 2) {
        this.$router.push({ path: "/mysubject" });
      } else if (key == 3 && this.role == "") {
        this.$router.push({ path: "/myClass" });
      } else if (key == 3 && this.role == "") {
        this.$router.push({ path: "/manClass" });
      } else if (key == 4) {
        this.$router.push({ path: "/myviedo" });
      }
      // else if(key=='sub1'){
      //   this.$router.push({path:'/trainexam'})}
      else if (key == "5-1") {
        this.$router.push({ path: "/mytrain" });
      } else if (key == "5-2") {
        this.$router.push({ path: "/myexam" });
      } else if (key == 6) {
        this.$router.push({ path: "/myscore" });
      } else if (key == 7) {
        this.$router.push({ path: "/myask" });
      } else if (key == 9) {
        this.$router.push({ path: "/Site" });
      }else if (key == "10-1") {
        this.$router.push({ path: "/examplan" });
      }else if (key == "10-2") {
        this.$router.push({ path: "/mark" });
      }else if (key == 11) {
        this.$router.push({ path: "/require" });
      }else {
        this.$router.push({ path: "/message" });
      }
    },
  },
};
</script>
