<style lang="stylus" scoped>
.mine
  border-top: 1px solid #eee;
  overflow hidden
  background:#eee;
  .banner
    margin:0 auto;
    height:123px;
    background-color: #4281ff;
    text-align center;
    position relative
    .roleshow
      position:absolute;
      width:1205px;
      bottom:17px;
      left:50%;
      transform:translateX(-50%)
      z-index 555;
      // position relative
      p
        position:absolute;
        margin-bottom:0px;
        overflow:hidden;
        height:26px;
        line-height:26px;
        right:0;
        bottom:0px;
        span:nth-child(1)
          font-size: 20px;
          color: #ffffff;
