<template>
    <div class="invlintrain">
        <div class="title">
            <h2></h2>
        </div>
        <div class="sea">
            <ul class="nav">
                <li
                    v-for="(item,index) in menuList"
                    :key="index"
                    @click="changenav(item)"
                    :class="{'active':current==item}"
                >{{item}}
                <p></p>
                </li>
            </ul>
            <div class="mod">
                <input
                type="text"
                placeholder=""
                v-model="key"
            >
            <button
                class="ban"
                @click="searchbtn"
            ></button>
            </div>
         </div>
        <div class="box">
            <a-table
              :columns="columns"
              :data-source="list"
              :rowKey="record=>record.id"
              :pagination="pagination"
              @change="changePage"
              :locale="{'emptyText': ''}"
              bordered
      >
        <div
          slot="status"
          slot-scope="index,record"
        >
          <span
            style="color: #ee9537;"
            v-if="record.status==0"
          ></span>
          <span
            style="color: #49af4f;"
            v-else-if="record.status==1"
          ></span>
          <span
            style="color: #ff0000;"
            v-else
          ></span>
        </div>
        <div
          slot="action"
          class="tableAction"
          slot-scope="index,record"
        >
          <span @click="todetail(record.id)"></span>
          <span @click="agren(record.confirmId)"></span>
          <span @click="refuse(record.confirmId)"></span>
        </div>
      </a-table>
        </div>  
    </div>
</template>

<script>
const columns = [
  {
    title: "",
    dataIndex: "index",
    width:70
  },
  {
    title: "",
    dataIndex: "title",
    width:250
  },
  {
    title: "",
    dataIndex: "startTime",
  },
  {
    title: "",
    dataIndex: "endTime",
  },
  {
    title: "",
    dataIndex: "trainjob",
    width:80
  },
  {
    title: "",
    scopedSlots: { customRender: "status" },
    width:80
  },
  {
    title: "",
    scopedSlots: { customRender: "action" },
    width:140
  },
];
import {getinvlintrainApi,getinvigilatorConfirmApi} from "@/request/api"
export default {
  name: 'invlintrain',
  data() {
    return {
      columns,
      current:"",
      searchInfo: {
        keyword: "",
        page: 1,
        pageSize: 10,
        type:2
      },
      pagination: { total:0, pageSize: 10 },
      list: [],
      key: "",
      menuList:["",""],
    };
  },
  created(){
     if(this.role==""){
      this.menuList.splice(1,1)
    }
    this.init()
  },
  computed: {
    role() {
      return this.$store.state.role;
    },
  },
  mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "mytrain":
        this.current = "";
        break;
      case "invlintrain":
        this.current = "";
        break;
    }
  },
  methods:{
    init(){
      this.getToken(getinvlintrainApi,this.searchInfo,res=>{
          this.list = res.data.rows.map((item, index) => ({
              index: (this.searchInfo.page - 1) * 10 + index + 1,
              title: item.onePlusXTraining.title,
              startTime: item.onePlusXTraining.startTime,
              endTime: item.onePlusXTraining.endTime,
              trainjob:item.title,
              status: item.status,
              id: item.onePlusXTraining.id,
              confirmId:item.id
          }));
      })
    },
    //
    searchbtn() {
      this.searchInfo.keyword = this.key;
      this.init();
    },
    //
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    //
    changenav(data){
        this.current = data;
        let path = "";
        switch (data) {
        case "":
            path = "/mytrain";
            break;
        case "":
            path = "/invlintrain";
            break;
        }
        this.$router.push({ path: path });
    },
    //
    todetail(id) {
      this.$router.push({
        path: "/ServiceDetail",
        query: {id},
      });
    },
    //
    agren(id){
       this.getToken(getinvigilatorConfirmApi,{confirmId:id,status:1},res=>{
          if(res.code==200){
            this.init()
            this.$message.success("")
          }
       })
    },
    //
    refuse(id){
        this.getToken(getinvigilatorConfirmApi,{confirmId:id,status:2},res=>{
          if(res.code==200){
            this.init()
            this.$message.success("")
          }
       })
    }
  }
}
</script>
<style lang="stylus">
.invlintrain .ant-table table {
  text-align: center;
}
.invlintrain thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  text-align: center;
  font-weight: 600;
  font-size: 16px;
}
.invlintrain .ant-table-bordered .ant-table-thead > tr > th,
.invlintrain .ant-table-bordered .ant-table-tbody > tr > td {
  border-right: 1px solid #e8e8e8;
  text-align: center;
}
.invlintrain .ant-table-thead > tr > th {
    color:#333;
    font-weight: 600;
    font-size: 14px;
    /* background-color: #eaeef7; */
    height:30px;  
}
.invlintrain .ant-table-bordered .ant-table-tbody{
  background:#fff;
}
.invlintrain .ant-table-pagination.ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
}
.invlintrain .ant-table-thead > tr > th, .ant-table-tbody > tr > td {
    padding: 10px 10px ;
}
</style>
<style lang="stylus" scoped>
.invlintrain{
    margin-bottom: 40px;
  .title {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-bottom:1px solid #eff2f2;
    h2 {
      height:45px;
      line-height: 45px;
      margin-left: 30px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
    }
  }

  .sea {
    height: 45px;
    margin-left: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    position: relative;
    justify-content space-between
    .nav {
      display: flex;
      height: 45px;
      line-height: 45px;
      margin-left: 12px;
      margin-top: 20px;

    li {
      width: 100px;
      margin-right: 30px;
      height: 35px;
      line-height: 35px;
      font-size: 16px;
      color: #333333;
      text-align: center;
       p{
        width: 21px;
        height: 2px;
        background-color: #4281ff;
        border-radius: 1px;
        text-align center;
        margin:0 auto;
        display:none
      }
    }
    li:last-child {
      margin-right: 0;
    }
    .active {
      color: #4281ff;
      font-weight: 600;
      p{
        display:block;
      }
    }
  }
    .mod{
      margin-right:30px;
      input {
      outline: none;
      width: 220px;
      height: 35px;
      border: solid 1px #d4d4d4;
      text-indent: 8px;
    }
    .ban {
      outline: none;
      width: 60px;
      height: 35px;
      background-color: #4281ff;
      color: #ffffff;
      font-size:14px;
      cursor: pointer;
      border:0
    }
    }
  }
   .box {
    margin-top:30px;
    margin-left: 30px;
    margin-right: 30px;
  }
}
   
</style>
