<template>
  <div class="talent education">
    <h2 class="title"><span>/ Talent Demand</span></h2>
    <div class="wrap">
      <div class="detail">
        <a-table
          :columns="columns"
          :data-source="list"
          :pagination="false"
          :locale="{ emptyText: '' }"
        >
          <div slot="address" slot-scope="index, record">
            {{ record.province }}{{ record.city }}
          </div>
          <div slot="expandedRowRender" slot-scope="text" class="talentDetail">
            <div v-html="text.content" class="editor"></div>
            <div class="btn" @click="openBox(text.id, isLogin)"></div>
          </div>
        </a-table>
        <div class="page">
          <pagination
            v-if="pagination.total"
            :pagination="pagination"
            @changePage="changePage"
          ></pagination>
        </div>
      </div>
      <!-- <a-empty
        v-else
        description=""
      /> -->
    </div>
    <modal-box
      :type="'other'"
      :isShow="isSignUp"
      @closemodal="isSignUp = false"
      :boxTitle="''"
      :width="640"
      :isfooter="true"
      @reset="reset()"
      @submit="submit()"
    >
      <div slot="modalContent" class="modalContent">
        <eform :formData="formData" class="form" ref="form">
          <div slot="name">
            <a-input
              v-decorator="[
                'title',
                {
                  rules: [
                    { required: true, message: '' },
                    { validator: validatorName },
                  ],
                },
              ]"
              placeholder=""
              class="input"
            />
            <p></p>
          </div>
          <div slot="content" style="position: relative">
            <a-upload
              v-decorator="[
                'file',
                {
                  rules: [
                    { required: true, message: '' },
                    { validator: validateFile },
                  ],
                },
              ]"
              :file-list="fileList"
              :remove="handleRemove"
              :before-upload="beforeUpload"
              :multiple="false"
              accept=".pdf,.doc,.docx,.ppt,.xls,.xlsx"
            >
              <a-button class="chooseBtn"></a-button>
            </a-upload>
            <p class="lastP">pdf/doc/docx/ppt/xls/xlsx></p>
            <p class="lastP">5M</p>
          </div>
          <div slot="newfooter" v-if="isfooter">123</div>
        </eform>
      </div>
    </modal-box>
    <!--  -->
    <div class="gohead" v-show="flag">
      <a href="#layout"></a>
    </div>
  </div>
</template>
<script>
const columns = [
  {
    title: "",
    dataIndex: "name",
  },
  {
    title: "",
    dataIndex: "title",
  },
  {
    title: "",
    dataIndex: "type",
  },
  {
    title: "",
    scopedSlots: { customRender: "address" },
  },
  {
    title: "",
    dataIndex: "needNum",
  },
  {
    title: "",
    dataIndex: "pubTime",
    width: 200,
  },
  {
    title: "",
    scopedSlots: { customRender: "action" },
    width: 70,
  },
];
import modalBox from "@/components/common/modalBox";
import pagination from "@/components/common/pagination";
import eform from "@/components/common/form";
import { getTalentListApi, sendFileApi, checkjobApi } from "@/request/api";
export default {
  name: "talent",
  components: { pagination, modalBox, eform },
  created() {
    this.init();
  },
  data() {
    const formData = [
      {
        label: "",
        scopedSlots: { customRender: "name" },
      },
      {
        label: "",
        scopedSlots: { customRender: "content" },
      },
    ];
    let validatorName = (rule, value, callback) => {
      if (value) {
        let reg = /^[\u4e00-\u9fa5a-zA-Z0-9]+$/;
        if (value == "" || reg.test(value)) {
          callback();
        } else {
          callback(new Error(""));
        }
      } else {
        callback(new Error(" "));
      }
    };
    let validateFile = (rule, value, callback) => {
      if (value) {
        const isJpgOrPng =
          value.file.type === "application/pdf" ||
          value.file.type === "application/msword" ||
          value.file.type ===
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
          value.file.type === "application/vnd.ms-powerpoint" ||
          value.file.type === "application/vnd.ms-excel" ||
          value.file.type ===
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
        // console.log(value.file.type)
        if (value.file.size / 1024 / 1024 > 5) {
          callback(new Error("5M"));
        } else if (!isJpgOrPng) {
          callback(new Error("pdf/doc/docx/ppt/xls/xlsx!"));
        } else {
          // console.log('123')
          callback();
        }
      } else {
        callback(new Error(" "));
      }
    };
    return {
      validateFile,
      validatorName,
      columns,
      formData,
      pagination: { pageSize: 10, total: 0 },
      list: [],
      searchInfo: {
        page: 1,
        pageSize: 10,
      },
      isSignUp: false,
      apiUrl: window.g.apiUrl,
      fileList: [],
      jobId: "",
      flag: false,
      isfooter: null,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  methods: {
    init() {
      this.getToken(getTalentListApi, this.searchInfo, (res) => {
        this.list = res.data.rows.map((item) => ({
          ...item,
          ...item.institute,
          id: item.id,
        }));
        this.pagination.total = res.data.count;
      });
    },
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    openBox(id, isLogin) {
      if (isLogin == 0) {
        
        this.getToken(checkjobApi, { jobId: id }, (res) => {
          if(res.code==472){
            this.$message.error("")
          }else{
            this.fileList = [];
            this.jobId = id;
            this.isSignUp = true;
          }
        });
      } else {
        this.$message.error("");
      }
    },
    handleRemove(file) {
      const index = this.fileList.indexOf(file);
      const newFileList = this.fileList.slice();
      newFileList.splice(index, 1);
      this.fileList = newFileList;
    },
    beforeUpload(file) {
      const isJpgOrPng =
        file.type === "application/pdf" ||
        file.type === "application/msword" ||
        file.type ===
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        file.type === "application/vnd.ms-powerpoint" ||
        file.type === "application/vnd.ms-excel" ||
        file.type ===
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
      if (!isJpgOrPng) {
        this.fileList = [];
        return false;
      } else if (file.size / 1024 / 1024 > 5) {
        this.fileList = [];
        return false;
      } else {
        this.fileList = [file];
      }
      return false;
    },
    reset() {
      this.$refs.form.form.resetFields(["title"]);
      this.$refs.form.form.resetFields(["file"]);
      this.fileList = [];
    },
    submit() {
      this.$refs.form.form.validateFields().then((res) => {
        console.log(res);
        let formData = new FormData();
        formData.append("title", res.title);
        formData.append("jobId", this.jobId);
        formData.append("file", this.fileList[0]);
        console.log(formData);
        this.getToken(sendFileApi, formData, (data) => {
          this.$message.success(data.message);
          this.isSignUp = false;
        });
      });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>
<style>
.talent .ant-table-row-expand-icon {
  border: none;
  background: none;
}
.talent .ant-table-row-collapsed::after {
  content: "";
  background: url("../assets/arrow-bottom.png");
  width: 15px;
  height: 15px;
  display: inline-block;
  background-position: 24px 24px;
}
.talent .ant-upload-list-item-name {
  width: 300px;
}
.ant-table-row-expanded::after {
  content: "";
  background: url("../assets/arrow-bottom.png");
  width: 15px;
  height: 15px;
  display: inline-block;
  background-position: 24px 24px;
  transform: rotate(180deg);
}
.talent .ant-table-row-expand-icon {
  position: absolute;
  right: 44px;
  margin-top: -6px;
}
.talent .ant-table-tbody > tr > td:nth-child(1),
.talent .ant-table-thead > tr:first-child > th:first-child {
  width: 0;
  padding: 0;
  min-width: 0;
}
.talent .ant-upload-list {
  position: absolute;
  top: 0;
  left: 100px;
}
.talent .ant-col-6 {
  width: 16% !important;
}
.talent .ant-col-15 {
  width: 84% !important;
}
.talent .ant-table-thead > tr > th {
  background-color: #ebebeb !important;
  color: #333333 !important;
  font-weight: bold !important;
  font-size: 15px;
}
.talent .ant-table-thead {
  height: 50px;
}
.talent .ant-table-tbody > tr > td:nth-child(2) {
  color: #4182fa;
}
.talent .ant-table-tbody > tr > td:nth-child(2) .editor {
  color: #666;
  padding-left: 60px;
}
.talent .ant-form .ant-form-item:nth-child(2) .ant-form-item-children-icon {
  margin-top: -36px;
}
.talent .ant-form .ant-form-item:nth-child(1) .ant-form-item-children-icon {
  margin-top: -28px;
}
.talent .ant-table-tbody > tr {
  height: 61px;
}
</style>
<style lang="stylus" scoped>
.talent {
  overflow: hidden;
  min-height: 500px;

  h2 {
    bacground: pink;
    padding-top: 30px;
    width: 400px;
    height: 24px;
    font-family: MicrosoftYaHei;
    font-size: 26px;
    font-weight: 200;
    color: #333333;
    padding-left: 69px;
    padding-bottom: 50px;

    span {
      font-size: 16px;
      font-weight: normal;
      letter-spacing: 0px;
      color: #999999;
    }
  }

  .wrap {
    padding: 0 69px;

    .page {
      margin-top: 34px;
      margin-bottom: 34px;
    }

    .talentDetail {
      .btn {
        display: inline-block;
        font-size: 16px;
        background: #fd8d56;
        padding: 8px 18px;
        margin-left: 58px;
        margin-bottom: 15px;
        margin-top: 13px;
      }
    }
  }

  .modalContent {
    padding: 26px 0;

    .input {
      width: 250px;
      height: 37px;
      border-radius: 0;
    }

    p {
      font-size: 12px;
      color: #bdbdbd;
      margin: 0;
    }

    .lastP {
      line-height: 25px;

      &:nth-child(3) {
        color: #4182fa;
      }
    }

    .chooseBtn {
      background: #fd8d56;
      color: #fff;
    }
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>