<template>
  <div
    id="introduction"
    class="education"
  >
    <h2 class="navtitle"><span>/ Alliance Introduction</span></h2>
    <div class="mod">
      <div
        class="ql-editor"
        v-if="info"
        v-html="info"
      ></div>
      <!-- <a-empty
        v-else
        description=""
      /> -->
      <div class="notbox"  v-else>
      <img
        src="../assets/blank/notlist.png"
        alt=""
      >
      <p class="inp"></p>
    </div>
    </div>
    <!--  -->
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#layout"></a>
    </div>
  </div>
</template>

<script>
import { geteducationListApi } from "@/request/api";
export default {
  name: "introduction",
  data() {
    return {
      info: null,
      flag: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },

  created() {
    this.init();
  },
  methods: {
    init() {
      this.getToken(geteducationListApi, {}, (res) => {
        this.info = res.data.content;
      });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
  },
};
</script>

<style lang="stylus" scoped>
#introduction {
  margin: 0 auto;
  min-height: 500px;
  padding-bottom: 30px;
    h2 {
      bacground:pink;
      padding-top:30px;
      width: 400px;
      height: 24px;
      font-family: MicrosoftYaHei;
      font-size: 26px;
      font-weight:200;
      color: #333333;
      padding-left: 69px;
      padding-bottom: 50px;
      span {
        font-size: 16px;
        font-weight: normal;
        letter-spacing: 0px;
        color: #999999;
      }
    }
  .mod {
    width: 1205px;
    background-color: #ffffff;
    padding-bottom: 144px;
    padding: 0 69px;
  }
  .notbox{
    margin:0 auto;
    margin-bottom:30px;
    margin-top:40px;
    text-align center;
    img {
      display:block
      margin:0 auto;
    }
  .inp {
      font-size: 18px;
      color: #666;
      margin: 0 auto;
      text-align: center;
      margin-top: 10px;
    }
  }

  .gohead {
    width: 66px;
    height: 58px;
    background-color: #fff;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 240px;
    bottom: 50px;
    cursor: pointer;

    a {
      width: 33px;
      height: 32px;
      display: inline;
      text-align: center;
      background: url('../assets/gohead.png') -18px -15px no-repeat;
    }
  }

  .gohead:hover {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }

  .gohead:active {
    a {
      background: url('../assets/gohead1.png') -18px -15px no-repeat;
    }
  }
}
</style>
