<template>
  <div class="Site">
    <div class="title">
      <h2></h2>
    </div>
    <div class="box">
        <ul class="filter">
            <li>
                <img src="../../assets/mine/siteapply1.png" alt="">
                <p></p>
                <!--  -->
                <template v-if="statusList.examSite==false">
                    <button v-if="statusList.examSiteStatus==null" class="applybtn" @click="applychange"></button>
                    <div class="applystatus" v-else-if="statusList.examSiteStatus==0">
                        <img src="../../assets/mine/wait.png" alt="">
                        <span></span>
                    </div>
                    <template v-else>
                        <div class="applystatus">
                            <img src="../../assets/mine/refuse.png" alt="">
                            <span></span>
                        </div>
                        <button class="reapplybtn" @click="applychange"></button>
                    </template>
                </template>
                <!--  -->
                <template v-else-if="statusList.examSite==true">
                    <div class="applystatus">
                        <img src="../../assets/mine/success.png" alt="">
                        <span></span>
                    </div>
                </template>
            </li>
            <li>
                <img src="../../assets/mine/siteapply2.png" alt="">
                <p></p>
                <template v-if="statusList.manageSite==false">
                    <button v-if="statusList.manageSiteStatus==null" class="applybtn" @click="manchange"></button>
                    <div class="applystatus" v-else-if="statusList.manageSiteStatus==0">
                        <img src="../../assets/mine/wait.png" alt="">
                        <span></span>
                    </div>
                    <template v-else>
                        <div class="applystatus">
                            <img src="../../assets/mine/refuse.png" alt="">
                            <span></span>
                        </div>
                        <button class="reapplybtn" @click="manchange"></button>
                    </template>
                </template>
                <!--  -->
                <template v-else-if="statusList.manageSite==true">
                    <div class="applystatus">
                        <img src="../../assets/mine/success.png" alt="">
                        <span></span>
                    </div>
                </template>
            </li>
        </ul>
    </div>
    <a-modal
      :title="type==0?'':''"
      :visible="applyModal"
      @ok="applyOk"
      @cancel="applycancel()"
      okText=""
      cancelText=""
      class="applymodal"
    >
       <div class="applymodbox">
           <div class="upper">
                <p class="unitname"><span></span><span>{{infolist.name}}</span></p>
                <p class="unitattr"><span></span><span>{{infolist.role}}</span></p>
                <div class="unitlogo">
                    <span>logo</span>
                    <img :src="infolist.logo" alt="">
                </div>
            </div>
            <eform action=""
               :formData="formData"
                class="under"
                ref="form"
            >
                <div slot="content" class="conbox" >
                    <a-textarea 
                      class="applycon" 
                      placeholder="" 
                      v-decorator="['content',{rules: [{ required: true, message: '' },{validator:validatorName}]}]"
                    >
                    </a-textarea>
               </div>
                <div slot="file" class="pro" >
                    <a-upload 
                        class="loadbtn"
                        :default-file-list="fileList" 
                        :remove="handleRemove" 
                        :before-upload="beforeUpload">
                        <a-button class="loadbtnspan"> <a-icon type="upload" />  </a-button>
                    </a-upload>
                    <!-- <a-button
                        class="downbtn"
                        type="primary"
                        :disabled="fileList.length === 0"
                        :loading="uploading"
                        style="margin-top: 16px"
                        @click="handleUpload"
                    >
                    {{ uploading ? 'Uploading' : '' }}
                    </a-button> -->
                </div>
            </eform>
       </div>
    </a-modal>
  </div>
</template>

<script>
// import reqwest from 'reqwest';
import eform from "@/components/common/form"
import {getinstituteCenterApi,siteApplyStatusApi,siteApplyApi,checkTrialSiteApi} from "@/request/api"
export default {
  name: 'Site',
  components:{eform},
    data(){
         const formData = [
            {
                label: "",
                scopedSlots: { customRender: "content" },
            },
            {
                label: "",
                scopedSlots: { customRender: "file" },
            },
        ];
        let validatorName = (rule, value, callback) => {
        if (value) {
            let reg = /^[\s\S]*/;
            if (value==""||reg.test(value)) {
                this.content=value
               callback();
            } else {
            callback(new Error(""));
            }
        } else {
            callback(new Error(" "));
        }
    };
    let validateFile = (rule, value, callback) => {
      if (value) {
        const isJpgOrPng =
          value.file.type === "application/pdf" ||
          value.file.type === "application/msword" ||
          value.file.type ===
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
          value.file.type === "application/vnd.ms-powerpoint" ||
          value.file.type === "application/vnd.ms-excel" ||
          value.file.type ===
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
        // console.log(value.file.type)
        if(!isJpgOrPng){
          callback(new Error("pdf/doc/docx/ppt/xls/xlsx!"));
        } else {
          // console.log('123')
          callback();
        }
      } else {
        callback(new Error(" "));
      }
    };
        return{
            applyModal:false,
            quillInfo:"",
            imageUrl:require("../../assets/mine/logo.png"),
            type:null,
            statusList:[],
            infolist:[],
            fileList:[],
            keyword:'',
            formData,
            validatorName,
            validateFile,
            content:'',
            uploading: false,
            api:window.g.apiUrl,
        }
    },
    created(){
        this.init()
    },
    methods:{
        init(){
            this.getToken(siteApplyStatusApi,{},res=>{
                this.statusList=res.data
            })
            this.getToken(getinstituteCenterApi,{},res=>{
                this.infolist=res.data
            })
        },
        //
        applychange(){
            this.getToken(checkTrialSiteApi,{},res=>{
                if(res.data.isTrialSite){
                    this.applyModal=true
                    this.type=0
                }else{
                    this.$message.error("")
                }
            })
            
        },
        //
        manchange(){
            this.getToken(checkTrialSiteApi,{},res=>{
                if(res.data.isTrialSite){
                    this.applyModal=true
                    this.type=1
                    
                }else{
                    this.$message.error("")
                }
            })
        },
        //
        applycancel(){
            this.applyModal=false
            this.$refs.form.form.resetFields();
            this.fileList = [];
        },
        handleRemove(file) {
            const index = this.fileList.indexOf(file);
            const newFileList = this.fileList.slice();
            newFileList.splice(index, 1);
            this.fileList = newFileList;
            // console.log("",this.fileList)
        },
        beforeUpload(file) {
           const isJpgOrPng =
            file.type === "application/pdf" ||
            file.type === "application/msword" ||
            file.type ===
            "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
            file.type === "application/vnd.ms-powerpoint" ||
            file.type === "application/vnd.ms-excel" ||
            file.type ===
            "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
            if (!isJpgOrPng) {
                this.fileList = [];
                return false;
            }else {
                this.fileList.push(file);
                //  console.log("",this.fileList)
            }
            return false;
        },
        applyOk(){
            if(this.type==0){
                // console.log("")
                this.$refs.form.form.validateFields().then((res) => {
                    let formData = new FormData();
                    formData.append("content", res.title);
                    formData.append("type",1)
                     this.fileList.forEach(function(item){
                    formData.append("file",item);
                    })
                    this.getToken(siteApplyApi, formData, (data) => {
                    this.$message.success(data.message);
                    this.applyModal = false;
                    this.init()
                    this.$refs.form.form.resetFields();
                    });
                });
            }else{
                // console.log("")
                this.$refs.form.form.validateFields().then((res) => {
                    let formData = new FormData();
                    formData.append("content", res.content);
                    formData.append("type",2)
                    this.fileList.forEach(function(item){
                        formData.append("file",item);
                    })
                
                    this.getToken(siteApplyApi, formData, (data) => {
                    this.$message.success(data.message);
                    this.applyModal = false;
                    this.init()
                    this.$refs.form.form.resetFields();
                    });
                });
            }
        }
    
    }
}
</script>
<style >
