}
.applymodal .applymodbox .under .pro .loadbtn .loadbtnspan{
    width: 83px;
	height: 29px;
	background-color: #fd8d56;
	border-radius: 3px;
    color:#fff;
    font-size:14px;
    cursor: pointer;
    border:0;
