    display:flex;
    justify-content: center;
    align-items: center;
}

.applymodal .applymodbox .under .pro .downbtn{
    width: 83px;
	height: 29px;
	background-color: #1070ff;
	border-radius: 3px;
    color:#fff;
    font-size:14px;
    cursor: pointer;
    border:0;
