    display:flex;
    justify-content: center;
    align-items: center;
}
.applymodal .ant-modal-footer {
    padding: 16px 16px;
    text-align: right;
    border-top: 1px solid #e8e8e8;
    border-radius: 0 0 4px 4px;
}
</style>
