<template>
  <div id="teachingCourse">
    <template v-if="bannerList.length>0">
      <div class="banner" v-for="item in bannerList" :key="item.id">
        <a :href="item.link">
            <img :src="item.img" alt="">
        </a>
      </div>
    </template>
    <div class="banner" v-else>
      <!-- <img
        src="../assets/banner/jxkt_banner.png"
        alt=""
      > -->
      <p>banner</p>
    </div>
    
    <div class="content">
      <div class="nav">
        <ul>
          <li
            v-for="(item,index) in menuList"
            :key="index"
            @click="changPage(item)"
            :class="{'active':current==item}"
          >{{item}}</li>
        </ul>
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import {getbannerApi} from "@/request/api"
export default {
  name: "teachingCourse",
  data() {
    return {
      menuList: ["", ""],
      current: "",
      bannerList:[]
    };
  },
  watch:{
     $route(to){
       let arr = to.path.split("/");
        switch (arr[arr.length - 1]) {
          case "course":
            this.current = "";
            break;
          case "cultivate":
            this.current = "";
            break;
        }
     }
  },
  created(){
     this.init()
  },
  mounted() {
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "course":
        this.current = "";
        break;
      case "cultivate":
        this.current = "";
        break;
    }
  },
  methods: {
    init(){
      this.getToken(getbannerApi,{module:2},res=>{
                this.bannerList=res.data
            })
    },
    changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/course";
          break;
        case "":
          path = "/cultivate";
          break;
      }
      this.$router.push({ path: path });
    },
  },
};
</script>

<style lang="stylus" scoped>
#teachingCourse {
  .banner {
    height:261px;
    margin:0 auto;
    p{
        color:#333;
        font-size:20px;
        width:100%;
        height:100%;
        display:flex;
        align-items center;
        justify-content center;
        background:#d5d5d5;
        text-align center;
    }
    img {
      width: 100%;
      height:100%;
    }
  }

  .nav {
    width: 152px;
    height: 181px;
    background-color: #ffffff;
    box-shadow: 0px 0px 3px 0px rgba(105, 105, 105, 0.1);
    border: solid 1px #eff2f2;
    position: absolute;
    left: -187px;
    top: 20px;

    ul {
      margin-top: 6px;

      li {
        width: 150px;
        height: 40px;
        line-height: 40px;
        padding-left: 23px;
        font-size: 16px;
        cursor: pointer;
      }

      .active {
        background-color: #bad2ff;
        font-weight: bold;
        font-size: 16px;
        padding-left: 20px;
        border-left: 3px solid #4182fa;
      }
    }
  }

  .content {
    width: 1205px;
    margin: 0 auto;
    min-height: 400px;
    position: relative;
  }
}
</style>
