<template>
  <div class="cultivateDetail">
    <div class="banner">
      <img
        src="../assets/banner/jxkt_banner.png"
        alt=""
      >
    </div>
    <div class="content">
      <div class="breadCrumb">
        <span @click="returnHome()"> ></span>
        <span @click="returncourse()"> ></span>
        <span> </span>
      </div>
      <div class="list">
        <div class="title">
          <h2>{{list.title}}</h2>
        </div>
        <div
          v-if="list.content"
          class="con ql-editor"
          v-html="list.content"
        ></div>
        <a-empty
          v-else
          description=""
        />
      </div>
      <div class="recommend">
        <div class="title">
          <h2></h2>
        </div>
        <a-empty
          v-if="ProgramCourse.length==0"
          description=""
        />
        <ul class="list">
          <li
            v-for="item in ProgramCourse"
            :key="item.id"
            @click="todetail(item.id)"
          >
            <img
              :src="item.cover"
              alt=""
            >
            <div class="detail">
              <p>{{item.title}}</p>
              <p>{{item.teacher}}
                <span v-if="item.isFree!=0"></span>
                <span
                  v-else
                  class="you"
                ></span>
              </p>
            </div>
          </li>
        </ul>
      </div>
      <div class="footer">
        <div class="right">
          <div
            class="btn"
            @click="gocultivate"
          ></div>
        </div>
      </div>
    </div>
    <div
      class="gohead"
      v-show="flag"
    >
      <a href="#cultivateDetail"></a>
    </div>

  </div>
</template>

<script>
import { getcultivateDetailApi } from "@/request/api";
export default {
  name: "cultivateDetail",
  data() {
    return {
      flag: false,
      culId: "",
      list: [],
      ProgramCourse: [],
    };
  },
  created() {
    this.culId = this.$route.query.id;
    this.init();
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
  methods: {
    init() {
      this.getToken(getcultivateDetailApi, { programId: this.culId }, (res) => {
        this.list = res.data;
        this.ProgramCourse = res.data.ProgramCourse;
      });
    },
    returnHome() {
      this.$router.push({ path: "/" });
    },
    returncourse() {
      this.$router.push({ path: "/cultivate" });
    },
    gocultivate() {
      this.$router.push({ path: "/cultivate" });
    },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 100) {
        // console.log(scrollTop)
        this.flag = true;
      } else {
        this.flag = false;
      }
    },
    todetail(id) {
      this.$router.push({ path: "/courseInfo", query: { id } });
    },
  },
};
</script>

<style lang="stylus" scoped>
.cultivateDetail {
  background: #eee;

  .banner {
    img {
      width: 100%;
    }
  }

  .content {
    width: 1205px;
    margin: 0 auto;
    min-height: 500px;
    background:#fff;

    .breadCrumb {
      height: 20px;
      padding-bottom: 58px;
      font-family: MicrosoftYaHei;
      font-size: 14px;
      padding-top: 25px;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 1px;
      color: #333333;
      margin-left: 70px;

      span:last-child {
        font-family: MicrosoftYaHei;
        font-size: 14px;
        font-weight: normal;
        letter-spacing: 1px;
        color: #999999;
      }

      span:hover {
        color: #4182fa;
        cursor: pointer;
      }
    }

    .list {
      min-height:200px;
      .title {
        width: 1065px;
        margin: 0 auto;
        border-bottom: 1px solid #eee;

        h2 {
          text-align: center;
          font-size: 26px;
          letter-spacing: 0px;
          color: #000000;
          margin-bottom: 0px;
          // font-weight: bold;
          margin-bottom: 24px;
        }
      }

      .con {
        margin: 0 auto;
        width: 1065px;
        margin-top: 30px;
      }
    }

    .recommend {
      margin-left: 70px;
      .title {
        h2 {
          color: #333;
          font-size: 20px;
          font-weight: 600;
          font-stretch: normal;
          letter-spacing: 0px;
          border-left: 5px solid #4182fa;
          margin-bottom: 0px;
          padding-left: 8px;
        }
      }

      .list {
        display: flex;
        margin-top: 30px;
        flex-wrap: wrap;
        min-height:250px;
        >li {
          margin-right: 20px;
          background-color: #ffffff;
          width: 254px;
          height: 225px;
          cursor: pointer;
          transition:0.3s;

          &:nth-child(4n) {
            margin-right: 0;
          }

          &:hover {
            box-shadow: 0px 0px 9px 0px rgba(73, 83, 104, 0.36);
            margin-top:-4px;
            transition:0.3s;
          }

          img {
            width: 254px;
            height: 143px;
          }

          .detail {
            height: 83px;
            width: 254px;
            border: 1px solid #ebebeb;
            border-top: 0;
            padding: 17px 0 14px 10px;

            p {
              maring: 0;
            }

            p:nth-child(1) {
              color: #333333;
              font-size: 14px;
              margin-bottom: 10px;
              font-weight: 500;
              font-family: MicrosoftYaHei;
              margin-bottom: 13px;
            }

            p:nth-child(2) {
              color: #666;
              font-size: 14px;
              position: relative;

              span {
                position: absolute;
                right: 18px;
                top: 50%;
                transform: translateY(-50%);
                color: #4182fa;
                font-size: 14px;
              }

              .you {
                color: #f4621f;
                font-size: 14px;
              }
            }
          }
        }
      }
    }

    .footer {
      width: 1062px;
      border-top: 2px solid #eee;
      margin: 0 auto;
      height: 89px;
      position: relative;
      margin-top: 70px;

      .right {
        width: 162px;
        height: 46px;
        display: flex;
        align-items: center;
        background-color: #4182fa;
        border-radius: 3px;
        position: absolute;
        right: 0px;
        top: 50px;
        transform: translateY(-50%);

        .btn {
          font-size: 14px;
          background: url('../assets/back.png') no-repeat 125px 15px;
        }
      }
    }
  }
}

.gohead {
  width: 66px;
  height: 58px;
  background-color: #f9f9fa;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 240px;
  bottom: 50px;
  cursor: pointer;

  a {
    width: 33px;
    height: 32px;
    display: inline;
    text-align: center;
    background: url('../assets/gohead3.png') -18px -15px no-repeat;
  }
}

.gohead:hover {
  a {
    background: url('../assets/gohead3.png') -18px -15px no-repeat;
  }
}

.gohead:active {
  a {
    background: url('../assets/gohead4.png') -18px -15px no-repeat;
  }
}
</style>
