<template>
    <div class="filter">
        <a-select @change="handleChange($event,0)" placeholder="" :value="defaultProfession">
            <a-select-option 
                class="level"
                style="width: 250px" 
                :value="item.id" 
                v-for="(item,index) in professionList" 
                :key="index">
                {{item.name}}
            </a-select-option>
        </a-select>
        <a-select @change="handleChange($event,1)" placeholder="" :value="defaultSkill">
            <a-select-option 
                class="skill"
                style="width: 172px"
                :value="item.id" 
                v-for="(item,index) in skillList" 
                :key="index">
                {{item.name}}
            </a-select-option>
        </a-select>
        <a-select @change="handleChange($event,2)" placeholder="" :value="defaultPaper">
            <a-select-option 
                style="width: 175px" 
                :value="item.id" 
                v-for="(item,index) in paperTypeList" 
                :key="index">
                {{item.name}}
            </a-select-option>
        </a-select>
    </div>
</template>
<script>
import {professionListApi,skillListApi} from "@/request/api"
export default {
    name:'examType',
    props:{defaultExamType:Array},
    created(){
        this.init()
    },
    data(){
        return{
            professionList:[],
            skillList:[],
            paperTypeList:[{name:'',id:0},{name:"",id:1},{name:'+',id:2}],
            arr:[],
            defaultProfession:undefined,
            defaultSkill:undefined,
            defaultPaper:undefined,
        }
    },
    methods:{
        init(){
            this.getToken(professionListApi,{},res=>{
                this.professionList=res.data
                this.getToken(skillListApi,{},res=>{
                    this.skillList=res.data
                    if(this.defaultExamType){
                        this.defaultProfession=this.defaultExamType[0]
                        this.defaultSkill=this.defaultExamType[1]
                        this.defaultPaper=this.defaultExamType[2]
                    }
                })
            })
        },
        handleChange(data,type){
            this.arr[type]=data
            if(type==0){
                this.defaultProfession=data
            }else if(type==1){
                this.defaultSkill=data
            }else{
                this.defaultPaper=data
            }
            this.$emit('changeExamType',this.arr)
        }
    }
}
</script>
<style lang="stylus" scoped>
.filter{
    display:flex;
    justify-content: space-between;
}
.filter .level{
    margin-right:10px;
}
.filter .skill{
    margin-right:10px;
}
.filter .ant-select-selection{
    height:40px;
    font-size: 15px;
    line-height: 40px;
    border-radius: 0px;
}
</style>