<template>
  <div class="training">
    <template v-if="bannerList.length>0">
        <div class="banner" v-for="item in bannerList" :key="item.id">
          <a :href="item.link">
              <img :src="item.img" alt="">
          </a>
        </div>
    </template>
    <div class="banner" v-else>
        <!-- <img src="../assets/trainingbanner.png" alt /> -->
        <p>banner</p>
    </div>
    <div class="searchbox">
      <div class="search">
        <h1>
          <em></em>
          <h2></h2>
        </h1>
        <div class="reg">
          <input type="text" placeholder="" v-model="keyword" />
          <img src="../assets/traininglogin.png" alt />
        </div>
        <div class="login">
          <button class="log" @click="search"></button>
          <img src="../assets/chaxun.png" />
        </div>
      </div>
    </div>
    <div class="content">
      <!--  -->
      <div class="jiegou" v-show="show">
        <h2></h2>
        <ul class="play">
          <template v-if="list.length>0">
            <li v-for="item in list" :key="item.id">
              <div class="imgtab">
                <img :src="item.imgUrl" alt />
                <p>
                  <span>
                    <b class="z" @click="godownload(item.imgUrl,isLogin)"></b>
                    <em></em>
                  </span>
                  <b @click="see(item.imgUrl)">
                    <b class="y"></b>
                    <em></em>
                  </b>
                </p>
              </div>
            </li>
          </template>
          <li v-else class="nofind">
            <img description="" src="../assets/trainingnofind.png" alt />
            <p></p>
          </li>
        </ul>
      </div>
    </div>
    <!--  -->
    <a-modal
      title=""
      class="trainmodal"
      width="1000px"
      :footer="null"
      :visible="flag"
      @cancel="flag=false"
      okText=""
    >
      <div class="md">
        <div class="bimg">
          <canvas id="myCanvas" width="889" height="624"></canvas>
        </div>
      </div>
      <div class="he">
        <button class="download" @click="godownload(bimg,isLogin)">
           
          <span></span>
        </button>
      </div>
    </a-modal>
    <loginModal
      :isShow="isShow"
      @closemodal="isShow=false"
      @logined="logined"
      @goregister="goregister"
      @forget="forget"
    ></loginModal>
  </div>
</template>
<script>
import axios from "axios";
import loginModal from "@/components/common/loginModal";
import { gettrainingListApi,getbannerApi} from "@/request/api";
export default {
  name: "training",
  components: { loginModal },
  data() {
    return {
      flag: false,
      show: false,
      keyword: "",
      imgUrl: null,
      list: [],
      arg: null,
      apiUrl: window.g.apiUrl,
      bimg: null,
      isShow: false,
      bannerList:[]
    };
  },
  created() {
    this.init();
    // this.isLogin = localStorage.getItem("isLogin");
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },
  // watch: {
  //   isLogin: function (val, oldval) {},
  // },
  mounted() {
    window.addEventListener("keydown", this.enterClick, false);
  },
  destroyed() {
    window.removeEventListener("keydown", this.enterClick, false);
  },
  methods: {
    init() {
      this.getToken(getbannerApi,{module:4},res=>{
                this.bannerList=res.data
        })
    },
    search() {
      this.show = true;
      this.arg = this.keyword;
      this.getToken(gettrainingListApi, { arg: this.arg }, (res) => {
        this.list = res.data;
        this.imgUrl = this.list.imgUrl;
        // console.log(res.data)
      });
    },
    enterClick(e) {
      if (e.key === "Enter") {
        this.show = true;
        this.arg = this.keyword;
        this.getToken(gettrainingListApi, { arg: this.arg }, (res) => {
          this.list = res.data;
          this.imgUrl = this.list.imgUrl;
          // console.log(res.data)
          // console.log(this.imgUrl)
        });
      }
    },
    godownload(imgUrl, isLogin) {
      if (isLogin == 0) {
        axios({
          url: this.apiUrl + "certificate/download",
          method: "post",
          data: { imgUrl: imgUrl },
          responseType: "blob",
        }).then((res) => {
          if (!res) {
            return;
          }
          let url = window.URL.createObjectURL(res.data);
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", ".jpg");
          document.body.appendChild(link);
          link.click();
        });
        this.isShow = false;
      } else {
        this.isShow = true;
      }
    },
    see(imgUrl) {
      this.flag = true;
      this.imgUrl = imgUrl;

      this.bimg = imgUrl;
      // console.log(this.bimg)
      var img = new Image();
      img.src = this.imgUrl;
      img.onload = () => {
        var convas = document.getElementById("myCanvas");
        var ctx = convas.getContext("2d");
        ctx.drawImage(img, 0, 0, 889, 624);
        ctx.rotate((-30 * Math.PI) / 180); // 
        for (let i = 0; i < 40; i++) {
          ctx.font = "26px microsoft yahei";
          ctx.fillStyle = "rgba(255,255,255,0.8)";
          ctx.fillText(
            "                                                                                                                                                                                                                                                                                        ",
            -1500 + i * 70,
            i * 70 - 450
          );
        }
        ctx.rotate((30 * Math.PI) / 180); //              
      };
    },
    nosee() {
      this.flag = false;
    },
    closeModal() {
      this.isShow = false;
      this.init();
    },
    logined() {
      this.isShow = false;
      this.init();
    },
    goregister() {
      this.$router.push({ path: "/register" }); //
      this.isShow = false;
    },
    forget() {
      this.$router.push({ path: "/forgetModal" });
      this.isShow = false;
    },
  },
};
</script>
<style>
.ant-modal-body {
  padding: 0px;
  font-size: 14px;
  word-wrap: break-word;
}
.ant-modal-title {
  margin: 0;
  color: #666;
  /* font-weight: 600; */
  font-size: 18px;
}
.ant-modal-close-x {
  display: block;
  width: 56px;
  height: 56px;
  font-size: 20px;
  font-style: normal;
  line-height: 56px;
  text-align: center;
  text-transform: none;
  text-rendering: auto;
}
.trainmodal .md {
  width: 1000px;
  text-align: center;
  background: #f0f0f0;
  padding-top: 15px;
  padding-bottom: 15px;
}
.trainmodal .he {
  height: 60px;
  line-height: 60px;
  padding-top: 10px;
}
.trainmodal .he .download {
  width: 140px;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
  background-color: #4182fa;
  border-radius: 3px;
  color: #fff;
  border: 0;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}
.trainmodal .download span {
  background: url("../assets/trainingdownload.png") no-repeat;
  width: 12px;
  height: 15px;
  z-index: 333;
  margin-left: 5px;
}
</style>
<style lang="stylus" scoped>
