<template>
  <div id="layout">
    <div
      class="header"
      :class="navBarFixed==true?'navBarWrap':''"
    >
      <div class="wrap">
        <div class="wrapLeft">
          <div
            class="imgbox"
            @click="gohome"
          >
            <img
              src="@/assets/logo.png"
              alt=""
            >
          </div>
          <ul >
            <li
              v-for="(item,index) in menuList"
              :key="index"
              @click="changPage(item)"
              :class="{'active':current==item}"
            >{{item}}</li>
          </ul>
        </div>
        <div
          v-if="isLogin==0"
          class="newuser"
        >
          <span
            class="loginuser"
            :title="username"
          >{{username.length>8?username.substring(0,8).concat("..."):username}}
          </span>
          <img
            v-if="icon"
            :src="icon"
            alt=""
          >
          <img
            v-else
            src="../assets/course/icon.png"
            alt=""
          >
          <div class="statusModel">
            <p class="roleshow" :title="username">
              <span class="namespan">{{username.length>10?username.substring(0,10).concat("..."):username}}</span> 
                    <span
                  v-if="role==''"
                  class="rolespan"
                ></span>
                <span
                  v-else-if="role==''"
                  class="rolespan"
                ></span>
                <span
                  v-else-if="role==''"
                  class="rolespan"
                ></span>
                <span
                  v-else-if="role==''"
                  class="rolespan"
                ></span>
                <span
                  v-else-if="role==''"
                  class="rolespan"
                ></span>
            </p>
            <p class="mineshow" :class="{'useractive':usershow}" @click="tomine"><span></span></p>
            <p class="loginshow"  @click="removeuser(null)"><span></span></p>
          </div>
          <!-- <span @click="removeuser(null)"></span> -->
        </div>
        <div
          v-else
          class="user"
        >
          <span
            class="log"
            :class="{'active':logshow}"
            @click="isShowBox()"
          ></span>/
          <span
            @click="toregister()"
            class="reg"
            :class="{'active':regshow}"
          ></span>
          <img
            src="../assets/course/icon_no.png"
            alt=""
          >
        </div>
      </div>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
    <div class="footer">
      <div class="top">
        <div class="mod">
          <ul class="ul1">
            <li></li>
            <li>
              <a
                href="http://www.huazhongcnc.com"
                target="_blank"
              ></a>
            </li>
          </ul>
          <ul class="ul2">
            <li></li>
            <li>800-880-0598</li>
            <li></li>
          </ul>
        </div>
      </div>
      <div class="bottom">
        <p>
          <span>
              ICP12013633-4
          </span>
          <span>
          
          </span>
        </p>
        
      </div>
    </div>
    <loginModal
      :isShow="isShow"
      @closemodal="isShow=false"
      @logined="logined"
      @goregister="goregister"
      @forget="forget"
    ></loginModal>
  </div>
</template>
<script>
import { logoutApi } from "@/request/api";
import loginModal from "@/components/common/loginModal";
// import { mapState,mapMutations} from 'vuex'
export default {
  name: "layout",
  components: { loginModal },
  watch: {
    $route(to) {
      let arr = to.path.split("/");
      this.locationPage(arr[arr.length - 1]);
    },

  },
  created() {
    // this.username=localStorage.getItem("username")
    if (localStorage.getItem("username")) {
      this.remindUserInfo();
    }
  },
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
    
    let arr = window.location.pathname.split("/");
    switch (arr[arr.length - 1]) {
      case "":
        this.current = "";
        break;
      case "course":
        this.current = "";
        break;
      case "courseInfo":
        this.current = "";
        break;
      case "catalog":
        this.current = "";
        break;
      case "catalogDetail":
        this.current = "";
        break;
      case "test":
        this.current = "";
        break;
      case "theoryexam":
        this.current = "";
        break;
      case "prexam":
        this.current = "";
        break;
      case "answer":
        this.current = "";
        break;
      case "answerDetail":
        this.current = "";
        break;
      case "answerNew":
        this.current = "";
        break;
      case "cultivate":
        this.current = "";
        break;
      case "cultivateDetail":
        this.current = "";
        break;
      case "teacherLecture":
        this.current = "";
        break;
      case "teacherLectureDetail":
        this.current = "";
        break;
      case "training":
        this.current = "";
        break;
      case "traincenter":
        this.current = "";
        break;
      case "company":
        this.current = "";
        break;
      case "companyDetail":
        this.current = "";
        break;
      case "introduction":
        this.current = "";
        break;
      case "alliance":
        this.current = "";
        break;
      case "talent":
        this.current = "";
        break;
      case "industry":
        this.current = "";
        break;
      case "quality":
        this.current = "";
        break;
      case "Service":
        this.current = "1+X";
        break;
       case "ServiceDetail":
        this.current = "1+X";
        break;
    }
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
    username() {
      return this.$store.state.username;
    },
    icon() {
      return this.$store.state.icon;
    },
    role() {
      return this.$store.state.role;
    },
  },

  data() {
    return {
      menuList: [
        "",
        "",
        "",
        "",
        "",
        "",
        "1+X",
        "",
      ],
      current: "",
      isShow: false,
      navBarFixed: false,
      ishsowuser: true,
      nvashow: true,
      logshow: false,
      regshow: false,
      isShowHeader: false,
      usershow:false
    };
  },
  methods: {
    changPage(data) {
      this.current = data;
      let path = "";
      switch (data) {
        case "":
          path = "/";
          break;
        case "":
          path = "/course";
          break;
        case "":
          path = "/teacherLecture";
          break;
        case "":
          path = "/training";
          break;
        case "":
          path = "/traincenter";
          break;
        case "":
          path = "/company";
          break;
        case "":
          path = "/introduction";
          break;
        case "1+X":
          path = "/Service";
          break;
      }
      this.$router.push({ path: path });
    },
    gohome() {
      this.$router.push({ path: "/" });
    },
    remindUserInfo() {
      this.$store.commit("toggleIsLogin", {
        isLogin: localStorage.getItem("isLogin"),
        role: localStorage.getItem("role"),
        username: localStorage.getItem("username"),
        icon: localStorage.getItem("icon"),
        authentation:localStorage.getItem("authentation"),
      });
    },
    isShowBox() {
      this.isShow = true;
      this.logshow = true;
      this.regshow = false;
    },
    toregister() {
      this.$router.push({ path: "/register" }); //
      this.logshow = false;
      this.regshow = true;
    },
    logined() {
      this.isShow = false;
    },
    tomine() {
         if(this.role==""||this.role==""){
          this.$router.push({ path: "/basicInfos" });
          this.usershow=true
          }else{
              this.$router.push({ path: "/basicInfo" });
              this.usershow=true
          }
      
      
    },
    goregister() {
      this.$router.push({ path: "/register" }); //
      this.isShow = false;
      this.logshow = false;
      this.regshow = true;
    },
    forget() {
      this.$router.push({ path: "/forgetModal" });
      this.isShow = false;
    },
    //
    removeuser() {
      this.$confirm({
        title:'',
        okText:'',
        cancelText:'',
        onOk:()=>{
          this.getToken(logoutApi, {}, (res) => {
            if (res.code == 200) {
              localStorage.setItem("isLogin", 1);
              localStorage.setItem("role", "");
              localStorage.setItem("username", "");
              localStorage.setItem("icon", "");
              localStorage.setItem("token", "");
              localStorage.setItem("authentation", "");
              localStorage.setItem("taskId", "");
              localStorage.setItem("chapterId", "");
              this.$store.commit("toggleIsLogin", {
                isLogin: 1,
                role: null,
                username: null,
                icon: null,
                token: null,
                authentation: null,
              });
              //
              this.$store.commit("getIds",{
                chapterId:null,
                taskId:null
              })
              //
              this.$router.push({ path: "/" });
            }
          });
        },
        onCancel(){
          // console.log(123)
        }
      })
    },
    locationPage(page) {
      if (
        page == "basicInfo" ||
        page == "manClass" ||
        page == "manstudent" ||
        page == "message" ||
        page == "myanswer" ||
        page == "myask" ||
        page == "myexam" ||
        page == "myPurge" ||
        page == "myreply" ||
        page == "myscore" ||
        page == "mystudent" ||
        page == "mysubject" ||
        page == "mytrain" ||
        page == "studyview" ||
        page == "trainexam" ||
        page == "myviedo" ||
        page == "myClass"||
        page == "basicInfos" ||
        page == "Site"||
        page == "require"||
        page == "mark"||
        page == "examplan"||
        page == "newexamplan"||
        page == "report"||
        page == "audit"
      ) {
        this.current = "";
      } else {
        // this.isShowHeader = false;
      }
      switch (page) {
        case "":
          this.current = "";
          break;
        case "course":
          this.current = "";
          break;
        case "courseInfo":
          this.current = "";
          break;
        case "catalog":
          this.current = "";
          break;
        case "catalogDetail":
          this.current = "";
          break;
        case "test":
          this.current = "";
          break;
        case "theoryexam":
          this.current = "";
          break;
        case "prexam":
          this.current = "";
          break;
        case "answer":
          this.current = "";
          break;
        case "answerDetail":
          this.current = "";
          break;
        case "answerNew":
          this.current = "";
          break;
        case "cultivate":
          this.current = "";
          break;
        case "cultivateDetail":
          this.current = "";
          break;
        case "teacherLecture":
          this.current = "";
          break;
        case "teacherLectureDetail":
          this.current = "";
          break;
        case "training":
          this.current = "";
          break;
        case "traincenter":
          this.current = "";
          break;
        case "company":
          this.current = "";
          break;
        case "companyDetail":
          this.current = "";
          break;
        case "introduction":
          this.current = "";
          break;
        case "alliance":
          this.current = "";
          break;
        case "allianceDetail":
          this.current = "";
          break;
        case "basicInfo":
          this.current = " ";
          break;
        case "basicInfos":
          this.current = " ";
          break;
        case "register":
          this.current = " ";
          break;
        case "forgetModal":
          this.current = " ";
          break;
        case "Service":
          this.current = "1+X";
          break;
        case "ServiceDetail":
          this.current = "1+X";
          break;
        case "examDetail":
          this.current = "1+X";
          break;
      }
    },
    // watchScroll() {
    //   var scrollTop =
    //     window.pageYOffset ||
    //     document.documentElement.scrollTop ||
    //     document.body.scrollTop;
    //   //   50 
    //   if (scrollTop > 80) {
    //     this.navBarFixed = true;
    //   } else {
    //     this.navBarFixed = false;
    //   }
    // },
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      //   50 
      if (scrollTop > 80 && !this.navBarFixed) {
        this.navBarFixed = true;
      } else if (scrollTop < 2 && this.navBarFixed) {
        this.navBarFixed = false;
        // console.log(`scrollTop${scrollTop}`);
      }
    },
  },
};
</script>
<style lang="stylus">

</style>
<style lang="stylus" scoped>
