                font-family: MicrosoftYaHei;
                font-size: 12px; 
                background-color: #f34b3f;
                border-radius: 2px;
                position:absolute;
                text-align center
                right:0;
                top:0;
                color: #fff;
             }
          }
          .mineshow{
            display:flex;
            align-items center;
            height: 40px;
            line-height: 40px;
            span{
              width:15px;
              height:16px;
              display:block;
              margin-right:9px;
              background:url("../assets/mine/mine1.png")
            }
          }
          .loginshow{
            display:flex;
            align-items center;
            height: 40px;
            line-height: 40px;
            span{
              width:15px;
              height:16px;
              display:block;
              margin-right:9px;
              background:url("../assets/mine/login1.png")
            }
          }
          .loginshow:hover{
            background-color: #4182fa;
            color:#fff;
            span{
              background:url("../assets/mine/login2.png")
            }
          }
          .mineshow:hover{
            background-color: #4182fa;
            color:#fff;
            span{
              background:url("../assets/mine/mine2.png")
            }
          }

          p:last-child {
            border-bottom: 0px;
          }
        }
      }
        
      .newuser:hover {
        .statusModel {
          display: block;
        }
      }
    }
  }
  // .content{
  //   padding-top: 80px;
  //   margin-top: -80px;
  // }
  .navBarWrap {
    position: fixed;
    width: 100%;
    left: 50%;
