<template>
  <div class="manstudent">
    <div class="title">
      <p>
        
        <span class="ban">{{title}}</span>
        <span class="course">{{courseTitle}}</span>
      </p>
      <p class="back" @click="tomanClass">
        <img src="../../assets/mine/back.png" alt />
        
      </p>
    </div>
    <div class="sea">
      <span></span>
      <div class="right">
        <input type="text" placeholder="" v-model="key" />
        <button class="ban" @click="searchbtn"></button>
        <button class="enterbtn" @click="joinbtn"></button>
        <button class="addbtn" @click="addbtn"></button>
      </div>
    </div>
    <div class="box">
      <a-table
        :columns="columns"
        :data-source="list"
        :rowKey="record=>record.id"
        :locale="{'emptyText': ''}"
        bordered
      >
        <div slot="action" class="tableAction" slot-scope="index,record">
          <span @click="rec(record)"></span>
          <span @click="del(record.id,record.name)"></span>
        </div>
      </a-table>
    </div>
    <a-modal
      title=""
      :visible="addshow"
      @ok="handleOk"
      @cancel="addcancel"
      class="addclassmodal"
      okText=""
      cancelText=""
    >
      <eform :formData="formDate" class="form" ref="form">
        <div slot="name">
          <a-input
            v-decorator="['name',{rules: [{ required: true, message: ' ' },{validator:validatorName}]}]"
            class="input"
          />
        </div>
        <div slot="gender">
          <a-radio-group v-decorator="['gender',{rules: [{ required: true, message: '' }]}]">
            <a-radio :value="1"></a-radio>
            <a-radio :value="2"></a-radio>
          </a-radio-group>
        </div>
        <div slot="idnum">
          <a-input
            v-decorator="['idnum',{rules: [{ required: true, message: '' },{validator:validatorIdnum}]}]"
            class="input"
          />
        </div>
        <div slot="phone">
          <a-input
            v-decorator="['phone',{rules: [{ required: true, message: '' },{validator:validatorphone}]}]"
            class="input"
          />
        </div>
        <div slot="school">
          <a-input
            v-decorator="['school',{rules: [{ required: true, message: '' }]}]"
            class="input"
          />
        </div>
        <div slot="mojor">
          <a-input v-decorator="['mojor']" class="input" />
        </div>
      </eform>
    </a-modal>
    <a-modal
      title=""
      :visible="recshow"
      @ok="recOk"
      @cancel="reccancel"
      :destroyOnClose="true"
      class="recmodal"
      okText=""
      cancelText=""
    >
      <eform :formData="formrec" class="form" :fields="fields" ref="form">
        <div slot="name">
          <a-input
            v-decorator="['name',{rules: [{ required: true, message: ' ' },{validator:validatorName}]}]"
            class="input"
          />
        </div>
        <div slot="gender">
          <a-radio-group v-decorator="['gender',{rules: [{ required: true, message: '' }]}]">
            <a-radio :value="1"></a-radio>
            <a-radio :value="2"></a-radio>
          </a-radio-group>
        </div>
        <div slot="idnum">
          <a-input
            v-decorator="['idnum',{rules: [{ required: true, message: '' },{validator:validatorIdnum}]}]"
            class="input"
          />
        </div>
        <div slot="phone">
          <a-input
            v-decorator="['phone',{rules: [{ required: true, message: '' },{validator:validatorphone}]}]"
            class="input"
          />
        </div>
        <div slot="school">
          <a-input
            v-decorator="['school',{rules: [{ required: true, message: '' }]}]"
            class="input"
          />
        </div>
        <div slot="mojor">
          <a-input v-decorator="['mojor']" class="input" />
        </div>
      </eform>
    </a-modal>
    <downloadAndUpload
      :uploadModal="uploadModal"
      @uploadcancel="uploadModal=false"
      :templateApiurl="'personalCenter/downloadTemplate'"
      :fileName="fileName"
      @upload="upload"
    ></downloadAndUpload>
    <!-- <div class="pagemodel">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>-->
  </div>
</template>

<script>
const columns = [
  {
    title: "",
    dataIndex: "index",
  },
  {
    title: "",
    dataIndex: "account",
    width: 120,
  },
  {
    title: "",
    dataIndex: "name",
  },
  {
    title: "",
    dataIndex: "sex",
  },
  {
    title: "",
    dataIndex: "idnum",
  },
  {
    title: "",
    dataIndex: "phone",
  },
  {
    title: "",
    dataIndex: "school",
    width: 120,
  },
  {
    title: "",
    dataIndex: "mojor",
    width: 120,
  },
  {
    title: "",
    scopedSlots: { customRender: "action" },
    width: 110,
  },
];
import eform from "@/components/common/form";
// import pagination from "@/components/common/pagination";
import downloadAndUpload from "@/components/common/downloadAndUpload";
import {
  getstudentApi,
  getaddstudentApi,
  getaddstudentclassApi,
  getimportUserToClassApi,
  getremovestudentApi,
  getupdatastudentApi,
} from "@/request/api";
export default {
  name: "manstudent",
  components: { eform, downloadAndUpload },
  data() {
    const formDate = [
      {
        label: "",
        scopedSlots: { customRender: "name" },
      },
      {
        label: "",
        scopedSlots: { customRender: "gender" },
      },
      {
        label: "",
        scopedSlots: { customRender: "idnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "phone" },
      },
      {
        label: "",
        scopedSlots: { customRender: "school" },
      },
      {
        label: "",
        scopedSlots: { customRender: "mojor" },
      },
    ];
    const formrec = [
      {
        label: "",
        scopedSlots: { customRender: "name" },
      },
      {
        label: "",
        scopedSlots: { customRender: "gender" },
      },
      {
        label: "",
        scopedSlots: { customRender: "idnum" },
      },
      {
        label: "",
        scopedSlots: { customRender: "phone" },
      },
      {
        label: "",
        scopedSlots: { customRender: "school" },
      },
      {
        label: "",
        scopedSlots: { customRender: "mojor" },
      },
    ];
    let validatorName = (rule, value, callback) => {
      let reg = /^[\u4e00-\u9fa5]/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorIdnum = (rule, value, callback) => {
      let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    let validatorphone = (rule, value, callback) => {
      let reg = /^1[3456789]\d{9}$/;
      if (value == "" || reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };

    return {
      columns,
      addshow: false,
      joinshow: true,
      formDate,
      validatorName,
      validatorIdnum,
      validatorphone,
      fields: null,
      searchInfo: {
        page: 1,
        pageSize: 10,
        keyword: "",
        classId: "",
      },
      pagination: { total: 0, pageSize: 10 },
      key: "",
      courseTitle: "",
      title: "",
      list: [],
      classId: "",
      sex: "",
      uploadModal: false,
      fileName: ".xlsx",
      formrec,
      recshow: false,
      userId: "",
      newId: "",
    };
  },
  created() {
    this.classtitle = this.$route.query.classtitle;
    this.title = this.$route.query.title;
    this.classId = this.$route.query.id;
    this.init();
  },
  methods: {
    init() {
      this.searchInfo.classId = this.classId;
      this.getToken(getstudentApi, this.searchInfo, (res) => {
        this.title = res.data.title;
        this.courseTitle = res.data.courseTitle;
        this.list = res.data.Student.map((item, index) => ({
          index: (this.searchInfo.page - 1) * 10 + index + 1,
          account: item.account,
          name: item.realname,
          sex: item.gender,
          idnum: item.idCardNum,
          phone: item.telephone,
          school: item.instituteName,
          mojor: item.major,
          id: item.id,
        }));
      });
    },
    //
    tomanClass() {
      this.$router.push({ path: "/manClass" });
    },
    //
    joinbtn() {
      this.uploadModal = true;
    },
    //   
    addbtn() {
      this.addshow = true;
    },
    //
    handleOk() {
      this.$refs.form.form.validateFields().then((res) => {
        if (res.gender == 1) {
          this.sex = "";
        } else if (res.gender == 2) {
          this.sex = "";
        }
        //
        this.getToken(
          getaddstudentApi,
          {
            realname: res.name,
            gender: this.sex,
            major: res.mojor,
            telephone: res.phone,
            idCardNum: res.idnum,
            instituteName: res.school,
          },
          (value) => {
            //
            this.getToken(
              getaddstudentclassApi,
              { userId: value.data.id, classId: this.classId },
              (req) => {
                if (req.code == 200) {
                  this.$message.success("");
                  this.init();
                  this.addshow = false;
                  this.$refs.form.form.resetFields(["name"]);
                  this.$refs.form.form.resetFields(["gender"]);
                  this.$refs.form.form.resetFields(["mojor"]);
                  this.$refs.form.form.resetFields(["phone"]);
                  this.$refs.form.form.resetFields(["idnum"]);
                  this.$refs.form.form.resetFields(["school"]);
                }
              }
            );
          }
        );
      });
    },
    //
    addcancel() {
      this.addshow = false;
      this.$refs.form.form.resetFields(["name"]);
      this.$refs.form.form.resetFields(["gender"]);
      this.$refs.form.form.resetFields(["mojor"]);
      this.$refs.form.form.resetFields(["phone"]);
      this.$refs.form.form.resetFields(["idnum"]);
      this.$refs.form.form.resetFields(["school"]);
    },
    //
    changePage(currentPage) {
      this.searchInfo.page = currentPage;
      this.init();
    },
    //
    searchbtn() {
      this.searchInfo.keyword = this.key;
      this.init();
    },
    //
    del(id, title) {
      this.newId = id;
      this.$confirm({
        title: `"${title}"`,
        okText: "",
        cancelText: "",
        onOk: () => {
          this.getToken(
            getremovestudentApi,
            { classId: this.classId, userId: this.newId },
            (res) => {
              if (res.code == 200) {
                this.init();
                this.$message.success("");
              }
            }
          );
        },
        onCancel() {
          // console.log('Cancel');
        },
      });
    },
    upload(data) {
      let formData = new FormData();
      formData.append("classId", this.classId);
      formData.append("file", data[0]);
      this.getToken(getimportUserToClassApi, formData, (res) => {
        if (res.code == 200) {
          this.uploadModal = false;
          this.init();
          this.$confirm({
            title: `${res.message}"`,
            okText: "",
            cancelText: "",
            onOk: () => {
              this.init();
              this.uploadModal = false;
            },
            onCancel() {
              // console.log('Cancel');
            },
          });
        }
      });
    },
    rec(data) {
      // console.log(data)
      this.recshow = true;
      this.userId = data.id;
      this.fields = data;
      this.fields.gender = data.gender == "" ? 1 : 2;
    },
    //
    recOk() {
      // this.$refs.form.form.setFieldsValue(["name"])=
      // this.$refs.form.form.setFieldsValue(["gender"])=
      // this.$refs.form.form.setFieldsValue(["mojor"])=
      // this.$refs.form.form.setFieldsValue(["phone"])=
      // this.$refs.form.form.setFieldsValue(["idnum"])=
      // this.$refs.form.form.setFieldsValue(["school"])=
      this.$refs.form.form.validateFields().then((res) => {
        if (res.gender == 1) {
          this.sex = "";
        } else if (res.gender == 2) {
          this.sex = "";
        }
        this.getToken(
          getupdatastudentApi,
          {
            userId: this.userId,
            realname: res.name,
            gender: this.sex,
            major: res.mojor,
            telephone: res.phone,
            idCardNum: res.idnum,
            instituteName: res.school,
          },
          (value) => {
            if (value.code == 200) {
              this.$message.success("");
              this.init();
              this.$refs.form.form.resetFields(["name"]);
              this.$refs.form.form.resetFields(["gender"]);
              this.$refs.form.form.resetFields(["mojor"]);
              this.$refs.form.form.resetFields(["phone"]);
              this.$refs.form.form.resetFields(["idnum"]);
              this.$refs.form.form.resetFields(["school"]);
              this.recshow = false;
            }
          }
        );
      });
    },
    //
    reccancel() {
      this.recshow = false;
      this.$refs.form.form.resetFields(["name"]);
      this.$refs.form.form.resetFields(["gender"]);
      this.$refs.form.form.resetFields(["mojor"]);
      this.$refs.form.form.resetFields(["phone"]);
      this.$refs.form.form.resetFields(["idnum"]);
      this.$refs.form.form.resetFields(["school"]);
    },
  },
};
</script>
<style >
.manstudent .ant-table table {
  text-align: center;
}
.manstudent thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
  text-align: center;
  font-weight: bolder;
  font-size: 14px;
  /* background-color: #eaeef7; */
  height: 36px;
}
.manstudent .ant-table-thead > tr > th {
  color: #333;
  font-weight: 600;
  font-size: 14px;
  /* background-color: #eaeef7; */
  height: 30px;
}
.manstudent .ant-table-bordered .ant-table-thead > tr > th,
.manstudent .ant-table-bordered .ant-table-tbody > tr > td {
  border-right: 1px solid #e8e8e8;
  text-align: center;
}
.manstudent .ant-table-pagination.ant-pagination {
  text-align: center;
  margin: 0px auto;
  margin-top: 50px;
  /* display: none; */
}
.recmodal .ant-modal-body {
  margin-top: 20px;
  padding: 0px;
  font-size: 14px;
  word-wrap: break-word;
}
</style>
<style lang="stylus" scoped>
.manstudent {
  margin-bottom: 50px;

  .title {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-bottom: 1px solid #eff2f2;
    display: flex;
    align-items: center;
    justify-content: space-between;

    p {
      height: 45px;
      line-height: 45px;
      margin-left: 30px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
      margin-bottom: 0px;

      .ban {
        color: #c56b2f;
        font-size: 14px;
        margin-left: 20px;
      }

      .course {
        color: #c56b2f;
        font-size: 14px;
        margin-left: 10px;
      }
    }

    .back {
      color: #4281ff;
      width: 50px;
      height: 30px;
      font-size: 14px;
      margin-right: 20px;
      line-height: 30px;
      cursor: pointer;

      img {
        width: 15px;
        height: 15px;
        margin-top: -3px;
      }
    }
  }

  .sea {
    height: 45px;
    margin-left: 30px;
    margin-right:30px;
    margin-top: 20px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    position: relative;

    span {
      color: #333;
      font-size: 16px;
      width:435px;
    }

    .right {
      flex:1;
      justify-content space-around;
      input {
        outline: none;
        width: 220px;
        height: 35px;
        border: solid 1px #d4d4d4;
        text-indent: 8px;
      }

      .ban {
        outline: none;
        width: 60px;
        height: 35px;
        background-color: #4281ff;
        color: #ffffff;
        font-size: 14px;
        cursor: pointer;
        border: 0;
      }

      .enterbtn {
        width: 75px;
        height: 35px;
        line-height: 30px;
        background: #169bdf;
        color: #fff;
        font-size: 14px;
        border: 0;
        border-radius: 3px;
        outline: none;
        // position: absolute;
        // top: 50%;
        // transform: translateY(-50%);
        // right: 120px;
        cursor: pointer;
        margin-left:30px;
      }

      .addbtn {
        width: 75px;
        height: 35px;
        line-height: 30px;
        background-color: #ff904f;
        color: #fff;
        font-size: 14px;
        border: 0;
        border-radius: 3px;
        outline: none;
        // position: absolute;
        // top: 50%;
        // transform: translateY(-50%);
        // right: 20px;
        cursor: pointer;
        margin-left:30px;
      }
    }
  }

  .box {
    margin-top: 30px;
    margin-left: 30px;
    margin-right: 30px;
  }

  .pagemodel {
    margin-top: 30px;
    padding-top: 30px;
  }
}
</style>
