<template>
  <div class="myviedo">
    <div class="title">
      <h2></h2>
    </div>
    <ul class="nav">
      <li
        @click="load"
        :class="{'active':loadshow}"
      >
      <p></p>
      </li>
      <li
        @click="nostart"
        :class="{'active':nostartshow}"
      >
      <p></p>
      </li>
      <li
        @click="finish"
        :class="{'active':finishshow}"
      >
      <p></p>
      </li>
    </ul>
    <a-empty
      class="empty"
      v-if="list.length==0"
      description=""
    />
    <div
      class="box"
      v-for="item in list"
      :key="item.id"
    >
      <div class="left" @click="toDetail(item.id)">
        <img
          :src="item.cover"
          alt=""
        >
        <template v-if="item.status==2">
          <div class="lectureTag1">
            <img
              src="../../assets/zhibo.png"
              alt=""
            >
            <div style="margin-left:10px"> </div>
          </div>
        </template>
        <template v-else-if="item.status==1">
          <div class="lectureTag">
            <div style="margin-left:45px"> </div>
          </div>
        </template>
        <template v-else>
          <div class="lectureTag">
            <img
              src="../../assets/zhibo.png"
              alt=""
            >
            {{item.startTime.slice(5,item.startTime.length-3)}} 
          </div>
        </template>
      </div>
      <div class="right" @click="toDetail(item.id)">
        <div
          class="lectureTitle"
          :title="item.title"
        >{{item.title}}</div>
        <div class="teacher">
          <img src="../../assets/teacher.png" />{{item.teacher}} <img src="../../assets/num.png" />{{item.appointNum}}
        </div>
        <div class="time">
          <p>{{item.startTime}}  {{item.endTime}}</p>
          <p
            v-if="item.joined"
            style="margin-top:12px"
          >{{item.status==2?'':''}}<span @click.prevent.stop.once="toRealLecture(item.url||item.playback)">{{item.status==2?item.playback:item.url}}</span></p>
        </div>
      </div>
    </div>
    <div class="pagemodel">
      <pagination
        :pagination="pagination"
        @changePage="changePage"
      ></pagination>
    </div>
  </div>
</template>

<script>
import { getlectureApi } from "@/request/api";
import pagination from "@/components/common/pagination";
export default {
  name: "myviedo",
  components: { pagination },
  data() {
    return {
      loadshow: true,
      nostartshow: false,
      finishshow: false,
      info: {
        status: 1,
        page: 1,
        pageSize: 4,
      },
      pagination: { total: "", pageSize: 4 },
      list: [],
    };
  },
  created() {
    this.init();
  },
  methods: {
    toRealLecture(id) {
      window.open(id);
    },
    init() {
      this.getToken(getlectureApi, this.info, (res) => {
        this.list = res.data.rows;
        this.pagination.total = res.data.count;
      });
    },
    load() {
      (this.loadshow = true),
        (this.nostartshow = false),
        (this.finishshow = false);
      this.info.status = 1;
      this.init();
    },
    nostart() {
      (this.loadshow = false),
        (this.nostartshow = true),
        (this.finishshow = false);
      this.info.status = 0;
      this.init();
    },
    finish() {
      (this.loadshow = false),
        (this.nostartshow = false),
        (this.finishshow = true);
      this.info.status = 2;
      this.init();
    },
    changePage(currentPage) {
      this.info.page = currentPage;
      this.init();
    },
    toDetail(id) {
      this.$router.push({ path: "/teacherLectureDetail", query: { id: id } });
    },
  },
};
</script>
<style>
.empty {
  margin: 0 auto;
  margin-top: 30px;
}
</style>
<style lang="stylus" scoped>
.myviedo {
  margin-bottom: 40px;
  .title {
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    border-bottom:1px solid #eff2f2;
    h2 {
      height:45px;
      line-height: 45px;
      margin-left: 30px;
      font-size: 18px;
      font-weight: bold;
      color: #333333;
    }
  }

  .nav {
    display: flex;
    height: 45px;
    line-height: 45px;
    margin-left: 10px;
    margin-top: 20px;

    li {
      width: 100px;
      margin-right: 20px;
      height: 35px;
      line-height: 35px;
      font-size: 16px;
      color: #333333;
      text-align: center;
      cursor pointer;
       p{
        width: 21px;
        height: 2px;
        background-color: #4281ff;
        border-radius: 1px;
        text-align center;
        margin:0 auto;
        display:none
      }
    }
    li:last-child {
      margin-right: 0;
    }
    .active {
      color: #4281ff;
      font-weight: 600;
      p{
        display:block;
      }
    }
  }
  >.box {
    margin-left: 30px;
    display: flex;
    cursor: pointer;
    height: 200px;
    margin-right: 30px;
   background-color: #fafafa;
    display: flex;
    margin-bottom: 20px;
    margin-top: 35px;
    cursor: pointer;

    &:hover {
      box-shadow: 0px 0px 10px #eee;
      .lectureTitle{
        color: #4182fa;
      }
    }

    .left {
      width: 350px;
      position: relative;

      >img {
        width: 100%;
        height: 100%;
      }

      .lectureTag {
        position: absolute;
        top: 8px;
        left: -3px;
        width: 168px;
        height: 40px;
        background: url('../../assets/video1.png');
        display: flex;
        align-items: center;
        color: #fff;
        padding-bottom: 5px;
        padding-left: 15px;

        img {
          margin-right: 10px;
        }
      }

      .lectureTag1 {
        position: absolute;
        top: 8px;
        left: -3px;
        width: 113px;
        height: 40px;
        background: url('../../assets/video3.png');
        display: flex;
        align-items: center;
        color: #fff;
        padding-bottom: 5px;
        padding-left: 15px;
      }
    }
  }

  .right {
    padding: 20px 20px 30px 36px;
    flex: 1;
    position: relative;
  }

  .lectureTitle {
    font-size: 18px;
    width:400px;
    color: #333;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    font-weight:bolder;

  }

  .teacher {
    font-size: 14px;
    color: #999;
    margin-top: 15px;
    display: flex;
    align-items: center;

    img {
      margin-right: 10px;

      &:nth-child(2) {
        margin-left: 50px;
      }
    }
  }

  .time {
    height: 80px;
    background: #f0f0f0;
    margin-top: 20px;
    padding: 12px 0 12px 12px;

    p {
      margin: 0;
      color: #666;
      font-size: 14px;

      span {
        color: #4182fa;
        cursor: pointer;
      }
    }
  }

  .teaBtn {
    position: absolute;
    bottom: 22px;
    width: 120px;
    height: 34px;
    line-height: 34px;
    text-align: center;
    background: #4182fa;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    border-radius: 3px;
  }

  .pagemodel {
    margin-top: 40px;
  }
}
</style>
